import React, { useMemo } from 'react';
import { useSelector } from 'react-redux';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';
import { Page } from '@mssgme/userpage-core';
import { EmptyPagePreview } from '../EmptyPagePreview';

export function PagePreview({ website: websitePreview, ...rest }) {
    const { website, page } = useWebsitePageBySlugs();
    const pageDraft = useSelector((state) => state.ui.pagePreview);
    const websiteData = useMemo(() => ({ ...website, ...websitePreview }), [website, websitePreview]);

    if (websiteData._id) {
        const hasActiveBlocks =
            (page ? page.blocks.filter((block) => block.enabled).length > 0 : false) ||
            (pageDraft && pageDraft.blocks.length > 0);

        if (hasActiveBlocks) {
            return <Page website={websiteData} page={pageDraft || page} {...rest} />;
        }
    }

    return <EmptyPagePreview hasEmptyBlocksTip website={websiteData} {...rest} />;
}

PagePreview.propTypes = {
    website: LandingsPropTypes.websitePartial,
};
