import { useContext, useLayoutEffect } from 'react';

import { useForceUpdate } from '@mssgme/hooks';
import { SelectionRangesContext } from './SelectionRangesContext';
import { useClientRect } from './useClientRect';

export const useSelectionItem = (id, isSelected, index = 0) => {
    const onTransitionEnd = useForceUpdate();
    const { hash, update } = useContext(SelectionRangesContext);
    const [ref, area] = useClientRect([index, id, isSelected, onTransitionEnd.count, hash]);

    useLayoutEffect(() => {
        if (!isSelected) {
            return;
        }

        update(id, area);

        return () => {
            update(id);
        };
    }, [id, isSelected, area.top, area.height]);

    return [{ ref, onTransitionEnd }, area];
};
