import React from 'react';

import { AnimateableRoutes } from '@mssgme/ui';
import { GuideBuilder, PageSetup } from './Page';
import { Help, SortHelp } from './Sheets';
import { websiteSettingsRouting } from './WebsiteSettingsRouting';
import { pagesRouting } from './PagesRouting';
import { shareRouting } from './ShareRouting';
import { pageStylingRouting } from './PageStylingRouting';
import { publishRouting } from './PublishRouting';

const routing = [
    { path: 'setup', component: PageSetup, exact: false },
    { path: 'sort', component: SortHelp },
    { path: 'help', component: Help },
    { path: 'guide', component: GuideBuilder },
    pagesRouting,
    websiteSettingsRouting,
    pageStylingRouting,
    publishRouting,
    shareRouting,
];

export const LandingsBuilderRouter = () => <AnimateableRoutes routing={routing} />;
