import { useMemo } from 'react';
import { cloneDeep, merge } from 'lodash';

import { CRM_IDENTITY } from '../../products';
import { useWebsiteData } from '../../hooks';

/**
 * @returns {typeof CRM_IDENTITY} CRM data
 */
export const useCRMData = () => {
    const { crm } = useWebsiteData();

    return useMemo(() => merge(cloneDeep(CRM_IDENTITY), crm), [crm]);
};
