import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiTrash } from 'react-icons/fi';

import {
    Feature,
    FormikProvider,
    IconButton,
    SaveButton,
    StreamlinedScreen,
    TextField,
    ToggleField,
    useEnhancedFormik,
} from '@mssgme/ui';
import { useNavigationUtils } from '@mssgme/hooks';
import { useProductsContext } from '../../../hooks';
import { ProductsField } from '../../../UI';
import { useCollectionEdit } from './useCollectionEdit';

export function CollectionEdit({ collectionId }) {
    const { t } = useTranslation('products');
    const { router, isSpecific, modal } = useProductsContext();
    const { collection, isFetching, isNew, isRedirecting, handleSubmit, handleRemoveClick } = useCollectionEdit(
        collectionId
    );
    const { generateUrl } = useNavigationUtils();
    const history = useHistory();
    const title = (collection.default ? t('labels.allProducts') : collection.name) || t('CollectionEdit.title');
    const formik = useEnhancedFormik({
        initialValues: collection,
        onSubmit: handleSubmit,
    });

    const handleRestrictedBack = useCallback(() => history.push(generateUrl('..')), [generateUrl]);

    useEffect(() => {
        formik.setFieldValue('products', collection.products);
        formik.setFieldTouched('products', false);
    }, [collection.products]);

    return (
        <Feature modal padding={false} rule="collections:edit" subject={collection} onClose={handleRestrictedBack}>
            <FormikProvider value={formik}>
                <StreamlinedScreen
                    modal={modal}
                    fullHeight={!isNew}
                    editing
                    guardChanges={!isRedirecting}
                    title={title}
                    rightButton={
                        <>
                            <SaveButton nopadding btnStyle="transparent">
                                {isNew ? t('ui:labels.save') : t('ui:labels.apply')}
                            </SaveButton>
                            {!(formik.dirty || isNew || collection.default) && (
                                <IconButton
                                    title={t('ui:button.remove')}
                                    disabled={isFetching}
                                    onClick={handleRemoveClick}
                                >
                                    <FiTrash size={24} color="red" />
                                </IconButton>
                            )}
                        </>
                    }
                    backPath={collectionId ? router.collection.toViewProducts(collectionId) : router.collection.toAll()}
                >
                    <form
                        noValidate
                        autoComplete="off"
                        onSubmit={formik.handleSubmit}
                        style={{
                            display: 'grid',
                            height: '100%',
                            gridTemplateRows: 'auto 1fr',
                            gridTemplateColumns: '1fr',
                        }}
                    >
                        <StreamlinedScreen.Section style={{ marginTop: isNew ? 0 : 8 }}>
                            {!isNew && !formik.values.published && (
                                <ToggleField
                                    builderInput
                                    name="published"
                                    label={t('CollectionEdit.fields.name')}
                                    disabled={isFetching || collection.default}
                                />
                            )}

                            <TextField
                                builderInput
                                name="name"
                                maxLength={100}
                                disabled={isFetching || collection.default}
                                autoFocus={isNew}
                                placeholder={t('CollectionEdit.placeholders.name')}
                            />
                        </StreamlinedScreen.Section>

                        {!isNew && (
                            <StreamlinedScreen.Section
                                style={{ padding: '6px 0 0 0', minHeight: 0, overflow: 'hidden' }}
                            >
                                <ProductsField
                                    collectionId={collection._id}
                                    name="products"
                                    label={t('CollectionEdit.fields.products')}
                                    toggleLabel={isNew ? t('ui:labels.add') : t('ui:labels.change')}
                                    disabled={isFetching || collection.default || !isSpecific}
                                />
                            </StreamlinedScreen.Section>
                        )}
                    </form>
                </StreamlinedScreen>
            </FormikProvider>
        </Feature>
    );
}

CollectionEdit.propTypes = {
    collectionId: PropTypes.string,
};
