import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { FaLock } from 'react-icons/fa';

import { ContentOverlay, PlanBadge } from '@mssgme/ui';

import styles from './SortableBlocksList.module.scss';

export const Overlay = ({ block, warning }) => {
    const { t } = useTranslation('landings');

    return (
        <ContentOverlay
            dashed
            className={styles.cropped}
            overlay={
                <>
                    {t('labels.block.hidden', {
                        kind: t(`admin-ui:labels.block.kinds.${block.kind}`),
                    })}
                    {warning && (
                        <PlanBadge plan={warning.target} style={{ position: 'absolute', right: -12 }}>
                            <FaLock size={8} />{' '}
                        </PlanBadge>
                    )}
                </>
            }
        />
    );
};

Overlay.propTypes = {
    block: PropTypes.object.isRequired,
    warning: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
};
