import React from 'react';

import { isSameArray } from '@mssgme/helpers';
import FieldRow from './FieldRow';
import { ButtonChoice } from '../Inputs';
import { ChoiceFieldProps } from './types';

export default function ChoiceField({ ordered, mask, unmask, onChange: parentChange, ...props }) {
    return (
        <FieldRow {...props}>
            {({
                field: { name, value, onChange, ...rest },
                form: { setFieldValue, setFieldTouched },
                meta: { initialValue },
            }) => (
                <ButtonChoice
                    name={name}
                    value={unmask ? unmask(value) : value}
                    onChange={(value) => {
                        const normalized = mask ? mask(value) : value;
                        let dirty = props.multiple
                            ? !isSameArray(normalized, initialValue, { ordered })
                            : initialValue !== normalized;

                        setFieldValue(name, dirty ? normalized : initialValue);
                        setFieldTouched(name, dirty, dirty);

                        parentChange && parentChange(name, normalized);
                    }}
                    {...rest}
                />
            )}
        </FieldRow>
    );
}

ChoiceField.propTypes = ChoiceFieldProps;
