import React from 'react';

import { DropDown, DropDownColor } from './DropDown';
import { ToolbarButton } from './ToolbarButton';
import { RichEditorProps } from './domainTypes';

import styles from './ToolbarColorButton.module.scss';

export const ToolbarColorButton = ({ value, icon: Icon, ...rest }) => (
    <ToolbarButton
        as={DropDown}
        renderContent={(onSelect) => <DropDownColor value={value} onSelect={onSelect} />}
        {...rest}
    >
        <div className={styles.iconContainer}>
            <Icon size={16} />
            <div className={styles.colorBar} style={{ background: value }} />
        </div>
    </ToolbarButton>
);

ToolbarColorButton.propTypes = RichEditorProps.commandItemSelectProps;
