import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';

import { cleanCommonClonedDocument, isUploadUsedOnPage } from '@mssgme/shared';
import { useActions, useBooleanHandler, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ImageBlobUpload, ImageField, ImageUpload, LandingsPropTypes, TagsField, TextField } from '@mssgme/ui';

import { TemplateEditForm } from '../../../../UI';
import { PageThumbnailRenderer } from './PageThumbnailRenderer';

const ThumbnailRendererField = ({ name, website, page, ...rest }) => {
    const previewName = `${name}Url`;
    const { api } = useActions();
    const { getFieldMeta, setFieldValue, setFieldTouched, setFieldError } = useFormikContext();
    const { value, error } = getFieldMeta(name);
    const { value: url } = getFieldMeta(previewName);
    const [blob, setBlob] = useState();
    const [asFile, handleFileSwitch, handleBlobSwitch] = useBooleanHandler();

    const handleUpload = useCallback(api.pageTemplates.uploadThumbnail);
    const handleChange = useCallback(
        ({ path, url }) => {
            setFieldValue(previewName, url);
            setFieldValue(name, path);
            setFieldTouched(name);
        },
        [name]
    );
    const handleError = useCallback(
        (e) => {
            setFieldError(name, e?.message);
            setFieldTouched(name, !!e, false);
        },
        [name]
    );

    return (
        <ImageField
            as={asFile ? ImageUpload : ImageBlobUpload}
            name={name}
            blob={asFile ? null : blob}
            disabled={!blob}
            previewName={previewName}
            innerLabel={asFile ? '( 750x1400 )' : undefined}
            previewUrl={value}
            upload={handleUpload}
            removable={asFile}
            controlProps={{ style: asFile ? { padding: 6, height: 'unset' } : null }}
            onChange={handleChange}
            onRemove={asFile ? undefined : handleFileSwitch}
            onDelete={asFile ? handleBlobSwitch : undefined}
            {...rest}
        >
            <PageThumbnailRenderer
                website={website}
                page={page}
                src={url}
                error={error}
                onChange={setBlob}
                onError={handleError}
            />
        </ImageField>
    );
};

ThumbnailRendererField.propTypes = {
    name: PropTypes.string.isRequired,
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
};

export function TemplateCreatePage() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const template = useMemo(() => ({
        public: false,
        name: '',
        primaryTags: [],
        secondaryTags: [],
        blocks: page.blocks.map(cleanCommonClonedDocument()),
        uploads: website.uploads
            .filter((upload) => isUploadUsedOnPage(upload, page))
            .map(cleanCommonClonedDocument(['url'])),
        theme: page.theme,
    }));
    const websitePreview = useMemo(() => ({ ...website, branding: false }), [website]);

    return (
        <TemplateEditForm
            title={t('Template.Edit.Create.heading')}
            template={template}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            <ThumbnailRendererField name="thumbnail" website={websitePreview} page={template} />

            <TextField
                builderInput
                required
                name="name"
                label={t('Template.fields.name.label')}
                placeholder={t('Template.fields.name.placeholder')}
                maxLength={250}
            />

            <TagsField
                builderInput
                name="primaryTags"
                label={t('Template.fields.tags.label')}
                placeholder={t('Template.fields.tags.placeholder')}
            />
        </TemplateEditForm>
    );
}
