import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { ChevronUpIcon } from '@mssgme/icons';
import { PortalModalSheet } from '../../../../../ModalSheet';
import { useModal } from '../../../../../Modal';
import { useEditorFocus } from '../useEditorFocus';

import styles from './DropDown.module.scss';

export const DropDown = ({ children, title, renderContent, onSelect, onClosing, ...rest }) => {
    const { saveFocus, restoreFocus } = useEditorFocus();
    const { isOpen, toggle } = useModal();

    const handleOpen = useCallback(
        (e) => {
            e.preventDefault();
            e.stopPropagation();

            saveFocus();
            toggle(true);
        },
        [saveFocus]
    );
    const handleSelect = useCallback(
        (value, ...args) => {
            toggle(false);

            if (value) {
                onSelect(value, ...args);
            }
        },
        [onSelect]
    );
    const handleClosing = useCallback(
        (...args) => {
            if (args[0] && false === onClosing?.(...args)) {
                return false;
            }

            restoreFocus();
        },
        [restoreFocus, onClosing]
    );

    return (
        <div onClick={handleOpen} {...rest}>
            {children}

            <span className={styles.chevronDown}>
                <ChevronUpIcon size={16} />
            </span>

            {isOpen && (
                <PortalModalSheet title={title} onClosing={handleClosing} onClose={handleSelect}>
                    {({ handleClose }) => renderContent(handleClose)}
                </PortalModalSheet>
            )}
        </div>
    );
};

DropDown.propTypes = {
    renderContent: PropTypes.func.isRequired,
    title: PropTypes.any,
    onSelect: PropTypes.func,
    onClosing: PropTypes.func,
};
