import React from 'react';
import { useActions, useFetch } from '@mssgme/hooks';
import { LoadingIndicator } from '@mssgme/ui';

import providers from './providers';

export default function ChangeCard() {
    const { api } = useActions();
    const { result } = useFetch(() => api.billing.subscription.changeCard(), []);

    if (!result) return <LoadingIndicator />;

    const Component = providers[result.provider].changeCard;

    return <Component inline {...result.params} />;
}
