import { useSelector } from 'react-redux';
import useSWR from 'swr';
import { useActions } from '@mssgme/hooks';

export function useProductPrices() {
    const { api } = useActions();
    const workspaceId = useSelector((state) => state.workspace._id);
    const { data } = useSWR(
        `api/workspaces/${workspaceId}/billing/products/prices`,
        () => api.billing.products.prices(),
        { suspense: true }
    );
    return data;
}
