import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { FiX } from 'react-icons/fi';

import { useActions } from '@mssgme/hooks';
import { Button, PortalModalSheet } from '@mssgme/ui';
import UnsubscribeReasonForm from './UnsubscribeReasonForm';

export default function Unsubscribe() {
    const { t } = useTranslation('billing');
    const { api } = useActions();
    const [isModal, toggleModal] = useToggle(false);
    const handlePause = useCallback((payload) => api.billing.subscription.cancel({ payload }), []);

    return (
        <>
            <Button
                btnStyle="secondary2"
                fullWidth
                isFetching={isModal}
                onClick={toggleModal}
                style={{ backgroundColor: '#f7f7fa' }}
                data-event="click-unsubscribe"
            >
                {t('billingHome.unsubscription.cancel')}
            </Button>
            {isModal && (
                <PortalModalSheet
                    onClose={toggleModal}
                    render={({ handleClose }) => (
                        <div>
                            <div
                                style={{ position: 'absolute', padding: 16, left: 0, top: 0, cursor: 'pointer' }}
                                onClick={handleClose}
                            >
                                <FiX size={22} color="#333" style={{ display: 'block' }} />
                            </div>
                            <div style={{ textAlign: 'center', paddingTop: 22 }}>
                                <div style={{ fontSize: 36, marginBottom: 20, lineHeight: 1 }}></div>
                                <h3
                                    style={{
                                        fontWeight: 'bold',
                                        fontSize: 20,
                                        lineHeight: '26px',
                                        margin: '0 0 22px 0',
                                    }}
                                >
                                    {t('UnsubscribeModal.title')}
                                </h3>
                                <div>
                                    <UnsubscribeReasonForm onSubmit={handlePause} onClose={handleClose} />
                                </div>
                            </div>
                        </div>
                    )}
                />
            )}
        </>
    );
}
