import { Styling } from '@mssgme/ui';
import React from 'react';
import GridIcon from '../../../icons/gridIcon.svg';
import ListIcon from '../../../icons/listIcon.svg';

export const servicesGridStylingFactory = ({ t }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('gridMode', {
                label: t('Page.fields.servicesGridMode.label'),
                options: [
                    { value: 'grid', label: t('Page.fields.servicesGridMode.values.grid'), icon: GridIcon },
                    { value: 'list', label: t('Page.fields.servicesGridMode.values.list'), icon: ListIcon },
                ].filter(Boolean),
            }),
        ]),
    };
};
