import { useCallback, useContext, useEffect, useState } from 'react';

import { expandRect, getSequencedRectRanges } from './helpers';
import { SelectionRangesContext } from './SelectionRangesContext';

export const useSelectionRanges = (blocks, expand = [0, 0]) => {
    const { listen } = useContext(SelectionRangesContext);
    const [ranges, setRanges] = useState([]);

    const handleUpdate = useCallback(
        (updatedRanges) => {
            if (!updatedRanges.length) {
                setRanges([]);
            }

            setRanges(
                Array.from(getSequencedRectRanges(blocks, updatedRanges), ({ id, area }) => ({
                    id,
                    area: expandRect(area, expand),
                }))
            );
        },
        [blocks]
    );

    useEffect(() => listen(handleUpdate), [handleUpdate]);

    return ranges;
};
