import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory, useParams } from 'react-router-dom';

import { Button, ModalPane } from '@mssgme/ui';
import { useActions, useFetching, useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';

export default function Delete() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const closePath = generateUrl('..');

    const { pending, run: handleDelete } = useFetching(async () => {
        await api.websites.delete({ websiteId: website._id });

        history.push('/pages');
    }, [website._id, history]);

    return (
        <ModalPane title={t('Website.Edit.Delete.heading')} onClose={closePath}>
            <p style={{ margin: 0, color: '#777', textAlign: 'center' }}>{t('Website.Edit.Delete.fieldTip')}</p>
            <Button tag={Link} to={generateUrl('..')} fullWidth style={{ marginTop: 16 }} btnStyle="secondary2">
                {t('labels.website.cancelDelete')}
            </Button>
            <Button fullWidth onClick={handleDelete} style={{ marginTop: 10 }} btnStyle="danger" isFetching={pending}>
                {t('labels.website.delete')}
            </Button>
        </ModalPane>
    );
}
