import React, { useMemo } from 'react';
import { get } from 'lodash';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { useThemesList } from '../../../../hooks';
import { PageEditForm, ThemeField } from '../../../../UI';

export function PageTheme() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const closePath = useMemo(() => generateUrl('../..'), [websiteSlug]);
    const options = useThemesList(page);

    return (
        <PageEditForm website={website} page={page} title={t('Editor.PageTheme.heading')} backPath={closePath}>
            {({ setFieldValue, setFieldTouched, getFieldMeta }) => (
                <ThemeField
                    grid
                    name="theme.colorId"
                    options={options}
                    page={page}
                    onChange={(value) => {
                        if (!value) {
                            return;
                        }

                        const option = options.find((item) => value === item.value);

                        if (option && option.theme) {
                            const { initialValue } = getFieldMeta('theme');
                            const changed = initialValue.colorId !== get(option, 'theme.colorId');

                            setFieldValue('theme', changed ? option.theme : initialValue);
                            setFieldTouched('theme', changed, changed);
                        }
                    }}
                />
            )}
        </PageEditForm>
    );
}
