import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { AnimatePresence, motion } from 'framer-motion';
import { useTranslation } from 'react-i18next';
import { useLocalStorage } from 'react-use';

import { Button, IconHand, IconHandTap } from '@mssgme/ui';

import styles from './SortHelp.module.scss';

export function SortHelp() {
    const { t } = useTranslation('landings');
    const [tipVisible, setTipVisible] = useLocalStorage('visualBuilderDragTip', true);

    const handleTipClose = useCallback(() => setTipVisible(false), [setTipVisible]);

    const content = tipVisible && (
        <motion.div className={styles.root}>
            <motion.div
                className={styles.overlay}
                variants={{
                    visible: { backgroundColor: 'rgba(0,0,0,.5)', transition: { ease: 'easeInOut', delay: 0.4 } },
                    hidden: { backgroundColor: 'rgba(0,0,0,0)', transition: { ease: 'easeInOut', delay: 0.1 } },
                }}
                transition={{ ease: 'easeInOut' }}
                initial="hidden"
                animate="visible"
                exit="hidden"
                enter="visible"
                onClick={handleTipClose}
            />
            <motion.div
                className={styles.card}
                variants={{
                    visible: { y: 0, transition: { ease: 'easeInOut', delay: 0.5 } },
                    hidden: { y: 'calc(100% + 20px)' },
                }}
                transition={{ ease: 'easeInOut' }}
                initial="hidden"
                animate="visible"
                exit="hidden"
                enter="visible"
            >
                <div>
                    <span className={styles.heading}>{t('Editor.SortHelp.hint.label')}</span>
                    <p className={styles.tip}>{t('Editor.SortHelp.hint.tip')}</p>
                </div>
                <div>
                    <motion.div
                        className={styles.block}
                        style={{ zIndex: 1 }}
                        animate={{
                            y: [0, 0, 66, 66, 66, 66, 0, 0],
                            boxShadow: [
                                '0 0 0px rgba(0,0,0,0)',
                                '0 0 20px rgba(0,0,0,.5)',
                                '0 0 20px rgba(0,0,0,.5)',
                                '0 0 0px rgba(0,0,0,0)',
                                '0 0 0px rgba(0,0,0,0)',
                                '0 0 20px rgba(0,0,0,.5)',
                                '0 0 20px rgba(0,0,0,.5)',
                                '0 0 0px rgba(0,0,0,0)',
                            ],
                            transition: {
                                ease: 'easeInOut',
                                delay: 1,
                                duration: 4,
                                times: [0, 0.05, 0.4, 0.45, 0.5, 0.55, 0.95, 1],
                                loop: 'Infinity',
                            },
                        }}
                    >
                        <motion.div
                            className={styles.handBlock}
                            animate={{
                                scale: [1, 0.9, 0.9, 1, 1, 0.9, 0.9, 1],
                                transition: {
                                    ease: 'easeInOut',
                                    delay: 1,
                                    duration: 4,
                                    times: [0, 0.05, 0.4, 0.45, 0.5, 0.55, 0.95, 1],
                                    loop: 'Infinity',
                                },
                            }}
                        >
                            <IconHand className={styles.hand} />
                            <motion.div
                                style={{ position: 'absolute', top: 0, left: 0 }}
                                animate={{
                                    opacity: [0, 1, 1, 0, 0, 1, 1, 0],
                                    transition: {
                                        ease: 'easeInOut',
                                        delay: 1,
                                        duration: 4,
                                        times: [0, 0.05, 0.4, 0.45, 0.5, 0.55, 0.95, 1],
                                        loop: 'Infinity',
                                    },
                                }}
                            >
                                <IconHandTap className={styles.hand} />
                            </motion.div>
                        </motion.div>
                    </motion.div>
                    <motion.div
                        className={styles.block}
                        animate={{
                            y: [0, 0, -66, -66, -66, -66, 0, 0],
                            transition: {
                                ease: 'easeInOut',
                                delay: 1,
                                duration: 4,
                                times: [0, 0.05, 0.4, 0.45, 0.5, 0.55, 0.95, 1],
                                loop: 'Infinity',
                            },
                        }}
                    />
                </div>
                <Button fullWidth style={{ marginTop: 16 }} onClick={handleTipClose}>
                    {t('Editor.SortHelp.ok')}
                </Button>
            </motion.div>
        </motion.div>
    );

    return (
        <AnimatePresence>
            {content}
        </AnimatePresence>
    );
}

SortHelp.propTypes = {
    setTipVisible: PropTypes.func.isRequired,
};
