import React from 'react';
import { Route } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { MssgmeWidget } from '@mssgme/ui';

const HELPS_IDS = {
    ru: '6059b4c0c69fd700180c18f6/6059b4c0c69fd700180c18fb',
    uk: '625e915b62aa81001f023e6d/625e915b62aa81001f023e6e',
    en: '6059bde2c69fd700180c1ef6/6059bde2c69fd700180c1efb',
};

export const HelpWidget = () => {
    const { i18n } = useTranslation();
    const helpId = HELPS_IDS[i18n.language];

    if (!helpId || process.env.NODE_ENV !== 'production') { // TODO: use REACT_APP_ENABLE_HELP_WIDGET
        return null;
    }

    return (
        <Route exact path="/pages/-/:websiteSlug">
            <MssgmeWidget src={`https://mssg.me/widget/${helpId}`} />
        </Route>
    );
};
