import PropTypes from 'prop-types';
import cn from 'classnames';
import React, { useCallback, useEffect, useMemo } from 'react';

import { cls } from '@mssgme/helpers';
import { useHorizontalTabbedScroll } from '../../hooks';

import styles from './TabsSelector.module.scss';

export const TabsSelector = ({ items, value, onSelect, noArrows, ...rest }) => {
    const {
        root,
        hasLeftBtn,
        moveLeft,
        list,
        hasRightBtn,
        moveRight,
        moveTo,
        currentIndex,
    } = useHorizontalTabbedScroll(items.length);
    const arrowStyles = useMemo(() => {
        const generateArrowsGradient = (deg) =>
            `linear-gradient(${deg}, #fff 0%, #fff 45%, ${'#ffffff'.substring(0, 7)}00 100%)`;

        return {
            left: {
                backgroundImage: [
                    generateArrowsGradient('90deg'),
                    generateArrowsGradient('90deg'),
                    generateArrowsGradient('90deg'),
                ],
            },
            right: {
                backgroundImage: [
                    generateArrowsGradient('-90deg'),
                    generateArrowsGradient('-90deg'),
                    generateArrowsGradient('-90deg'),
                ],
            },
        };
    });

    const handleClick = useCallback(
        (e, item) => {
            e.preventDefault();

            const index = items.findIndex(({ value }) => value === item.value);

            if (item.value === value) {
                // onSelect(null);
            } else {
                moveTo(index);
                onSelect(item.value, index);
            }
        },
        [value, items, moveTo, onSelect]
    );

    useEffect(() => {
        if (items.length) {
            moveTo(0);
            onSelect(items[0].value);
        }
    }, [items.length]);

    useEffect(() => {
        const index = items.findIndex(({ value: itemValue }) => itemValue === value);

        if (items.length) {
            moveTo(index);
        }
    }, [value, currentIndex]);

    return (
        <div ref={root} {...cls(styles.root, rest)}>
            {hasLeftBtn && !noArrows && (
                <div className={styles.controlContainerLeft} style={arrowStyles.left} onClick={moveLeft}>
                    <div className={styles.control}>&larr;</div>
                </div>
            )}
            <ul className={styles.list} ref={list}>
                {items.map((item, index) => {
                    const isActive = value === item.value;

                    return (
                        <li
                            key={index}
                            data-index={index}
                            className={cn({ [styles.item]: true, [styles.active]: isActive })}
                            onClick={(e) => handleClick(e, item)}
                        >
                            {(item.props && item.props.children) || item.label}
                        </li>
                    );
                })}
            </ul>
            {hasRightBtn && !noArrows && (
                <div className={styles.controlContainerRight} style={arrowStyles.right} onClick={moveRight}>
                    <div className={styles.control}>&rarr;</div>
                </div>
            )}
        </div>
    );
};

TabsSelector.propTypes = {
    items: PropTypes.array.isRequired,
    value: PropTypes.string,
    onSelect: PropTypes.func,
    noArrows: PropTypes.bool,
};
