import PropTypes from 'prop-types';
import React, { createContext, useEffect, useMemo, useRef, useState } from 'react';
import { noop } from 'lodash';

import { makeSelectionObserver } from './helpers';

export const SelectionRangesContext = createContext({ ref: { current: null }, update: noop, listen: noop });

export const SelectionRangesContextProvider = ({ children, hash }) => {
    const ref = useRef();
    const [state, setState] = useState([]);
    const { update, emit, listen } = useMemo(
        () =>
            makeSelectionObserver({
                get: () => ref.current || [],
                set: (ranges) => {
                    ref.current = ranges;
                    setState(ranges);
                },
            }),
        []
    );

    const context = useMemo(() => ({ hash, ref, update, listen }), [hash]);

    useEffect(emit, [state]);

    return <SelectionRangesContext.Provider value={context}>{children}</SelectionRangesContext.Provider>;
};

SelectionRangesContextProvider.propTypes = {
    hash: PropTypes.any,
};
