import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useActions, useNavigationUtils, useWebsitePage, useWebsiteBySlug, useRestriction } from '@mssgme/hooks';
import { StyleEditor } from '@mssgme/ui';
import { useWebsiteEditor } from '../../../../hooks';
import { BackgroundPageImageField, PageEditForm } from '../../../../UI';

const BackgroundImageField = ({ name, basePath, colorPath, opacityPath }) => {
    const { api } = useActions();
    const { websiteId } = useWebsiteEditor();

    return (
        <BackgroundPageImageField
            accept="image/png,image/x-png,image/jpeg"
            imageName={name}
            colorName={colorPath.replace('$1', basePath)}
            opacityName={opacityPath.replace('$1', basePath)}
            upload={({ payload }) => api.websites.upload({ payload, websiteId })}
        />
    );
};

BackgroundImageField.propTypes = {
    name: PropTypes.string.isRequired,
    basePath: PropTypes.string.isRequired,
    colorPath: PropTypes.string.isRequired,
    opacityPath: PropTypes.string.isRequired,
};

const customStyleFields = { backgroundImage: BackgroundImageField };

export function PageStylingBackgroundImage() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { handleBlockedEvent } = useRestriction('page-styling', { type: 'backgroundImage' });
    const closePath = useMemo(() => generateUrl('../..'), [websiteSlug]);

    return (
        <PageEditForm
            website={website}
            page={page}
            title={t('Page.fields.backgroundImage.label')}
            backPath={closePath}
            blocked={handleBlockedEvent}
        >
            {({ styling }) => (
                <StyleEditor
                    styling={styling}
                    customFields={customStyleFields}
                    focusPaths={[{ path: 'backgroundImage', undecorated: true }]}
                />
            )}
        </PageEditForm>
    );
}
