import React from 'react';
import { Redirect } from 'react-router-dom';

import { useWebsitePageBySlugs } from '@mssgme/hooks';

export const EditRedirect = () => {
    const { website, page } = useWebsitePageBySlugs();

    return (
        <Redirect to={`/pages/-/${website._id}/${page._id}/edit`} />
    );
};
