import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { useRestriction } from '@mssgme/hooks';

export const ProtectAccess = ({ strict, rule, subject, context, children }) => {
    const { warning, hasWarning, showWarning } = useRestriction(rule, subject, context);

    const handleClick = useCallback(
        (e) => {
            e.preventDefault && e.preventDefault();
            e.stopPropagation && e.stopPropagation();
            showWarning();
        },
        [showWarning]
    );

    if (strict && warning?.code === 'forbidden') {
        return null;
    }

    if (typeof children === 'function') {
        return children(hasWarning ? handleClick : undefined, warning);
    }

    if (hasWarning) {
        return <div onClick={handleClick}>{children}</div>;
    }

    return children;
};

ProtectAccess.propTypes = {
    rule: PropTypes.string.isRequired,
    strict: PropTypes.bool,
    subject: PropTypes.any,
    context: PropTypes.any,
};
