import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { MdSearch } from 'react-icons/md';

import { filters } from '@mssgme/helpers';
import {
    CheckableItem,
    Form,
    FormikProvider,
    PortalModalSheet,
    RestrictionScope,
    TextField,
    useEnhancedFormik,
} from '@mssgme/ui';
import { FilterMessage } from '../../FilterMessage';

import styles from './CollectionsSelector.module.scss';

const mapValues = (values) =>
    Object.entries(values)
        .filter(([, value]) => value)
        .map(([key]) => key);

export const CollectionsSelector = ({ value, items, groupItems, renderItem, onDone, ...rest }) => {
    const { t } = useTranslation('products');
    const [filter, setFilter] = useState('');
    const filtered = useMemo(() => (filter ? items.filter(filters.bySubstring(filter, 'name')) : items), [
        filter,
        items,
    ]);
    const grouped = useMemo(() => groupItems && groupItems(filtered), [groupItems, filtered]);
    const initialValues = useMemo(() => value.reduce((acc, id) => ((acc[id] = true), acc), {}), [value]);

    const handleSubmit = useCallback(
        (values) => {
            if (values) {
                onDone({ action: 'success', value: mapValues(values) });
            } else {
                onDone({ action: 'cancel' });
            }
        },
        [onDone]
    );

    const handleFilter = useCallback(({ target: { value } }) => setFilter(value));
    const formik = useEnhancedFormik({ initialValues, onSubmit: handleSubmit });
    const handleToggle = useCallback(({ _id }) => formik.setFieldValue(_id, !formik.values[_id]), [formik.values]);

    const renderItems = (items) =>
        items.map((col) => (
            <CollectionItem
                key={col._id}
                col={col}
                active={formik.values[col._id]}
                renderItem={renderItem}
                onClick={handleToggle}
            />
        ));
    const renderGroups = (groups) =>
        groups.map(({ key, label, items }, index) => (
            <React.Fragment key={`group-${key || index}`}>
                <li className={styles.groupItem}>{label}</li>
                {renderItems(items)}
            </React.Fragment>
        ));

    return (
        <FormikProvider value={formik}>
            <PortalModalSheet
                padding={false}
                portalTo="#visual-builder,#mainRoot"
                onClose={formik.handleSubmit}
                title={t('CollectionsSelector.heading')}
                {...rest}
            >
                <RestrictionScope fullWidth>
                    <Form autoComplete="off" className={styles.root}>
                        {filtered.length > 0 ? (
                            <ul className={styles.list}>{grouped ? renderGroups(grouped) : renderItems(filtered)}</ul>
                        ) : (
                            filter && (
                                <FilterMessage
                                    title={t('labels.notFound.collections.heading')}
                                    description={t('labels.notFound.collections.description')}
                                />
                            )
                        )}

                        {items.length > 1 && (
                            <div className={styles.filter}>
                                <TextField
                                    builderInput
                                    value={filter}
                                    onChange={handleFilter}
                                    placeholder={t('labels.search')}
                                    icon={MdSearch}
                                    autoComplete="off"
                                    clearable
                                />
                            </div>
                        )}
                    </Form>
                </RestrictionScope>
            </PortalModalSheet>
        </FormikProvider>
    );
};

CollectionsSelector.propTypes = {
    value: PropTypes.arrayOf(PropTypes.string).isRequired,
    items: PropTypes.arrayOf(PropTypes.shape()).isRequired,
    isInitialDirty: PropTypes.bool,
    groupItems: PropTypes.func,
    renderItem: PropTypes.func,
    onDone: PropTypes.func,
};

const CollectionItem = ({ col, active, renderItem, onClick }) => {
    const { t } = useTranslation('products');
    const handleClick = useCallback(() => {
        if (!col.isFixed) {
            onClick(col);
        }
    }, [col, onClick]);

    return (
        <li className={cn(styles.listItem, col.isFixed && styles.fixed)} onClick={handleClick}>
            {renderItem ? (
                renderItem(col, active, styles)
            ) : (
                <CheckableItem active={active} disabled={!!col.isFixed}>
                    <div className={styles.title}>{col.name}</div>
                    <div className={styles.subtitle}>{t('labels.products', { count: col.publishedProductsCount })}</div>
                </CheckableItem>
            )}
        </li>
    );
};

CollectionItem.propTypes = {
    col: PropTypes.object.isRequired,
    active: PropTypes.bool,
    renderItem: PropTypes.func,
    onClick: PropTypes.func.isRequired,
};
