import React from 'react';
import { useSelector } from 'react-redux';
import { Redirect } from 'react-router';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils } from '@mssgme/hooks';
import { SecondLevelScreen } from '@mssgme/ui';

import { ChangeCard } from '../UI';

import styles from './ChangeCardPage.module.scss';

export default function ChangeCardPage() {
    const { t } = useTranslation('billing');
    const { generateUrl } = useNavigationUtils();
    const changePaymentMethod = useSelector(
        ({ billingDetails: { result } }) => result?.subscription?.actions?.changePaymentMethod
    );

    if (!changePaymentMethod) return <Redirect to={generateUrl('..')} />;

    return (
        <SecondLevelScreen
            preferDynamicBack
            light
            backPath={{
                pathname: generateUrl('..'),
                state: { preventDynamicBack: true },
            }}
        >
            <div className={styles.contentContainer}>
                <div className={styles.heading}>
                    <h1>{t('ChangeCardPage.title')}</h1>
                    <p>{t('ChangeCardPage.subtitle')}</p>
                </div>
                <div className={styles.form}>
                    <ChangeCard />
                </div>
            </div>
        </SecondLevelScreen>
    );
}
