import React from 'react';
import PropTypes from 'prop-types';

import { ModalDialog } from '../Modal';
import { Button } from '../Inputs';

export function PromptDialog({ children, acceptLabel, isOpen, onAccept, onClose, ...rest }) {
    if (!isOpen) {
        return null;
    }

    const onClick = (e) => {
        e && e.preventDefault();
        onAccept();
    };

    return (
        <ModalDialog onClose={onClose} modal={{ size: 'small' }} {...rest}>
            <p>{children}</p>
            <p>
                <Button fullWidth onClick={onClick}>
                    {acceptLabel}
                </Button>
            </p>
        </ModalDialog>
    );
}

PromptDialog.propTypes = {
    ...ModalDialog.propTypes,
    onAccept: PropTypes.func,
    onClose: PropTypes.func,
};
