import React, { useMemo } from 'react';
import { CgDarkMode } from 'react-icons/cg';
import { useTranslation } from 'react-i18next';
import { get, omit, pick } from 'lodash';

import { ColorThemes, getThemeById, mergeTheme } from '@mssgme/shared';
import { useAutoTheme } from '@mssgme/userpage-core';

export const useThemesList = (page) => {
    const { t } = useTranslation('landings');
    const { theme: autoTheme } = useAutoTheme();
    const colorId = get(page, 'theme.colorId');
    const baseTheme = getThemeById(colorId);
    const themeDiff = useMemo(() => omit(mergeTheme(baseTheme, page.theme || {}), ['colorId', 'light', 'dark']), [
        colorId,
        page.theme,
    ]);

    return useMemo(() => {
        const pageTheme = pick(page.theme || {}, [
            'page.backgroundImage',
            'page.backgroundImageOpacity',
            'page.alignment',
        ]);
        const mergedTheme = mergeTheme(autoTheme, pageTheme);
        const themes = [
            [
                'autoTheme',
                {
                    name: 'autoTheme',
                    theme: {
                        ...mergedTheme,
                        colorId: 'autoTheme',
                        light: 'bwTheme',
                        dark: 'wbTheme',
                    },
                },
            ],
            ...Object.entries(ColorThemes),
        ];
        const weights = {
            bwTheme: 3,
            wbTheme: 3,
            autoTheme: 2,
            lightTheme: 1,
            darkTheme: 1,
        };

        return themes
            .map(([value, { name, theme }]) => {
                const isAuto = theme.colorId === 'autoTheme';
                return {
                    value,
                    label: isAuto ? (
                        <div>
                            <CgDarkMode style={{ margin: '0 2px -2px 0' }} />
                            {t('Editor.PageTheme.items.auto')}
                        </div>
                    ) : (
                        name
                    ),
                    theme: isAuto ? theme : mergeTheme(themeDiff, theme),
                };
            })
            .sort((a, b) => {
                return (weights[b.value] || 0) - (weights[a.value] || 0);
            });
    }, [themeDiff, autoTheme]);
};
