import React, { useMemo } from 'react';
import PropTypes from 'prop-types';

import { useAutoTheme } from '@mssgme/userpage-core';

import styles from './ThemeField.module.scss';

export const ThemePreview = ({ theme }) => {
    const { isAuto, theme: resolved, opposite } = useAutoTheme(theme);
    const colors = (isAuto ? resolved : theme).colors;
    const contrastColors = isAuto ? opposite.colors : null;
    const style = useMemo(
        () => ({
            root: { backgroundColor: colors.background, clipPath: isAuto ? 'polygon(0 0, 0% 100%, 100% 0)' : null },
            text: { color: colors.text },
            button: { backgroundColor: colors.buttonBackground },
        }),
        [colors, isAuto]
    );
    const contrast = useMemo(
        () =>
            contrastColors && {
                root: { backgroundColor: contrastColors.background, clipPath: 'polygon(100% 100%, 0% 100%, 100% 0)' },
                text: { color: contrastColors.text },
                button: { backgroundColor: contrastColors.buttonBackground },
            },
        [contrastColors]
    );

    return (
        <>
            <div className={styles.thumbnail} style={style.root}>
                <div className={styles.thumbnailContent}>
                    <div className={styles.text} style={style.text}>
                        Text
                    </div>
                    <div className={styles.button} style={style.button} />
                </div>
            </div>

            {contrast && (
                <div className={styles.thumbnail} style={contrast.root}>
                    <div className={styles.thumbnailContent}>
                        <div className={styles.text} style={contrast.text}>
                            Text
                        </div>
                        <div className={styles.button} style={contrast.button} />
                    </div>
                </div>
            )}
        </>
    );
};

ThemePreview.propTypes = {
    theme: PropTypes.shape({
        colorId: PropTypes.string.isRequired,
        colors: PropTypes.object.isRequired,
    }).isRequired,
};
