import { useMemo } from 'react';
import { useMeasure } from 'react-use';
import { getContourColor } from '@mssgme/helpers';

export const useRendererConfig = ({
    blocks,
    selection,
    theme,
    isAnimated,
    isMobile,
    isViewMode,
    isMobileMode,
    isWebsitePublished,
    areaWidth = 455,
    gutterWidth = 80,
}) => {
    const { border: contrastedColor } = getContourColor(
        theme.backgroundColor,
        theme.colors.buttonBackground,
        theme.colors.buttonTextColor
    );
    const [ref, { width }] = useMeasure();
    const scale = ((width || areaWidth) - gutterWidth) / (width || areaWidth);

    const config = useMemo(
        () => ({
            selection: selection.items,
            isScaled: !isViewMode,
            isViewMode,
            isMobileMode,
            isWebsitePublished,
            isAnimated,
            isMobile,
            blocks,
            contrastedColor,
            width,
            scale,
            theme,
        }),
        [
            blocks,
            isMobile,
            isAnimated,
            isViewMode,
            isMobileMode,
            isWebsitePublished,
            selection.hash,
            contrastedColor,
            width,
            scale,
        ]
    );

    return [ref, config];
};
