import { useCallback, useEffect, useRef, useState } from 'react';
import { useDeepCompareEffect, useMeasure } from 'react-use';

import { useMergedRefs } from '@mssgme/hooks';

const noop = (e) => {
    e?.preventDefault();
    e?.stopPropagation();

    return false;
};
const defaultOptions = {
    active: true,
    threshold: 0,
    dataElement: 'element-drag',
};
const identity = { x: 0, y: 0 };

/**
 * @param {*} [config] Drag options
 * @param {boolean} [config.active] Is drag active
 * @param {number} [config.threshold] Threshold of movement before element is considered dragged
 * @param {string} [config.dataElement] Drag element data attribute
 * @returns {{
 *   isDown: boolean,
 *   isDragging: boolean,
 *   origin: { x: number, y: number },
 *   offset: { x: number, y: number },
 *   bind: *
 * }} Bind attributes and drag state
 */
export const useDrag = (config) => {
    const {
        active = defaultOptions.active,
        threshold = defaultOptions.threshold,
        dataElement = defaultOptions.dataElement,
    } = config || defaultOptions;
    const [state, setState] = useState({
        isDown: false,
        isDragging: false,
        origin: identity,
        offset: identity,
    });

    const handleDown = useCallback((e) => {
        let node = e.target;

        while (!(node.dataset && node.dataset.element === dataElement)) {
            if (node.parentNode) {
                node = node.parentNode;
            } else {
                return;
            }
        }

        const coords = (e.changedTouches && e.changedTouches[0]) || e;

        setState({
            isDown: true,
            isDragging: false,
            origin: { x: coords.clientX, y: coords.clientY },
            offset: identity,
        });
    }, []);
    const handleUp = useCallback(() => {
        setState((state) => ({ ...state, isDown: false, isDragging: false, offset: identity }));
    }, []);
    const handleMove = useCallback(
        (e) => {
            const coords = (e.changedTouches && e.changedTouches[0]) || e;
            const x = coords.clientX - state.origin.x;
            const y = coords.clientY - state.origin.y;
            const dx = state.offset.x - x;
            const dy = state.offset.y - y;

            if (dx || dy) {
                setState((state) => ({
                    ...state,
                    isDragging: state.isDown && (state.isDragging || dx ** 2 + dy ** 2 >= threshold ** 2),
                    offset: state.isDown ? { x, y } : identity,
                }));

                noop(e);
            }
        },
        [state.origin]
    );
    const handleContextMenu = useCallback((e) => (e.pointerType === 'touch' ? noop(e) : true), []);

    useEffect(() => {
        if (state.isDown) {
            window.addEventListener('mousemove', handleMove);
            window.addEventListener('touchmove', handleMove, { passive: false });
            window.addEventListener('mouseup', handleUp);
            window.addEventListener('touchend', handleUp);
            window.addEventListener('contextmenu', handleContextMenu, { passive: false });

            return () => {
                window.removeEventListener('mousemove', handleMove);
                window.removeEventListener('touchmove', handleMove);
                window.removeEventListener('mouseup', handleUp);
                window.removeEventListener('touchend', handleUp);
                window.removeEventListener('contextmenu', handleContextMenu);
            };
        }

        window.addEventListener('touchstart', handleDown, { passive: false });

        return () => {
            window.removeEventListener('touchstart', handleDown);
        };
    }, [state.isDown, handleMove, handleUp]);

    useEffect(() => {
        if (active) {
            return;
        }

        handleUp();
    }, [active]);

    return {
        ...state,
        bind: {
            'data-element': dataElement,
            onMouseDown: handleDown,
            onDragStart: noop,
            onSelect: noop,
            onClick: state.isDragging ? noop : undefined,
        },
    };
};

const calcArea = ({ cx, cy, width, height }, origin, offset) => {
    const x1 = origin.x - cx;
    const x2 = x1 + offset.x;
    const y1 = origin.y - cy;
    const y2 = y1 + offset.y;
    const left = Math.max(Math.min(x1, x2), 0);
    const top = Math.max(Math.min(y1, y2), 0);
    const right = Math.min(Math.max(x1, x2), width);
    const bottom = Math.min(Math.max(y1, y2), height);

    return {
        left,
        top,
        right,
        bottom,
        width: right - left,
        height: bottom - top,
    };
};

export const useAreaDrag = () => {
    const { isDragging, bind, origin, offset } = useDrag();
    const [innerRef, parent] = useMeasure();
    const [relative, setRelative] = useState({ cx: 0, cy: 0, width: 0, height: 0 });
    const [area, setArea] = useState(null);
    const objectRef = useRef();
    const ref = useMergedRefs([objectRef, innerRef]);
    const current = calcArea(relative, origin, offset);

    useEffect(() => {
        let node = objectRef.current;

        if (!(isDragging && node)) {
            return;
        }

        let x = window.pageXOffset || document.documentElement.scrollLeft;
        let y = window.pageYOffset || document.documentElement.scrollTop;

        const rect = node.getBoundingClientRect();

        x += rect.left;
        y += rect.top;

        // bug: would not work with scrollable (overflow: auto|scroll) intermediate containers
        // while (node) {
        //     x += (node.offsetLeft || 0) - node.scrollLeft;
        //     y += (node.offsetTop || 0) - node.scrollTop;
        //
        //     node = node.offsetParent;
        // }

        if (relative.cx !== x || relative.cy !== y) {
            setRelative({ cx: x, cy: y, width: parent.width, height: parent.height });
        }
    }, [isDragging]);

    useDeepCompareEffect(() => {
        if (isDragging) {
            setArea(current);
        }
    }, [isDragging, current]);

    return [
        { ref, ...bind },
        {
            isDragging,
            parent,
            area,
            current: isDragging ? current : null,
        },
    ];
};
