import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { useNavigationUtils } from '@mssgme/hooks';
import { Button, ModalPane } from '@mssgme/ui';

export function PublishedMessage({ btnPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const closePath = generateUrl('../..');

    return (
        <ModalPane title={t('Editor.PublishedMessage.heading')} onClose={closePath}>
            <p style={{ margin: 0, color: '#777', textAlign: 'center' }}>{t('Editor.PublishedMessage.message')}</p>
            <Button
                fullWidth
                tag={Link}
                to={{ pathname: generateUrl(btnPath), state: { from: 'publishedMessage' } }}
                style={{ marginTop: 16 }}
            >
                {t('Editor.PublishedMessage.button')}
            </Button>
        </ModalPane>
    );
}

PublishedMessage.propTypes = {
    btnPath: PropTypes.string,
};

PublishedMessage.defaultProps = {
    btnPath: '../share',
};
