import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useToggle } from 'react-use';

import { SupportModal } from '@mssgme/support';

export function SupportPage({ backPath }) {
    const history = useHistory();
    const [ready, setReady] = useToggle();

    // if this modal is rendered with render prop inside 'mainRoot' container, the container ref is not available at
    // the first render
    useEffect(() => setReady(true), []);

    return ready ? <SupportModal onClose={() => history.push(backPath)} /> : null;
}

SupportPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
