import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useActions, useFeedbackStore, useListFilter, useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { Input, StreamlinedScreen, TabsSelector } from '@mssgme/ui';
import { SearchIcon } from '@mssgme/icons';

import { FeedbackItemsList } from './FeedbackItemsList';
import { TotalSummary } from './TotalSummary';
import { TagsList } from './TagsList';

import styles from './FeedbackList.module.scss';

const hashValues = (o) =>
    Object.entries(o)
        .map(([key, value]) => `${key}-${value}`)
        .join(':');

export function FeedbackList() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { setFeedbackItemsFilter } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { _id: websiteId } = useWebsiteBySlug(websiteSlug);
    const { filter: rawFilter, counts, totals, tags } = useFeedbackStore();
    const { filter, setFilter, handleFilter } = useListFilter(
        { term: '', status: 'new', website: websiteId },
        setFeedbackItemsFilter
    );
    const closePath = generateUrl('..');

    const handleStatus = useCallback(
        (status) => setFilter((old) => (old.status === status ? old : { ...old, status })),
        []
    );

    const handleTagSelect = useCallback((tag) => setFilter((old) => ({ ...old, term: `#${tag}` })), []);

    const options = useMemo(() => {
        const makeOption = (status, label) => {
            const count = counts[label || status];
            const total = totals[label || status];

            return {
                value: status,
                props: {
                    children: (
                        <div className={styles.tab}>
                            {t(`Feedback.View.List.labels.state.${label || status}`)}
                            {!!count && (
                                <div className={styles.counter}>
                                    <div>{count}</div>
                                </div>
                            )}
                            {!!total?.length && (
                                <div className={styles.counter}>
                                    <TotalSummary totals={total} />
                                </div>
                            )}
                        </div>
                    ),
                },
            };
        };

        return [
            makeOption('new'),
            makeOption('processing'),
            makeOption('accepted'),
            makeOption('rejected'),
            // makeOption(null, 'all'),
        ];
    }, [hashValues(counts), hashValues(totals)]);

    return (
        <StreamlinedScreen
            padding
            modal
            backPath={closePath}
            title={t('Feedback.View.List.heading')}
            topPanelChildren={
                <>
                    <Input
                        builderInput
                        name="term"
                        placeholder={t('ui:labels.search')}
                        icon={SearchIcon}
                        autoComplete="off"
                        clearable
                        value={filter.term || ''}
                        onChange={handleFilter}
                    />

                    {!!tags?.length && (
                        <TagsList style={{ marginTop: 6 }} tags={tags} onClick={handleTagSelect} />
                    )}

                    <TabsSelector
                        style={{ marginTop: 6 }}
                        items={options}
                        value={rawFilter.status}
                        onSelect={handleStatus}
                    />
                </>
            }
            contentStyles={{ display: 'block' }}
        >
            <StreamlinedScreen.Section style={{ minHeight: 'calc(100% - 8px)' }}>
                <FeedbackItemsList websiteId={websiteId} />
            </StreamlinedScreen.Section>
        </StreamlinedScreen>
    );
}
