import React from 'react';

import { AnimateableRoutes } from '@mssgme/ui';
import { BlockEditContextProvider } from '../hooks';
import { ChooseBlock, ConvertServices, EDITOR_ROUTES, useEditorsPreload } from './Sheets';
import { LandingsEditorCRMRouter } from './LandingsEditorCRMRouter';

const editorsRouting = EDITOR_ROUTES.map(([path, component]) => ({
    path,
    component,
    exact: false,
    props: {
        backPath: '../..',
    },
}));

const routing = [
    {
        path: 'add-block',
        component: ChooseBlock,
        exact: true,
        children: editorsRouting,
    },
    {
        path: 'block',
        children: [
            ...editorsRouting.map((route) => ({
                ...route,
                path: `${route.path}/:blockId`,
                props: {
                    backPath: '../../..',
                },
            })),
            { path: ':kind/:blockId/convert', component: ConvertServices },
        ],
    },
    {
        path: [
            'add-block/:kind',
            'block/:kind/:blockId',
        ],
        component: LandingsEditorCRMRouter,
        exact: false,
    },
];

export const LandingsEditorsRouter = () => {
    const isReady = useEditorsPreload();

    if (!isReady) {
        return null;
    }

    return (
        <BlockEditContextProvider>
            <AnimateableRoutes routing={routing} />
        </BlockEditContextProvider>
    );
};
