import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import SelectSimple from '../SelectSimple';

export function SelectMessengerType({ platform, ...props }) {
    const { t } = useTranslation('ui');
    let messengerTypes = [];

    if (platform === 'whatsapp') {
        messengerTypes.push(
            {
                label: t('selectMessengerType.phone'),
                value: 'private',
            },
            {
                label: t('selectMessengerType.chat'),
                value: 'public',
            },
            {
                label: t('selectMessengerType.auto'),
                value: 'auto',
            }
        );
    }

    if (platform === 'telegram') {
        messengerTypes.push(
            {
                label: t('selectMessengerType.channel'),
                value: 'private',
            },
            {
                label: t('selectMessengerType.secret'),
                value: 'secret',
            },
            {
                label: t('selectMessengerType.auto'),
                value: 'auto',
            }
        );
    }

    if (platform === 'viber') {
        messengerTypes.push(
            {
                label: t('selectMessengerType.phone'),
                value: 'private',
            },
            {
                label: t('selectMessengerType.public'),
                value: 'public',
            },
            {
                label: t('selectMessengerType.community'),
                value: 'community',
            }
        );
    }

    return <SelectSimple {...props} options={messengerTypes} />;
}

SelectMessengerType.propTypes = {
    platform: PropTypes.string.isRequired,
};
