import React, { useState } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import { PortalModalSheet } from '@mssgme/ui';
import { ArrowLeftIcon, CloseIcon } from '@mssgme/icons';
import { GuideControl } from './GuideControl';

import styles from './GuideSlider.module.scss';

const defaultRenderControl = (props) => <GuideControl {...props} />;

export function GuideSlider({ children, modal = false, onClose, onFinish, renderControl = defaultRenderControl }) {
    const [currentSlideIndex, setCurrentSlideIndex] = useState(0);
    const slidesCount = React.Children.count(children);
    const isLastSlide = currentSlideIndex === slidesCount - 1;
    const isFirstSlide = currentSlideIndex === 0;
    const slidesWidth = 100 * slidesCount;
    const slideWidth = 100 / slidesCount;
    const BackIcon = isFirstSlide ? CloseIcon : ArrowLeftIcon;

    const handleCloseButtonClick = () => {
        if (isFirstSlide) {
            onClose?.();
        } else {
            setCurrentSlideIndex(currentSlideIndex - 1);
        }
    };

    const handleControlClick = () => {
        if (isLastSlide) {
            onFinish?.();
        } else {
            setCurrentSlideIndex(currentSlideIndex + 1);
        }
    };

    const slider = (
        <div className={styles.root}>
            {modal && (
                <div onClick={handleCloseButtonClick} className={styles.closeBackButton}>
                    <BackIcon size={22} />
                </div>
            )}
            <div className={styles.slidesContainer}>
                <div
                    className={styles.slidesList}
                    style={{
                        width: `${slidesWidth}%`,
                        transform: `translateX(-${currentSlideIndex * slideWidth}%)`,
                    }}
                >
                    {React.Children.map(children, (child) => (
                        <div className={styles.slideItem} style={{ width: `${slideWidth}%` }}>
                            {child}
                        </div>
                    ))}
                </div>
            </div>
            <ul className={styles.sliderBullets}>
                {React.Children.map(children, (_, index) => (
                    <li key={index} className={cn({ [styles.active]: currentSlideIndex === index })} />
                ))}
            </ul>
            <div className={styles.actions}>
                {renderControl({
                    onClick: handleControlClick,
                    currentSlideIndex,
                    isLastSlide,
                    isFirstSlide,
                })}
            </div>
        </div>
    );

    if (modal) {
        return (
            <PortalModalSheet padding={false} fullHeight onClose={onClose} inverse>
                {slider}
            </PortalModalSheet>
        );
    }

    return slider;
}

GuideSlider.propTypes = {
    onClose: PropTypes.func,
    onFinish: PropTypes.func,
    modal: PropTypes.bool,
    renderControl: PropTypes.func,
};
