import { useCallback, useEffect, useMemo, useState } from 'react';
import { useUnmount } from 'react-use';
import { debounce } from 'lodash';

import { tidyFilter } from '@mssgme/helpers';
import { useProducts } from '@mssgme/hooks';

// todo: move debounce logic to saga?
export const useProductsBase = ({ specifier, collectionId, list, setProductsFilter, clearProductsStore, selector }) => {
    const [isLoading, setIsLoading] = useState(true);
    const products = useProducts(specifier, selector);
    const filter = useMemo(() => {
        const tidy = tidyFilter(products.filter);

        if (Object.keys(tidy).length) {
            return JSON.stringify(tidy);
        }
    }, [products.filter]);

    const load = useCallback(
        async ({ keep, ...query } = {}) => {
            await list({
                guid: products.guid + (keep ? 0 : 1),
                query: {
                    ...specifier,
                    ...(collectionId && { collection: collectionId }),
                    ...query,
                },
            });
        },
        [products.guid, collectionId, specifier]
    );

    const loadMore = useCallback(async () => {
        if (products.isFetching || !products.next) {
            return;
        }

        await load({
            keep: 1,
            ...(products.next && { next: products.next }),
            ...(filter && { filter }),
        });
    }, [load, products.next, products.isFetching, filter]);

    const clearFilter = useCallback(() => setProductsFilter({ collectionId, name: '' }), [collectionId]);
    const setFilter = useCallback(({ target: { name, value } }) => setProductsFilter({ [name]: value, collectionId }), [
        collectionId,
    ]);
    const debouncedQuery = useMemo(
        () =>
            debounce(async (...args) => {
                await list(...args);

                setIsLoading(false);
            }, 400),
        []
    );

    useEffect(() => {
        setIsLoading(true);

        void debouncedQuery({
            guid: products.guid + 1,
            query: {
                ...specifier,
                ...(collectionId && { collection: collectionId }),
                ...(filter && { filter }),
            },
        });
    }, [products.guid, collectionId, filter, specifier]);

    useUnmount(() => {
        debouncedQuery.cancel();
        clearProductsStore();
    });

    return {
        collectionId,
        products: isLoading ? { ...products, items: [], isFetching: true, next: null } : products,
        setFilter,
        clearFilter,
        load,
        loadMore,
    };
};
