import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { prettifyUrl } from '@mssgme/shared';
import { ArrayField, TextField } from '@mssgme/ui';
import { VideoGalleryItemSchema } from '../components';

const VideoGalleryItem = ({ value, className, textClassName }) => (
    <div className={className}>
        <div className={textClassName}>
            {value.annotation ? value.annotation.replace(/\n/g, ' ') : prettifyUrl(value.url)}
        </div>
    </div>
);

VideoGalleryItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const VideoGalleryItemsField = (props) => {
    const { t } = useTranslation('landings');
    const itemFactory = useCallback(() => ({ url: '', annotation: '' }));

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.VideoGallery.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.VideoGallery.itemAddMore')}
            modalTitle={t('Page.Edit.VideoGallery.itemFormHeading')}
            Item={VideoGalleryItem}
            validationSchema={VideoGalleryItemSchema}
            {...props}
        >
            <TextField
                required
                builderInput
                name="url"
                label={t('Page.Edit.VideoGallery.fields.url.label')}
                placeholder={t('Page.Edit.VideoGallery.fields.url.placeholder')}
                autoCapitalize="off"
            />
            <TextField
                builderInput
                name="annotation"
                label={t('Page.Edit.VideoGallery.fields.annotation.label')}
                placeholder={t('Page.Edit.VideoGallery.fields.annotation.placeholder')}
                maxLength={50}
            />
        </ArrayField>
    );
};

VideoGalleryItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
