import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextField, FieldTip } from '@mssgme/ui';

Stripe.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Stripe({ enabled, name }) {
    const { t } = useTranslation('products');

    return (
        <>
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}publicKey`}
                label={t('fields.payments.stripe.publicKey.label')}
                placeholder={t('fields.payments.stripe.publicKey.placeholder')}
                disabled={!enabled}
                required
            />
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}secretKey`}
                label={t('fields.payments.stripe.secretKey.label')}
                placeholder={t('fields.payments.stripe.secretKey.placeholder')}
                disabled={!enabled}
                required
            />
            <FieldTip>
                <p style={{ margin: 0 }} dangerouslySetInnerHTML={{ __html: t('Payments.Stripe.tip') }} />
            </FieldTip>
        </>
    );
}
