import PropTypes from 'prop-types';
import React, { useMemo } from 'react';

import { Theme } from '@mssgme/ui';

export const TextStylePreview = ({ t, calcProps, ...props }) => {
    const found = props.options.find((option) => option.value === props.value);
    const fieldProps = (found && found.props) || {};
    const style = useMemo(() => Theme.compile({ textStyle: props.value }), [props.value.join(',')]);

    return (
        <div {...calcProps(props, fieldProps)} style={style}>
            {(props.supplementaryData && props.supplementaryData.textLabel) || t('landings:labels.text')}
        </div>
    );
};

TextStylePreview.propTypes = {
    supplementaryData: PropTypes.any,
    options: PropTypes.arrayOf(PropTypes.object).isRequired,
    value: PropTypes.arrayOf(PropTypes.string).isRequired,
    calcProps: PropTypes.func.isRequired,
    t: PropTypes.func.isRequired,
};

TextStylePreview.defaultProps = {
    supplementaryData: null,
};
