import PropTypes from 'prop-types';
import React from 'react';

import { useClientRect } from './useClientRect';
import { SelectionRangesContextProvider } from './SelectionRangesContext';
import { SelectionRanges } from './SelectionRanges';

export const SelectionArea = ({ children, blocks, active, ...rest }) => {
    const [ref, rect] = useClientRect();

    return (
        <SelectionRangesContextProvider hash={~~rect.top}>
            <div ref={ref} {...rest}>
                {children}
            </div>

            <SelectionRanges blocks={blocks} active={active} offset={rect} />
        </SelectionRangesContextProvider>
    );
};

SelectionArea.propTypes = {
    blocks: PropTypes.arrayOf(PropTypes.object.isRequired).isRequired,
    active: PropTypes.bool,
};
