import React, { useCallback, useMemo } from 'react';
import { useFormikContext } from 'formik';

import { cls } from '@mssgme/helpers';
import FieldRow from '../FieldRow';
import { RichTextFieldProps } from '../types';
import { defaultTheme } from './themes';
import { nodes } from './nodes';
import { RichTextFieldInput } from './RichTextFieldInput';

import styles from './RichTextField.module.scss';

export default function RichTextField({ placeholder, inputStyles, ...props }) {
    const { setFieldValue, getFieldMeta } = useFormikContext();
    const { value } = getFieldMeta(props.name);
    const inputStylesWithSelectionColors = {
        ...inputStyles,
        '--selection-color': inputStyles.backgroundColor,
        '--selection-bg-color': inputStyles.color,
    };

    const editorConfig = useMemo(
        () => ({
            namespace: 'MyEditor',
            theme: defaultTheme,
            onError(error) {
                throw error;
            },
            nodes,
            editorState: value,
        }),
        []
    );

    const handleChange = useCallback(
        (state, html) => {
            setFieldValue(props.name, JSON.stringify(state));
            setFieldValue('html', html);
        },
        [props.name, setFieldValue]
    );

    return (
        <FieldRow translate="no" {...cls([styles.root, 'notranslate'], props)}>
            {/*manually hide Google Translate extension popup button when editor is rendered, as it ignores `translate="no"` attribute*/}
            <style>{'#gtx-trans{display: none;}'}</style>

            <RichTextFieldInput
                style={inputStylesWithSelectionColors}
                placeholder={placeholder}
                editorConfig={editorConfig}
                onChange={handleChange}
            />
        </FieldRow>
    );
}

RichTextField.propTypes = RichTextFieldProps;
