import { productsSettingsRouting } from '@mssgme/products';
import {
    WebsiteBrandingEdit,
    WebsiteDelete,
    WebsiteDomainEdit,
    WebsiteEmbeddedEdit,
    WebsiteLocaleEdit,
    WebsiteNameEdit,
    WebsiteSeoEdit,
    WebsiteSettings,
    WebsiteStatusEdit,
    WebsiteTracking,
    WebsiteTrackingPlatformEdit,
    WebsiteUrlEdit,
} from './Website';
import { NoBlocksMessage } from './Page';
import { TemplateCreatePage } from './Template';

export const websiteSettingsRouting = {
    path: 'settings',
    component: WebsiteSettings,
    children: [
        {
            path: 'tracking',
            component: WebsiteTracking,
            children: [
                { path: ':platform', component: WebsiteTrackingPlatformEdit },
            ],
        },
        { path: 'seo', component: WebsiteSeoEdit },
        { path: 'url', component: WebsiteUrlEdit },
        { path: 'domain', component: WebsiteDomainEdit },
        { path: 'name', component: WebsiteNameEdit },
        { path: 'locale', component: WebsiteLocaleEdit },
        { path: 'embedded', component: WebsiteEmbeddedEdit },
        { path: 'status', component: WebsiteStatusEdit },
        { path: 'branding', component: WebsiteBrandingEdit },
        { path: 'template', component: TemplateCreatePage },
        { path: 'delete', component: WebsiteDelete },
        { path: 'no-blocks', component: NoBlocksMessage },
        { path: 'products', ...productsSettingsRouting },
    ],
};
