import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { FaFacebookF, FaGoogle, FaYandex } from 'react-icons/fa';
import { SiTiktok } from 'react-icons/si';

import { useWebsiteBySlug } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';

const menu = (t, tracking) => [
    {
        to: 'facebook',
        label: t('labels.platform.facebook'),
        icon: FaFacebookF,
        preview: tracking.facebook || t('ui:labels.notSet'),
    },
    {
        to: 'google',
        label: t('labels.platform.google'),
        icon: FaGoogle,
        preview: tracking.google || t('ui:labels.notSet'),
    },
    {
        to: 'yandex',
        label: t('labels.platform.yandex'),
        icon: FaYandex,
        preview: tracking.yandex || t('ui:labels.notSet'),
        restrict: {
            rule: 'ru-integrations',
            exclusive: true,
        },
    },
    {
        to: 'tiktok',
        label: t('labels.platform.tiktok'),
        icon: SiTiktok,
        preview: tracking.tiktok || t('ui:labels.notSet'),
    },
];

export function WebsiteTracking() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const { tracking } = useWebsiteBySlug(websiteSlug);
    const items = useMemo(() => menu(t, tracking), [t, tracking]);

    return <AclMenuPane title={t('Feedback.View.Settings.heading')} items={items} />;
}
