import { $isListNode } from '@lexical/list';
import { $findMatchingParent } from '@lexical/utils';
import { $isHeadingNode, $isQuoteNode } from '@lexical/rich-text';

const isSupportedType = (node) => $isQuoteNode(node) || $isListNode(node) || $isHeadingNode(node);

export function getNodeBlockType(editor, node, defaultValue) {
    let element = node.getKey() === 'root' ? node : node.getTopLevelElementOrThrow();

    if (!editor.getElementByKey(element.getKey())) {
        return defaultValue;
    }

    if (isSupportedType(element)) {
        element = $findMatchingParent(node, isSupportedType) || element;
    } else {
        return element.getType() || defaultValue;
    }

    if ($isQuoteNode(element)) {
        return element.getType();
    }

    return element.getTag() || defaultValue;
}
