import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { PromptModal } from '@mssgme/admin-ui';

export function BlockDeletionModal({ count = 1, ...props }) {
    const { t } = useTranslation('landings');

    return (
        <PromptModal
            title={t('labels.deleteBlockHeading', { count })}
            message={t('labels.deleteBlock', { count })}
            okText={t('labels.block.delete', { count })}
            {...props}
        />
    );
}

BlockDeletionModal.propTypes = {
    count: PropTypes.number,
    handle: PropTypes.func.isRequired,
};
