import React from 'react';
import { useTranslation } from 'react-i18next';

import { MessengersItemsField, StreamlinedScreen } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { ProductsSettingsForm } from '../ProductsSettingsForm';

export function ProductsContacts() {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();

    return (
        <StreamlinedScreen
            preferDynamicBack
            modal
            inverse={false}
            nopadding={false}
            fullHeight={false}
            backPath={router.settings.toSettings()}
            title={t('ProductsContacts.title')}
        >
            <ProductsSettingsForm successRedirectPath={router.settings.toSettings()}>
                <MessengersItemsField
                    builderInput
                    mode="small"
                    name="contacts.messengers"
                    // label={t('ProductsContacts.labels.messengers')}
                />
            </ProductsSettingsForm>
        </StreamlinedScreen>
    );
}
