import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { useCollections, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, FieldTip } from '@mssgme/ui';
import { Products } from '@mssgme/userpage-core';
import { useProductsContext } from '@mssgme/products';
import {
    BlockEditForm,
    EditStylingForm,
    ProductsBlockSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';
import { CollectionItemsField } from './CollectionItemsField';

export function ProductsEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl, pathname } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const { bind, isNew } = useBlockEditor({ website, page, blockId, backPath, as: ProductsEdit });
    const { specifier } = useProductsContext();
    const { items, allPublishedProductsCount } = useCollections(specifier);
    const adminPath = useMemo(() => generateUrl('./products'), []);
    const isBlankState = isNew && allPublishedProductsCount <= 0;

    // const makeTotalLabel = useCallback((value) => value || t('Page.Edit.Products.labels.noLimit'), [t]);

    return (
        <BlockEditForm
            {...bind}
            validationSchema={ProductsBlockSchema}
            isInitialDirty={isNew && !isBlankState}
            title={t('Page.Edit.Products.heading')}
        >
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ minHeight: 290, maxHeight: 290, marginBottom: 16 }}>
                        <bind.blockComponent block={values} isMobileMode />
                    </StyledBlockPreview>

                    {/*<TextField*/}
                    {/*    builderInput*/}
                    {/*    optional*/}
                    {/*    name="title"*/}
                    {/*    label={t('Page.Edit.Products.fields.title.label')}*/}
                    {/*    placeholder={t('Page.Edit.Products.fields.title.placeholder')}*/}
                    {/*    maxLength={50}*/}
                    {/*/>*/}

                    {/*<RangeSliderField*/}
                    {/*    builderInput*/}
                    {/*    name="limit"*/}
                    {/*    label={t('Page.Edit.Products.fields.limit.label')}*/}
                    {/*    step={1}*/}
                    {/*    min={2}*/}
                    {/*    max={20}*/}
                    {/*    makeLabel={makeTotalLabel}*/}
                    {/*/>*/}

                    {/*<RangeSliderField*/}
                    {/*    builderInput*/}
                    {/*    name="total"*/}
                    {/*    label={t('Page.Edit.Products.fields.total.label')}*/}
                    {/*    step={1}*/}
                    {/*    min={0}*/}
                    {/*    max={20}*/}
                    {/*    makeLabel={makeTotalLabel}*/}
                    {/*/>*/}

                    {items.filter((item) => item.published && item.publishedProductsCount > 0).length > 1 ? (
                        <CollectionItemsField
                            builderInput
                            // optional
                            name="collections.include"
                            label={t('Page.Edit.Products.fields.collectionsInclude.label')}
                        />
                    ) : (
                        <FieldTip tipId="landings:Page.Edit.Products.tips.noCollections" />
                    )}

                    <Button
                        fullWidth
                        btnStyle="primary"
                        tag={Link}
                        to={{ pathname: adminPath, state: { blockUrl: pathname } }}
                        style={{ marginTop: 8, marginBottom: 16 }}
                    >
                        {isBlankState
                            ? t('Page.Edit.Products.labels.addProducts')
                            : t('Page.Edit.Products.labels.manageCRM')}
                    </Button>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ProductsEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

ProductsEdit.block = Products;
