import { useCallback } from 'react';
import { useLocalStorage } from 'react-use';

export function useExpired(key, days) {
    const [expired, setExpired] = useLocalStorage(key, 0);
    const isExpired = Date.now() >= expired;

    const renew = useCallback(() => {
        setExpired(Date.now() + 864e5 * days);
    }, [days]);

    return [isExpired, renew];
}
