import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsites } from '@mssgme/hooks';
import { FloatingActions, ListNoStyle, TopLevelScreen } from '@mssgme/ui';
import { BannerContainer } from '@mssgme/billing';
import { useWebsitesFeedbackCounts } from '../../../../hooks';
import { CreateWebsiteButton } from '../../../../UI';
import { WebsiteItem } from './WebsiteItem';

import styles from './WebsitesList.module.scss';

export function WebsitesList() {
    const { t } = useTranslation('landings');
    const websites = useWebsites();
    const hasWebsites = websites.length > 0;
    const ids = useMemo(() => websites.map(({ _id }) => _id), [websites]);
    const { feedbacks } = useWebsitesFeedbackCounts(ids);

    return (
        <TopLevelScreen title={hasWebsites && t('Builder.View.Home.heading')}>
            {hasWebsites ? (
                <>
                    <ListNoStyle>
                        {websites.map((website, index) => (
                            <WebsiteItem
                                key={website._id || index}
                                website={website}
                                feedbacks={feedbacks[website._id]}
                            />
                        ))}
                        <BannerContainer />
                    </ListNoStyle>
                    <FloatingActions>
                        <CreateWebsiteButton />
                    </FloatingActions>
                </>
            ) : (
                <div className={styles.emptyMessage}>
                    <h2>{t('Builder.View.Home.empty.title')}</h2>
                    <p>{t('Builder.View.Home.empty.message')}</p>
                    <div>
                        <CreateWebsiteButton />
                    </div>
                </div>
            )}
        </TopLevelScreen>
    );
}
