import React from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { Link, Redirect } from 'react-router-dom';

import { useNavigationUtils, useUpgradeLocation, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, ModalPane } from '@mssgme/ui';
import { usePublishCheck } from '../../../../hooks';

import styles from './HasAdvancedBlocksMessage.module.scss';

export function HasAdvancedBlocksMessage() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website } = useWebsitePageBySlugs();
    const upgradeUrl = useUpgradeLocation();
    const warning = usePublishCheck(website.pages);
    const closePath = generateUrl('../../../edit');

    if (!warning) return <Redirect to={closePath} />;

    return (
        <ModalPane title={t(`Editor.HasAdvancedBlocksMessage.${warning.code}.header`)} onClose={closePath}>
            <ul className={styles.blocksList}>
                {warning.blocks?.map((block) => (
                    <li key={block.kind}>
                        <span>{t(`admin-ui:labels.block.kinds.${block.kind}`)}</span>
                        <span>{`x${block.qty} `}</span>
                    </li>
                ))}
            </ul>
            <p className={styles.p}>
                <Trans
                    t={t}
                    i18nKey={`Editor.HasAdvancedBlocksMessage.${warning.code}.message`}
                    values={{ plan: warning.target }}
                />
            </p>
            <Button
                fullWidth
                tag={Link}
                to={upgradeUrl}
                style={{ marginTop: 20, fontSize: 16 }}
                data-button="advanced-blocks-upgrade"
            >
                {t(`Editor.HasAdvancedBlocksMessage.${warning.code}.button`, { plan: warning.target })}
            </Button>
        </ModalPane>
    );
}
