import React from 'react';

import { isSameArray } from '@mssgme/helpers';
import { TagsInput } from '../Inputs';
import { TagsFieldProps } from './types';
import FieldRow from './FieldRow';

export default function TagsField({ onChange, ...props }) {
    return (
        <FieldRow {...props}>
            {({ field: { name, ...field }, form: { setFieldValue, setFieldTouched }, meta: { initialValue } }) => (
                <TagsInput
                    name={name}
                    {...field}
                    onChange={(tags) => {
                        const dirty = !isSameArray(initialValue, tags, { ordered: true });

                        setFieldValue(name, dirty ? tags : initialValue);
                        setFieldTouched(name, dirty, dirty);

                        onChange && onChange(name, tags);
                    }}
                />
            )}
        </FieldRow>
    );
}

TagsField.propTypes = TagsFieldProps;
