import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useRouteMatch } from 'react-router-dom';
import { MdOutlineDesktopWindows } from 'react-icons/md';
import { BsPhone } from 'react-icons/bs';

import { AddButtonPlaceholder } from './AddButtonPlaceholder';
import { DashboardActionButton } from './DashboardActionButton';

import styles from './Dashboard.module.scss';

PreviewDashboardActions.propTypes = {
    isEmptyPage: PropTypes.bool.isRequired,
};

export function PreviewDashboardActions({ isEmptyPage }) {
    const { t } = useTranslation('landings');
    const { url } = useRouteMatch();

    return (
        <div className={styles.row}>
            <DashboardActionButton
                to={`${url}/preview/mobile`}
                title={t('Editor.Dashboard.items.preview')}
                icon={BsPhone}
                disabled={isEmptyPage}
                rounded
            />

            <AddButtonPlaceholder />

            <DashboardActionButton
                to={`${url}/preview/desktop`}
                title={t('Editor.Dashboard.items.preview')}
                icon={MdOutlineDesktopWindows}
                disabled={isEmptyPage}
                rounded
            />
        </div>
    );
}
