import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import WebFont from 'webfontloader';
import cn from 'classnames';

import styles from './SelectFont.module.scss';
import { useTranslation } from 'react-i18next';

const DEFAULT_FONTS = {
    families: {
        google: [
            'Alice',
            'Amatic SC',
            'Caveat',
            'Comfortaa',
            'Cormorant',
            'IBM Plex Sans',
            'Inter',
            'Kelly Slab',
            'Lobster',
            'Manrope',
            'Merriweather',
            'Montserrat',
            'Montserrat Alternates',
            'Noto Sans',
            'Noto Serif',
            'Open Sans',
            'Oswald',
            'Pacifico',
            'Pangolin',
            'Playfair Display',
            'PT Mono',
            'PT Sans',
            'PT Serif',
            'Raleway',
            'Roboto Condensed',
            'Roboto Slab',
            'Roboto', 'Rubik',
            'Russo One',
            'Source Code Pro',
            'Ubuntu',
            'Unbounded',
        ],
        websafe: [
            'Arial',
            'Comic Sans MS',
            'Georgia',
            'Helvetica',
            'Impact',
            'Times New Roman',
        ],
    },
    variants: [300,400,500,600,700,900],
    subsets: ['latin','cyrillic'],
};

const WEBFONT_LOAD_OPTIONS = {
    classes: false,
    events: false,
    google: {
        families: DEFAULT_FONTS.families.google.map((family) => `${family}:${DEFAULT_FONTS.variants.join(',')}:${DEFAULT_FONTS.subsets.join(',')}`),
    },
};

const OPTIONS = [
    ...DEFAULT_FONTS.families.google.map((family) => ({ family, provider: 'google' })),
    ...DEFAULT_FONTS.families.websafe.map((family) => ({ family, provider: 'websafe' })),
].sort((a, b) => a.family < b.family ? -1 : a.family > b.family ? 1 : 0);

const SelectFont = ({ onChange, name, value }) => {
    const { t } = useTranslation('ui');

    useEffect(() => {
        WebFont.load(WEBFONT_LOAD_OPTIONS);
    }, []);

    return (
        <ul className={styles.list}>
            {OPTIONS.map((font, index) => {
                const isActive = font.family === value.family;

                return (
                    <li
                        key={index}
                        className={cn(styles.item, isActive && styles.active)}
                        style={{ fontFamily: font.family }}
                        onClick={() => onChange({ name, value: font })}
                    >
                        <h2 className={styles.label}>{font.family}</h2>
                        <p className={styles.subLabel}>{t('SelectFont.example_text')}</p>
                    </li>
                );
            })}
        </ul>
    );
};

SelectFont.propTypes = {
    onChange: PropTypes.func.isRequired,
    name: PropTypes.string.isRequired,
    value: PropTypes.object,
};

export default SelectFont;
