import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { TabsSelector } from '@mssgme/ui';
import { hasPrimaryTag, BLANK_TAG, TEMPLATE_TAGS } from './utils';

import styles from './ChooseTemplate.module.scss';

export const TagTabs = ({ templates, value, onSelect }) => {
    const { t } = useTranslation('landings');

    const options = useMemo(
        () => [...TEMPLATE_TAGS.filter(({ tag }) => templates.find(hasPrimaryTag(tag))), BLANK_TAG],
        [templates]
    );
    const tabsOptions = useMemo(() => {
        return options.map(({ tag, icon, label }) => {
            return {
                value: tag,
                props: {
                    children: (
                        <div className={cn(styles.tab, { [styles.active]: value === tag })}>
                            {icon} {label && t(`Website.Edit.ChooseTemplate.tags.${label}`)}
                        </div>
                    ),
                },
            };
        });
    }, [value, options]);

    return (
        <div className={styles.tabs}>
            <TabsSelector
                className={styles.tabsSelector}
                items={tabsOptions}
                value={value}
                onSelect={onSelect}
                noArrows
            />
        </div>
    );
};

TagTabs.propTypes = {
    templates: PropTypes.array.isRequired,
    value: PropTypes.string,
    onSelect: PropTypes.func.isRequired,
};
