import cn from 'classnames';
import React, { useState } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { round } from 'lodash';

import { WarningMessage } from '@mssgme/ui';

import { useFormatPrice, usePlanDetails } from '../../hooks';
import getDiscount from '../../utils/getDiscount';
import IconCheck from '../../../icons/icon_check.svg';
import styles from './SinglePlanPageContent.module.scss';
import SubscribeButton from './SubscribeButton';
import ChangePlanButton from './ChangePlanButton';

export default function SinglePlanPageContent() {
    const { t } = useTranslation('billing');
    const { plan: planId } = useParams();
    const { plan, subscription, isCurrentPlan, currentPlanPeriod } = usePlanDetails(planId);
    const [period, setPeriod] = useState(isCurrentPlan && currentPlanPeriod === 'annually' ? 'monthly' : 'annually');
    const formatPrice = useFormatPrice();

    const discount = getDiscount(plan.prices.monthly.amount, plan.prices.annually.amount);
    const features = t(`PlansPage.features.${plan.id}`, { returnObjects: true });

    const renderButton = () => {
        if (!subscription) {
            return <SubscribeButton planId={planId} period={period} />;
        }
        if (subscription.actions.changePlan) {
            return <ChangePlanButton planId={planId} period={period} />;
        }
        return (
            <WarningMessage>
                {t(subscription.status === 'trialing' ? 'warnings.trial' : 'warnings.resume_subscription')}
            </WarningMessage>
        );
    };

    return (
        <>
            <div className={styles.contentContainer}>
                <div className={styles.heading}>
                    <h1>{t('SinglePlanPage.heading', { planName: plan.name })}</h1>
                    <p>{t('SinglePlanPage.subHeading')}</p>
                </div>
                <ul className={styles.plansList}>
                    <li
                        className={cn({
                            [styles.active]: period === 'monthly',
                            [styles.disabled]: isCurrentPlan && currentPlanPeriod === 'monthly',
                        })}
                        onClick={() => setPeriod('monthly')}
                        data-event="select-period"
                        data-period="monthly"
                    >
                        <div>
                            <div className={styles.plansListTextLine}>
                                <span>{formatPrice(plan.prices.monthly)}</span> {t('pagePlans.per_month')}
                            </div>
                        </div>
                        {isCurrentPlan && currentPlanPeriod === 'monthly' && (
                            <span className={cn(styles.label, styles.currentPeriod)}>
                                {t('pagePlans.currentPeriod')}
                            </span>
                        )}
                    </li>
                    <li
                        className={cn({
                            [styles.active]: period === 'annually',
                            [styles.disabled]: isCurrentPlan && currentPlanPeriod === 'annually',
                        })}
                        onClick={() => setPeriod('annually')}
                        data-event="select-period"
                        data-period="annually"
                    >
                        <div>
                            <div className={styles.plansListTextLine}>
                                <span>{formatPrice(plan.prices.annually)}</span> {t('pagePlans.per_year')}
                            </div>
                            <div className={styles.plansListTextLine}>
                                {t('ui:labels.or')}{' '}
                                <span>
                                    {formatPrice({
                                        amount: round(plan.prices.annually.amount / 12, 2),
                                        currency: plan.prices.annually.currency,
                                    })}
                                </span>{' '}
                                {t('pagePlans.per_month')}
                            </div>
                            {isCurrentPlan && currentPlanPeriod === 'annually' ? (
                                <span className={cn(styles.label, styles.currentPeriod)}>
                                    {t('pagePlans.currentPeriod')}
                                </span>
                            ) : discount ? (
                                <span className={styles.label}>{t('pagePlans.discount', { discount })}</span>
                            ) : null}
                        </div>
                    </li>
                </ul>
                <ul className={styles.featuresList}>
                    {features.map((key, index) => (
                        <li key={'feat_' + index}>
                            <div className={styles.checkIcon}>
                                <IconCheck />
                            </div>
                            {t(key)}
                        </li>
                    ))}
                </ul>
                <div className={styles.footerCta}>
                    {renderButton()}
                    <div className={styles.terms}>
                        <a href="https://mssg.me/privacy-policy" target="_blank" rel="noopener noreferrer">
                            {t('pagePlans.privacy')}
                        </a>
                        <a href="https://mssg.me/terms-of-use" target="_blank" rel="noopener noreferrer">
                            {t('pagePlans.terms')}
                        </a>
                    </div>
                </div>
            </div>
        </>
    );
}
