import React from 'react';

import FieldRow from './FieldRow';
import { PriceCompoundInput, priceToStrTag } from '../Inputs';
import { PriceFieldProps } from './types';

export default function PriceCompoundField({ onChange, ...props }) {
    return (
        <FieldRow {...props}>
            {({
                field: { name, ...field },
                form: { errors, touched, setFieldValue, setFieldTouched },
                meta: { initialValue },
            }) => (
                <PriceCompoundInput
                    {...field}
                    name={name}
                    errors={touched[name] && errors[name]}
                    onChange={
                        onChange ||
                        ((e) => {
                            setFieldValue(name, e.target.value, false);
                            setFieldTouched(
                                name,
                                priceToStrTag(initialValue || {}) !== priceToStrTag(e.target.value),
                                false
                            );
                        })
                    }
                />
            )}
        </FieldRow>
    );
}

PriceCompoundField.propTypes = PriceFieldProps;
