import { object, string, array } from 'yup';

export const ListItemSchema = object({
    content: string()
        .trim()
        .ensure()
        .min(1, 'common:errorsList.block_list_item_length')
        .max(800, 'common:errorsList.block_list_item_length'),
});

export const ListBlockSchema = object({
    items: array().min(1, 'common:errorsList.block_list_count'),
});
