import { useMemo } from 'react';
import { useAsync } from 'react-use';

import { useActions } from '@mssgme/hooks';

const SHOW_COUNTS_FOR = ['new', 'processing'];

export const useSingleWebsiteFeedbackCounts = (websiteId, types = SHOW_COUNTS_FOR) => {
    const { api } = useActions();

    const { value, loading } = useAsync(async () => api.feedbacks.count({ websiteId }), [websiteId]);
    const feedbacks = useMemo(() => {
        const isEmpty = !(value && types.some((type) => value[type]));

        if (isEmpty) {
            return;
        }

        return types.map((status) => ({ status, count: value[status] || 0 }));
    }, [value, types]);

    return {
        loading,
        feedbacks,
    };
};

export const useWebsitesFeedbackCounts = (websiteIds, types = SHOW_COUNTS_FOR) => {
    const { api } = useActions();

    const { value, loading } = useAsync(
        async () =>
            websiteIds && websiteIds.length && api.feedbacks.perWebsiteCount({ payload: { websiteIds, types } }),
        [websiteIds, types]
    );

    const feedbacks = useMemo(() => {
        const result = {};

        if (value) {
            for (const [id, bucket] of Object.entries(value)) {
                result[id] = types.map((status) => ({ status, count: bucket[status] || 0 }));
            }
        }

        return result;
    }, [value, types]);

    return {
        loading,
        feedbacks,
    };
};
