import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { FiChevronRight } from 'react-icons/fi';

import { cls } from '@mssgme/helpers';

import styles from './EntityItemPane.module.scss';

export default function EntityItemPane({ title, url, status, badge, tag: Tag, children, dataElement, ...rest }) {
    const content = (
        <>
            <span className={styles.title} title={title}>
                {title}
            </span>
            {url && <span className={styles.url}>{url}</span>}
            {status && <span className={styles.status}>{status}</span>}
            <div className={styles.chevron}>
                {badge}
                <FiChevronRight size={22} color="#333" />
            </div>
        </>
    );

    return (
        <Tag
            {...cls([styles.root, !!children && styles.container], rest)}
            {...(dataElement ? { 'data-element': dataElement } : {})}
        >
            {children}
            {children ? <div className={styles.wrapper}>{content}</div> : content}
        </Tag>
    );
}

EntityItemPane.propTypes = {
    title: PropTypes.string.isRequired,
    url: PropTypes.string,
    status: PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.element]),
    children: PropTypes.any,
    tag: PropTypes.any,
    className: PropTypes.string,
    style: PropTypes.object,
    badge: PropTypes.oneOfType([PropTypes.bool, PropTypes.element]),
    dataElement: PropTypes.string,
};

EntityItemPane.defaultProps = {
    tag: Link,
    url: '',
    status: '',
    children: null,
    className: '',
    dataElement: '',
};
