import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import styles from './RangeSlider.module.scss';

export function RangeSlider({ makeLabel, showLabels, disabled, ...props }) {
    return (
        <div className={cn(styles.root, disabled && styles.disabled)}>
            {showLabels && <div className={styles.label}>{makeLabel ? makeLabel(props.min) : props.min}</div>}
            <div className={styles.inputWrapper}>
                <input type="range" disabled={disabled} {...props} />
            </div>
            {showLabels && (
                <div className={cn(styles.label, styles.right)}>{makeLabel ? makeLabel(props.value) : props.value}</div>
            )}
        </div>
    );
}

RangeSlider.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.number.isRequired,
    disabled: PropTypes.bool,
    min: PropTypes.number.isRequired,
    max: PropTypes.number.isRequired,
    step: PropTypes.number.isRequired,
    makeLabel: PropTypes.func,
    showLabels: PropTypes.bool,
    onChange: PropTypes.func.isRequired,
};

RangeSlider.defaultProps = {
    makeLabel: undefined,
    showLabels: true,
    disabled: false,
};
