import { useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { trans } from '@mssgme/shared';

/**
 * Load `userwebsite` localization. If `language` parameter is not specified, uses i18n-next current language.
 *
 * @param {(language: string) => Promise<object>} loader Translate file loader (dynamic import, etc.)
 * @param {'en'|'uk'|'ru'} [language] An override for language that would be passed to the loader as first param (`i18n.language`, if not specified)
 * @returns {{language: string, isLoaded: boolean, i18n: i18n}} Returns loader status (language, loading state)
 */
export function useFrontendTranslations(loader, language) {
    const { i18n } = useTranslation();
    const [isLoaded, setLoaded] = useState(false);

    const loadLanguage = useMemo(() => {
        const supportedLngs = i18n.options.supportedLngs.filter((lang) => lang !== 'cimode');
        return supportedLngs.includes(language) ? language : supportedLngs[0] || 'en';
    }, [language, i18n.language, i18n.options.supportedLngs]);

    useEffect(() => {
        async function load() {
            try {
                const locales = await loader(loadLanguage);

                trans.extend(locales.default || locales);
            } finally {
                setLoaded(true);
            }
        }

        void load();
    }, [loadLanguage]);

    return { isLoaded, i18n, language: loadLanguage };
}
