import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils } from '@mssgme/hooks';
import { EntityItemPane, LandingsPropTypes } from '@mssgme/ui';
import { prettifyUrl } from '@mssgme/shared';
import { useWebsiteUrl } from '../../../../hooks';
import { FeedbacksBadge } from '../../../../UI';

WebsiteItem.propTypes = {
    website: PropTypes.object.isRequired,
    feedbacks: PropTypes.arrayOf(
        PropTypes.shape({
            status: LandingsPropTypes.feedbackStatus.isRequired,
            count: PropTypes.number.isRequired,
        })
    ),
};

export function WebsiteItem({ website, feedbacks }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const absoluteUrl = useWebsiteUrl(website);

    return (
        <EntityItemPane
            key={website._id}
            title={website.name}
            url={website.scaffold ? '' : prettifyUrl(absoluteUrl)}
            status={!website.enabled && t('ui:labels.notPublished')}
            to={generateUrl(`-/${website._id}`)}
            badge={feedbacks && <FeedbacksBadge short feedbacks={feedbacks} />}
        />
    );
}
