import React, { useCallback } from 'react';
import { Provider } from 'react-redux';

import { BlockBase, BLOCKS_MAP } from '@mssgme/userpage-core';
import { computeComponentInsets } from './SortableBlocksList';

export const useBlockRenderer = (rendererConfig, store) => {
    return useCallback(
        (block, index) => {
            const insets = computeComponentInsets(block, index, rendererConfig);
            const Component = BLOCKS_MAP[block.kind];
            const key = block._id || `${block.kind}-${index}`;

            const result = Component ? (
                <Component
                    key={key}
                    id={`block-${block._id}`}
                    interactive={rendererConfig.isViewMode}
                    isViewMode={rendererConfig.isViewMode}
                    isMobileMode={rendererConfig.isMobileMode}
                    isWebsitePublished={rendererConfig.isWebsitePublished}
                    block={block}
                    insets={insets}
                    sortable
                />
            ) : (
                <BlockBase key={key} insets={insets} sortable>
                    {`<Block @${block.kind} #${block._id.slice(-6)} />`}
                </BlockBase>
            );

            return rendererConfig.isViewMode ? (
                <Provider key={key} store={store}>
                    {result}
                </Provider>
            ) : (
                result
            );
        },
        [rendererConfig, store]
    );
};
