import { Styling } from '@mssgme/ui';

export const imageStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                fields.borderRadius({
                    min: 1,
                    max: 200,
                    if: (get) => get('padding'),
                    disabledValue: null,
                }),
                Styling.s.range('width', {
                    label: t('Page.fields.width.label'),
                    min: 10,
                    max: 100,
                    step: 1,
                    if: (get) => get('padding'),
                    disabledValue: null,
                }),
                Styling.s.list('alignment', {
                    label: t('Page.fields.alignment.label'),
                    options: [
                        { value: 'left', label: t('Page.fields.alignment.values.left') },
                        { value: 'center', label: t('Page.fields.alignment.values.center') },
                        { value: 'right', label: t('Page.fields.alignment.values.right') },
                    ],
                    disabledValue: null,
                    if: (get) => get('width') < 100,
                }),
                Styling.s.list('padding', {
                    label: t('Page.fields.padding.label'),
                    options: [
                        { value: true, label: t('Page.fields.padding.values.yes') },
                        { value: false, label: t('Page.fields.padding.values.no') },
                    ],
                }),
            ],
        ]),
    };
};
