import { useMemo } from 'react';

import { checkRestrictions } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';

/**
 * @param {{ blocks: *[], theme: { colorId: string, styleId: string }}} page Page/template
 * @returns {{
 *   upgrade: false;
 *   warning: false;
 * } | {
 *   upgrade: string;
 *   warning: { message: string, code: 'unpaid'|'forbidden'|'upgrade' };
 * }} Access error as a `warning` and string ID of the plan to upgrade to if  <i>false</i>
 */
export const usePageUpgradeCheck = (page) => {
    const workspace = useWorkspace();
    const blocks = page?.blocks;
    const theme = page?.theme;

    return useMemo(() => {
        let warning = false;
        const context = { workspace };
        const callback = (error) => {
            warning = (error?.code === 'upgrade') && error.target ? error : false;
        };

        theme && checkRestrictions(context, 'page-themes:edit', theme, callback);

        if (!warning && blocks?.length) {
            checkRestrictions(context, 'page-blocks-publish', blocks, callback);
        }

        return {
            warning,
            upgrade: (warning && warning.target) || false,
        };
    }, [workspace, blocks, theme]);
};
