import { useCallback, useEffect, useMemo, useState } from 'react';
import { useAsync } from 'react-use';
import { useHistory } from 'react-router-dom';
import { pick } from 'lodash';

import { isSameArray } from '@mssgme/helpers';
import { useActions, useCollections } from '@mssgme/hooks';
import { handleError } from '@mssgme/lib';
import { useProductsContext } from '../../../hooks';

export const useCollectionEdit = (collectionId) => {
    const isNew = !collectionId;
    const history = useHistory();
    const { api } = useActions();
    const { router, specifier } = useProductsContext();
    const { items, isFetching } = useCollections(specifier);
    const { loading, value } = useAsync(async () => collectionId && api.collections.products.get({ collectionId }), [
        collectionId,
    ]);
    const currentCollection = useMemo(
        () =>
            items.find((col) => col._id === collectionId) || {
                name: '',
                slug: '',
                website: null,
                default: false,
                published: true,
                products: [],
                ...specifier,
            },
        [items, specifier]
    );
    const [collection, setCollection] = useState(() => ({ products: [], ...currentCollection }));
    const [isRedirecting, setRedirecting] = useState(false);

    const handleSubmit = useCallback(
        async (values, { setValues, setTouched }) => {
            const payload = pick(values, ['name', 'website', 'slug', 'published', 'default']);
            const { items, collectionId: updatedId } = isNew
                ? await api.collections.create({ payload })
                : await api.collections.update({ collectionId, payload });
            const result = { ...items.find((col) => col._id === updatedId), products: values.products };

            if (values.products) {
                await api.collections.products.update({ collectionId: updatedId, payload: { ids: values.products } });
            }

            setCollection(result);
            setRedirecting(true);
            setValues(result);
            setTouched({});

            const route = router.collection.toViewProducts(updatedId);

            if (isNew) {
                history.replace(route);
            } else {
                history.push(route);
            }
        },
        [collectionId]
    );

    const handleRemoveClick = useCallback(
        async (e) => {
            e.preventDefault();

            try {
                await api.collections.delete({ collectionId });

                setRedirecting(true);
                setTimeout(() => history.push(router.collection.toAll()), 100);
            } catch (e) {
                handleError(e);
            }
        },
        [collectionId]
    );

    useEffect(() => {
        if (collectionId && !collection._id) {
            setCollection((old) => ({ products: old.products || [], ...currentCollection }));
        }
    }, [currentCollection]);

    useEffect(
        () =>
            void setCollection((old) => {
                const products = value || [];

                return isSameArray(old.products || [], products, { ordered: true }) ? old : { ...old, products };
            }),
        [value]
    );

    return {
        isNew,
        isFetching: isFetching || loading,
        isRedirecting,
        collection,
        handleSubmit,
        handleRemoveClick,
    };
};
