import React from 'react';
import PropTypes from 'prop-types';
import { FiAlignLeft, FiAlignCenter, FiAlignRight, FiAlignJustify } from 'react-icons/fi';
import { MdDoNotDisturb } from 'react-icons/md';
import { AiOutlineBold, AiOutlineUnderline, AiOutlineItalic, AiOutlineStrikethrough } from 'react-icons/ai';

import { Styling, iconFieldPropsFactory } from '@mssgme/ui';
import { TextStylePreview } from '../TextStylePreview';

const SizeIcon = ({ fontSize }) => {
    return <div style={{ fontSize, fontWeight: 'bold', lineHeight: 1 }}>Aa</div>;
};

SizeIcon.propTypes = {
    fontSize: PropTypes.number.isRequired,
};

const makeField = (metaOrFactory, data) =>
    typeof metaOrFactory === 'function' ? metaOrFactory(data) : { ...data, ...metaOrFactory };

/* eslint-disable react/display-name */
export const makeStylingFieldFactories = ({ t }) => {
    return {
        fontSize: (metaOrFactory) => {
            return Styling.s.list(
                'fontSize',
                makeField(metaOrFactory, {
                    label: t('Page.fields.fontSize.label'),
                    options: [
                        {
                            value: '1.3em',
                            label: t('Page.fields.fontSize.values.big'),
                            icon: () => <SizeIcon fontSize={22} />,
                        },
                        {
                            value: '1em',
                            label: t('Page.fields.fontSize.values.medium'),
                            icon: () => <SizeIcon fontSize={16} />,
                        },
                        {
                            value: '0.8em',
                            label: t('Page.fields.fontSize.values.small'),
                            icon: () => <SizeIcon fontSize={14} />,
                        },
                    ],
                })
            );
        },
        textAlign: (metaOrFactory) => {
            return Styling.s.list(
                'textAlign',
                makeField(metaOrFactory, {
                    label: t('Page.fields.align.label'),
                    options: [
                        { value: 'left', label: t('Page.fields.align.values.left'), icon: FiAlignLeft },
                        { value: 'center', label: t('Page.fields.align.values.center'), icon: FiAlignCenter },
                        { value: 'right', label: t('Page.fields.align.values.right'), icon: FiAlignRight },
                        { value: 'justify', label: t('Page.fields.align.values.justify'), icon: FiAlignJustify },
                    ],
                })
            );
        },
        align: (metaOrFactory) => {
            return Styling.s.list(
                'align',
                makeField(metaOrFactory, {
                    label: t('Page.fields.align.label'),
                    options: [
                        { value: 'left', label: t('Page.fields.align.values.left'), icon: FiAlignLeft },
                        { value: 'center', label: t('Page.fields.align.values.center'), icon: FiAlignCenter },
                        { value: 'right', label: t('Page.fields.align.values.right'), icon: FiAlignRight },
                    ],
                })
            );
        },
        textStyle: (metaOrFactory) => {
            return Styling.s.list(
                'textStyle',
                makeField(metaOrFactory, {
                    label: t('Page.fields.textStyle.label'),
                    multiple: true,
                    toggle: true,
                    initial: ['normal'],
                    mask: (value) => {
                        if (value.length <= 0) {
                            return ['normal'];
                        }

                        if (value[value.length - 1] === 'normal') {
                            return ['normal'];
                        }

                        return value.length > 1 ? value.filter((style) => style !== 'normal') : value;
                    },
                    options: [
                        { value: 'normal', label: t('Page.fields.textStyle.values.normal'), icon: MdDoNotDisturb },
                        { value: 'bold', label: t('Page.fields.textStyle.values.bold'), icon: AiOutlineBold },
                        {
                            value: 'underline',
                            label: t('Page.fields.textStyle.values.underline'),
                            icon: AiOutlineUnderline,
                        },
                        { value: 'italic', label: t('Page.fields.textStyle.values.italic'), icon: AiOutlineItalic },
                        {
                            value: 'line-through',
                            label: t('Page.fields.textStyle.values.line-through'),
                            icon: AiOutlineStrikethrough,
                        },
                    ],
                    preview: TextStylePreview,
                })
            );
        },
        color: (metaOrFactory) => {
            return Styling.s.color(
                'color',
                makeField(metaOrFactory, {
                    label: t('Page.fields.color.label'),
                    restrict: 'page-styling',
                })
            );
        },
        borderRadius: (metaOrFactory) => {
            return Styling.s.range(
                'borderRadius',
                makeField(metaOrFactory, {
                    label: t('Page.fields.borderRadius.label'),
                    min: 0,
                    max: 200,
                    step: 1,
                    restrict: 'page-styling',
                })
            );
        },
        icon: (metaOrFactory) => {
            return Styling.s.icon(
                'icon',
                makeField(metaOrFactory, {
                    label: t('Page.fields.icon.label'),
                    ...iconFieldPropsFactory({
                        t,
                        nullable: true,
                        groupLabel: (group) => t(`Page.fields.icon.groups.${group}`),
                        skip: ['vk'],
                    }),
                })
            );
        },
    };
};
