import { useI18n } from '@mssgme/i18n';

export const useProductsTranslations = () => {
    return useI18n('products', (language) =>
        import(
            /* webpackChunkName: "locales.products" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/products.json`
        )
    );
};
