import { useMemo } from 'react';

import { useUser } from './useUser';

/**
 * @param {{ email: boolean; telegram: boolean; webhook: boolean }} [crm] Optional CRM settings
 * @returns {{telegram: boolean; email: boolean; webhook: boolean; enabled: boolean}} Hash of enabled settings
 */
export function useNotificationsSettings(crm) {
    const { notificationSettings } = useUser();

    return useMemo(() => {
        const result = {
            email: false,
            telegram: false,
            webhook: false,
            enabled: false,
        };

        for (const type of ['email', 'telegram', 'webhook']) {
            result[type] = !!(notificationSettings?.[type]?.enabled && !(crm && !crm.notifications[type]));
        }

        result.enabled = result.email || result.telegram;

        return result;
    }, [notificationSettings, crm]);
}
