import TinyColor from 'tinycolor2';

import { defaultBackground, defaultBlockType, defaultColor, defaultFontSize } from '../constants';
import { blockTypesMap } from '../blockTypes';

export const isPartiallyEqual = (base, target) => {
    for (const [prop, value] of Object.entries(base)) {
        if (target[prop] !== value) {
            return false;
        }
    }

    return true;
};

export const normalizeColor = (color) => color && TinyColor(color).toHex8String();

export const fontSizeAccessor = {
    prop: 'font-size',
    defaultValue: defaultFontSize,
    normalize: (textOrValue) => {
        if (textOrValue === undefined || textOrValue === '') {
            return defaultFontSize;
        }

        return typeof textOrValue === 'number' ? `${textOrValue}px` : String(textOrValue);
    },
};

export const colorAccessor = {
    prop: 'color',
    normalize: (textOrValue) => (textOrValue ? normalizeColor(textOrValue) : defaultColor),
};

export const backgroundAccessor = {
    prop: 'background',
    normalize: (textOrValue) =>
        normalizeColor(textOrValue && textOrValue !== 'transparent' ? textOrValue : defaultBackground),
};

export const getSupportedBlockType = (blockType) => {
    if (blockTypesMap[blockType]) {
        return blockType;
    }

    return defaultBlockType;
};

export const initialState = {
    isText: false,
    isEmpty: false,
    isLink: false,
    isBold: false,
    isItalic: false,
    isUnderline: false,
    isStrikethrough: false,
    isList: false,
    isEditable: false,
    blockType: defaultBlockType,
    actualBlockType: defaultBlockType,
    fontSize: defaultFontSize,
    color: defaultColor,
    background: defaultBackground,
};
