import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useField, useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { useAsyncFn } from 'react-use';

import { useActions } from '@mssgme/hooks';
import { ValidationError } from '@mssgme/api-client';
import { Button } from '@mssgme/ui';

export const WebhookTestButton = ({ withPurchase, disabled }) => {
    const formik = useFormikContext();
    const { t } = useTranslation();
    const { api } = useActions();
    const [{ value = '' }] = useField('endpoint');
    const [isValid, setValid] = useState(false);
    const [status, setStatus] = useState('');
    const [{ loading: isLoading }, test] = useAsyncFn(async () => {
        setStatus(0);
        setValid(false);
        formik.setErrors({});

        try {
            const { status } = await api.endpoints.test({ payload: { endpoint: value, withPurchase } });
            setStatus(status);

            if (status === 'ok') {
                setValid(true);
            }
        } catch (e) {
            setStatus(e.message);

            if (ValidationError.is(e)) {
                formik.setErrors(e.errors);
            }
        }
    }, [formik, value, withPurchase]);

    return (
        <>
            {!!status && (
                <div style={{ color: isValid ? 'green' : 'red' }}>
                    {t('ConnectWebhook.labels.testResult', {
                        status: status !== 'ok' ? status : t('ConnectWebhook.labels.ok'),
                    })}
                </div>
            )}

            <Button
                fullWidth
                type="button"
                btnStyle="secondary"
                isFetching={isLoading}
                disabled={disabled}
                onClick={test}
                style={{ marginTop: 16 }}
            >
                {t('ConnectWebhook.labels.test')}
            </Button>

            <Button
                fullWidth
                type="submit"
                btnStyle="primary"
                disabled={disabled || isLoading || !isValid}
                style={{ marginTop: 16 }}
            >
                {t('ui:labels.apply')}
            </Button>
        </>
    );
};

WebhookTestButton.propTypes = {
    withPurchase: PropTypes.bool,
    disabled: PropTypes.bool,
};
