import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useToggle } from 'react-use';
import { useField } from 'formik';
import { useTranslation } from 'react-i18next';

import { CloudLightningIcon } from '@mssgme/icons';
import { Button, ToggleField } from '@mssgme/ui';
import { WebhookConnectorForm } from './WebhookConnectorForm';

ConnectWebhookField.propTypes = {
    name: PropTypes.string.isRequired,
};

function ConnectWebhookField({ name }) {
    const { t } = useTranslation();
    const [{ onChange }, { value: { enabled, endpoint } = {} }] = useField(name);

    const [isConnecting, toggleConnecting] = useToggle();

    const handleChange = useCallback(
        (value) => {
            toggleConnecting(false);

            if (value) {
                onChange({ target: { name, value } });
            }
        },
        [name]
    );

    return (
        <div style={{ marginBottom: 10 }}>
            {enabled ? (
                <ToggleField
                    builderInput
                    name={`${name}.enabled`}
                    label={t('accountForm.label.notifications.webhook')}
                />
            ) : (
                <Button fullWidth onClick={toggleConnecting} type="button" beforeIcon={<CloudLightningIcon />}>
                    {t('ConnectWebhook.connect')}
                </Button>
            )}

            {isConnecting && <WebhookConnectorForm endpoint={endpoint} onChange={handleChange} />}
        </div>
    );
}

export default ConnectWebhookField;
