import IconFAQExclusiveOne from '../../../icons/FaqExclusiveOne.svg';
import IconFAQExclusiveAll from '../../../icons/FaqExclusiveAll.svg';

import { Styling } from '@mssgme/ui';

export const accordionStylingFactory = ({ fields, t, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                fields.textStyle((field) => ({
                    ...field,
                    path: 'headerStyle',
                    options: field.options
                        .filter(({ value }) => !['normal', 'bold'].includes(value))
                        .map((option) =>
                            option.value !== 'bold' ? option : { ...option, props: { style: { display: 'none' } } }
                        ),
                    initial: ['bold'],
                    mask: null,
                    style: { marginBottom: 16 },
                    supplementaryData: {
                        textLabel: t('landings:Page.Edit.Accordion.labels.question'),
                    },
                })),
                fields.textStyle((field) => ({
                    ...field,
                    options: field.options
                        .filter(({ value }) => !['normal'].includes(value))
                        .map((option) =>
                            option.value !== 'bold' ? option : { ...option, props: { style: { display: 'none' } } }
                        ),
                    initial: [],
                    mask: null,
                    style: { marginBottom: 16 },
                    supplementaryData: {
                        textLabel: t('landings:Page.Edit.Accordion.labels.answer'),
                    },
                })),
                fields.color(),
                Styling.s.list('exclusive', {
                    label: t('Page.fields.exclusive.label'),
                    shortLabel: t('Page.fields.exclusive.shortLabel'),
                    options: [
                        { value: true, label: t('Page.fields.exclusive.items.one'), icon: IconFAQExclusiveOne },
                        { value: false, label: t('Page.fields.exclusive.items.any'), icon: IconFAQExclusiveAll },
                    ],
                }),
            ],
        ]),
    };
};
