import { Styling } from '@mssgme/ui';

export const videoStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('ratio', {
                label: t('Page.fields.ratio.label'),
                options: [
                    {
                        value: 'rectangle',
                        label: t('Page.fields.ratio.values.rectangle.label'),
                        shortLabel: t('Page.fields.ratio.values.rectangle.shortLabel'),
                    },
                    {
                        value: 'square',
                        label: t('Page.fields.ratio.values.square.label'),
                        shortLabel: t('Page.fields.ratio.values.square.shortLabel'),
                    },
                    isAdvancedMode && {
                        value: 'vertical',
                        label: t('Page.fields.ratio.values.vertical.label'),
                        shortLabel: t('Page.fields.ratio.values.vertical.shortLabel'),
                    },
                ],
            }),
            isAdvancedMode && [
                Styling.s.list('padding', {
                    label: t('Page.fields.padding.label'),
                    options: [
                        { value: 'yes', label: t('Page.fields.padding.values.yes') },
                        { value: 'no', label: t('Page.fields.padding.values.no') },
                    ],
                }),
                fields.borderRadius({
                    max: 200,
                    if: (get) => get('padding') === 'yes',
                    disabledValue: 0,
                }),
            ],
        ]),
    };
};
