import { HeadingNode, QuoteNode } from '@lexical/rich-text';
import { ListItemNode, ListNode } from '@lexical/list';
import { TableCellNode, TableNode, TableRowNode } from '@lexical/table';
import { AutoLinkNode, LinkNode } from '@lexical/link';

const patchedNodeExportDOMStyle = (node, original, editor) => {
    const result = original.call(node, editor);
    const element = result.element;

    if (element) {
        element.style.textAlign = node.getFormatType();

        const direction = node.getDirection();

        if (direction) {
            element.dir = direction;
        }
    }

    return result;
};

const patched = Symbol.for('_patchedExport');
const patchExportDom = (klass, patch) => {
    if (klass[patched]) {
        return;
    }

    const original = klass.prototype.exportDOM;

    klass[patched] = true;
    klass.prototype.exportDOM = function(editor) {
        return patch(this, original, editor);
    };
};

// todo: remove this when alignment style application is fixed for Heading, Quote and ListItem nodes when exporting to html
patchExportDom(HeadingNode, patchedNodeExportDOMStyle);
patchExportDom(QuoteNode, patchedNodeExportDOMStyle);
patchExportDom(ListItemNode, patchedNodeExportDOMStyle);

export const nodes = [
    HeadingNode,
    ListNode,
    ListItemNode,
    QuoteNode,
    TableNode,
    TableCellNode,
    TableRowNode,
    AutoLinkNode,
    LinkNode,
];
