import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { FaExchangeAlt } from 'react-icons/fa';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ServicesGrid } from '@mssgme/userpage-core';
import {
    BlockEditForm,
    EditStylingForm,
    ServicesGridBlockSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';
import { ServicesGridItemsField } from './ServicesGridItemsField';

// import { ServicesGridContactsField } from './ServicesGridContactsField';

export function ServicesGridEdit({ blockId, backPath }) {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: ServicesGridEdit });
    const handleConvert = useCallback(() => history.push(generateUrl('./convert')), []);

    return (
        <BlockEditForm
            {...bind}
            validationSchema={ServicesGridBlockSchema}
            title={t('Page.Edit.ServicesGrid.heading')}
            actions={
                <FaExchangeAlt
                    size={16}
                    style={{ transform: 'rotate(90deg)', color: '#007aff' }}
                    onClick={handleConvert}
                />
            }
        >
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview
                        page={page}
                        style={{ marginBottom: 16 }}
                        contentStyle={{ height: 255, padding: 0 }}
                    >
                        <bind.blockComponent block={values} interactive={false} delay={100} />
                    </StyledBlockPreview>

                    <ServicesGridItemsField name="items" />

                    {/*<ServicesGridContactsField name="contacts" style={{ marginBottom: 16 }} />*/}
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ServicesGridEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

ServicesGridEdit.block = ServicesGrid;
