import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextareaField } from '@mssgme/ui';
import { Text } from '@mssgme/userpage-core';
import { BlockEditForm, BlockStyleProvider, EditStylingForm, useBlockEditor } from '../components';

export function TextEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: TextEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Text.heading')} contentStyles={{ padding: '0 0 16px 0' }}>
            {({ values, styling }) => (
                <EditStylingForm
                    page={page}
                    element={values}
                    styling={styling}
                    toolPanelStyle={{ margin: 0, width: '100%' }}
                >
                    <BlockStyleProvider block={values}>
                        {(style) => (
                            <TextareaField
                                required
                                builderInput
                                style={{
                                    marginBottom: 0,
                                    color: values.content ? style.color : 'inherit',
                                }}
                                inputStyles={values.content ? style : { borderRadius: 0 }}
                                name="content"
                                placeholder={t('Page.Edit.Text.fields.content.placeholder')}
                                maxLength={1000}
                            />
                        )}
                    </BlockStyleProvider>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

TextEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

TextEdit.block = Text;
