import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { IoMdCreate } from 'react-icons/io';

import { Button, FloatingActions, IconButton, ScrollableBox, SearchInput, StreamlinedScreen } from '@mssgme/ui';
import { useCollectionsMain, useProductsSublist, useProductsContext } from '../../../hooks';
import { ProductsList, ProductsListFooter } from '../../../UI';

import styles from './CollectionProducts.module.scss';

export function CollectionProducts({ collectionId }) {
    const { t } = useTranslation('products');
    const { router, isSpecific, specifier, modal } = useProductsContext();
    const { collection } = useCollectionsMain(specifier, (col) => col._id === collectionId);
    const { products, setFilter, loadMore } = useProductsSublist(specifier, collectionId);
    const title = collection ? (collection.default ? t('labels.allProducts') : collection.name) : '';

    return (
        <StreamlinedScreen
            modal={modal}
            title={title}
            rightButton={
                <div className={styles.headerButtons}>
                    {!collection?.default && (
                        <IconButton
                            as={Link}
                            to={router.collection.toEdit(collectionId)}
                            title={t('Collections.tipEditCollection')}
                        >
                            <IoMdCreate size={22} color="#000" />
                        </IconButton>
                    )}
                </div>
            }
            topPanelChildren={<SearchInput name="name" value={products.filter.name} onChange={setFilter} />}
            backPath={router.collection.toAll()}
        >
            <ScrollableBox nopadding style={{ flex: 1, marginTop: 8, background: '#fff' }}>
                <StreamlinedScreen.Section
                    nopadding
                    style={{
                        margin: products.items.length ? 0 : 'auto',
                        padding: `0 0 ${products.next && loadMore ? 16 : 0} 0`,
                    }}
                >
                    <ProductsList products={products.items} collectionId={collectionId} />
                    <ProductsListFooter products={products} loadMore={loadMore} />
                </StreamlinedScreen.Section>
            </ScrollableBox>

            {isSpecific && (
                <FloatingActions padded style={{ marginTop: 8 }}>
                    <Button fullWidth tag={Link} to={router.product.toNew(collection?.default ? null : collectionId)}>
                        + {t('pageProducts.tipAddProduct')}
                    </Button>
                </FloatingActions>
            )}
        </StreamlinedScreen>
    );
}

CollectionProducts.propTypes = {
    collectionId: PropTypes.string.isRequired,
};
