import React from 'react';

import { Styling } from '@mssgme/ui';

/* eslint-disable react/display-name */
export const listStylingFactory = ({ fields, t, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('listLabel', {
                label: t('Page.fields.listLabel.label'),
                shortLabel: t('Page.fields.listLabel.shortLabel'),
                options: [
                    {
                        value: 'dot',
                        label: t('Page.fields.listLabel.items.dot'),
                        icon: () => <div style={{ lineHeight: '24px', fontSize: 28, padding: '0 5px' }}></div>,
                    },
                    {
                        value: 'number',
                        label: t('Page.fields.listLabel.items.number'),
                        icon: () => <div style={{ lineHeight: '24px', fontSize: 18, padding: '0 5px' }}>1.</div>,
                    },
                    {
                        value: 'line',
                        label: t('Page.fields.listLabel.items.line'),
                        icon: () => <div style={{ lineHeight: '24px', fontSize: 28, padding: '0 5px' }}>-</div>,
                    },
                ],
            }),
            isAdvancedMode && [
                fields.textStyle((field) => ({
                    ...field,
                    options: field.options
                        .filter(({ value }) => !['normal', 'line-through'].includes(value))
                        .map((option) =>
                            option.value !== 'bold' ? option : { ...option, props: { style: { display: 'none' } } }
                        ),
                    initial: ['normal'],
                    mask: null,
                    style: { marginBottom: 16 },
                })),
                fields.color(),
            ],
        ]),
    };
};
