import { useCallback, useState } from 'react';

import { useFetching } from '@mssgme/hooks';
import { NotificationCenter } from '@mssgme/ui';

export const usePromptAction = (handler, onDone) => {
    const [context, setContext] = useState(null);
    const { pending, run } = useFetching(async (context) => {
        try {
            const result = await handler(context);

            onDone && onDone(context, result);
        } catch (e) {
            NotificationCenter.getInstance().danger(e.message, { showClose: true });
        }
    });

    const handle = useCallback(
        async (confirmed) => {
            setContext(null);

            if (!confirmed) {
                return;
            }

            return run(context);
        },
        [context, onDone]
    );

    return {
        pending,
        isShown: !!context,
        initiate: setContext,
        run,
        handle,
    };
};
