import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { CheckIcon, ClockIcon, CloseCircleIcon, DeleteIcon } from '@mssgme/icons';
import { format } from '@mssgme/i18n';
import { useActions, useFeatures, useFeedbackStore, useNavigationUtils, useInfiniteList } from '@mssgme/hooks';
import { Button, LoadingIndicator, SwipeableList } from '@mssgme/ui';

import { byTerm } from './byTerm';
import { SwipeableFeedbackItem } from './SwipableFeedbackItem';

import styles from './FeedbackItemsList.module.scss';

const actions = [
    { action: 'processing', icon: ClockIcon, safe: true },
    { action: 'accepted', icon: CheckIcon, safe: true },
    { action: 'rejected', icon: CloseCircleIcon },
    { action: 'delete', icon: DeleteIcon, filter: ({ features }) => features('delete_feedbacks') },
];
const filterAction = (context) => (item) => !!context.safe === !!item.safe && !(item.filter && !item.filter(context));
const addHeaders = function* (feedbacks) {
    let lastDay = null;

    for (const feedback of feedbacks) {
        const day = ~~(feedback.timestamp / (60000 * 60 * 24));

        if (day !== lastDay) {
            lastDay = day;
            yield { type: 'header', day, data: new Date(feedback.timestamp) };
        }

        yield { type: 'item', day, data: feedback };
    }
};

export function FeedbackItemsList({ websiteId }) {
    const features = useFeatures();
    const store = useFeedbackStore();
    const { api } = useActions();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { isFetching, items, hasMore, handleLoadMore } = useInfiniteList(
        store,
        { website: -1, term: byTerm },
        (query) => api.feedbacks.list({ websiteId, query })
    );
    const chunks = useMemo(() => Array.from(addHeaders(items)), [items]);

    const handleAction = useCallback(async (item, { action }) => {
        if (action === 'delete') {
            return api.feedbacks.delete({
                websiteId: item.website,
                feedbackId: item._id,
            });
        }

        return api.feedbacks.update({
            websiteId: item.website,
            feedbackId: item._id,
            payload: { status: action },
        });
    }, []);

    const makeActions = useCallback(
        ({ item }, left) =>
            actions.filter(filterAction({ features, safe: !!left })).map(({ action, icon }) => ({
                action,
                icon,
                title: t(`Feedback.View.List.labels.state.${action}`),
                props: { disabled: item.status === action, className: cn(styles.action, styles[action]) },
            })),
        [features]
    );

    return (
        <>
            <SwipeableList>
                {chunks.map(({ type, day, data }, index) =>
                    type === 'header' ? (
                        <li key={`${day}-date`} className={styles.dateSection}>
                            {format(data, 'LL')}
                        </li>
                    ) : (
                        <SwipeableFeedbackItem
                            key={data._id || index}
                            item={data}
                            to={generateUrl(data._id)}
                            as="li"
                            actions={makeActions}
                            onAction={handleAction}
                        />
                    )
                )}
            </SwipeableList>

            {!items.length &&
                (isFetching ? (
                    <LoadingIndicator color="#2979ff" />
                ) : (
                    <div className={styles.emptyMessage}>
                        <h3>{t('Feedback.View.List.emptyMessage')}</h3>
                    </div>
                ))}

            {hasMore && (
                <Button fullWidth style={{ marginTop: 16 }} isFetching={isFetching} onClick={handleLoadMore}>
                    {t('Feedback.View.List.labels.loadMore')}
                </Button>
            )}
        </>
    );
}

FeedbackItemsList.propTypes = {
    websiteId: PropTypes.string.isRequired,
};
