import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';
import { createPortal } from 'react-dom';
import { AnimatePresence } from 'framer-motion';

import { useActions, useFetching } from '@mssgme/hooks';
import { Button, ModalSheet, ModalSheetPane, ModalSheetHeader, LoadingIndicator } from '@mssgme/ui';

import styles from './PaymentPreviewModal.module.scss';

// TODO: refactor
export default function PaymentPreviewModal({ plan, period, isOpen, onAccept, onClose, pendingPlanChange }) {
    const mainRootEl = document.getElementById('mainRoot');

    const { t } = useTranslation('billing');
    const { api } = useActions();
    const { run, result: payment, getValidationError: getPreviewError } = useFetching(
        () => api.billing.previewPlanChange({ payload: { planId: plan.id, period } }),
        [plan, period]
    );
    const currency = payment?.currency;
    const error = getPreviewError('message');

    useEffect(() => {
        if (isOpen) {
            void run();
        }
    }, [isOpen, run]);

    if (!mainRootEl) {
        return null;
    }

    return createPortal(
        <AnimatePresence>
            {isOpen && (
                <ModalSheet onClose={onClose}>
                    <ModalSheetPane
                        header={<ModalSheetHeader title={t('PlansPage.changePlanModal.title')} closePath={onClose} />}
                    >
                        {!payment ? (
                            error ? (
                                <div className={styles.error}>{error}</div>
                            ) : (
                                <div className={styles.loadingContainer}>
                                    <LoadingIndicator />
                                </div>
                            )
                        ) : (
                            <>
                                <div className={styles.order}>
                                    <ul className={styles.orderList}>
                                        <li>
                                            <span>{t('ConfirmSubscriptionPage.order.plan')}</span>
                                            <span>
                                                {t(`ConfirmSubscriptionPage.order.${period}`, { plan: plan.name })}
                                            </span>
                                        </li>
                                        <li>
                                            <span>{t('ConfirmSubscriptionPage.order.total')}</span>
                                            <span>{`${payment.amount > 0 ? payment.amount : 0} ${currency}`}</span>
                                        </li>
                                    </ul>
                                </div>
                                {payment.amount < 0 && (
                                    <p style={{ margin: '0 0 16px 6px', color: '#777', fontSize: 14 }}>
                                        {t('SinglePlanPage.balanceMessage', {
                                            balanceAmount: -payment.amount,
                                            currency: currency,
                                        })}
                                    </p>
                                )}
                                <Button tag="div" fullWidth onClick={onAccept} isFetching={pendingPlanChange}>
                                    {t('PlansPage.changePlanModal.changePlan')}
                                </Button>
                            </>
                        )}
                    </ModalSheetPane>
                </ModalSheet>
            )}
        </AnimatePresence>,
        mainRootEl
    );
}

PaymentPreviewModal.propTypes = {};
