import { getDOMRangeRect } from './getDOMRangeRect';
import { setFloatingElemPosition } from './setFloatingElemPosition';

const VERTICAL_GAP = 10;
const HORIZONTAL_OFFSET = 5;

export function placeFloatingSelectionElem(
    editor,
    floatingElem,
    anchorElem,
    allowEmpty = false,
    verticalGap = VERTICAL_GAP,
    horizontalOffset = HORIZONTAL_OFFSET
) {
    const selection = window.getSelection();
    const rootElement = editor.getRootElement();
    const hasRange = selection && (allowEmpty || !selection.isCollapsed) && rootElement?.contains(selection.anchorNode);
    const rangeRect = hasRange && getDOMRangeRect(selection, rootElement);

    return setFloatingElemPosition(rangeRect, floatingElem, anchorElem, verticalGap, horizontalOffset);
}
