import { useMemo } from 'react';

import { filters, tidyFilter } from '@mssgme/helpers';

export function useFilter(items, filter, matchers) {
    const matcher = useMemo(() => filters.match(items, matchers), [items]);

    return useMemo(() => {
        const normalizedFilter = tidyFilter(filter);
        const filtered = matcher(normalizedFilter);

        return [filtered, normalizedFilter];
    }, [matcher, filter]);
}
