import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { cls } from '@mssgme/helpers';

import styles from './TagsList.module.scss';

const TagItem = ({ tag, onClick }) => {
    const handleClick = useCallback(() => onClick?.(tag), [tag, onClick]);

    return (
        <li key={tag} className={styles.tag} onClick={handleClick}>#{tag}</li>
    );
};

TagItem.propTypes = {
    tag: PropTypes.string.isRequired,
    onClick: PropTypes.func,
};

export const TagsList = ({ tags, onClick, ...rest }) => {

    return (
        <ul {...cls([styles.tags, !!onClick && styles.interactive], rest)}>
            {tags.map((tag) => (
                <TagItem key={tag} tag={tag} onClick={onClick} />
            ))}
        </ul>
    );
};

TagsList.propTypes = {
    tags: PropTypes.arrayOf(PropTypes.string.isRequired).isRequired,
    onClick: PropTypes.func,
};
