import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import {
    Button,
    ButtonChoice,
    CheckableItem,
    Label,
    LandingsPropTypes,
    PortalModalSheet,
    Row,
    StatusBadge,
    CopyLink,
} from '@mssgme/ui';
import { usePaymentConfigs } from '@mssgme/userpage-core';
import { useWebsiteUrl } from '../../hooks';

import styles from './FeedbackItemForm.module.scss';

const statusMap = {
    new: 'new',
    pending: 'processing',
    paid: 'accepted',
    failed: 'rejected',
};

export const PurchaseStatusModal = ({ website, purchase, onSubmit, onClose }) => {
    const { t } = useTranslation('landings');
    const { online } = usePaymentConfigs();
    const [prompt, setPrompt] = useState();
    const absoluteUrl = useWebsiteUrl(website);
    const orderUrl = purchase.status === 'failed' && !!online.length && `${absoluteUrl}/orders/${purchase._id}`;

    const options = useMemo(() => {
        const makeOption = (status) => ({
            value: status,
            // eslint-disable-next-line react/display-name
            label: (config) => (
                <CheckableItem active={config.active}>
                    <div className={styles.choiceLabel}>
                        <StatusBadge status={statusMap[status]} />
                        {t(`ui:orders.paymentStatus.${status}`)}
                    </div>
                </CheckableItem>
            ),
        });

        return [
            // makeOption('new'),
            // makeOption('pending'),
            makeOption('paid'),
            makeOption('failed'),
        ];
    }, [t]);

    const handleStatus = useCallback((status) => purchase.status !== status && setPrompt(status), [purchase.status]);
    const handleSubmit = useCallback(
        (submit) => {
            setPrompt();
            submit && onSubmit && onSubmit({ status: prompt });
        },
        [prompt, onSubmit]
    );

    return (
        <PortalModalSheet title={t('Feedback.Edit.Item.labels.orderStatus')} onClose={onClose}>
            <ButtonChoice
                containerClassName={styles.paymentStatusSelect}
                choiceClassName={styles.choice}
                activeClassName={styles.active}
                value={purchase.status}
                options={options}
                onChange={handleStatus}
            />

            {orderUrl && (
                <Row style={{ marginTop: 16 }}>
                    <Label>{t('Feedback.Edit.Item.labels.orderLink')}</Label>
                    <CopyLink href={orderUrl} />
                </Row>
            )}

            {prompt && (
                <PortalModalSheet title={t('Feedback.Edit.Purchase.heading')} onClose={handleSubmit}>
                    {({ handleTrue }) => (
                        <div>
                            <div>
                                {t('Feedback.Edit.Purchase.confirmStatusChange', {
                                    paymentStatus: t(`ui:orders.paymentStatus.${prompt}`),
                                })}
                            </div>

                            <Button fullWidth btnStyle="primary" style={{ marginTop: 16 }} onClick={handleTrue}>
                                {t('ui:labels.apply')}
                            </Button>
                        </div>
                    )}
                </PortalModalSheet>
            )}
        </PortalModalSheet>
    );
};

PurchaseStatusModal.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    purchase: LandingsPropTypes.order.isRequired,
    onSubmit: PropTypes.func,
    onClose: PropTypes.func,
};
