import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import config from 'config';
import { LoadingIndicator } from '@mssgme/ui';

function getPriceFromEvent(e) {
    const { total: amount, currency } = e.eventData.checkout.prices.customer;
    return { amount, currency };
}

const PADDLE_CHECKOUT_COMMON_OPTIONS = {
    disableLogout: true,
    allowQuantity: false,
    coupon: config.paddle_coupon,
};

const PADDLE_CHECKOUT_INLINE_OPTIONS = {
    ...PADDLE_CHECKOUT_COMMON_OPTIONS,
    method: 'inline',
    frameTarget: 'checkout-container',
    frameInitialHeight: 416,
    frameStyle: 'width:100%; background-color: transparent; border: none;',
};

const PADDLE_CHECKOUT_OVERLAY_OPTIONS = {
    ...PADDLE_CHECKOUT_COMMON_OPTIONS,
};

export const propTypes = {
    onPaymentComplete: PropTypes.func,
    onApplyCoupon: PropTypes.func,
    onRemoveCoupon: PropTypes.func,
    onComplete: PropTypes.func,
    onChangeCountry: PropTypes.func,
    onError: PropTypes.func,
    onLoad: PropTypes.func,
    onSelectPaymentMethod: PropTypes.func,
    onClose: PropTypes.func,
    country: PropTypes.string,
    email: PropTypes.string,
    product: PropTypes.number,
    title: PropTypes.string,
    locale: PropTypes.string,
    passthrough: PropTypes.object,
    inline: PropTypes.bool,
};

export default function PaddleCheckout({
    onPaymentComplete,
    onApplyCoupon,
    onRemoveCoupon,
    onComplete,
    onChangeCountry,
    onError,
    onLoad,
    onSelectPaymentMethod,
    onClose,
    inline = false,
    ...props
}) {
    const eventCallback = (e) => {
        switch (e.event) {
            case 'Checkout.PaymentComplete':
                onPaymentComplete?.({ isPendingReview: !!e.eventData.flagged });
                break;
            case 'Checkout.Coupon.Remove':
                onRemoveCoupon?.({ price: getPriceFromEvent(e) });
                break;
            case 'Checkout.Coupon.Applied': {
                onApplyCoupon?.({ price: getPriceFromEvent(e), coupon: e.eventData.coupon.coupon_code });
                break;
            }
            case 'Checkout.Complete':
                onComplete?.();
                break;
            case 'Checkout.Location.Submit': {
                onChangeCountry?.({ price: getPriceFromEvent(e), country: e.eventData.country });
                break;
            }
            case 'Checkout.Error':
                onError?.({ message: e.eventData.error, type: e.eventData.error_type });
                break;
            case 'Checkout.Loaded':
                onLoad?.({ price: getPriceFromEvent(e) });
                break;
            case 'Checkout.PaymentMethodSelected':
                onSelectPaymentMethod?.({ method: e.eventData.payment_method.toLowerCase() });
                break;
            case 'Checkout.Close':
                onClose?.();
                break;
            default:
                break;
        }
    };
    const checkoutOptions = inline ? PADDLE_CHECKOUT_INLINE_OPTIONS : PADDLE_CHECKOUT_OVERLAY_OPTIONS;
    const { isReady } = usePaddleCheckout({
        ...checkoutOptions,
        ...props,
        successCallback: () => {
            // keep it empty to prevent default checkout success behavior
        },
        setup: { eventCallback },
    });

    if (!inline) return null;

    return (
        <div className="checkout-container">
            {!isReady && (
                <div
                    style={{
                        width: '100%',
                        height: 300,
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                    }}
                >
                    <LoadingIndicator />
                </div>
            )}
        </div>
    );
}

PaddleCheckout.propTypes = propTypes;

export function loadPaddle(opts) {
    return new Promise((resolve, reject) => {
        try {
            if ('Paddle' in window) {
                window.Paddle.Options(opts);
                resolve(window.Paddle);
            } else {
                const a = document.createElement('script');
                a.src = 'https://cdn.paddle.com/paddle/paddle.js';
                a.async = true;
                const b = document.getElementsByTagName('script')[0];
                b.parentNode.insertBefore(a, b);
                a.addEventListener('load', () => {
                    if (config.paddle_sandbox) {
                        window.Paddle.Environment.set('sandbox');
                    }

                    window.Paddle.Setup(opts);
                    resolve(window.Paddle);
                });
                a.addEventListener('error', reject);
            }
        } catch (err) {
            reject(err);
        }
    });
}

export function usePaddle({ eventCallback }) {
    const [paddle, setPaddle] = useState(null);

    useEffect(() => {
        async function init() {
            const paddle = await loadPaddle({
                vendor: config.paddle_vendor_id,
                eventCallback,
            });
            setPaddle(paddle);
        }

        init();
    }, [eventCallback]);

    return paddle;
}

export function usePaddleCheckout({ setup, ...checkoutOptions }) {
    const paddle = usePaddle(setup);
    const [isReady, setIsReady] = useState(false);

    useEffect(() => {
        if (!isReady && paddle) {
            paddle.Checkout.open(checkoutOptions);
            const paddleIframe = document.querySelector('.paddle-frame');
            paddleIframe.addEventListener('load', () => {
                setIsReady(true);
            });
        }
    }, [paddle, isReady]);

    return { paddle, isReady };
}
