import cn from 'classnames';
import TinyColor2 from 'tinycolor2';
import PropTypes from 'prop-types';
import React, { useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import styles from './ColorPicker.module.scss';
import ColorPaletteField from './ColorPaletteField';
import ColorCustomField from './ColorCustomField';

const tabs = [
    {
        label: 'ColorPicker.tabs.palette',
        component: ColorPaletteField,
    },
    {
        label: 'ColorPicker.tabs.custom',
        component: ColorCustomField,
    },
];

export default function ColorPicker({ small, value, onChange, colors }) {
    const { t } = useTranslation('ui');
    const tabWidth = 100 / tabs.length;
    const color = new TinyColor2(value).toHex8String();
    const palette = useMemo(() => colors.map((paletteColor) => new TinyColor2(paletteColor).toHex8String()), [colors]);
    const [activeTabIndex, setActiveTabIndex] = useState(() => {
        const isInPalette = palette.includes(color);

        if (isInPalette) {
            return 0;
        } else {
            return 1;
        }
    });

    return (
        <div className={styles.root}>
            {tabs.length > 1 && (
                <div className={styles.controls}>
                    {tabs.map(({ label }, index) => (
                        <div
                            key={index}
                            className={cn({ [styles.control]: true, [styles.active]: activeTabIndex === index })}
                            onClick={() => setActiveTabIndex(index)}
                        >
                            {t(label)}
                        </div>
                    ))}
                    <div
                        className={styles.activeUnderLine}
                        style={{ width: tabWidth + '%', left: activeTabIndex * tabWidth + '%' }}
                    />
                </div>
            )}
            <div
                className={styles.tabsContainer}
                style={{ width: tabs.length * 100 + '%', transform: `translateX(-${activeTabIndex * tabWidth}%)` }}
            >
                {tabs.map(({ label, component: Component }) => (
                    <div key={label} style={{ width: tabWidth + '%', padding: '16px 16px 0 16px' }}>
                        <Component small={small} value={color} onChange={onChange} colors={palette} />
                    </div>
                ))}
            </div>
        </div>
    );
}

ColorPicker.propTypes = {
    small: PropTypes.bool,
    value: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    colors: PropTypes.array,
};

ColorPicker.TRANSPARENT = '#ffffff00';

ColorPicker.defaultProps = {
    colors: [
        '#eb3b5aff',
        '#fc5c65ff',
        '#fa8231ff',
        '#fd9644ff',
        '#f7b731ff',
        '#fed330ff',
        '#20bf6bff',
        '#26de81ff',
        '#0fb9b1ff',
        '#2bcbbaff',
        '#2d98daff',
        '#45aaf2ff',
        '#3867d6ff',
        '#4b7becff',
        '#8854d0ff',
        '#a55eeaff',
        '#a5b1c2ff',
        '#d1d8e0ff',
        '#000000ff',
        '#333333ff',
        '#6f6f6fff',
        '#d2d2d2ff',
        '#ffffffff',
        ColorPicker.TRANSPARENT,
    ],
};
