import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { IoMdImage } from 'react-icons/io';
import { Link } from 'react-router-dom';

import { mediaUtils } from '@mssgme/shared';
import { LandingsPropTypes, LazyImage } from '@mssgme/ui';
import { printPrice, useOrderProducts } from '@mssgme/userpage-core';
import { useProductsContext } from '@mssgme/products';
import { DataItem } from './DataItem';

import styles from './OrderItems.module.scss';

export const PurchaseItems = ({ website, purchase, resolver }) => {
    const { t } = useTranslation('landings');
    const { router } = useProductsContext();
    const products = useOrderProducts(website, purchase, resolver);

    return (
        <DataItem label={t('Feedback.Edit.Item.labels.orderItems')}>
            {products.map(({ productId, price, quantity, product: { photoUrl, name, notes } }) => (
                <div key={productId} className={styles.item}>
                    <div className={styles.thumbnail}>
                        {photoUrl ? (
                            <LazyImage src={mediaUtils.generateProductThumbnailUrl(photoUrl)} alt={name} />
                        ) : (
                            <div className={styles.emptyPlaceholder}>
                                <IoMdImage size={40} />
                            </div>
                        )}
                    </div>

                    <div className={styles.meta}>
                        <Link className={styles.title} to={router.product.toEdit(productId)}>{name}</Link>
                        <div className={styles.details}>
                            <span className={styles.price}>{printPrice(price)} &times; {quantity}</span>

                            <span className={styles.notes}>{notes}</span>
                        </div>
                    </div>
                </div>
            ))}
        </DataItem>
    );
};

PurchaseItems.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    purchase: LandingsPropTypes.order.isRequired,
    resolver: PropTypes.func,
};
