import arrayMove from 'array-move';
import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useState } from 'react';
import { useFormikContext } from 'formik';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { isEqual } from 'lodash';

import { isMatchId } from '@mssgme/helpers';
import { useCollections } from '@mssgme/hooks';
import { Button, DragHandle, FieldRow, PortalModalSheet } from '@mssgme/ui';
import { useProductsContext } from '@mssgme/products';

import { CollectionItemField } from './CollectionItemField';

import styles from './CollectionItemsField.module.scss';

const SortableCollectionField = SortableElement(CollectionItemField);

const SortableCollectionsField = SortableContainer(({ name, items, existing, onToggle, ...rest }) => (
    <FieldRow name={name} className={styles.root} {...rest}>
        {items.map((item, index) => {
            const isActive = existing.includes(item._id);

            return (
                <SortableCollectionField
                    key={item._id}
                    index={index}
                    // collection={isActive ? 1 : 0}
                    item={item}
                    sortable
                    active={isActive}
                    onToggle={onToggle}
                />
            );
        })}
    </FieldRow>
));

SortableCollectionsField.propTypes = {
    ...SortableCollectionsField.propTypes,
    items: PropTypes.arrayOf(PropTypes.object).isRequired,
    existing: PropTypes.array.isRequired,
    name: PropTypes.string.isRequired,
    onToggle: PropTypes.func.isRequired,
};

SortableCollectionsField.defaultProps = {
    ...SortableCollectionsField.defaultProps,
    shouldCancelStart: DragHandle.shouldCancelStart,
    helperClass: cn(DragHandle.helperClass, styles.sortable),
    lockAxis: 'y',
    lockToContainerEdges: true,
    lockOffset: '0%',
};

export const CollectionItemsField = ({ name, label, builderInput, required, optional, ...rest }) => {
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const { value } = getFieldMeta(name);
    const { specifier } = useProductsContext();
    const { items } = useCollections(specifier);
    const filteredCollections = items.filter((item) => item.published && item.publishedProductsCount > 0);
    const activeCollections = value.map((id) => filteredCollections.find(isMatchId(id))).filter(Boolean);
    const excludedCollections = filteredCollections.filter(({ _id }) => !value.includes(_id));
    const [isModal, setIsModal] = useState(false);
    const [collections, setCollections] = useState([...activeCollections, ...excludedCollections]);
    const allCollectionsValue = filteredCollections.map(({ _id }) => _id);

    const handleOpenModal = () => {
        if (isEqual(value, [])) {
            setFieldValue(name, allCollectionsValue);
        }

        setIsModal(true);
    };

    const handleCloseModal = () => {
        if (isEqual(value, allCollectionsValue)) {
            setFieldValue(name, []);
        }

        setIsModal(false);
    };

    // const { combined } = useMemo(
    //     () => ({
    //         combined: [
    //             ...value.map((id) => items.find(isMatchId(id))).filter(Boolean),
    //             ...items.filter(({ _id }) => !value.includes(_id)),
    //         ],
    //     }),
    //     [items, value]
    // );

    const handleToggle = useCallback(
        ({ _id }) => {
            if (isEqual([_id], value)) {
                return;
            }

            const markedCollections = collections.map((col) => {
                if ((value.includes(col._id) && col._id !== _id) || (!value.includes(col._id) && col._id === _id)) {
                    return { ...col, active: true };
                }

                if (col._id === _id && value.includes(_id)) {
                    return { ...col, active: false };
                }

                return col;
            });
            const valueToSet = markedCollections.filter((col) => col.active).map((col) => col._id);

            // const toggled = value.includes(_id) ? value.filter((id) => id !== _id) : [...value, _id];

            // todo: fix id's of deleted/moved collections
            setFieldValue(name, valueToSet);
            setFieldTouched(name, true, true);
        },
        [name, value, items, collections]
    );

    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (oldIndex === newIndex) {
                return;
            }

            setCollections(arrayMove(collections, oldIndex, newIndex));
        },
        [collections, setCollections]
    );

    useEffect(() => {
        const valueToSet = collections.filter(({ _id }) => value.includes(_id)).map((col) => col._id);

        if (!isEqual(valueToSet, value)) {
            setFieldValue(name, valueToSet);
        }
    }, [collections]);

    useEffect(() => {
        setCollections([...activeCollections, ...excludedCollections]);
    }, [filteredCollections.length]);

    return (
        <>
            <Button
                fullWidth
                btnStyle="secondary2"
                tag="div"
                onClick={handleOpenModal}
                style={{ backgroundColor: '#f7f7fa', color: '#2979ff', userSelect: 'none' }}
            >
                {label}
            </Button>
            {isModal && (
                <PortalModalSheet
                    title={label}
                    onClose={handleCloseModal}
                    render={() => (
                        <SortableCollectionsField
                            name={name}
                            // label={label}
                            items={collections}
                            existing={value}
                            builderInput={builderInput}
                            required={required}
                            optional={optional}
                            onSortEnd={onSortEnd}
                            onToggle={handleToggle}
                            {...rest}
                        />
                    )}
                />
            )}
        </>
    );
};

CollectionItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    label: PropTypes.string,
    builderInput: PropTypes.bool,
    required: PropTypes.bool,
    optional: PropTypes.bool,
};
