import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';

import { cls } from '@mssgme/helpers';
import { DragHandle, LandingsPropTypes } from '@mssgme/ui';
import { useSelectionItem } from '../SelectionArea';
import { useConditionalCallback } from './useConditionalCallback';
import { CheckMark } from './CheckMark';
import { Overlay } from './Overlay';

import styles from './SortableBlocksList.module.scss';

export const InteractiveBlock = ({
    block,
    scale,
    index,
    isScaled,
    isInteractive,
    isSelected,
    isAnimated,
    children,
    warning,
    style: rawStyle,
    onClick,
    onDoubleTap,
    onToggle,
    ...rest
}) => {
    const [bind, { height }] = useSelectionItem(block._id, isSelected, index);

    const actualScale = isScaled ? scale : 1;
    const style = useMemo(() => {
        const transform = `scale(${actualScale})`;
        const marginBottom = -((height / actualScale) * (1 - actualScale));

        return { transform, marginBottom };
    }, [height, actualScale]);

    const handleClick = useConditionalCallback(() => onClick?.(block), isInteractive, [block, onClick]);
    const handleDoubleTap = useConditionalCallback(() => onDoubleTap?.(block), isInteractive, [block, onDoubleTap]);
    const handleToggle = useConditionalCallback(() => onToggle?.(block), isInteractive, [block, onToggle]);

    if (isInteractive && !block.enabled) {
        return <div />;
    }

    const containerStyle = rawStyle && block.enabled ? rawStyle : null;

    return (
        <div
            id={`block-${block._id}`}
            data-block={block._id}
            data-block-kind={block.kind}
            style={containerStyle}
            {...cls([!isInteractive && styles.nonInteractive, !block.enabled && styles.hiddenWrapper], rest)}
        >
            <DragHandle
                small="medium"
                iconSize={12}
                active={isScaled}
                hidden={isInteractive}
                className={styles.dragHandle}
            />

            <div className={styles.contentWrapper} onClick={handleClick} onDoubleClick={handleDoubleTap}>
                <div
                    {...bind}
                    style={style}
                    className={cn(styles.content, !!height && styles.ready, isAnimated && styles.animated)}
                >
                    {block.enabled ? (
                        <React.Suspense fallback={null}>{children}</React.Suspense>
                    ) : (
                        <Overlay block={block} warning={warning} />
                    )}
                </div>
            </div>

            <CheckMark isSelected={isSelected} onClick={handleToggle} />
        </div>
    );
};

InteractiveBlock.propTypes = {
    block: LandingsPropTypes.block.isRequired,
    index: PropTypes.number.isRequired,
    children: PropTypes.any.isRequired,
    scale: PropTypes.number,
    warning: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
    style: PropTypes.object,
    isScaled: PropTypes.bool,
    isInteractive: PropTypes.bool,
    isSelected: PropTypes.bool,
    isAnimated: PropTypes.bool,
    onClick: PropTypes.func,
    onDoubleTap: PropTypes.func,
    onToggle: PropTypes.func,
};
