import { useEffect, useRef, useState } from 'react';
import { useAsyncFn } from 'react-use';

import { useUpdateMarker } from './useUser';

// TODO: remove
export function useUpdatedUser(fn) {
    const fnRef = useRef(fn);
    const [isLoading, setIsLoading] = useState(false);

    fnRef.current = fn;

    const [{ value: marker }, update] = useAsyncFn(() => {
        setIsLoading(true);

        return Promise.resolve(fnRef.current()).then(
            (marker) => {
                if (marker) {
                    return marker;
                }

                setIsLoading(false);
            },
            (e) => {
                setIsLoading(false);
                throw e;
            }
        );
    }, []);
    const isUpdated = useUpdateMarker(marker);

    useEffect(() => {
        if (isUpdated) {
            setIsLoading(false);
        }
    }, [isUpdated]);

    return { isLoading, isUpdated, update };
}
