import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { FiArrowLeft } from 'react-icons/fi';

import styles from './SecondLevelScreenHeader.module.scss';

export const SecondLevelScreenHeader = ({ title, backPath, titleRightContent, preferDynamicBack, slim }) => {
    const history = useHistory();
    const dynamicBack = preferDynamicBack && !history.location.state?.preventDynamicBack;

    const handleBack = useCallback(
        (e) => {
            if (dynamicBack) {
                e.preventDefault();
                history.goBack();
            }
        },
        [dynamicBack]
    );

    return (
        <div className={cn(styles.root, slim && styles.slim)}>
            <Link to={backPath} className={styles.backLink} onClick={handleBack}>
                <FiArrowLeft size={24} color="#333" />
            </Link>
            {title && <h1 className={styles.title}>{title}</h1>}
            {titleRightContent}
        </div>
    );
};

SecondLevelScreenHeader.propTypes = {
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.bool]),
    backPath: PropTypes.oneOfType([PropTypes.string, PropTypes.object, PropTypes.func]).isRequired,
    titleRightContent: PropTypes.any,
    slim: PropTypes.bool,
    preferDynamicBack: PropTypes.bool,
};

SecondLevelScreenHeader.defaultProps = {
    title: null,
    titleRightContent: null,
    preferDynamicBack: false,
};
