import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextField, FieldTip } from '@mssgme/ui';

Liqpay.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Liqpay({ enabled, name = '' }) {
    const { t } = useTranslation('products');

    return (
        <>
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}publicKey`}
                label={t('fields.payments.liqpay.publicKey.label')}
                placeholder={t('fields.payments.liqpay.publicKey.placeholder')}
                disabled={!enabled}
                required
            />
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}privateKey`}
                label={t('fields.payments.liqpay.privateKey.label')}
                placeholder={t('fields.payments.liqpay.privateKey.placeholder')}
                disabled={!enabled}
                required
            />
            <FieldTip>
                <p style={{ margin: 0 }} dangerouslySetInnerHTML={{ __html: t('Payments.Liqpay.tip') }} />
            </FieldTip>
        </>
    );
}
