import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useFormikContext } from 'formik';
import { get, isEqual } from 'lodash';

import { ThemingPropTypes } from '../../../../domainTypes';
import { StylingContext, useStylingAccessors, useThemeUnits } from '../../../../hooks';
import {
    ChoiceField,
    ColorField,
    FieldRow,
    ListField,
    NumberField,
    RangeSliderField,
    StyledSelectField,
    ToggleField,
    IconsField,
} from '../../../Form';
import { ColorPicker } from '../../../Inputs';

import styles from './StyleEditor.module.scss';

const CustomRangeSlider = (props) => {
    const units = useThemeUnits(props.name);
    const makeLabel = useCallback((value) => `${value} ${units}`, [units]);

    return (
        <div style={{ margin: '-4px 0 16px 0' }}>
            <RangeSliderField makeLabel={makeLabel} {...props} />
        </div>
    );
};

CustomRangeSlider.propTypes = RangeSliderField.propTypes;

/* eslint-disable react/display-name, react/prop-types */
const FIELDS_MAP = {
    list: ListField,
    select: StyledSelectField,
    color: (props) => (
        <div style={{ margin: '-16px -16px 0 -16px', width: 'calc(100% + 32)' }}>
            <ColorField as={ColorPicker} {...props} />
        </div>
    ),
    icon: IconsField,
    number: NumberField,
    range: CustomRangeSlider,
    toggle: ToggleField,
    listOrToggle: (props) => {
        return props.options.length > 1 ? <ListField {...props} /> : <ToggleField {...props} />;
    },
    choice: (props) => (
        <ChoiceField containerClassName={styles.choicesContainer} choiceClassName={styles.choice} {...props} />
    ),
};
/* eslint-enable react/display-name */

const itemPath = (parent, item) => (parent && item ? `${parent}.${item}` : parent || item);
const fieldOpts = (field, focusPaths) =>
    field.type !== 'child' && (focusPaths.length ? focusPaths.find(({ path }) => path === field.path) : {});

const StylesField = ({
    field: {
        type,
        path,
        if: constraint,
        preview,
        disabledValue,
        previewValue,
        supplementaryData,
        applyImmediately,
        basePath,
        restrict,
        hint,
        ...rest
    },
    stylesPath,
    customFields,
    getter,
    onApply,
    undecorated,
}) => {
    const fullPath = itemPath(stylesPath, path);
    const Field = rest.as ? FieldRow : FIELDS_MAP[type] || customFields[type];
    const disabled = constraint && !constraint(getter);

    const fieldProps = {
        disabled,
        name: fullPath,
        ...(basePath && { basePath: stylesPath }),
        ...(undecorated && { label: false, tag: 'div', className: null }),
        ...(applyImmediately && { onChange: onApply }),
    };

    if (hint) {
        return (
            <div>
                <p style={{ margin: '0 0 16px 0', color: '#777' }}>{hint}</p>
                <Field {...rest} {...fieldProps} />
            </div>
        );
    }

    return <Field {...rest} {...fieldProps} />;
};

StylesField.propTypes = {
    field: PropTypes.object.isRequired,
    stylesPath: PropTypes.string.isRequired,
    customFields: PropTypes.object.isRequired,
    undecorated: PropTypes.bool,
    getter: PropTypes.func.isRequired,
    onApply: PropTypes.func,
};

const StylesSection = ({ focusPaths, section: { label, styles }, stylesPath, customFields, getter, onApply }) => {
    return (
        <div>
            {label && label !== 'default' && <div style={{ marginBottom: 10 }}>{label}</div>}

            <div>
                {styles.map((field) => {
                    const opts = fieldOpts(field, focusPaths);

                    if (!opts) {
                        return null;
                    }

                    return (
                        <StylesField
                            key={field.path}
                            field={field}
                            customFields={customFields}
                            stylesPath={stylesPath}
                            getter={getter}
                            onApply={onApply}
                            {...opts}
                        />
                    );
                })}
            </div>
        </div>
    );
};

StylesSection.propTypes = {
    section: ThemingPropTypes.section.isRequired,
    focusPaths: ThemingPropTypes.focusPaths.isRequired,
    stylesPath: PropTypes.string.isRequired,
    customFields: PropTypes.object.isRequired,
    getter: PropTypes.func.isRequired,
    onApply: PropTypes.func,
};

export const StyleEditor = ({
    styling,
    styling: {
        definition: { sections },
    },
    focusPaths,
    customFields,
    renderFooter,
    onApply,
    ...rest
}) => {
    const accessors = useStylingAccessors(styling);
    const { values } = useFormikContext();
    const [initial] = useState(values);
    const isDirty = useMemo(() => {
        for (const { styles } of sections) {
            for (const field of styles) {
                if (!fieldOpts(field, focusPaths)) {
                    continue;
                }

                const fullPath = itemPath(accessors.stylesPath, field.path);
                const initialValue = get(initial, fullPath);
                const currentValue = get(values, fullPath);

                if (!isEqual(initialValue, currentValue)) {
                    return true;
                }
            }
        }

        return false;
    }, [initial, values, focusPaths, accessors.stylesPath, sections]);

    return (
        <div {...rest}>
            <StylingContext.Provider value={accessors}>
                {sections.map((section) => (
                    <StylesSection
                        key={section.label}
                        section={section}
                        focusPaths={focusPaths}
                        getter={accessors.getter}
                        customFields={customFields}
                        onApply={onApply}
                        stylesPath={accessors.stylesPath}
                    />
                ))}

                {renderFooter && renderFooter({ isDirty })}
            </StylingContext.Provider>
        </div>
    );
};

StyleEditor.propTypes = {
    styling: ThemingPropTypes.styling.isRequired,
    focusPaths: ThemingPropTypes.focusPaths,
    customFields: PropTypes.object,
    renderFooter: PropTypes.func,
    onApply: PropTypes.func,
};

StyleEditor.defaultProps = {
    focusPaths: [],
    customFields: {},
    renderFooter: null,
    onApply: null,
};
