import useSWR from 'swr';
import { useActions } from '@mssgme/hooks';

const SWR_OPTIONS = {
    revalidateOnMount: true,
    suspense: true,
    shouldRetryOnError: false,
};

export default function useInit() {
    const { api } = useActions();
    useSWR('init', async () => {
        await Promise.all([
            api.users.me(),
            api.workspaces.retrieve(),
            api.billing.details(),
            api.websites.list(),
            api.collections.list(),
        ]);
        return true;
    }, SWR_OPTIONS);
}
