import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { ArrayField, TextField, TextareaField } from '@mssgme/ui';
import { AccordionItemSchema } from '../components';

const AccordionItem = ({ value, className, textClassName }) => (
    <div className={className}>
        <div className={textClassName}>{value.title?.replace(/\n/g, ' ')}</div>
    </div>
);

AccordionItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const AccordionItemsField = (props) => {
    const { t } = useTranslation('landings');
    const itemFactory = useCallback(() => ({ title: '', content: '' }));

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.Accordion.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.Accordion.itemAddMore')}
            modalTitle={t('Page.Edit.Accordion.itemFormHeading')}
            Item={AccordionItem}
            validationSchema={AccordionItemSchema}
            {...props}
        >
            <TextField
                required
                builderInput
                name="title"
                label={t('Page.Edit.Accordion.fields.title.label')}
                placeholder={t('Page.Edit.Accordion.fields.title.placeholder')}
                maxLength={250}
            />
            <TextareaField
                required
                builderInput
                name="content"
                label={t('Page.Edit.Accordion.fields.content.label')}
                placeholder={t('Page.Edit.Accordion.fields.content.placeholder')}
                maxLength={20000}
            />
        </ArrayField>
    );
};

AccordionItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
