import IconList from '../../../icons/messengersList.svg';
import IconCompact from '../../../icons/messengersCompact.svg';
import IconBeefy from '../../../icons/messengersCompactText.svg';

import { Styling } from '@mssgme/ui';

export const messengersStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('messengersMode', {
                label: t('Page.fields.messengersMode.label'),
                options: [
                    {
                        value: 'compact',
                        label: t('Page.fields.messengersMode.values.compact'),
                        icon: IconCompact,
                    },
                    {
                        value: 'beefy',
                        label: t('Page.fields.messengersMode.values.beefy'),
                        icon: IconBeefy,
                    },
                    { value: 'list', label: t('Page.fields.messengersMode.values.list'), icon: IconList },
                ],
            }),
            isAdvancedMode && [
                Styling.s.list('themeColors', {
                    label: t('Page.fields.themeColors.label'),
                    options: [
                        { value: true, label: t('Page.fields.themeColors.values.yes') },
                        { value: false, label: t('Page.fields.themeColors.values.no') },
                    ],
                }),
                fields.borderRadius({
                    min: 0,
                    max: 25,
                    if: (get) => get('padding'),
                    disabledValue: null,
                }),
            ],
        ]),
    };
};
