import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useWebsitePage, useWebsiteBySlug, useNavigationUtils, useRestriction } from '@mssgme/hooks';
import { SelectFont } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageStylingFonts() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { handleBlockedEvent } = useRestriction('page-styling', { type: 'fonts' });
    const closePath = useMemo(() => generateUrl('../..'), [websiteSlug]);

    return (
        <PageEditForm
            website={website}
            page={page}
            title={t('Page.fields.font.label')}
            backPath={closePath}
            blocked={handleBlockedEvent}
        >
            {({ values, setFieldValue }) => {
                const currentFont = values?.theme?.page?.font || { family: 'Inter', provider: 'google' };

                return (
                    <SelectFont name="theme.page.font" onChange={({ name, value }) => setFieldValue(name, value)} value={currentFont} />
                );
            }
            }
        </PageEditForm>
    );
}
