import React from 'react';
import PropTypes from 'prop-types';

import { ModalSheetHeader, ModalSheetPane } from '../ModalSheet';
import { RestrictionScope } from '../Features';

export const PaneContent = ({ contentStyles, nopadding, motionProps, children, ...rest }) => {
    return (
        <ModalSheetPane
            header={<ModalSheetHeader {...rest} />}
            style={contentStyles}
            padding={!nopadding}
            motionProps={motionProps}
        >
            <RestrictionScope fullWidth>{children}</RestrictionScope>
        </ModalSheetPane>
    );
};

PaneContent.propTypes = {
    ...ModalSheetHeader.propTypes,
    contentStyles: PropTypes.object,
    nopadding: PropTypes.bool,
    motionProps: PropTypes.object,
};
