import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { cls } from '@mssgme/helpers';
import styles from './SocialButton.module.scss';

export const SocialButton = ({
    as: Tag = Link,
    isBio,
    social,
    label,
    backgroundImage,
    backgroundColor,
    icon: Icon,
    iconSize = 20,
    ...rest
}) => {
    const style = useMemo(() => ({ backgroundImage, backgroundColor }), [backgroundImage, backgroundColor]);
    const { t } = useTranslation('landings');

    return (
        <Tag data-button={`btn-share-socials-${social}`} {...cls(styles.buttonContainer, rest)}>
            <div className={styles.button} style={style}>
                <div className={styles.icon}><Icon size={iconSize} /></div>

                <div className={styles.label}>
                    {isBio && t('Editor.ShareSocial.bio')}{' '}{label}
                </div>
            </div>
        </Tag>
    );
};

SocialButton.propTypes = {
    as: PropTypes.elementType,
    icon: PropTypes.elementType.isRequired,
    social: PropTypes.string.isRequired,
    label: PropTypes.any,
    backgroundImage: PropTypes.string,
    backgroundColor: PropTypes.string,
    iconSize: PropTypes.number,
    isBio: PropTypes.bool,
};
