import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { useToggle } from 'react-use';
import { merge } from 'lodash';

import { useActions } from '@mssgme/hooks';
import {
    EnhancedFormik,
    Form,
    LandingsPropTypes,
    ModalEditPane,
    renderNode,
    SaveButton,
    usePresenceRemoval,
} from '@mssgme/ui';
import { TemplateSchema } from '../../validators';

export function TemplateEditForm({ template, children, title, backPath, successRedirectPath, ...rest }) {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const [isClosing, setClosing] = useToggle(false);
    const isNew = !template._id;
    const initialValues = merge(
        {
            public: false,
            name: '',
            primaryTags: [],
            secondaryTags: [],
            blocks: [],
            uploads: [],
        },
        template
    );

    const close = useCallback(
        (redirect) => {
            setClosing(true);

            setTimeout(() => history.push(redirect), 100);
        },
        [history]
    );
    const handleSubmit = useCallback(
        async (payload) => {
            let redirect = successRedirectPath || backPath;

            if (isNew) {
                const result = await api.pageTemplates.create({ payload });

                redirect = `/pages/templates/${result._id}`;
            } else {
                await api.pageTemplates.update({ templateId: template._id, payload });
            }

            close(redirect);
        },
        [template._id, close]
    );

    usePresenceRemoval();

    return (
        <EnhancedFormik
            initialValues={initialValues}
            onSubmit={handleSubmit}
            validationSchema={TemplateSchema}
            {...rest}
        >
            {(form) => (
                <ModalEditPane guardChanges={!isClosing} onClose={backPath} title={title}>
                    <Form autoComplete="off">
                        {renderNode(children, form)}

                        <SaveButton
                            fullWidth
                            size="standart"
                            isInitialHidden={false}
                            disabled={isClosing || !form.dirty || !form.isValid}
                            style={{ marginTop: 16 }}
                            isFetching={form.isSubmitting}
                        >
                            {isNew ? t('labels.template.create') : t('ui:labels.apply')}
                        </SaveButton>
                    </Form>
                </ModalEditPane>
            )}
        </EnhancedFormik>
    );
}

TemplateEditForm.propTypes = {
    template: LandingsPropTypes.partials.template.isRequired,
    children: PropTypes.any.isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.string,
    successRedirectPath: PropTypes.string,
};

TemplateEditForm.defaultProps = {
    backPath: '/templates',
    successRedirectPath: '/templates',
};
