import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { SiTelegram } from 'react-icons/si';

import { useConnectTelegram } from '@mssgme/hooks';
import { Button, FieldTip, ToggleField } from '@mssgme/ui';

ConnectTelegramField.propTypes = {
    name: PropTypes.string.isRequired,
};

function ConnectTelegramField({ name }) {
    const { t } = useTranslation();
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { isDisabled, isLoading, isConnected, setConnected, connect } = useConnectTelegram(`${name}.enabled`);
    const { value: { enabled } = {} } = getFieldMeta(name);

    useEffect(() => void setConnected(enabled), [enabled]);
    useEffect(() => void setFieldValue(`${name}.enabled`, isConnected), [isConnected]);

    return (
        <div style={{ marginBottom: 10 }}>
            {enabled ? (
                <ToggleField
                    builderInput
                    name={`${name}.enabled`}
                    label={t('accountForm.label.notifications.telegram')}
                />
            ) : (
                <>
                    <FieldTip style={{ marginBottom: 10 }} tipId="common:ConnectTelegram.click_button_to_connect" />

                    <Button
                        fullWidth
                        onClick={connect}
                        disabled={isDisabled}
                        isFetching={isLoading}
                        type="button"
                        beforeIcon={<SiTelegram />}
                    >
                        {t('ConnectTelegram.connect')}
                    </Button>
                </>
            )}
        </div>
    );
}

export default ConnectTelegramField;
