import { useEffect } from 'react';

import { isMobile } from '@mssgme/helpers';

import styles from './WidgetFix.module.scss';

// todo: remove this. Add widget custom style support
/**
 * Applies custom styling to a mssgme widget when blocks selection is not empty.
 *
 * @deprecated Remove when custom widget styling implemented
 * @param {boolean} hasSelection Apply custom widget styling if `hasSelection` is `true`
 */
export const useWidgetFix = (hasSelection) => {
    useEffect(() => {
        if (!isMobile()) {
            return;
        }

        const classes = document.getElementById('mssgme_widget')?.classList;

        if (!classes) {
            return;
        }

        classes.add(styles.animated);

        if (hasSelection) {
            classes.add(styles.hidden);
        } else {
            classes.remove(styles.hidden);
        }

        return () => {
            classes.remove(styles.hidden);
            classes.remove(styles.animated);
        };
    }, [hasSelection]);
};
