import React from 'react';

import { useUser, useAutoSwitchApp, useWorkspace } from '@mssgme/hooks';
import { AccountBlocked } from '../../UI';
import { MainPage } from '../MainPage';
import { EmptyWorkspace } from '../EmptyWorkspace';
import useInit from './useInit';

export default function PrivatePage(props) {
    useInit();
    const user = useUser();
    const workspace = useWorkspace();
    useAutoSwitchApp();

    if (!user) { // DO NOT REMOVE
        return null;
    }

    if (user.blocked || workspace?.blocked) {
        return <AccountBlocked isBlockedByIp={user.isBlockedByIp} />;
    }

    if (user.workspaces.length === 0) {
        return <EmptyWorkspace />;
    }

    // workspace can be null here, but downstream dependencies expect it not to
    if (!workspace) {
        return null;
    }

    return <MainPage {...props} />;
}
