import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextareaField, TextField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageSeoEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();

    return (
        <PageEditForm
            restrictRule="page-seo:edit"
            title={t('Page.Edit.SEO.heading')}
            website={website}
            page={page}
            backPath={generateUrl('..')}
        >
            <TextField
                builderInput
                label={t('Page.fields.seo.title.label')}
                name="seo.title"
                placeholder={website.seo?.title || t('Page.fields.seo.title.placeholder')}
                maxLength={80}
            />
            <TextareaField
                builderInput
                label={t('Page.fields.seo.description.label')}
                name="seo.description"
                placeholder={website.seo?.description || t('Page.fields.seo.description.placeholder')}
                maxLength={200}
                maxRows={4}
            />
        </PageEditForm>
    );
}
