import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation, Trans } from 'react-i18next';
import { Link, Redirect, useLocation } from 'react-router-dom';

import { Button, ModalPane, FormField } from '@mssgme/ui';
import { useActions, useFetching, useBilling } from '@mssgme/hooks';
import styles from './DeleteAccountPage.module.scss';

export function DeleteAccountPage({ backPath }) {
    const { t } = useTranslation();
    const { api } = useActions();
    const [disabled, setDisabled] = useState(true);
    const location = useLocation();
    const { isSubscribed } = useBilling();
    const password = t('DeleteAccountPage.password');

    const { pending, fulfilled, run: deleteAccount } = useFetching(api.users.deleteAccount);

    const handleSubmit = useCallback((e) => {
        e.preventDefault();
        deleteAccount();
    }, [deleteAccount]);

    const handleChange = useCallback((e) => {
        e.preventDefault();
        setDisabled(e.target.value.toLowerCase() !== password.toLowerCase());
    }, [password]);

    if (fulfilled) {
        return <Redirect to={{ pathname: '/account-deleted', state: { referrer: location } }} />;
    }

    return (
        <ModalPane title={t('DeleteAccountPage.heading')} onClose={backPath}>
            <form className={styles.form} onSubmit={handleSubmit}>
                <div className={styles.description}>
                    <Trans i18nKey="DeleteAccountPage.description" />
                </div>
                <FormField
                    id="delete-account-password"
                    label={t('DeleteAccountPage.label', { password })}
                    placeholder={t('DeleteAccountPage.placeholder')}
                    onChange={handleChange}
                    disabled={isSubscribed}
                />
                <Button
                    fullWidth
                    onClick={deleteAccount}
                    disabled={isSubscribed || disabled}
                    btnStyle="danger"
                    type="submit"
                    isFetching={pending}
                >
                    {t('DeleteAccountPage.delete')}
                </Button>
                <Button tag={Link} to={backPath} fullWidth btnStyle="secondary2">
                    {t('DeleteAccountPage.cancel')}
                </Button>
            </form>
        </ModalPane>
    );
}

DeleteAccountPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
