import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { SidebarHeader, NavBackButton } from '@mssgme/ui';

import styles from './PlaceholderPage.module.scss';

export const PlaceholderPage = ({ location }) => {
    const { t } = useTranslation();
    const history = useHistory();
    const handleBack = useCallback(() => {
        history.goBack();
    }, [history]);

    return (
        <div className={styles.root}>
            <SidebarHeader
                heading={t('PlaceholderPage.heading')}
                leftButton={<NavBackButton tag="a" onClick={handleBack} />}
            />

            <p>{t('PlaceholderPage.labels.invalidAddress')}</p>
            <div className={styles.uri}>
                {window.location.origin}
                <b>{location.pathname}</b>
            </div>
        </div>
    );
};

PlaceholderPage.propTypes = {
    location: PropTypes.object.isRequired,
};
