import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';

function Matrix(canvas) {
    this.intervalId = 0;
    this.canvas = canvas;
    this.canvas.width = window.innerWidth;
    this.canvas.height = window.innerHeight;
    this.ctx = canvas.getContext('2d');
    this.width = canvas.width;
    this.height = canvas.height;
    this.yPositions = [];
    this.ySpeeds = [];
    this.yTimes = [];
    this.lastChars = [];

    for (let i = 0; i < this.width / 10; i++) {
        this.yPositions[i] = Math.random() * (this.height / 10);
        this.ySpeeds[i] = (Math.random() + 0.3) * 0.5;
        this.yTimes[i] = 0;
        this.lastChars[i] = ' ';
    }

    this.ctx.font = '10pt Consolas';
}

Matrix.prototype.Clear = function() {
    const fill = this.ctx.fillStyle;
    this.ctx.fillStyle = '#000000';
    this.ctx.fillRect(0, 0, 400, 300);
    this.ctx.fillStyle = fill;

    for (let i = 0; i < 20; i++) {
        this.Draw();
    }
};

Matrix.prototype.Draw = function() {
    requestAnimationFrame(this.Draw.bind(this));

    this.ctx.fillStyle = '#00FF00';
    for (let i = 0; i < this.lastChars.length; i++) {
        this.ctx.fillText(this.lastChars[i], i * 10 + 1, this.yPositions[i] * 10);
    }

    this.ctx.fillStyle = 'rgba(0, 0, 0, 0.04)';
    this.ctx.fillRect(0, 0, this.width, this.height);

    this.ctx.fillStyle = '#FFFFFF';
    for (let x = 0; x < this.yPositions.length; x++) {
        if (this.yTimes[x] > 1) {
            const charCode = Math.random() > 0.9 ? Math.random() * 15 + 12688 : Math.random() * 93 + 33;
            const char = String.fromCharCode(charCode);
            this.lastChars[x] = char;
            this.ctx.fillText(char, x * 10 + 1, this.yPositions[x] * 10 + 10);
            this.yPositions[x]++;
            if (this.yPositions[x] * 10 > this.height) this.yPositions[x] = 0;
            this.yTimes[x] = 0;
        }

        this.yTimes[x] += this.ySpeeds[x];
    }
};

Matrix.prototype.Start = function() {
    this.Draw();
};

const onLoad = () => {
    let lastTime = 0;
    const vendors = ['webkit', 'moz'];
    for (let x = 0; x < vendors.length && !window.requestAnimationFrame; ++x) {
        window.requestAnimationFrame = window[vendors[x] + 'RequestAnimationFrame'];
        window.cancelAnimationFrame =
            window[vendors[x] + 'CancelAnimationFrame'] || window[vendors[x] + 'CancelRequestAnimationFrame'];
    }

    if (!window.requestAnimationFrame)
        window.requestAnimationFrame = function(callback, element) {
            const currTime = new Date().getTime();
            const timeToCall = Math.max(0, 16 - (currTime - lastTime));
            const id = window.setTimeout(() => callback(currTime + timeToCall), timeToCall);
            lastTime = currTime + timeToCall;
            return id;
        };

    if (!window.cancelAnimationFrame)
        window.cancelAnimationFrame = function(id) {
            clearTimeout(id);
        };
};

export const MatrixOverlay = ({ onTimeout, duration = 2000 }) => {
    const canvasRef = useRef(null);

    useEffect(() => {
        const matrix = new Matrix(canvasRef.current);

        matrix.Start();
        onLoad();

        const timer = setTimeout(onTimeout, duration);

        return () => {
            clearTimeout(timer);
        };
    }, [canvasRef, onTimeout, duration]);

    return (
        <motion.div
            transition={{ duration: 1, ease: 'easeInOut' }}
            style={{ width: '100%', height: '100%', position: 'absolute', top: 0, left: 0, zIndex: 10, opacity: 0.7 }}
            initial={{ opacity: 0 }}
            animate={{ opacity: 0.7 }}
            exit={{ opacity: 0 }}
        >
            <canvas ref={canvasRef} width={400} height={300} style={{ position: 'absolute', top: 0, left: 0 }} />
        </motion.div>
    );
};

MatrixOverlay.propTypes = {
    onTimeout: PropTypes.func,
    duration: PropTypes.number,
};
