import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import { pick } from 'lodash';

import { isMatchedById } from '@mssgme/shared';
import { isMatchId } from '@mssgme/helpers';
import { useCollections } from './useCollections';
import { useWorkspace } from './useWorkspace';

export function useWebsites() {
    return useSelector((state) => state.websites);
}

export function useWebsite(predicate) {
    const website = useSelector((state) => state.websites.find(predicate));
    const specifier = useMemo(() => ({ website: website?._id }), [website?._id]);
    const { crm } = useWorkspace();
    const { items: collections } = useCollections(specifier);

    return useMemo(() => {
        if (!website) {
            return {
                homePageId: null,
                pages: [],
                collections,
                crm,
            };
        }

        return {
            ...website,
            collections,
        };
    }, [crm, website, collections]);
}

export function useWebsiteById(id) {
    return useWebsite(isMatchedById(id));
}

export function useWebsiteBySlug(slug) {
    return useWebsite(isMatchedById(slug)); //TODO: all routes replace website slug with id
}

export function clearWebsitePayload(website) {
    return pick(website, [
        'name',
        'slug',
        'seo',
        'tracking',
        'enabled',
        'branding',
        'embeddedCode',
        'scaffold',
        'menu',
        'theme',
        'localization',
    ]);
}

export function clearPagePayload(website) {
    return pick(website, ['enabled', 'name', 'slug', 'seo', 'tracking', 'theme']);
}

export function useWebsitePage(website, predicate) {
    const homePageId = website.homePageId;
    const page = website.pages.find(predicate || isMatchId(homePageId));

    return {
        website,
        page,
        homePageId,
        pageId: page?._id,
        websiteId: website._id,
    };
}

export function useWebsitePageBySlugs() {
    const { websiteSlug, pageSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const predicate = pageSlug && isMatchId(pageSlug);

    return useWebsitePage(website, predicate);
}
