import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useMeasure, useUnmount } from 'react-use';

import { isMatchId } from '@mssgme/helpers';
import { getDefaultTheme, getThemeOverride } from '@mssgme/shared';
import { useEventCallback, useNavigationUtils, useTemplates, useWebsitesFactory } from '@mssgme/hooks';
import { Button, ModalPane, usePresenceRemoval, Swiper } from '@mssgme/ui';
import { TemplateSlide } from './TemplateSlide';
import { TagTabs } from './TagTabs';
import { createBlankTemplate, filterTemplatesByTags, getPrimaryTag, hasPrimaryTag, TEMPLATE_TAGS } from './utils';

import styles from './ChooseTemplate.module.scss';

export default function ChooseTemplate() {
    const { t } = useTranslation('landings');
    const { create, pending } = useWebsitesFactory();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { templates, pending: isGetTemplatesPending } = useTemplates();
    const [swiper, setSwiper] = useState(null);
    const [slideWrapperRef, { height: slideWrapperHeight, width: slideWrapperWidth }] = useMeasure();
    const [pageWrapperRef, { height: pageWrapperHeight }] = useMeasure();
    const availableTemplates = useMemo(
        () => [...filterTemplatesByTags(templates, TEMPLATE_TAGS), createBlankTemplate({ t })],
        [templates, t]
    );
    const [currentTemplateTag, setCurrentTemplateTag] = useState(getPrimaryTag(availableTemplates[0]));
    const [currentTemplateId, setCurrentTemplateId] = useState(availableTemplates[0]?._id);
    // const [themeId, setThemeId] = useState(getDefaultTheme().colorId);
    const [themeId, setThemeId] = useState(null);
    const slideStyle = useMemo(() => ({ height: slideWrapperHeight + 43 }), [slideWrapperHeight]);
    // const options = useThemesList({ theme: getThemeById(themeId) });
    const scale = slideWrapperHeight / pageWrapperHeight;
    const slideOffset = (slideWrapperWidth - slideWrapperWidth * scale) / 2;
    const customTemplatesCount = availableTemplates.length - 1;

    const backPath = useMemo(() => generateUrl('.'), []);

    const getSlideStyle = useEventCallback((index) => ({
        zIndex: availableTemplates[index]._id === currentTemplateId ? -1 : 0,
    }));

    const handleSlide = useCallback((index) => swiper?.slideTo(index), [swiper]);
    const onTabSelect = useCallback(
        (tag) => {
            setCurrentTemplateTag(tag);

            const slideIndex = availableTemplates.findIndex(hasPrimaryTag(tag));

            if (slideIndex >= 0) {
                handleSlide(slideIndex);
            }
        },
        [handleSlide]
    );

    const onSlideChangeBySwipe = useCallback(
        (swiper) => {
            const template = availableTemplates[swiper.activeIndex];

            setCurrentTemplateId(template._id);
            setCurrentTemplateTag(getPrimaryTag(template));
        },
        [availableTemplates]
    );

    const handleCreate = useCallback(() => {
        const template = availableTemplates.find(isMatchId(currentTemplateId));
        const theme = themeId
            ? getThemeOverride(template.theme, themeId)
            : template._id
                ? template.theme
                : getDefaultTheme();

        void create({
            theme,
            templateId: currentTemplateId,
        });
    }, [availableTemplates, currentTemplateId, themeId]);

    useEffect(() => {
        if (isGetTemplatesPending || customTemplatesCount) {
            if (customTemplatesCount && !currentTemplateId) {
                setCurrentTemplateId(availableTemplates[0]._id);
            }

            return;
        }

        void create();
    }, [isGetTemplatesPending, customTemplatesCount]);

    useUnmount(() => {
        setSwiper(null);
    });

    usePresenceRemoval();

    if (isGetTemplatesPending || !customTemplatesCount) {
        return null;
    }

    return (
        <ModalPane fullHeight onClose={backPath} nopadding inverse>
            <div className={styles.root}>
                <div className={styles.header}>
                    <h1>{t('Website.Edit.ChooseTemplate.heading')}</h1>
                    <p>{t('Website.Edit.ChooseTemplate.subHeading')}</p>
                </div>

                <TagTabs templates={availableTemplates} value={currentTemplateTag} onSelect={onTabSelect} />

                <div className={styles.slider}>
                    {!isGetTemplatesPending && (
                        <Swiper
                            onSwiper={setSwiper}
                            pagination={false}
                            className={styles.swiper}
                            slideStyle={getSlideStyle}
                            onSlideChange={onSlideChangeBySwipe}
                        >
                            {availableTemplates.map((template, index) => {
                                const isLast = index === availableTemplates.length - 1;

                                // eslint-disable-next-line react/prop-types,react/display-name
                                return Object.assign(({ isPrev, isNext }) => (
                                    <TemplateSlide
                                        wrapperRef={isLast ? slideWrapperRef : null}
                                        contentRef={isLast ? pageWrapperRef : null}
                                        style={slideStyle}
                                        isPrev={isPrev}
                                        isNext={isNext}
                                        index={index}
                                        template={template}
                                        themeId={themeId}
                                        scale={scale}
                                        offset={slideOffset}
                                        onClick={handleSlide}
                                    />
                                ), { key: template._id });
                            })}
                        </Swiper>
                    )}
                </div>

                {/*<ThemeInput*/}
                {/*    radio*/}
                {/*    value={themeId}*/}
                {/*    options={options}*/}
                {/*    style={{ marginBottom: 16 }}*/}
                {/*    onChange={setThemeId}*/}
                {/*/>*/}

                <div className={styles.footer}>
                    <Button fullWidth className={styles.button} isFetching={pending} onClick={handleCreate}>
                        {t('Website.Edit.ChooseTemplate.select')}
                    </Button>
                </div>
            </div>
        </ModalPane>
    );
}
