import { Styling } from '@mssgme/ui';

import ContentWidthButton from '../../../icons/ContentWidthButton.svg';
import FullWidthButton from '../../../icons/FullWidthButton.svg';

export const linkStylingFactory = ({ t, fields, isAdvancedMode, features }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                fields.icon(),
                fields.color({
                    path: 'backgroundColor',
                    label: t('Page.fields.backgroundColor.shortLabel'),
                }),
                fields.color({
                    path: 'color',
                    label: t('Page.fields.color.label'),
                    if: (get) => !get('contrastedColor'),
                    disabledValue: null,
                }),
                Styling.s.list('contrastedColor', {
                    label: t('Page.fields.contrastedColor.label'),
                    options: [
                        { value: true, label: t('Page.fields.contrastedColor.values.on') },
                        { value: false, label: t('Page.fields.contrastedColor.values.off') },
                    ],
                    disabledValue: null,
                    if: () => features('link_auto_branding'),
                }),
            ],
            Styling.s.list('width', {
                label: t('Page.fields.buttonWidth.label'),
                options: [
                    {
                        value: 'adaptive',
                        label: t('Page.fields.buttonWidth.values.adaptive'),
                        icon: ContentWidthButton,
                    },
                    { value: 'full', label: t('Page.fields.buttonWidth.values.full'), icon: FullWidthButton },
                ],
                if: isAdvancedMode && ((get) => get('padding')),
            }),
            isAdvancedMode &&
                fields.borderRadius({
                    min: 1,
                    max: 30,
                    if: (get) => get('padding'),
                    disabledValue: null,
                }),
        ]),
    };
};
