import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import styles from './SortableBlocksList.module.scss';

export const CheckMark = ({ isSelected, onClick }) => {
    return (
        <div className={cn(styles.checkMark, isSelected && styles.checked)} onClick={onClick}>
            <div className={styles.checkIcon} />
        </div>
    );
};

CheckMark.propTypes = {
    isSelected: PropTypes.bool,
    onClick: PropTypes.func,
};
