import { object, string } from 'yup';

export const TemplateSchema = object({
    thumbnailUrl: string()
        .ensure()
        .trim()
        .required('common:errorsList.invalid_url')
        .max(800, 'common:errorsList.url_length'),
    name: string()
        .ensure()
        .trim()
        .required('common:errorsList.template_name_required')
        .min(3, 'common:errorsList.template_name_length')
        .max(250, 'common:errorsList.template_name_length'),
});
