import PropTypes from 'prop-types';
import React from 'react';
import { matchPath, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { RoutingTypes } from '../../domainTypes';
import { useRoutes } from './useRoutes';
import { PresentableRoute } from './PresentableRoute';

/**
 * @template {{
 *   path: string|string[];
 *   component?: ComponentType;
 *   exact?: boolean;
 *   strict?: boolean;
 *   sensitive?: boolean;
 *   props?: *;
 * }} Route
 * @template {Route & { children?: RouteDefinition[] }} RouteDefinition
 * @param {object} props Props
 * @param {RouteDefinition[]} props.routing Route definitions list
 * @param {string} [props.base] Optional base path if no `base` specified - current matched route path would be used
 * @returns {ReactNode} React node
 */
export const AnimateableRoutes = ({ routing, base }) => {
    const { pathname } = useLocation();
    const resolved = useRoutes(routing, pathname, base);

    return (
        <AnimatePresence>
            {resolved.map((route) => {
                const matched = matchPath(pathname, route);

                return matched && (
                    <PresentableRoute key={matched.path} match={matched} {...route} />
                );
            })}
        </AnimatePresence>
    );
};

AnimateableRoutes.propTypes = {
    base: PropTypes.string,
    routing: RoutingTypes.children.isRequired,
};
