import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';
import { useUnmount } from 'react-use';

import { SubscriptionFormBlockKind } from '@mssgme/shared';
import { useActions, useDownload, useListFilter, useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { Button, Feature, Input, PlanBadge, ProtectAccess, StreamlinedScreen } from '@mssgme/ui';
import { DownloadIcon, SearchIcon } from '@mssgme/icons';
import { AudienceList } from './AudienceList';

import styles from './WebsiteAudience.module.scss';

export function WebsiteAudience() {
    const { t } = useTranslation('landings');
    const { api, setAudienceFilter, clearAudienceStore } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { _id: websiteId, pages } = useWebsiteBySlug(websiteSlug);
    const { filter, handleFilter } = useListFilter({ term: '', website: websiteId }, setAudienceFilter);
    const hasForm = useMemo(
        () => pages.some(({ blocks }) => blocks.some(({ kind }) => kind === SubscriptionFormBlockKind)),
        [pages]
    );
    const closePath = generateUrl('..');

    const { download, pending } = useDownload(() => api.websites.audience.downloadUrl({ websiteId }));

    useUnmount(clearAudienceStore);

    return (
        <StreamlinedScreen
            padding
            modal
            backPath={closePath}
            title={t('Website.View.Audience.heading')}
            topPanelChildren={
                <Input
                    builderInput
                    name="term"
                    placeholder={t('ui:labels.search')}
                    icon={SearchIcon}
                    autoComplete="off"
                    clearable
                    value={filter.term || ''}
                    onChange={handleFilter}
                />
            }
            contentStyles={{ display: 'block' }}
            rightButton={
                <ProtectAccess rule="audience-download">
                    {(onClick, warning) => (
                        <Button
                            className={styles.rightButton}
                            isFetching={pending}
                            btnStyle="transparent"
                            socialStyle="socialBtnOnly"
                            onClick={onClick || download}
                        >
                            <DownloadIcon size={24} />

                            {warning?.target && <PlanBadge plan={warning.target} className={styles.badge} />}
                        </Button>
                    )}
                </ProtectAccess>
            }
        >
            <StreamlinedScreen.Section style={{ minHeight: 'calc(100% - 8px)' }}>
                <Feature rule="audience">
                    <AudienceList websiteId={websiteId} hasForm={hasForm} />
                </Feature>
            </StreamlinedScreen.Section>
        </StreamlinedScreen>
    );
}
