import { useCallback } from 'react';

import { useTheme } from './useTheme';
import { Theme } from '../components';

const itemPath = (parent, item) => (parent && item ? `${parent}.${item}` : parent || item);

export function useStylingDefinitions(definitions) {
    const theme = useTheme();

    return useCallback(
        (themePropPath, path, parent = '') => {
            const parts = itemPath(parent, path).split('.');
            let target = definitions;

            for (const part of parts) {
                if (target[part]) {
                    target = target[part];
                } else if (target.sections) {
                    for (const { styles } of target.sections) {
                        const found = styles.find((field) => field.type === 'child' && field.path === part);

                        if (found) {
                            target = { sections: found.sections };

                            break;
                        }
                    }
                }

                if (!target) {
                    break;
                }
            }

            const definition = { parent, path, theme, sections: [], ...target };

            return {
                definition,
                diff: (child) => Theme.diff(theme, child, definition),
                enrich: (child, setDisabled) => Theme.enrich(theme, child, definition, setDisabled),
                propPath: themePropPath,
            };
        },
        [definitions, theme]
    );
}
