import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import LexicalErrorBoundary from '@lexical/react/LexicalErrorBoundary';

import { $generateHtmlFromNodes } from '@lexical/html';
import { OnChangePlugin } from '@lexical/react/LexicalOnChangePlugin';
import { RichTextPlugin } from '@lexical/react/LexicalRichTextPlugin';
import { ContentEditable } from '@lexical/react/LexicalContentEditable';
import { HistoryPlugin } from '@lexical/react/LexicalHistoryPlugin';
import { ListPlugin } from '@lexical/react/LexicalListPlugin';
import { LinkPlugin } from '@lexical/react/LexicalLinkPlugin';
import { TabIndentationPlugin } from '@lexical/react/LexicalTabIndentationPlugin';
import { LexicalComposer } from '@lexical/react/LexicalComposer';

import { FloatingLinkPlugin, ToolbarPlugin } from './plugins';

import styles from './RichTextField.module.scss';

export const RichTextFieldInput = ({ editorConfig, placeholder, style, onChange }) => {
    const handleChange = useCallback(
        /**
         * @param {EditorState} editorState Updated state
         * @param {LexicalEditor} editor Editor instance
         * @returns {void}
         */
        (editorState, editor) => editorState.read(() => onChange(editorState.toJSON(), $generateHtmlFromNodes(editor))),
        [onChange]
    );

    return (
        <LexicalComposer initialConfig={editorConfig}>
            <RichTextPlugin
                placeholder={
                    <div className={styles.editorPlaceholder} style={{ color: style.color }}>
                        {placeholder}
                    </div>
                }
                contentEditable={<ContentEditable tabIndex={0} className={styles.editorInput} style={style} />}
                ErrorBoundary={LexicalErrorBoundary}
            />
            <OnChangePlugin ignoreHistoryMergeTagChange ignoreSelectionChange onChange={handleChange} />
            <HistoryPlugin />
            <ListPlugin />
            <LinkPlugin />
            <TabIndentationPlugin />

            <ToolbarPlugin nowrap />
            <FloatingLinkPlugin />
        </LexicalComposer>
    );
};

RichTextFieldInput.propTypes = {
    placeholder: PropTypes.any,
    style: PropTypes.any,
    editorConfig: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
};
