import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import { LoadingIndicator, ModalPane } from '@mssgme/ui';

export const SetupBypass = ({ initialId, onApply }) => {
    const { t } = useTranslation('landings');

    useEffect(() => {
        if (initialId) {
            const id = setTimeout(() => onApply(initialId), 1000);

            return () => clearTimeout(id);
        }
    }, [initialId, onApply]);

    return (
        <ModalPane title={t('Page.Edit.Setup.labels.settingUp')}>
            <div style={{ margin: '24px 0 48px 0', position: 'relative' }}>
                <LoadingIndicator />
            </div>
        </ModalPane>
    );
};

SetupBypass.propTypes = {
    initialId: PropTypes.string,
    onApply: PropTypes.func.isRequired,
};
