import React from 'react';

import { RichEditorProps } from './domainTypes';
import { DropDown, DropDownItems } from './DropDown';
import { ToolbarButton } from './ToolbarButton';

export const ToolbarDropdownButton = ({ value, items, ...rest }) => (
    <ToolbarButton
        as={DropDown}
        renderContent={(onSelect) => <DropDownItems value={value} items={items} onSelect={onSelect} />}
        {...rest}
    />
);

ToolbarDropdownButton.propTypes = RichEditorProps.commandItemSelectProps;
