import PropTypes from 'prop-types';
import React, { useState, useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';
import { FiSmile } from 'react-icons/fi';
import { FaIcons } from 'react-icons/fa';

import { useActions } from '@mssgme/hooks';
import { FieldRow, ImageField, PortalModalSheet, IconsField, iconFieldPropsFactory, KnobButton } from '@mssgme/ui';
import { Glyph } from '@mssgme/userpage-core';
import { useWebsiteEditor } from '../../../../hooks';

import styles from './GlyphField.module.scss';

const Picker = React.lazy(() => import('./Picker'));
const emojisToShowFilter = (emoji) => !emoji.unified || !emoji.unified.startsWith('1F9D1-');

export const GlyphField = ({ name, allowedTypes, ...rest }) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { websiteId } = useWebsiteEditor();
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value: glyph = { type: 'icon', url: '', icon: '', text: '' } } = getFieldMeta(name);
    const [mode, setMode] = useState();

    const handleIconsClick = useCallback(() => setMode('icon'), []);
    const handleEmoticonsClick = useCallback(() => setMode('emoticon'), []);
    const handleApply = useCallback(
        (data) => {
            if (data) {
                for (const [prop, value] of Object.entries({ url: '', icon: '', text: '', ...data })) {
                    setFieldValue(`${name}.${prop}`, value);
                }
            }

            setMode();
        },
        [mode]
    );

    const iconsFieldProps = useMemo(
        () =>
            iconFieldPropsFactory({
                t,
                nullable: false,
                groupLabel: (group) => t(`Page.fields.icon.groups.${group}`),
                skip: ['vk'],
            }),
        [t]
    );

    const isImageAllowed = !allowedTypes || allowedTypes.includes('image');
    const isIconAllowed = !allowedTypes || allowedTypes.includes('icon');
    const isEmoticonAllowed = !allowedTypes || allowedTypes.includes('emoticon');

    const preview = <Glyph glyph={glyph} size={26} className={styles.glyph} />;
    const actions = (
        <>
            {isIconAllowed && (
                <KnobButton style={{ marginLeft: 8 }} onClick={handleIconsClick}>
                    <FaIcons size={16} />
                </KnobButton>
            )}
            {isEmoticonAllowed && (
                <KnobButton onClick={handleEmoticonsClick}>
                    <FiSmile size={16} />
                </KnobButton>
            )}
        </>
    );
    const modals = (
        <>
            {mode === 'icon' && (
                <PortalModalSheet
                    fullWidth
                    paneMotionProps={{ layout: false }}
                    title={t('Page.fields.glyph.iconModalHeading')}
                    style={{ zIndex: 2000 }}
                    onClose={handleApply}
                    render={({ handleClose }) => (
                        <IconsField
                            {...iconsFieldProps}
                            name={`${name}.icon`}
                            onChange={(_, value) => {
                                handleClose({ type: 'icon', icon: value });
                            }}
                        />
                    )}
                />
            )}

            {mode === 'emoticon' && (
                <PortalModalSheet
                    fullWidth
                    paneMotionProps={{ layout: false }}
                    title={t('Page.fields.glyph.emoticonModalHeading')}
                    padding={false}
                    style={{ zIndex: 2000 }}
                    onClose={handleApply}
                    render={({ handleClose }) => (
                        <Picker
                            emoji={glyph.text || ''}
                            native
                            useButton={false}
                            color="#007aff"
                            title=""
                            emojisToShowFilter={emojisToShowFilter}
                            onSelect={(emoji) => {
                                handleClose(emoji && { type: 'emoticon', text: emoji.native });
                            }}
                            style={{
                                width: '100%',
                                border: 0,
                            }}
                        />
                    )}
                />
            )}
        </>
    );

    if (!isImageAllowed) {
        return (
            <>
                <FieldRow name={name} {...rest}>
                    <div
                        style={{
                            display: 'flex',
                            justifyContent: 'space-between',
                            alignItems: 'center',
                            height: 38,
                            padding: '0 6px',
                            backgroundColor: '#f5f5f5',
                            borderRadius: 10,
                        }}
                    >
                        {preview}
                        <div style={{ display: 'flex' }}>{actions}</div>
                    </div>
                </FieldRow>
                {modals}
            </>
        );
    }

    return (
        <>
            <ImageField
                name={`${name}.url`}
                upload={(payload) => api.websites.upload({ ...payload, websiteId })}
                onChange={({ path, url }) => {
                    handleApply({ type: 'image', url: path || '', absoluteUrl: url || '' });
                }}
                {...rest}
                actions={actions}
            >
                {preview}
            </ImageField>
            {modals}
        </>
    );
};

GlyphField.propTypes = {
    name: PropTypes.string.isRequired,
    allowedTypes: PropTypes.arrayOf(PropTypes.oneOf(['image', 'icon', 'emoticon'])),
};

GlyphField.defaultProps = {
    allowedTypes: ['image', 'icon', 'emoticon'],
};
