import { Styling } from '@mssgme/ui';
import { ExternalLinkIcon, FilterIcon, MoreHorizontalIcon } from '@mssgme/icons';

export const productsStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                Styling.s.list('filterMode', {
                    label: t('Page.fields.filterMode.label'),
                    options: [
                        {
                            value: 'buttons',
                            label: t('Page.fields.filterMode.values.buttons'),
                            icon: ExternalLinkIcon,
                        },
                        {
                            value: 'tabs',
                            label: t('Page.fields.filterMode.values.tabs'),
                            icon: isAdvancedMode ? MoreHorizontalIcon : FilterIcon,
                        },
                    ],
                }),
                fields.borderRadius({
                    min: 1,
                    max: 30,
                    if: (get) => ['buttons', 'tabs'].includes(get('filterMode')),
                }),
                fields.color({
                    path: 'color',
                    label: t('Page.Edit.Products.styleLabels.textColor'),
                }),
                fields.color({
                    path: 'productsCategoryTextColor',
                    label: t('Page.Edit.Products.styleLabels.categoryTextColor'),
                }),
                fields.color({
                    path: 'backgroundColor',
                    label: t('Page.Edit.Products.styleLabels.categoryButtonColor'),
                    if: (get) => ['buttons', 'tabs'].includes(get('filterMode')),
                }),
                fields.color({
                    path: 'buttonTextColor',
                    label: t('Page.Edit.Products.styleLabels.categoryActiveTextColor'),
                    if: (get) => ['tabs'].includes(get('filterMode')),
                }),
                fields.color({
                    path: 'buttonBackground',
                    label: t('Page.Edit.Products.styleLabels.categoryActiveButtonColor'),
                    if: (get) => ['tabs'].includes(get('filterMode')),
                }),
            ],
        ]),
    };
};
