import React from 'react';

import { AnimateableRoutes } from '@mssgme/ui';
import { PageProductsRouting } from '@mssgme/userpage-core';

const routing = [
    {
        path: [
            'add-block/:kind',
            'block/:kind/:blockId',
            'preview/:previewDevice(desktop|mobile)',
        ],
        component: PageProductsRouting,
        exact: false,
    },
];

export const LandingsEditorProductsRouter = () => <AnimateableRoutes routing={routing} />;
