import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useField } from 'formik';
import { noop } from 'lodash';

import { cls, isSameArray } from '@mssgme/helpers';
import { Button, FieldRow, ScrollableBox, useModal } from '@mssgme/ui';

import { useProductsList } from '../../../hooks';
import { ProductsList } from '../../ProductsListDraggable';
import { ProductsSelector } from '../../ModalSelectors';

import styles from './ProductsField.module.scss';

export const ProductsField = ({ collectionId, label, toggleLabel, ...props }) => {
    const { isOpen, toggle } = useModal();
    const [, { value = [] }, { setValue }] = useField(props.name);
    const products = useProductsList(collectionId);
    const mapped = useMemo(() => products.items.filter(({ _id }) => value.includes(_id)).slice(0, 500), [
        value,
        products.items,
    ]);

    const handleToggle = useCallback(() => {
        if (props.disabled) {
            return;
        }

        toggle();
    }, [props.disabled, toggle]);
    const handleDone = useCallback(
        ({ action, value: changed }) => {
            if (action === 'success' && !isSameArray(value, changed)) {
                setValue(changed);
            }

            toggle(false);
        },
        [value]
    );

    const labelNode = toggleLabel ? (
        <>
            <div className={styles.labelText}>
                {label} ({value.length})
            </div>
            <Button
                className={styles.labelButton}
                tag="a"
                noborder
                nopadding
                btnStyle="transparent"
                disabled={props.disabled}
                onClick={handleToggle}
            >
                {toggleLabel}
            </Button>
        </>
    ) : (
        label
    );

    return (
        <FieldRow label={labelNode} {...cls(styles.row, props)}>
            <div className={styles.separator} />

            <ScrollableBox nopadding className={styles.list} onClick={handleToggle}>
                <ProductsList className={cn(props.disabled && styles.disabled)} products={mapped} onClick={noop} />
            </ScrollableBox>

            {isOpen && <ProductsSelector products={products} value={value} onDone={handleDone} />}
        </FieldRow>
    );
};

ProductsField.propTypes = {
    ...FieldRow.propTypes,
    name: PropTypes.string.isRequired,
};
