import React from 'react';
import { useTranslation } from 'react-i18next';

import { handleError } from '@mssgme/lib';
import { useActions, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Analytics, Feature, ModalPane } from '@mssgme/ui';

export function WebsiteAnalytics() {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { websiteId } = useWebsitePageBySlugs();
    const closePath = generateUrl('..');
    const getStats = (from = 'now-7d', to = 'now') =>
        api.websites.statistics({ websiteId, query: { from, to } }).catch(handleError);

    const groups = [
        {
            action: 'page.view',
            label: t('Page.View.Analytics.groups.visits'),
        },
        {
            action: 'messenger.click',
            label: t('Page.View.Analytics.groups.clicks_messengers'),
        },
        {
            action: 'link.click',
            label: t('Page.View.Analytics.groups.clicks_links'),
        },
        {
            action: 'internal.click',
            label: t('Page.View.Analytics.groups.clicks_internal'),
        },
        {
            action: 'menu.click',
            label: t('Page.View.Analytics.groups.clicks_menu'),
        },
    ];

    return (
        <ModalPane title={t('Page.View.Analytics.heading')} onClose={closePath}>
            <Feature rule="analytics">
                <Analytics groups={groups} showDelta getStats={getStats} />
            </Feature>
        </ModalPane>
    );
}
