import React from 'react';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import PropTypes from 'prop-types';
import { ListInput, PortalModalSheet } from '@mssgme/ui';
import { useField } from 'formik';

const LANGUAGES = {
    ru: '',
    en: 'English',
    uk: '',
};
const LIST_OPTIONS = Object.entries(LANGUAGES).map(([value, label]) => ({ value, label }));

export default function ChangeLanguage({ name }) {
    const { t } = useTranslation();
    const [isLangChoose, setIsLangChoose] = useToggle(false);
    const [, { value } , { setValue }] = useField(name);

    return (
        <>
            <div style={{ marginBottom: 12 }}>
                <div
                    style={{
                        fontWeight: 500,
                        fontSize: 14,
                        lineHeight: '20px',
                        paddingLeft: 10,
                        marginBottom: 4,
                    }}
                >
                    {t('accountForm.label.language')}
                </div>
                <div
                    onClick={setIsLangChoose}
                    style={{
                        height: 38,
                        borderRadius: 10,
                        backgroundColor: '#f5f5f5',
                        display: 'flex',
                        alignItems: 'center',
                        padding: '0 16px',
                        cursor: 'pointer',
                    }}
                >
                    {LANGUAGES[value]}
                    <span style={{ marginLeft: 'auto', fontSize: 14, color: '#007aff' }}>
                        {t('accountForm.button.change')}
                    </span>
                </div>
            </div>
            {isLangChoose && (
                <PortalModalSheet
                    title={t('accountForm.label.chooseLang')}
                    onClose={(lang) => {
                        setIsLangChoose(false);
                        lang && setValue(lang);
                    }}
                    render={({ handleClose }) => (
                        <ListInput
                            value={value}
                            options={LIST_OPTIONS}
                            onChange={handleClose}
                        />
                    )}
                />
            )}
        </>
    );
}

ChangeLanguage.propTypes = {
    name: PropTypes.string.isRequired,
};
