import { useCallback, useEffect } from 'react';
import { $createParagraphNode, $getSelection, $isRangeSelection, COMMAND_PRIORITY_CRITICAL } from 'lexical';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { mergeRegister } from '@lexical/utils';
import { $setBlocksType } from '@lexical/selection';
import { $createHeadingNode, $createQuoteNode } from '@lexical/rich-text';

import { HEADING_CONVERSION_COMMAND, PARAGRAPH_CONVERSION_COMMAND, QUOTE_CONVERSION_COMMAND } from './constants';

export const TypeConversionPlugin = () => {
    const [editor] = useLexicalComposerContext();
    const registerConversionCommand = useCallback(
        (command, createElement) => editor.registerCommand(
            command,
            (payload) => {
                editor.update(() => {
                    const selection = $getSelection();

                    if ($isRangeSelection(selection)) {
                        $setBlocksType(selection, () => createElement(payload));
                    }
                });

                return true;
            },
            COMMAND_PRIORITY_CRITICAL
        ),
        [editor]
    );

    useEffect(() => mergeRegister(
        registerConversionCommand(PARAGRAPH_CONVERSION_COMMAND, $createParagraphNode),
        registerConversionCommand(QUOTE_CONVERSION_COMMAND, $createQuoteNode),
        registerConversionCommand(HEADING_CONVERSION_COMMAND, $createHeadingNode)
    ), [registerConversionCommand]);

    return null;
};
