import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import { isMatchedById } from '@mssgme/shared';
import { FileIcon, HomeIcon } from '@mssgme/icons';
import { useRestriction } from '@mssgme/hooks';
import { Badge, LandingsPropTypes, PlanBadge } from '@mssgme/ui';
import { getPageName } from '@mssgme/userpage-core';

import styles from './MenuItem.module.scss';

export const MenuItem = ({ value, className, textClassName, context: website }) => {
    const isHomePage = website.homePageId === value.page;
    const page = website.pages.find(isMatchedById(value.page));
    const label = value.label || getPageName(page, isHomePage);
    const Icon = isHomePage ? HomeIcon : FileIcon;
    const { warning } = useRestriction('page-publish', page, { website });

    return (
        <div className={className} title={page._id}>
            <Icon size={22} />
            <div className={cn(styles.label, textClassName)}>
                {label?.replace(/\n/g, ' ')}
                {warning && <>{' '}<PlanBadge plan={warning.target || 'PRO'} /></>}
            </div>
            <div className={styles.details}>
                {!isHomePage && (
                    <div className={styles.slug}>/{page?.slug}</div>
                )}
                <Badge dot badgeStyle={value.enabled ? 'success' : 'flat'} />
            </div>
        </div>
    );
};

MenuItem.propTypes = {
    context: LandingsPropTypes.website.isRequired,
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};
