import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { ImageGallery } from '@mssgme/userpage-core';
import {
    BlockEditForm,
    EditStylingForm,
    ImageGalleryBlockSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';
import { ImageGalleryItemsField } from './ImageGalleryItemsField';

export function ImageGalleryEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: ImageGalleryEdit });

    return (
        <BlockEditForm {...bind} validationSchema={ImageGalleryBlockSchema} title={t('Page.Edit.ImageGallery.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} interactive={false} sortable />
                    </StyledBlockPreview>

                    <ImageGalleryItemsField name="images" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ImageGalleryEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

ImageGalleryEdit.block = ImageGallery;
