import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { FiX } from 'react-icons/fi';
import { useTranslation } from 'react-i18next';

import { useUpgradeLocation } from '@mssgme/hooks';

import styles from './BaseTopBanner.module.scss';

const BaseTopBanner = ({ onClose, text }) => {
    const upgradeUrl = useUpgradeLocation();
    const { t } = useTranslation('ui');

    return (
        <div className={styles.root}>
            <div className={styles.close} onClick={onClose} role="button" data-event="close-upgrade" data-variant="top">
                <FiX size={16} color="#fff" />
            </div>
            <Link to={upgradeUrl} className={styles.content} data-event="click-upgrade" data-variant="top-banner">
                <p dangerouslySetInnerHTML={{ __html: text }} />
                <div className={styles.button}>{t('UpgradeTopBanner.button')}</div>
            </Link>
        </div>
    );
};

BaseTopBanner.propTypes = {
    onClose: PropTypes.func,
    text: PropTypes.string.isRequired,
};

export default BaseTopBanner;
