import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { Audio } from '@mssgme/userpage-core';
import { useBlockEditor, BlockEditForm, EditStylingForm, StyledBlockPreview } from '../components';

export function AudioEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: AudioEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Audio.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <TextField
                        required
                        builderInput
                        label={t('Page.Edit.Audio.fields.url.label')}
                        placeholder={t('Page.Edit.Audio.fields.url.placeholder')}
                        name="url"
                        autoCapitalize="off"
                        style={{ marginBottom: 0 }}
                    />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

AudioEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

AudioEdit.block = Audio;
