import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';
import ActionButton from '../ActionButton';

import styles from './KnobButton.module.scss';

const KnobButton = ({ type = 'primary', simple, ...rest }) => {
    return <ActionButton {...cls([styles.root, styles[type], simple && styles.simple], rest)} />;
};

KnobButton.propTypes = {
    type: PropTypes.oneOf(['secondary', 'primary', 'primary2', 'danger', 'transparent']),
    simple: PropTypes.bool,
};

export default KnobButton;
