import { Styling } from '@mssgme/ui';

export const audioStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                Styling.s.list('padding', {
                    label: t('Page.fields.padding.label'),
                    options: [
                        { value: 'yes', label: t('Page.fields.padding.values.yes') },
                        { value: 'no', label: t('Page.fields.padding.values.no') },
                    ],
                }),
                fields.borderRadius({
                    max: 200,
                    if: (get) => get('padding') === 'yes',
                    disabledValue: 0,
                }),
            ],
        ]),
    };
};
