export function getProps(props) {
    const {
        style,
        className,
        name,
        label,
        labelClassName,
        errorClassName,
        restrictRule,
        restrictBadge,
        required,
        optional,
        disabled,
        controlProps,
        ...rest
    } = props;
    const row = {
        required,
        optional,
        style,
        className,
        name,
        label,
        labelClassName,
        errorClassName,
        restrictRule,
        restrictBadge,
        disabled,
    };
    const control = { name, disabled, required, ...controlProps, ...rest };

    return { row, control };
}
