import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextareaField } from '@mssgme/ui';
import { Heading } from '@mssgme/userpage-core';
import { BlockEditForm, BlockStyleProvider, EditStylingForm, useBlockEditor } from '../components';

const sizesMap = {
    huge: {
        fontSize: 34 / 16 + 'em',
        fontWeight: 'bolder',
    },
    large: {
        fontSize: 24 / 16 + 'em',
        fontWeight: 'bold',
    },
    medium: {
        fontSize: 20 / 16 + 'em',
        fontWeight: 600,
    },
    small: {
        fontSize: 18 / 16 + 'em',
        fontWeight: 500,
    },
};

const getInputStyles = ({ size }) => ({
    fontSize: sizesMap[size].fontSize,
    fontWeight: sizesMap[size].fontWeight,
    lineHeight: 1.5,
});

export function HeadingEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: HeadingEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Heading.heading')} contentStyles={{ padding: '0 0 16px 0' }}>
            {({ styling, values }) => (
                <EditStylingForm
                    page={page}
                    element={values}
                    styling={styling}
                    toolPanelStyle={{ margin: 0, width: '100%' }}
                >
                    <BlockStyleProvider block={values} resolver={getInputStyles}>
                        {(style) => (
                            <TextareaField
                                required
                                builderInput
                                style={{
                                    marginBottom: 0,
                                    color: values.content ? style.color : 'inherit',
                                }}
                                inputStyles={values.content ? style : { borderRadius: 0 }}
                                name="content"
                                placeholder={t('Page.Edit.Heading.fields.content.placeholder')}
                                maxLength={250}
                            />
                        )}
                    </BlockStyleProvider>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

HeadingEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

HeadingEdit.block = Heading;
