import { useActions } from '@mssgme/hooks';
import { usePromptAction } from '@mssgme/admin-ui';

export const useBlockDeletion = (onDone) => {
    const actions = useActions();

    return usePromptAction(actions.api.blocks.deleteMany, onDone);
};

export const useBlockToggle = (onDone) => {
    const actions = useActions();

    return usePromptAction(actions.api.blocks.toggle, onDone);
};

export const useBlockGrouping = (onDone) => {
    const actions = useActions();

    return usePromptAction(actions.api.blocks.group, onDone);
};

export const useBlockCloning = (onDone) => {
    const actions = useActions();

    return usePromptAction(actions.api.blocks.clone, onDone);
};
