import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { format } from '@mssgme/i18n';
import { useFeature } from '@mssgme/hooks';
import { LandingsPropTypes, TagsInput } from '@mssgme/ui';
import { DataItem } from './DataItem';
import { DataList } from './DataList';
import { DropdownStatus } from './DropdownStatus';
import { ItemForm } from './ItemForm';
import { EditableData } from './EditableData';
import { PurchaseDetails } from './PurchaseDetails';
import { PurchaseItems } from './PurchaseItems';

import styles from './FeedbackItemForm.module.scss';

export function FeedbackItemForm({ website, item, isFetching, productResolver, onSubmit, onPurchaseSubmit }) {
    const { t } = useTranslation('landings');
    const { contacts, additional } = useMemo(() => {
        const contacts = ['name', 'phone', 'email'].map((type) => item.data.filter((f) => f.type === type)).flat();
        const additional = item.data.filter((i) => !contacts.includes(i));
        const labels = {
            Name: 'name',
            Phone: 'phone',
            Comment: 'comment',
            Paid: 'paid',
            'Shipping address': 'address',
        };
        const mapLabels = (items) =>
            items.map((item) =>
                labels[item.label] ? { ...item, label: t(`Feedback.Edit.Item.labels.${labels[item.label]}`) } : item
            );

        return {
            contacts: mapLabels(contacts),
            additional: mapLabels(additional),
        };
    }, [item.data]);
    const canChangeTags = useFeature('tag_feedbacks');

    const handleStatus = useCallback((status) => onSubmit && onSubmit({ status }), [onSubmit]);
    const handleTags = useCallback((tags) => onSubmit && onSubmit({ tags }), [onSubmit]);

    return (
        <ItemForm item={item} onSubmit={onSubmit}>
            {({ handleSubmit }) => (
                <div>
                    <div className={styles.statusContainer}>
                        <DropdownStatus
                            className={styles.statusSelect}
                            disabled={isFetching || !onSubmit}
                            value={item.status}
                            onChange={handleStatus}
                        />
                    </div>

                    <DataItem label={t('Feedback.Edit.Item.labels.date')}>
                        {format(new Date(item.timestamp), 'LLL')}
                    </DataItem>

                    {(canChangeTags || !!item.tags?.length) && (
                        <DataItem label={t('Feedback.Edit.Item.labels.tags')}>
                            <TagsInput onChange={handleTags} value={item.tags} disabled={!canChangeTags} />
                        </DataItem>
                    )}

                    {item.purchase && (
                        <PurchaseDetails website={website} purchase={item.purchase} onSubmit={onPurchaseSubmit} />
                    )}

                    <DataList data={contacts} typed />
                    <DataList data={additional} cut />

                    {item.purchase && (
                        <>
                            <div className={styles.divider} />
                            <PurchaseItems website={website} purchase={item.purchase} resolver={productResolver} />
                        </>
                    )}

                    {!!(item.notes || onSubmit) && (
                        <>
                            <div className={styles.divider} />

                            <EditableData
                                name="notes"
                                label={t('Feedback.Edit.Item.labels.notes')}
                                maxLength={5000}
                                editable={!!onSubmit}
                                onSubmit={handleSubmit}
                            />
                        </>
                    )}
                </div>
            )}
        </ItemForm>
    );
}

FeedbackItemForm.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    item: PropTypes.object.isRequired,
    isFetching: PropTypes.bool,
    productResolver: PropTypes.func,
    onSubmit: PropTypes.func,
    onPurchaseSubmit: PropTypes.func,
};
