import moment from 'moment';
import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { FiArrowLeft } from 'react-icons/fi';

import { useNavigationUtils, useTemplates, useUser, useWorkspace } from '@mssgme/hooks';
import { ListNoStyle, EntityItemPane, LandingsPropTypes } from '@mssgme/ui';
import { getDefaultTheme, TemplatePill } from '../../../../UI';

import styles from './TemplatesList.module.scss';

export const TemplateItem = ({ generateUrl, template }) => {
    const { t } = useTranslation('landings');

    return (
        <EntityItemPane
            key={template._id}
            title={template.name}
            url={t('ui:labels.date', { date: moment(template.createdAt) })}
            status={template.public && t('Template.View.List.labels.published')}
            to={generateUrl(template._id)}
        >
            <TemplatePill className={styles.preview} template={template} badge="small" />
        </EntityItemPane>
    );
};

TemplateItem.propTypes = {
    generateUrl: PropTypes.func.isRequired,
    template: LandingsPropTypes.template.isRequired,
};

export function TemplatesList() {
    const userId = useUser()._id;
    const workspaceId = useWorkspace()._id;
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { templates } = useTemplates();
    const { personal, workspace, common } = useMemo(() => {
        const getType = (template) => {
            if (template.author === userId) {
                return 0;
            }

            if (template.workspace === workspaceId) {
                return 1;
            }

            return 2;
        };
        const theme = getDefaultTheme();
        const all = templates.map((template) => ({ ...template, theme, type: getType(template) }));

        return {
            personal: all.filter(({ type }) => type === 0),
            workspace: all.filter(({ type }) => type === 1),
            common: all.filter(({ type }) => type === 2),
        };
    }, [templates, userId, workspaceId]);
    const showLabels = +!!personal.length + !!workspace.length + !!common.length > 1;
    const renderList = (templates, label) => (
        <ListNoStyle>
            {showLabels && <h2>{label}</h2>}
            {templates.map((template) => (
                <TemplateItem key={template._id} template={template} generateUrl={generateUrl} />
            ))}
        </ListNoStyle>
    );

    return (
        <div className={cn(styles.root, showLabels && styles.multipleGroups)}>
            <div className={styles.pageHeader}>
                <Link to={generateUrl('..')} className={styles.backLink}>
                    <FiArrowLeft size={24} color="#333" />
                </Link>
                <h1 className={styles.title}>{t('Template.View.List.heading')}</h1>
            </div>
            <div className={styles.pagesContainer}>
                {!!personal.length && renderList(personal, t('Template.View.List.labels.personal'))}
                {!!workspace.length && renderList(workspace, t('Template.View.List.labels.workspace'))}
                {!!common.length && renderList(common, t('Template.View.List.labels.common'))}
            </div>
        </div>
    );
}
