import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { cls } from '@mssgme/helpers';

import styles from './FloatingActions.module.scss';

export const FloatingActions = ({ children, gap = 32, padded, pullout, style: baseStyle, ...rest }) => {
    const style = useMemo(() => (padded ? baseStyle : { bottom: gap, margin: gap, ...baseStyle }), [
        baseStyle,
        gap,
    ]);

    return (
        <div style={style} {...cls([styles.root, padded && styles.padded, pullout && styles.pullout], rest)}>
            {children}
        </div>
    );
};

FloatingActions.propTypes = {
    gap: PropTypes.number,
    style: PropTypes.object,
    padded: PropTypes.bool,
    pullout: PropTypes.bool,
};
