import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import config from 'config';
import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { ListField } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';

export default function Locale() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const options = useMemo(
        () => config.languages.map((value) => ({ label: t(`Website.Edit.Locale.locales.${value}`), value })),
        [t]
    );

    return (
        <WebsiteEditForm
            title={t('Website.Edit.Locale.heading')}
            website={website}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            <ListField name="localization.lang" options={options} />
        </WebsiteEditForm>
    );
}
