import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AnimateableRoutes } from '@mssgme/ui';
import { ProductsPage } from '@mssgme/products';
import { LandingsContextsProvider } from '../UI';
import { PageSetup } from './Page';
import { WebsiteAnalytics, WebsiteAudience, WebsiteHome } from './Website';
import { shareRouting } from './ShareRouting';
import { EditRedirect } from './EditRedirect';
import { LandingsPageRouter } from './LandingsPageRouter';
import { websiteSettingsRouting } from './WebsiteSettingsRouting';
import { publishRouting } from './PublishRouting';
import { feedbacksRouting } from './FeedbacksRouting';

const routing = [
    { path: 'products', component: ProductsPage, exact: false },
    { path: 'setup', component: PageSetup, exact: false },
    { path: 'analytics', component: WebsiteAnalytics },
    { path: 'audience', component: WebsiteAudience },
    websiteSettingsRouting,
    feedbacksRouting,
    publishRouting,
    shareRouting,
];

export const LandingsWebsiteRouter = () => {
    const { website, page } = useWebsitePageBySlugs();

    if (!page) {
        return <Redirect to="/" />;
    }

    return (
        <LandingsContextsProvider base={`/pages/-/${website._id}`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug/setup" component={PageSetup} />
                <Route path="/pages/-/:websiteSlug/edit" component={EditRedirect} />
                <Route path="/pages/-/:websiteSlug/:pageSlug([a-f\d]{24})" component={LandingsPageRouter}/>
                <Route path="/pages/-/:websiteSlug">
                    <WebsiteHome />
                    <AnimateableRoutes routing={routing} />
                </Route>
            </Switch>
        </LandingsContextsProvider>
    );
};
