import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { useField } from 'formik';
import { useTranslation } from 'react-i18next';

import { LandingsPropTypes, ArrayField, TextField, ToggleField } from '@mssgme/ui';
import { useWebsiteMenu } from '@mssgme/userpage-core';
import { MenuItem } from './MenuItem';

const getItemKey = (item) => item.page;

export const MenuItemsField = ({ website, ...props }) => {
    const { t } = useTranslation('landings');
    const [{ value }, , { setValue }] = useField(props.name);

    const { items, patch } = useWebsiteMenu(website, value);
    const getItemCollection = useCallback((item) => (item.page === website.homePageId) ? 0 : 1, [website.homePageId]);

    useEffect(() => {
        if (patch) {
            setValue(items);
        }
    }, [patch, items]);

    return (
        <ArrayField
            modalTitle={t('Website.Edit.Menu.itemFormHeading')}
            Item={MenuItem}
            maxItems={-1}
            getItemKey={getItemKey}
            getItemCollection={getItemCollection}
            getItemSortable={getItemCollection}
            context={website}
            value={patch ? [] : items}
            isDeletable={false}
            {...props}
        >
            <ToggleField
                builderInput
                name="enabled"
                label={t('Website.Edit.Menu.fields.enabled.label')}
            />

            <TextField
                builderInput
                name="label"
                label={t('Website.Edit.Menu.fields.label.label')}
                placeholder={t('Website.Edit.Menu.fields.label.placeholder')}
                maxLength={250}
            />
        </ArrayField>
    );
};

MenuItemsField.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
