import PropTypes from 'prop-types';
import cn from 'classnames';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useLocalStorage } from 'react-use';

import { CloseIcon } from '@mssgme/icons';
import { IconButton } from '../Inputs';
import styles from './FieldTip.module.scss';

export default function FieldTip({
    className,
    title,
    children,
    bottomComponent,
    noTitle,
    tipId,
    tipParams,
    styled,
    ...props
}) {
    const { t } = useTranslation('ui');
    const isWarning = styled === 'warning';
    const tipTitle = title ? title : t(isWarning ? 'FieldTip.warningLabel' : 'FieldTip.defaultLabel');
    const tipContent = children || (tipId && t(tipId, tipParams));
    const [showTip, toggleTip] = useLocalStorage(`tip.${tipId}`, true);
    const handleClose = useCallback(() => toggleTip(false));

    if (!showTip) {
        return null;
    }

    return (
        <div className={cn(styles.root, styles[styled], className)} {...props}>
            {!noTitle && (
                <div className={styles.title}>
                    <span>{tipTitle}</span>

                    {tipId && (
                        <IconButton className={styles.close} onClick={handleClose}>
                            <CloseIcon />
                        </IconButton>
                    )}
                </div>
            )}

            {typeof tipContent === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: tipContent }} />
            ) : (
                <div>{tipContent}</div>
            )}

            {bottomComponent}
        </div>
    );
}

FieldTip.propTypes = {
    className: PropTypes.string,
    title: PropTypes.string,
    children: PropTypes.any,
    bottomComponent: PropTypes.any,
    noTitle: PropTypes.bool,
    tipId: PropTypes.string,
    tipParams: PropTypes.object,
    styled: PropTypes.oneOf(['default', 'warning']),
};

FieldTip.defaultProps = {
    className: '',
    bottomComponent: null,
    title: null,
    noTitle: false,
    styled: 'default',
};
