export const stylingDefinitionsFactory = ({ t, isAdvancedStylingMode, features }) => {
    const fields = makeStylingFieldFactories({ t, isAdvancedStylingMode });
    const context = { t, isAdvancedMode: isAdvancedStylingMode, features, fields };

    return {
        page: pageStylingFactory(context),
        blocks: {
            // do not remove: block styling auto-generation factory call
            textrich: textRichStylingFactory(context),
            timer: timerStylingFactory(context),
            giphy: giphyStylingFactory(context),
            customcode: customCodeStylingFactory(context),
            subscriptionform: subscriptionFormStylingFactory(context),
            custom: customStylingFactory(context),
            servicesgrid: servicesGridStylingFactory(context),
            products: productsStylingFactory(context),
            hat: hatStylingFactory(context),
            divider: dividerStylingFactory(context),
            heading: headingStylingFactory(context),
            text: textStylingFactory(context),
            image: imageStylingFactory(context),
            imagegallery: imageGalleryStylingFactory(context),
            link: linkStylingFactory(context),
            video: videoStylingFactory(context),
            videogallery: videoGalleryStylingFactory(context),
            messengers: messengersStylingFactory(context),
            accordion: accordionStylingFactory(context),
            quote: quoteStylingFactory(context),
            audio: audioStylingFactory(context),
            list: listStylingFactory(context),
            socials: socialsStylingFactory(context),
            features: featuresStylingFactory(context),
        },
    };
};
