import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { capitalize } from 'lodash';
import { FiCopy, FiGlobe, FiLink, FiShoppingCart, FiToggleRight, FiTrash } from 'react-icons/fi';
import { MdCode, MdTitle, MdTranslate } from 'react-icons/md';
import { AiOutlineBarChart } from 'react-icons/ai';

import config from 'config';
import { prettifyUrl } from '@mssgme/shared';
import { useFeatures, useWebsitePageBySlugs } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';
import { MssgmeLogoIcon } from '@mssgme/icons';

import { usePublish } from '../../../../hooks';

import SeoIcon from '../../../../icons/seoIcon.svg';

const menu = (t, website, page, canPublish, feature) => {
    const activeAnalyticsPlatforms = Object.entries(website.tracking)
        .filter((entry) => !!entry[1])
        .map((entry) => capitalize(entry[0]))
        .join(', ')
    ;

    return [
        {
            to: !website.enabled && !page.blocks.length ? 'publish/no-blocks' : 'status',
            label: t('Website.View.Settings.items.status'),
            icon: FiToggleRight,
            preview: website.enabled ? t('ui:labels.yes') : t('ui:labels.no'),
            filter: (!website.scaffold && canPublish) || website.enabled,
            'data-event': 'click-website-settings',
            'data-feature': 'status',
        },
        {
            to: 'name',
            label: t('Website.View.Settings.items.name'),
            icon: MdTitle,
            preview: website.name,
            'data-event': 'click-website-settings',
            'data-feature': 'name',
        },
        {
            to: 'url',
            label: t('Website.View.Settings.items.slug'),
            icon: FiLink,
            preview: `${prettifyUrl(config.main_url)}/${website.slug}`,
            'data-event': 'click-website-settings',
            'data-feature': 'url',
        },
        {
            to: 'domain',
            label: t('Website.View.Settings.items.domain'),
            icon: FiGlobe,
            restrict: { rule: 'domain', iconOnly: true },
            preview: website.domain?.name || t('ui:labels.notSet'),
            'data-event': 'click-website-settings',
            'data-feature': 'domain',
        },
        {
            to: 'locale',
            label: t('Website.View.Settings.items.locale'),
            icon: MdTranslate,
            preview: website.localization?.lang
                ? t(`Website.Edit.Locale.locales.${website.localization?.lang}`)
                : t('Website.Edit.Locale.locales.auto'),
            'data-event': 'click-website-settings',
            'data-feature': 'locale',
        },
        {
            to: 'seo',
            label: t('Website.View.Settings.items.seo'),
            icon: SeoIcon,
            iconProps: { style: { display: 'block', width: 18, height: 18, color: '#333' } },
            restrict: { rule: 'page-seo:edit', iconOnly: true },
            preview: website.seo.title || website.seo.description ? t('labels.changed') : t('labels.asDefault'),
            'data-event': 'click-website-settings',
            'data-feature': 'seo',
        },
        {
            to: 'tracking',
            label: t('Website.View.Settings.items.tracking'),
            icon: AiOutlineBarChart,
            restrict: { rule: 'page-tracking:edit', iconOnly: true },
            preview: activeAnalyticsPlatforms || t('ui:labels.notSet'),
            'data-event': 'click-website-settings',
            'data-feature': 'tracking',
        },
        {
            to: 'embedded',
            label: t('Website.View.Settings.items.embedded'),
            icon: MdCode,
            preview: website.embeddedCode?.content,
            restrict: { rule: 'page-embedding:edit', iconOnly: true },
            'data-event': 'click-website-settings',
            'data-feature': 'embedded',
        },
        {
            to: 'branding',
            label: t('Website.View.Settings.items.branding'),
            icon: MssgmeLogoIcon,
            iconProps: { style: { display: 'block', width: 16, position: 'relative', left: 1 } },
            restrict: { rule: 'page-branding:edit' },
            preview: website.branding ? t('ui:labels.on') : t('ui:labels.off'),
            'data-event': 'click-website-settings',
            'data-feature': 'branding',
        },
        {
            to: 'products',
            label: t('Website.View.Settings.items.products'),
            icon: FiShoppingCart,
            iconProps: { style: { display: 'block', width: 19 } },
            'data-event': 'click-products-settings',
            'data-feature': 'products',
        },
        {
            to: 'template',
            label: t('Website.View.Settings.items.template'),
            icon: FiCopy,
            iconProps: { style: { display: 'block', width: 19 } },
            restrict: { rule: 'page-templates:create' },
            filter: !!(page.blocks.length && feature('templates')),
            'data-event': 'click-website-settings',
            'data-feature': 'template',
        },
        {
            to: 'delete',
            label: t('labels.website.delete'),
            labelColor: '#dc5960',
            icon: FiTrash,
            iconColor: '#dc5960',
            'data-event': 'click-website-settings',
            'data-feature': 'delete',
        },
    ];
};

export function WebsiteSettings() {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { canPublish } = usePublish(website);
    const feature = useFeatures();
    const items = useMemo(() => menu(t, website, page, canPublish, feature), [t, website, page, canPublish, feature]);

    return <AclMenuPane title={t('Website.View.Settings.heading')} items={items} />;
}
