import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { blockTypesMap } from './blockTypes';
import { RichEditorProps, ToolbarDropdownButton } from '../ui';

export const isBlockType = (blockType) => !!blockTypesMap[blockType];

export const StyleMenuButton = (props) => {
    const { t } = useTranslation('ui');
    const def = blockTypesMap[props.value];
    const label = def?.label && t(`RTE.blockStyle.${def.label}`);

    const items = useMemo(() => Object.entries(blockTypesMap).map(([type, { label, className, command }]) => {
        const resolvedLabel = t(`RTE.blockStyle.${label}`);
        const resolvedText = <span className={className}>{resolvedLabel}</span>;

        return {
            command,
            value: type,
            payload: type,
            text: resolvedText,
            label: resolvedLabel,
        };
    }), [t]);

    return (
        <ToolbarDropdownButton
            fixedSize
            label={label}
            text={label}
            items={items}
            {...props}
        />
    );
};

StyleMenuButton.propTypes = {
    disabled: PropTypes.bool,
    value: RichEditorProps.blockType.isRequired,
    onSelect: PropTypes.func.isRequired,
};
