import { useEffect } from 'react';

import { useActions } from '@mssgme/hooks';

export const BillingObserver = () => {
    const { api } = useActions();

    useEffect(() => {
        api.listenSubscriptionCreated();
        api.listenSubscriptionUpdated();
        api.listenSubscriptionCancelled();
        api.listenPaymentSucceeded();
        api.listenPaymentInitialSucceeded();
        api.listenPaymentFailed();
        api.listenPaymentRetryFailed();
        api.listenHighRiskTransactionUpdated();
    }, []);

    return null;
};

BillingObserver.propTypes = {};
