import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { useActions, useGlobalMessengers, useWorkspace, useRestriction } from '@mssgme/hooks';
import { EnhancedFormik, Form, renderNode, RestrictionScope, SaveButton, usePresenceRemoval } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';

const pickNonEmpty = (list) => {
    for (const resolver of list) {
        const items = !resolver || Array.isArray(resolver) ? resolver : resolver();

        if (items?.length) {
            return items;
        }
    }

    return [];
};

export function ProductsSettingsForm({
    children,
    successRedirectPath,
    initialData = {},
    initialDirty = false,
    showSubmit = true,
    doRedirect = true,
    restrictRule = null,
    ...rest
}) {
    const history = useHistory();
    const workspace = useWorkspace();
    const defaultMessengers = useGlobalMessengers();
    const { api } = useActions();
    const { t } = useTranslation();
    const { router, specifier, crm, setEntry } = useProductsContext();
    const initial = useMemo(
        () => ({
            defaultCurrency: crm.defaultCurrency || 'USD',
            notifications: crm.notifications || {
                email: true,
                telegram: true,
                webhook: true,
            },
            onBoarding: crm.onBoarding,
            onBoardingStep: crm.onBoardingStep,
            contacts: {
                messengers: pickNonEmpty([
                    crm.contacts.messengers,
                    defaultMessengers?.map(({ type, platform, value }) => ({ type, platform, value })),
                    () =>
                        workspace.card?.messengers?.map((messenger) => ({
                            type: messenger.type,
                            platform: messenger.messenger,
                            value: messenger.name,
                        })),
                ]),
            },
            payments: crm.payments || {},
            form: {
                successMessage: '',
                fields: [],
                ...crm.form,
                successURL: {
                    title: crm.form?.successURL?.title || '',
                    action: crm.form?.successURL?.action || {
                        type: 'url',
                        url: '',
                    },
                },
            },
            ...initialData,
        }),
        [crm, defaultMessengers, workspace.card?.messengers]
    );
    const { handleBlockedEvent } = useRestriction(restrictRule, crm || initial);

    usePresenceRemoval();

    const handleSubmit = useCallback(
        async (values) => {
            const entryUrl = router.isBackOverriddenBack && router.toEntry();

            if (specifier?.website) {
                await api.websites.crm.settings.update({
                    websiteId: specifier.website,
                    payload: values,
                });
            } else {
                await api.crm.settings.update({ payload: values });
            }

            // todo: this is a workaround for context reset
            if (entryUrl) {
                setEntry(entryUrl);
            }

            if (doRedirect) {
                history.push(successRedirectPath || router.toHome());
            }
        },
        [workspace, specifier]
    );

    return (
        <EnhancedFormik initialValues={initial} onSubmit={handleSubmit} {...rest}>
            {(form) => (
                <RestrictionScope>
                    <Form autoComplete="off" style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
                        {React.Children.map(children, (child) => child && renderNode(child, form))}

                        {showSubmit && (
                            <SaveButton
                                fullWidth
                                size="standart"
                                isInitialHidden={false}
                                disabled={!(initialDirty || form.dirty)}
                                style={{ marginTop: 16 }}
                                isFetching={form.isSubmitting}
                                {...(handleBlockedEvent ? { onClick: handleBlockedEvent } : {})}
                            >
                                {t('ui:labels.apply')}
                            </SaveButton>
                        )}
                    </Form>
                </RestrictionScope>
            )}
        </EnhancedFormik>
    );
}

ProductsSettingsForm.propTypes = {
    children: PropTypes.any.isRequired,
    successRedirectPath: PropTypes.string,
    initialData: PropTypes.object,
    initialDirty: PropTypes.bool,
    showSubmit: PropTypes.bool,
    doRedirect: PropTypes.bool,
    restrictRule: PropTypes.string,
};
