import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { camelCase } from 'lodash';

import styles from './DomainStatusMark.module.scss';

export const DomainStatusMark = ({ state }) => {
    const { t } = useTranslation('landings');

    return (
        <div className={styles.root}>
            <div
                className={cn(styles.mark, styles[camelCase(state)])}
                title={state && t(`Website.Edit.Domain.state.${state}`)}
            />
        </div>
    );
};

DomainStatusMark.propTypes = {
    state: PropTypes.oneOf(['new', 'wait_ssl', 'ready']),
};
