import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useRouteMatch } from 'react-router-dom';

import { GlobalMessengersContext } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';
import { PageDataContext, WebsiteDataContext } from '@mssgme/userpage-core';
import { ProductsContextProvider } from '@mssgme/products';
import { WebsiteEditorContext } from '../hooks';

// todo: refactor all context and merge into one where appropriate
export const LandingsContextsProvider = ({ website, page, base, children }) => {
    const websiteId = website._id;
    const pageId = page._id;
    const context = useMemo(() => ({ websiteId, pageId, blockId: null }), [websiteId, pageId]);
    const defaultMessengers = useMemo(
        () => page.blocks?.find((block) => block.kind === 'Messengers')?.messengers || null,
        [page.blocks]
    );
    const settingsPath = useRouteMatch({ path: `${base}/settings`, exact: false });
    const settings = settingsPath && `${base}/settings/products`;

    return (
        <WebsiteDataContext.Provider value={website}>
            <PageDataContext.Provider value={page}>
                <ProductsContextProvider base={base} settings={settings} websiteId={website._id}>
                    <GlobalMessengersContext.Provider value={defaultMessengers}>
                        <WebsiteEditorContext.Provider value={context}>
                            {children}
                        </WebsiteEditorContext.Provider>
                    </GlobalMessengersContext.Provider>
                </ProductsContextProvider>
            </PageDataContext.Provider>
        </WebsiteDataContext.Provider>
    );
};

LandingsContextsProvider.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    base: PropTypes.string,
};
