import React from 'react';
import PropTypes from 'prop-types';
import { round } from 'lodash';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import { useNavigationUtils } from '@mssgme/hooks';
import { Button } from '@mssgme/ui';
import CheckMark from '../../icons/icon_check.svg';
import formatCurrency from '../utils/formatCurrency';

import styles from './PlanPane.module.scss';

export default function PlanPane({ Tag, plan }) {
    const { t } = useTranslation('billing');
    const { generateUrl } = useNavigationUtils();
    const subscription = useSelector((state) => state.billingDetails.result?.subscription);
    const isCurrentPlan = subscription?.plan?.id === plan.id;
    const currentPlanPeriod = subscription?.period;
    const currentPrice = subscription?.price || plan.prices[currentPlanPeriod];
    const features = t(`PlansPage.features.${plan.id}`, { returnObjects: true });

    return (
        <Tag className={styles.planPane}>
            <div className={styles.planPaneHeader}>
                <h2>
                    <span className={styles.planName}>{plan.name}</span>
                    {isCurrentPlan && (
                        <span className={styles.currentPlanLabel}>{t('PlansPage.common.currentPlan')}</span>
                    )}
                </h2>
                {isCurrentPlan && (
                    <p>
                        <span>
                            {currentPrice.amount} {formatCurrency(currentPrice.currency)}
                        </span>
                        {` ${t(`PlansPage.common.${currentPlanPeriod}`)}`}
                    </p>
                )}
                {!isCurrentPlan && (
                    <p>
                        <span>
                            {round(plan.prices.annually.amount / 12, 2)} {formatCurrency(plan.prices.annually.currency)}
                        </span>
                        {` ${t('PlansPage.common.perMonth')}`}
                    </p>
                )}
            </div>
            {!subscription && (
                <Button
                    tag={Link}
                    to={generateUrl(plan.id)}
                    fullWidth
                    outline={plan.id !== 'pro400'}
                    style={{ border: plan.id !== 'pro400' ? '1px solid' : 'none', fontSize: 16, fontWeight: '600' }}
                >
                    {t('PlansPage.common.choose')} {plan.name}
                </Button>
            )}
            {subscription && (
                <Button
                    tag={Link}
                    to={generateUrl(plan.id)}
                    fullWidth
                    outline
                    style={{ border: '1px solid', fontSize: 16, fontWeight: '600' }}
                >
                    {isCurrentPlan
                        ? t('PlansPage.common.changePlanPeriod')
                        : t('PlansPage.common.changePlanTo', { plan: plan.name })}
                </Button>
            )}
            <ul className={styles.planPaneFeaturesList}>
                {features.map((feature, index) => (
                    <li key={index}>
                        <div className={styles.checkmark}>
                            <CheckMark />
                        </div>
                        {t(feature)}
                    </li>
                ))}
            </ul>
        </Tag>
    );
}

PlanPane.propTypes = {
    Tag: PropTypes.any,
    plan: PropTypes.object.isRequired,
};

PlanPane.defaultProps = {
    Tag: 'div',
};
