import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { cls } from '@mssgme/helpers';
import styles from './Badge.module.scss';

const Badge = ({ as: Tag = 'span', badgeStyle, inverse, dot, ...rest }) => (
    <Tag {...cls(classNames(styles.root, styles[badgeStyle], dot && styles.dot, inverse && styles.inverse), rest)} />
);

Badge.propTypes = {
    as: PropTypes.elementType,
    badgeStyle: PropTypes.oneOf(['primary', 'danger', 'success', 'warning', 'flat']),
    dot: PropTypes.bool,
    inverse: PropTypes.bool,
};

export default Badge;
