import { object, string, array, mixed } from 'yup';
import isURL from 'validator/lib/isURL';
import isURI from 'isuri';

const isPhone = (val) => /^\+\d{3,}$/.test(val);

export const ImageGalleryItemSchema = object({
    url: string().required('common:errorsList.image_required'),
    externalUrl: string().test('is-valid', 'common:errorsList.enter_url', (value) => {
        if (value) {
            if (isURL(value, { allow_underscores: true })) {
                return true;
            }

            return isURI.isValid(value) && !value.match(/^https?:\/\//);
        }

        return true;
    }),
    annotation: string()
        .ensure()
        .max(50, 'common:errorsList.block_image_annotation_length'),
    action: object({
        type: string().oneOf(['url', 'tel', 'sms', 'email', 'page', 'anchor', 'fullImage', 'nothing'], 'Invalid action type'),
        url: string()
            .when('type', {
                is: (value) => value === 'url',
                then: string().test('is-valid-url', 'common:errorsList.enter_url', (value) => {
                    if (!value) return false;
                    if (isURL(value, { allow_underscores: true })) {
                        return true;
                    }

                    return isURI.isValid(value) && !value.match(/^https?:\/\//);
                }),
            })
            .when('type', {
                is: (value) => ['tel', 'sms'].includes(value),
                then: string().test('is-valid-tel', 'common:errorsList.invalid_phone_number', (value) => {
                    if (isPhone(value)) {
                        return true;
                    }
                }),
            })
            .when('type', {
                is: (value) => value === 'email',
                then: string().required('common:errorsList.email_invalid').email('common:errorsList.email_invalid'),
            })
            .when('type', {
                is: (value) => ['fullImage', 'nothing'].includes(value),
                then: string().test('is-valid-nothing', '', () => {
                    return true;
                }),
            }),
        page: mixed()
            .when('type', {
                is: (value) => value === 'page',
                then: mixed().nullable().required('common:errorsList.empty_field'),
            })
            .when('type', {
                is: (value) => ['fullImage', 'nothing'].includes(value),
                then: mixed().nullable().test('is-valid-nothing', '', () => {
                    return true;
                }),
            }),
    }),
});

export const ImageGalleryBlockSchema = object({
    images: array().min(1, 'common:errorsList.block_image_gallery_count'),
});
