import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useRouteMatch } from 'react-router-dom';

import { PlusIcon } from '@mssgme/icons';
import { LandingsPropTypes, useIsMobile } from '@mssgme/ui';
import { useEditorsPreload } from '../../Sheets';
import { DefaultDashboardActions } from './DefaultDashboardActions';
import { PreviewDashboardActions } from './PreviewDashboardActions';
import { TargetedDashboardActions } from './TargetedDashboardActions';
// import { PaywallLink } from './PaywallLink';

import styles from './Dashboard.module.scss';

export function Dashboard({ isFocusMode, isEmptyPage, onClearSelection, ...rest }) {
    const hasSelection = !!rest.selected.length;
    const isMobile = useIsMobile();
    const isInSelectionMode = hasSelection && !isFocusMode;
    const { t } = useTranslation('landings');
    const { url } = useRouteMatch();
    const isReady = useEditorsPreload();

    const handleClear = useCallback(
        (e) => {
            e?.preventDefault();
            e?.stopPropagation();
            onClearSelection();
        },
        [onClearSelection]
    );

    return (
        <div
            className={cn({
                [styles.root]: true,
                [styles.sideways]: isInSelectionMode,
                [styles.focusMode]: isFocusMode,
                [styles.onlyFirstRow]: isMobile && !hasSelection,
            })}
        >
            <div className={styles.content}>
                <TargetedDashboardActions isEmptyPage={isEmptyPage} {...rest} />
                <DefaultDashboardActions isEmptyPage={isEmptyPage} {...rest} />
                <PreviewDashboardActions isEmptyPage={isEmptyPage} {...rest} />
            </div>

            <div className={cn(styles.addButton, !isReady && styles.disabled)}>
                <Link // use 'PaywallLink' to enable paywall check
                    // showPaywall={isEmptyPage} // uncomment this to enable paywall modal
                    to={isFocusMode ? url : `${url}/add-block`}
                    title={!isFocusMode ? t('labels.block.create') : ''}
                    disabled={!isReady}
                    onClick={isInSelectionMode ? handleClear : undefined}
                >
                    <PlusIcon size={24} />
                </Link>
            </div>
        </div>
    );
}

Dashboard.propTypes = {
    isFocusMode: PropTypes.bool.isRequired,
    isEmptyPage: PropTypes.bool.isRequired,
    isDisabled: PropTypes.bool,
    selected: PropTypes.arrayOf(PropTypes.string).isRequired,
    blocks: PropTypes.arrayOf(LandingsPropTypes.block.isRequired).isRequired,
    onClearSelection: PropTypes.func.isRequired,
    onAction: PropTypes.func.isRequired,
};
