import React from 'react';
import PropTypes from 'prop-types';
import { useUpgradeLocation } from '@mssgme/hooks';
import EntityItemPane from '../EntityItemPane';

export default function BaseBanner({ title, description }) {
    const upgradeUrl = useUpgradeLocation();

    return (
        <EntityItemPane
            title={title}
            url={description}
            to={upgradeUrl}
            style={{
                width: '100%',
                border: '1px solid #2979FF',
                background: '#F3F8FF',
                boxShadow: 'none',
            }}
            dataElement="upgrade-pane"
            data-event="click-upgrade"
            data-variant="banner"
        />
    );
}

BaseBanner.propTypes = {
    title: PropTypes.string.isRequired,
    description: PropTypes.string.isRequired,
};
