import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { omit } from 'lodash';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { useHistory } from 'react-router-dom';
import { FiMoreHorizontal } from 'react-icons/fi';

import { useActions, useEventCallback, useRestriction, useWebsiteById } from '@mssgme/hooks';
import {
    Form,
    FormikProvider,
    LandingsPropTypes,
    ModalEditPane,
    SaveButton,
    ToggleField,
    useEnhancedFormik,
    usePresenceRemoval,
} from '@mssgme/ui';
import { WebsiteEditorContext } from '../../../hooks';
import { useThemeStylingDefinitions } from '../../Theming';
import { BlockEditActions } from './BlockEditActions';

import styles from './BlockEditForm.module.scss';

export function BlockEditForm({
    websiteId,
    page,
    block,
    stylingPath,
    parentStylingPath,
    themePropPath,
    children,
    successRedirectPath: successPath,
    canToggle,
    initialValues,
    validationSchema,
    blockComponent,
    isInitialDirty,
    isDeleted,
    isSafe,
    backPath,
    basePath,
    actions,
    onSubmit,
    onDone,
    ...rest
}) {
    const website = useWebsiteById(websiteId);
    const pageId = page._id;
    const blockId = block && block._id;
    const isNew = !blockId;
    const formikRef = useRef();
    const context = useMemo(() => ({ websiteId, pageId, blockId }), [websiteId, pageId, blockId]);
    const history = useHistory();
    const { api } = useActions();
    const { t } = useTranslation('landings');
    const [isClosing, setClosing] = useToggle(false);
    const [isActionsShown, toggleActionsShown] = useToggle(false);
    const [redirect, setRedirect] = useState(null);
    const getThemingForPath = useThemeStylingDefinitions();
    const styling = getThemingForPath(themePropPath, stylingPath, parentStylingPath);
    const initial = useMemo(() => ({ ...initialValues, theme: styling.enrich(initialValues.theme) }), [
        initialValues,
        styling,
    ]);
    const { warning, handleBlockedEvent } = useRestriction('page-blocks:edit', block || initialValues);
    const { warning: publishWarning, handleBlockedEvent: handleBlockedPublishEvent } = useRestriction(
        'page-blocks-publish:edit',
        block || initialValues
    );

    const handleBlock = useCallback((location) => !location.pathname.startsWith(basePath), [basePath]);

    const handleDone = useEventCallback(
        async (redirect, payload) => {
            if (onDone) {
                const event = { blockId, isNew: !blockId, block: formikRef.current.values, ...payload };

                if (false === (await onDone(event))) {
                    return;
                }
            }

            if (redirect) {
                if (typeof redirect === 'function') {
                    setClosing(true);

                    redirect();
                } else {
                    setRedirect(redirect);
                }
            }

            toggleActionsShown(false);
        },
        []
    );
    const handleSuccess = useEventCallback((payload) => {
        formikRef.current.setStatus('success');
        void handleDone(successPath, payload);
    });
    const handleCancel = useEventCallback(() => void handleDone(backPath, { action: 'cancel' }));
    const handleSubmit = async (values) => {
        const payload = { ...values, theme: styling.diff(values.theme) };
        let id = context.blockId;

        if (block) {
            await api.blocks.update({ ...context, payload: { ...block, ...payload } });
        } else {
            ({ _id: id } = await api.blocks.create({ ...omit(context, ['blockId']), payload }));
        }

        handleSuccess({ action: 'update', blockId: id });
    };

    const formik = useEnhancedFormik({
        initialValues: initial,
        validationSchema,
        onSubmit: onSubmit || handleSubmit,
    });

    formikRef.current = formik;

    usePresenceRemoval();

    useEffect(() => {
        if (redirect) {
            setRedirect(null);
            setTimeout(() => history.push(redirect), 100);
        }
    }, [redirect]);

    return (
        <FormikProvider value={formik}>
            <WebsiteEditorContext.Provider value={context}>
                <ModalEditPane
                    fullWidth
                    onClose={handleCancel}
                    guardChanges={isSafe && !isClosing && !warning && !publishWarning && !isDeleted && handleBlock}
                    rightButton={() => {
                        const isModified = formik.dirty || isInitialDirty;

                        if (isModified) {
                            return (
                                <SaveButton
                                    noborder
                                    nopadding
                                    className={styles.saveButton}
                                    btnStyle="transparent"
                                    size="small"
                                    isNew={isNew}
                                    isInitialHidden={false}
                                    disabled={!formik.isValid || isClosing}
                                    isFetching={formik.isSubmitting}
                                    {...(warning
                                        ? { onClick: handleBlockedEvent }
                                        : isNew && publishWarning && website.enabled
                                            ? { onClick: handleBlockedPublishEvent }
                                            : {})}
                                />
                            );
                        }

                        if (isNew) {
                            return null;
                        }

                        return (
                            <>
                                {actions &&
                                    React.Children.map(actions, (icon) => (
                                        <div className={styles.menuIcon} onClick={icon.props.onClick}>
                                            {icon}
                                        </div>
                                    ))}
                                <div className={styles.menuIcon} onClick={toggleActionsShown}>
                                    <FiMoreHorizontal size={25} color="#333" />
                                </div>
                            </>
                        );
                    }}
                    {...rest}
                >
                    <Form className={styles.flexForm} autoComplete="off">
                        {canToggle && <ToggleField label={t('Page.fields.enabled.label')} name="enabled" />}

                        <React.Suspense fallback={null}>
                            {children({
                                ...formik,
                                styling,
                                values: { ...formik.values, theme: styling.enrich(formik.values.theme, true) },
                                as: blockComponent,
                            })}
                        </React.Suspense>
                    </Form>

                    {isActionsShown && (
                        <BlockEditActions
                            isFetching={!!(isClosing || redirect)}
                            websiteId={websiteId}
                            pageId={pageId}
                            block={block}
                            onSuccess={handleSuccess}
                            onClose={toggleActionsShown}
                        />
                    )}
                </ModalEditPane>
            </WebsiteEditorContext.Provider>
        </FormikProvider>
    );
}

BlockEditForm.propTypes = {
    websiteId: PropTypes.string.isRequired,
    page: LandingsPropTypes.page.isRequired,
    block: LandingsPropTypes.block,
    children: PropTypes.func.isRequired,
    title: PropTypes.string,
    stylingPath: PropTypes.string,
    parentStylingPath: PropTypes.string,
    themePropPath: PropTypes.string,
    backPath: PropTypes.any.isRequired,
    basePath: PropTypes.string.isRequired,
    actions: PropTypes.any,
    successRedirectPath: PropTypes.any.isRequired,
    nopadding: PropTypes.bool,
    isInitialDirty: PropTypes.bool,
    isDeleted: PropTypes.bool,
    isSafe: PropTypes.bool,
    canToggle: PropTypes.bool,
    canDelete: PropTypes.bool,
    initialValues: PropTypes.object,
    validationSchema: PropTypes.object,
    blockComponent: PropTypes.oneOfType([PropTypes.elementType, PropTypes.bool]),
    onSubmit: PropTypes.func,
    onDone: PropTypes.func,
};

BlockEditForm.defaultProps = {
    stylingPath: '',
    parentStylingPath: '',
    themePropPath: '',
    title: '',
    block: null,
    validationSchema: null,
    canToggle: true,
    canDelete: true,
    isInitialDirty: false,
    isDeleted: false,
    isSafe: true,
    initialValues: null,
    blockComponent: false,
    onSubmit: null,
    onDone: null,
};
