import React from 'react';
import { matchPath, useLocation, useRouteMatch } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { TemplateSettings } from './View';
import { TemplateName, TemplateStatus, TemplateTags, TemplateDelete } from './Edit';

export const TemplateRouter = () => {
    const location = useLocation();
    const { path } = useRouteMatch();

    const match = (to, Component) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} {...matched.params} />;
    };

    return (
        <AnimatePresence>
            {match({ path: `${path}/settings`, exact: false }, TemplateSettings)}
            {match({ path: `${path}/settings/name`, exact: true }, TemplateName)}
            {match({ path: `${path}/settings/status`, exact: true }, TemplateStatus)}
            {match({ path: `${path}/settings/tags`, exact: true }, TemplateTags)}
            {match({ path: `${path}/settings/delete`, exact: true }, TemplateDelete)}
        </AnimatePresence>
    );
};
