import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { Link, useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiCheck, FiCopy, FiShare2, FiCode } from 'react-icons/fi';
import { motion } from 'framer-motion';

import { cls } from '@mssgme/helpers';
import { useCopyText, useNavigationUtils } from '@mssgme/hooks';
import IconQr from '../../../icons/iconQR.svg';

import styles from './PersonalLinkNav.module.scss';

const animateObject = (delay) => ({
    scale: [1, 1.2, 1],
    transition: {
        ease: 'easeInOut',
        delay,
        duration: 1,
        times: [0, 0.3, 0.6],
    },
});

export default function PersonalLinkNav({ textToCopy, sharePageTo, qrPageTo, embedTo, website, ...rest }) {
    const { i18n, t } = useTranslation('ui');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { isCopied, copyText } = useCopyText();
    const history = useHistory();
    const isWebsitePublished = website.enabled;
    const shouldAnimate =
        history.location && history.location.state && history.location.state.from === 'publishedMessage';
    const wrapperClass = cn({ [styles.navItemWrapper]: true, [styles[i18n.language]]: true });
    const itemClass = cn({ [styles.navItem]: true, [styles[i18n.language]]: true });

    const handleNotPublishedClick = (event) => {
        event.preventDefault();

        history.push(generateUrl('publish/not-published'));
    };

    return (
        <motion.div
            animate={
                shouldAnimate
                    ? {
                        opacity: [0, 1],
                        height: [0, 80],
                        transition: {
                            ease: 'easeInOut',
                            duration: 0.6,
                            times: [0, 0.6],
                            opacity: { delay: 0.3 },
                        },
                    }
                    : {}
            }
            {...cls(styles.root, rest)}
        >
            {textToCopy && (
                <motion.div
                    className={wrapperClass}
                    animate={shouldAnimate ? animateObject(0.9) : {}}
                    data-button="btn-share-copy-link"
                >
                    <div
                        style={{ pointerEvents: isCopied ? 'none' : 'auto' }}
                        className={cn(itemClass, {
                            [styles.button]: true,
                            [styles.copied]: isCopied,
                        })}
                        onClick={isWebsitePublished ? () => copyText(textToCopy) : handleNotPublishedClick}
                    >
                        <div className={styles.navItemIcon}>
                            <FiCopy size={22} />
                            <FiCheck size={22} />
                        </div>
                        <span className={styles.navItemLabel}>
                            {isCopied ? t('labels.copied') : t('labels.copyLink')}
                        </span>
                    </div>
                </motion.div>
            )}
            {qrPageTo && (
                <motion.div
                    className={wrapperClass}
                    animate={shouldAnimate ? animateObject(1.2) : {}}
                    data-button="btn-share-qr"
                >
                    <Link
                        className={itemClass}
                        to={qrPageTo}
                        onClick={isWebsitePublished ? null : handleNotPublishedClick}
                    >
                        <div className={styles.navItemIcon}>
                            <IconQr style={{ width: 24, height: 24 }} />
                        </div>
                        <span className={styles.navItemLabel}>{t('labels.qrCode')}</span>
                    </Link>
                </motion.div>
            )}
            {sharePageTo && (
                <motion.div
                    className={wrapperClass}
                    animate={shouldAnimate ? animateObject(1.5) : {}}
                    data-button="btn-open-share-page"
                >
                    <Link
                        className={itemClass}
                        to={sharePageTo}
                        onClick={isWebsitePublished ? null : handleNotPublishedClick}
                    >
                        <div className={styles.navItemIcon}>
                            <FiShare2 size={22} color="#333" />
                        </div>
                        <span className={styles.navItemLabel}>{t('labels.share')}</span>
                    </Link>
                </motion.div>
            )}
            {embedTo && (
                <motion.div
                    className={wrapperClass}
                    animate={shouldAnimate ? animateObject(1.8) : {}}
                    data-button="btn-embed-code"
                >
                    <Link className={itemClass} to={embedTo}>
                        <div className={styles.navItemIcon}>
                            <FiCode style={{ width: 24, height: 24 }} />
                        </div>
                        <span className={styles.navItemLabel}>{t('labels.widget')}</span>
                    </Link>
                </motion.div>
            )}
        </motion.div>
    );
}

PersonalLinkNav.propTypes = {
    textToCopy: PropTypes.string,
    sharePageTo: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    qrPageTo: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    embedTo: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    website: PropTypes.object,
};
