import PropTypes from 'prop-types';
import React from 'react';

import { useNavigationUtils } from '@mssgme/hooks';
import { usePresenceRemoval } from '../../hooks';
import { ModalPane } from '../ModalEditPane';
import { BlockListItemPreview, BlockListProps } from '../BlockListItemPreview';

export const AclMenuPane = ({ title, items, onClose }) => {
    const { generateUrl, state } = useNavigationUtils();
    const closePath = onClose || state?.backPath || generateUrl('..');

    usePresenceRemoval();

    return (
        <ModalPane title={title} onClose={closePath}>
            <BlockListItemPreview.List>
                {items
                    .filter((item) => item.filter !== false)
                    .map((item) => (
                        <BlockListItemPreview.ACLItem key={item.label} item={item} generateUrl={generateUrl} />
                    ))}
            </BlockListItemPreview.List>
        </ModalPane>
    );
};

AclMenuPane.propTypes = {
    title: PropTypes.any,
    items: PropTypes.arrayOf(BlockListProps.item.isRequired).isRequired,
    onClose: PropTypes.oneOfType([PropTypes.func, PropTypes.string]),
};
