import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { useNavigationUtils } from './useNavigationUtils';
import { useAccessControl } from './useAccessControl';

const PATHS = __LEGACY__
    ? { subscribed: '/s/billing', unsubscribed: '/s/billing/plans/pro400' }
    : { subscribed: '/billing', unsubscribed: '/billing/plans/pro400' };

export function useUpgradeLocation({ preventDynamicBack = false, reason = true, code, target = 'pro' } = {}) {
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { isAdmin } = useAccessControl();
    const subscription = useSelector((state) => state.billingDetails.result?.subscription);

    const location = useMemo(() => {
        const plan = subscription?.plan?.id || 'basic';
        const pathname = generateUrl(subscription ? PATHS.subscribed : PATHS.unsubscribed);
        const reasonCode = code || (isAdmin ? 'upgrade' : 'manager');

        return {
            pathname,
            state: {
                preventDynamicBack,
                reason: reason && { code: reasonCode, plan, target },
                referrer: generateUrl('.'),
            },
        };
    }, [subscription]);

    return location;
}
