import React, { useCallback, useContext, useMemo, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { SearchBar, SearchContext, SearchContextManager, Carousel } from '@giphy/react-components';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { giphy_public_key } from 'config';
import { Tabs, Tab } from '@mssgme/ui';

import poweredByGiphySrc from '../../../../img/poweredByGiphy.png';

import styles from './GiphySelector.module.scss';

export function GiphyComponents({ backgroundColor }) {
    const { t } = useTranslation('landings');
    const { fetchGifs, searchKey } = useContext(SearchContext);
    const { setFieldValue } = useFormikContext();
    const ref = useRef(null);

    const handleGifClick = useCallback(
        (gif) => {
            const previewEl = document.querySelector('[data-el="preview-block"]');

            setFieldValue('giphyId', gif.id);
            previewEl?.scrollIntoView({ block: 'start', behavior: 'smooth' });
        },
        [setFieldValue]
    );

    return (
        <div ref={ref} className={styles.giphyComponents}>
            <Carousel
                key={searchKey}
                gutter={8}
                borderRadius={10}
                gifHeight={100}
                fetchGifs={fetchGifs}
                noLink
                onGifClick={handleGifClick}
                backgroundColor={backgroundColor}
                hideAttribution
                className={styles.carousel}
                noResultsMessage={<div className={styles.noResults}>{t('Page.Edit.Giphy.carousel.notFound')}</div>}
            />
            <SearchBar placeholder={t('Page.Edit.Giphy.placeholders.search')} className={styles.searchInput} />
        </div>
    );
}

GiphyComponents.propTypes = {
    backgroundColor: PropTypes.string,
};

GiphyComponents.defaultProps = {
    backgroundColor: 'transparent',
};

export function GiphySelector(props) {
    const { t } = useTranslation('landings');
    const [tabIndex, setTabIndex] = useState(0);
    const options = useMemo(() => ({ type: tabIndex ? 'stickers' : 'gifs', limit: 5 }), [tabIndex]);

    return (
        <SearchContextManager apiKey={giphy_public_key} options={options}>
            <div style={{ marginBottom: 12 }} className={styles.root}>
                <Tabs tabIndex={tabIndex} onSelect={setTabIndex} className={styles.tabs}>
                    <Tab>{t('Page.Edit.Giphy.tabs.gifs')}</Tab>
                    <Tab>{t('Page.Edit.Giphy.tabs.stickers')}</Tab>
                </Tabs>
            </div>
            <GiphyComponents {...props} />
            <img src={poweredByGiphySrc} alt="Powered by Giphy" className={styles.giphyAttribution} />
        </SearchContextManager>
    );
}
