import React from 'react';
import PropTypes from 'prop-types';
import { FORMAT_ELEMENT_COMMAND, FORMAT_TEXT_COMMAND } from 'lexical';
import { useTranslation } from 'react-i18next';
import { BsChatSquareQuote, BsLink45Deg, BsListOl, BsListUl, BsTypeStrikethrough } from 'react-icons/bs';
import { INSERT_ORDERED_LIST_COMMAND, INSERT_UNORDERED_LIST_COMMAND, REMOVE_LIST_COMMAND } from '@lexical/list';
import { TOGGLE_LINK_COMMAND } from '@lexical/link';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';

import { cls } from '@mssgme/helpers';
import { useUiFlags } from '@mssgme/hooks';
import {
    AlignCenterIcon,
    AlignJustifyIcon,
    AlignLeftIcon,
    AlignRightIcon,
    BoldIcon,
    ColorFillIcon,
    ColorTextIcon,
    ItalicIcon,
    UnderlineIcon,
} from '@mssgme/icons';

import { ButtonGroup, ToolbarButton, ToolbarColorButton, useHorizontalScroll } from '../ui';
import { PARAGRAPH_CONVERSION_COMMAND, QUOTE_CONVERSION_COMMAND, STYLE_FORMAT_COMMAND } from './constants';
import { useRichEditorContext } from './RichEditorContext';
import { HistoryControls } from './HistoryControls';
import { isBlockType, StyleMenuButton } from './StyleMenuButton';
import { FontSizeMenuButton } from './FontSizeMenuButton';
import { TypeConversionPlugin } from './TypeConversionPlugin';
import { StyleFormatPlugin } from './StyleFormatPlugin';

import styles from './ToolbarPlugin.module.scss';

export const ToolbarPlugin = ({ small, nowrap, ...rest }) => {
    const { t } = useTranslation('ui');
    const { isAdvancedStylingMode } = useUiFlags();
    const {
        blockType,
        actualBlockType,
        isBold,
        isItalic,
        isUnderline,
        isStrikethrough,
        isList,
        isLink,
        background,
        color,
        fontSize,
    } = useRichEditorContext();
    const [editor] = useLexicalComposerContext();
    const scrollRef = useHorizontalScroll();

    return (
        <div ref={scrollRef} {...cls([styles.toolbar, small && styles.small, nowrap && styles.nowrap], rest)}>
            <TypeConversionPlugin />
            <StyleFormatPlugin />

            {isAdvancedStylingMode && (
                <HistoryControls />
            )}

            <ButtonGroup>
                <StyleMenuButton
                    value={blockType}
                    disabled={!isBlockType(actualBlockType)}
                    onSelect={(item) => editor.dispatchCommand(item.command, item.payload)}
                />

                {isAdvancedStylingMode && (
                    <FontSizeMenuButton
                        disabled={'paragraph' !== blockType}
                        value={fontSize}
                        onSelect={(item) => editor.dispatchCommand(item.command, item.payload)}
                    />
                )}
            </ButtonGroup>

            <ButtonGroup>
                <ToolbarButton
                    label={t('RTE.formatting.bold')}
                    active={isBold}
                    icon={BoldIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'bold')}
                />
                <ToolbarButton
                    label={t('RTE.formatting.italic')}
                    active={isItalic}
                    icon={ItalicIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'italic')}
                />
                <ToolbarButton
                    label={t('RTE.formatting.underlined')}
                    active={isUnderline}
                    icon={UnderlineIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'underline')}
                />
                {isAdvancedStylingMode && (
                    <ToolbarButton
                        label={t('RTE.formatting.strikethrough')}
                        active={isStrikethrough}
                        icon={BsTypeStrikethrough}
                        onClick={() => editor.dispatchCommand(FORMAT_TEXT_COMMAND, 'strikethrough')}
                    />
                )}
            </ButtonGroup>

            <ButtonGroup>
                <ToolbarColorButton
                    restriction="page-colors"
                    label={t('RTE.fontFill')}
                    value={background}
                    icon={ColorFillIcon}
                    onSelect={(background) => editor.dispatchCommand(STYLE_FORMAT_COMMAND, { background })}
                />
                <ToolbarColorButton
                    restriction="page-colors"
                    label={t('RTE.fontColor')}
                    value={color}
                    icon={ColorTextIcon}
                    onSelect={(color) => editor.dispatchCommand(STYLE_FORMAT_COMMAND, { color })}
                />
            </ButtonGroup>

            <ButtonGroup>
                <ToolbarButton
                    label={t('RTE.align.left')}
                    icon={AlignLeftIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_ELEMENT_COMMAND, 'left')}
                />
                <ToolbarButton
                    label={t('RTE.align.center')}
                    disabled={isList}
                    icon={AlignCenterIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_ELEMENT_COMMAND, 'center')}
                />
                <ToolbarButton
                    label={t('RTE.align.right')}
                    disabled={isList}
                    icon={AlignRightIcon}
                    onClick={() => editor.dispatchCommand(FORMAT_ELEMENT_COMMAND, 'right')}
                />
                {isAdvancedStylingMode && (
                    <ToolbarButton
                        label={t('RTE.align.justify')}
                        icon={AlignJustifyIcon}
                        onClick={() => editor.dispatchCommand(FORMAT_ELEMENT_COMMAND, 'justify')}
                    />
                )}
            </ButtonGroup>

            <ButtonGroup>
                <ToolbarButton
                    label={t('RTE.blockStyle.quote')}
                    active={actualBlockType === 'quote'}
                    icon={BsChatSquareQuote}
                    onClick={() => editor.dispatchCommand(
                        actualBlockType === 'quote' ? PARAGRAPH_CONVERSION_COMMAND : QUOTE_CONVERSION_COMMAND
                    )}
                />
                <ToolbarButton
                    label={t('RTE.blockStyle.ul')}
                    active={actualBlockType === 'ul'}
                    icon={BsListUl}
                    onClick={() => editor.dispatchCommand(
                        actualBlockType === 'ul' ? REMOVE_LIST_COMMAND : INSERT_UNORDERED_LIST_COMMAND
                    )}
                />
                <ToolbarButton
                    label={t('RTE.blockStyle.ol')}
                    active={actualBlockType === 'ol'}
                    icon={BsListOl}
                    onClick={() => editor.dispatchCommand(
                        actualBlockType === 'ol' ? REMOVE_LIST_COMMAND : INSERT_ORDERED_LIST_COMMAND
                    )}
                />
            </ButtonGroup>

            {isAdvancedStylingMode && (
                <ButtonGroup>
                    <ToolbarButton
                        label={t('RTE.insertLink')}
                        active={isLink}
                        icon={BsLink45Deg}
                        onClick={() => editor.dispatchCommand(TOGGLE_LINK_COMMAND, isLink ? null : 'https://')}
                    />
                </ButtonGroup>
            )}
        </div>
    );
};

ToolbarPlugin.propTypes = {
    nowrap: PropTypes.bool,
    small: PropTypes.bool,
};
