import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextareaField, TextField } from '@mssgme/ui';
import { Quote } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

export function QuoteEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: QuoteEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Quote.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <TextareaField
                        required
                        builderInput
                        label={t('Page.Edit.Quote.fields.content.label')}
                        placeholder={t('Page.Edit.Quote.fields.content.placeholder')}
                        name="content"
                        maxLength={1000}
                    />

                    <TextField
                        builderInput
                        label={t('Page.Edit.Quote.fields.author.label')}
                        placeholder={t('Page.Edit.Quote.fields.author.placeholder')}
                        name="author"
                        maxLength={100}
                    />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

QuoteEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

QuoteEdit.block = Quote;
