import PropTypes from 'prop-types';
import React, { useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { useTemplates } from '@mssgme/hooks';
import { getDefaultTheme } from '@mssgme/shared';
import { LoadingIndicator, ModalPane } from '@mssgme/ui';
import { TemplatePill } from './TemplatePill';

import styles from './commonStyles.module.scss';

export const SetupForm = ({ backPath, onApply }) => {
    const { t } = useTranslation('landings');
    const { templates, pending } = useTemplates();

    const theme = getDefaultTheme();
    const options = useMemo(
        () => [
            {
                value: null,
                label: t('Page.Edit.Setup.labels.empty'),
            },
            ...templates.map((template) => ({
                value: template._id,
                label: template.name,
                page: { ...template, theme },
            })),
        ],
        [templates, theme]
    );

    useEffect(() => {
        if (!pending && !templates.length) {
            onApply();
        }
    }, [pending, templates.length, onApply]);

    if (!templates.length) {
        return null;
    }

    return (
        <ModalPane title={t('Page.Edit.Setup.heading')} onClose={backPath}>
            <div className={styles.root}>
                {pending ? (
                    <div style={{ margin: '16px 0' }}>
                        <LoadingIndicator />
                    </div>
                ) : (
                    <div className={styles.grid}>
                        <div className={styles.pitch}>{t('Page.Edit.Setup.labels.tip')}</div>

                        {options.map((option) => (
                            <TemplatePill key={option.value} option={option} onSelect={onApply} />
                        ))}
                    </div>
                )}
            </div>
        </ModalPane>
    );
};

SetupForm.propTypes = {
    backPath: PropTypes.string,
    onApply: PropTypes.func.isRequired,
};
