import React, { useCallback } from 'react';

import { isSameArray } from '@mssgme/helpers';
import { StyledSelectInput } from '../Inputs';
import { SelectFieldProps } from './types';
import FieldRow from './FieldRow';
import { useField } from 'formik';

export default function StyledSelectField({ onChange, multiple, noOptionsMessage, ...props }) {
    const [, { initialValue }, { setValue, setTouched }] = useField(props.name);

    const handleChange = useCallback((value) => {
        const dirty = multiple
            ? !isSameArray(initialValue, value, { ordered: true })
            : value !== initialValue;

        setValue(name, dirty ? value : initialValue);
        setTouched(name, dirty, dirty);

        onChange?.(name, value);
    }, [initialValue, setTouched, setValue, onChange]);

    return (
        <FieldRow {...props}>
            {({ field: { name, value, disabled, ...rest } }) => (
                <StyledSelectInput
                    {...rest}
                    name={name}
                    multiple={multiple}
                    disabled={disabled}
                    value={value}
                    noOptionsMessage={noOptionsMessage}
                    onChange={handleChange}
                />
            )}
        </FieldRow>
    );
}

StyledSelectField.propTypes = SelectFieldProps;
