import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { object } from 'yup';

import { PAYMENT_PROVIDER_FORMS } from '../../../UI';
import { PaymentsSettingsScreen } from './PaymentsSettingsScreen';

PaymentProviderSettingsScreen.propTypes = {
    provider: PropTypes.string.isRequired,
};

export function PaymentProviderSettingsScreen({ provider }) {
    const { t } = useTranslation('products');
    const { title, form: Form, schema } = PAYMENT_PROVIDER_FORMS[provider];
    const validationSchema = useMemo(
        () =>
            object({
                payments: object({ [provider]: schema }),
            }),
        [provider, schema]
    );

    return (
        <PaymentsSettingsScreen
            provider={provider}
            validationSchema={validationSchema}
            title={t(`Payments.${title}.heading`)}
        >
            {(enabled) => <Form enabled={enabled} name={`payments.${provider}`} />}
        </PaymentsSettingsScreen>
    );
}
