import PropTypes from 'prop-types';
import React from 'react';

import { DataItem } from './DataItem';
import { DetailView } from './DetailView';

import styles from './FeedbackItemForm.module.scss';

export const DataList = ({ data, typed, divide = true, cut = false }) => {
    if (!data.length) {
        return null;
    }

    return (
        <>
            {divide && <div className={styles.divider} />}

            {data.map((field, index) => (
                <DataItem key={field.label + index} label={field.label} spread={typed}>
                    <DetailView item={field} cut={cut} />
                </DataItem>
            ))}
        </>
    );
};

DataList.propTypes = {
    data: PropTypes.arrayOf(
        PropTypes.shape({
            type: PropTypes.string.isRequired,
        })
    ).isRequired,
    typed: PropTypes.bool,
    divide: PropTypes.bool,
    cut: PropTypes.bool,
};
