import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import DragIcon from '../../../icons/drag.svg';
import DragLinesIcon from '../../../icons/drag-lines.svg';
import styles from './DragHandle.module.scss';

export const DragHandle = ({ small, active = true, hidden, iconSize = 6, ...rest }) => {
    const Tag = small === 'medium' ? DragLinesIcon : DragIcon;

    return (
        <div
            data-element="drag-handle"
            {...cls(
                cn(
                    {
                        [styles.root]: true,
                        [styles.small]: small && typeof small !== 'string',
                        [styles.active]: active,
                        [styles.hidden]: hidden,
                    },
                    styles[small]
                ),
                rest
            )}
        >
            <Tag style={{ width: iconSize }} />
        </div>
    );
};

DragHandle.propTypes = {
    small: PropTypes.oneOfType([PropTypes.bool, PropTypes.oneOf(['small', 'medium', 'auto'])]),
    iconSize: PropTypes.number,
    active: PropTypes.bool,
    hidden: PropTypes.bool,
};

DragHandle.shouldCancelStart = ({ target }) => {
    let node = target;

    while (node) {
        if (node.dataset && node.dataset.element === 'drag-handle') {
            return false;
        }

        node = node.parentNode;
    }

    return true;
};

DragHandle.helperClass = styles.sortable;

export default DragHandle;
