import React from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Button } from '@mssgme/ui';

import styles from './SomethingWrong.module.scss';

export default function SomethingWrong({ error }) {
    const { t } = useTranslation('common');

    if (error.name === 'AuthorizationError') {
        return <Redirect to="/logout" />;
    }

    if (error.name === 'NetworkError') {
        return (
            <div className={styles.root}>
                <div className={styles.content}>
                    <h2 className={styles.heading}>
                        {t('SomethingWrong.networkError')}
                    </h2>
                    <p>
                        <Button btnStyle="danger" onClick={() => window.location.reload()} fullWidth>
                            {t('SomethingWrong.reload')}
                        </Button>
                    </p>
                </div>
            </div>
        );
    }

    return (
        <div className={styles.root}>
            <div className={styles.content}>
                <h2 className={styles.heading}>
                    {t('SomethingWrong.error')}
                </h2>
                <code className={styles.code}>
                    {error.message}
                </code>
                <p>
                    <Button fullWidth btnStyle="danger" tag="a" href="/logout">
                        {t('SomethingWrong.logout')}
                    </Button>
                </p>
            </div>
        </div>
    );
}

SomethingWrong.propTypes = {
    error: PropTypes.oneOfType([
        PropTypes.instanceOf(Error),
        PropTypes.shape({
            name: PropTypes.string,
            message: PropTypes.string,
            stack: PropTypes.string,
        }),
    ]).isRequired,
};
