import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { TextareaField } from '@mssgme/ui';

import { DataItem } from './DataItem';

export const EditableData = ({ label, name, editable, onSubmit, ...rest }) => {
    const { t } = useTranslation('landings');
    const handleSubmit = useCallback(() => onSubmit && onSubmit(), [onSubmit]);

    return (
        <DataItem label={label}>
            <TextareaField
                name={name}
                builderInput
                maxRows={50}
                style={{ marginTop: 6 }}
                disabled={!editable}
                placeholder={editable ? t('Feedback.Edit.Item.placeholders.notes') : ''}
                onBlur={handleSubmit}
                {...rest}
            />
        </DataItem>
    );
};

EditableData.propTypes = {
    editable: PropTypes.bool,
    label: PropTypes.any,
    name: PropTypes.string.isRequired,
    onSubmit: PropTypes.func.isRequired,
};
