import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ModalPane } from '@mssgme/ui';
import { useWebsiteUrl } from '../../../../hooks';
import { SocialButton } from './SocialButton';
import { SOCIAL_BUTTONS } from './buttons';

export function Socials({ socials, backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const shareUrl = useWebsiteUrl(website, page);
    const closePath = generateUrl(backPath || '..');
    const isBio = socials === 'bio';

    const buttons = useMemo(
        () => Object.entries(SOCIAL_BUTTONS)
            .filter(([, button]) => !isBio || button.isBio)
            .map(([social, button]) => (
                button.isBio
                    ? { social, to: generateUrl(social), ...button }
                    : { social, url: shareUrl, ...button }
            )),
        [isBio, shareUrl]
    );

    return (
        <ModalPane title={t(`Editor.ShareSocial.${isBio ? 'bio' : 'heading'}`)} onClose={closePath}>
            <div>
                {buttons.map((button) => (
                    <SocialButton key={button.social} {...button} />
                ))}
            </div>
        </ModalPane>
    );
}

Socials.propTypes = {
    socials: PropTypes.string,
    backPath: PropTypes.string,
};
