import { $getSelection, $isRangeSelection } from 'lexical';
import {
    getLinkNode,
    getNodeBlockType,
    getNodeStyle,
    getSelectedNode,
    isEditableTextNode,
    isEmptyEditor,
} from '../../utils';
import { backgroundAccessor, colorAccessor, fontSizeAccessor, getSupportedBlockType } from './utils';

export const getEditorContextState = (editor, current) => {
    const selection = $getSelection();

    if (!$isRangeSelection(selection)) {
        return;
    }

    const node = getSelectedNode(selection);
    const actualBlockType = getNodeBlockType(editor, node, current.blockType);
    const blockType = getSupportedBlockType(actualBlockType);

    const fontSize = getNodeStyle(selection, fontSizeAccessor);
    const color = getNodeStyle(selection, colorAccessor);
    const background = getNodeStyle(selection, backgroundAccessor);

    const isText = isEditableTextNode(selection, node);
    const isEmpty = isEmptyEditor();

    // Update text format
    const isBold = selection.hasFormat('bold');
    const isItalic = selection.hasFormat('italic');
    const isUnderline = selection.hasFormat('underline');
    const isStrikethrough = selection.hasFormat('strikethrough');

    // update text style
    const isLink = !!getLinkNode(selection);
    const isList = actualBlockType === 'ul' || actualBlockType === 'ol';

    return {
        isText,
        isEmpty,
        isBold,
        isItalic,
        isUnderline,
        isStrikethrough,
        isList,
        isLink,
        blockType,
        actualBlockType,
        fontSize,
        color,
        background,
    };
};
