import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom';

import { useAccessControl } from '@mssgme/hooks';
import SinglePlanPage from './SinglePlanPage';
import ConfirmSubscriptionPage from './ConfirmSubscriptionPage';
import PlansPage from './PlansPage';
import BillingHome from './BillingHome';
import ChangeCardPage from './ChangeCardPage';
import CheckoutCompleteSuccessfullyPage from './CheckoutCompleteSuccessfullyPage';

export default function BillingRouting() {
    const { isAdmin } = useAccessControl();
    const { path } = useRouteMatch();
    const hasRiskTransaction = useSelector(
        (state) => state.workspace.billing?.highRiskTransaction?.status === 'pending'
    );

    useEffect(() => {
        document.body.style.setProperty('--bg', 'var(--color-white)');

        return () => {
            document.body.style.removeProperty('--bg');
        };
    }, []);

    if (!isAdmin) return null;

    return (
        <Switch>
            {hasRiskTransaction && <Redirect from={`${path}/*`} to={path} />}
            <Route
                exact
                path={[`${path}/plans/:plan/:period/:trial?`, `${path}/pay/:plan/:period?`]}
                component={ConfirmSubscriptionPage}
            />
            <Route exact path={`${path}/plans/:plan`} component={SinglePlanPage} />
            <Route exact path={`${path}/plans`} component={PlansPage} />
            <Route exact path={`${path}/change-card`} component={ChangeCardPage} />
            <Route exact path={`${path}/checkout-complete-successfully`} component={CheckoutCompleteSuccessfullyPage} />
            <Route exact path={path} component={BillingHome} />
            <Redirect to={path} />
        </Switch>
    );
}
