import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useLocation } from 'react-router-dom';

import { useFeature } from '@mssgme/hooks';
import { FieldTip } from '@mssgme/ui';
import { AccountEditForm } from '../AccountEditForm';
import ConnectEmailField from './ConnectEmailField';
import ConnectTelegramField from './ConnectTelegramField';
import ConnectWebhookField from './ConnectWebhookField';

export function NotificationsPage({ backPath }) {
    const { t } = useTranslation();
    const { state } = useLocation();
    const webhookEnabled = useFeature('notifications_webhook');

    return (
        <AccountEditForm title={t('notificationsPage.heading')} backPath={backPath}>
            {state?.commonSettingsTip && (
                <FieldTip style={{ marginBottom: 10 }}>{t('notificationsPage.commonSettingsTip')}</FieldTip>
            )}

            <ConnectEmailField name="notificationSettings.email" />
            <ConnectTelegramField name="notificationSettings.telegram" />

            {webhookEnabled && <ConnectWebhookField name="notificationSettings.webhook" />}
        </AccountEditForm>
    );
}

NotificationsPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
