import React, { useMemo } from 'react';
import PropTypes from 'prop-types';

import { ChevronDownIcon } from '@mssgme/icons';
import { ModalSheet, ModalSheetHeader, ModalSheetPane, SaveButton } from '@mssgme/ui';
import { BlockSelector } from '../BlockSelector';

import styles from './AnchorSelector.module.scss';

const AnchorSelector = ({
    name,
    value,
    onChange,
    onClose,
    website,
    page,
    togglePageModal,
    ...rest
}) => {
    const isMultiplePagesAvailable = useMemo(() => website
        .pages
        .filter(({ enabled, blocks }) => enabled && blocks.length > 0)
        .length > 1,
    [website]);

    return (
        <ModalSheet fullHeight {...rest}>
            <ModalSheetPane className={styles.selector} padding={false} header={
                <ModalSheetHeader
                    style={{ background: 'var(--modal-bg)', zIndex: 1 }}
                    title={
                        <div className={styles.modalTitle} onClick={isMultiplePagesAvailable ? togglePageModal : null}>
                            <div className={styles.pageName}>{page.name}</div>
                            {isMultiplePagesAvailable && <div className={styles.chevronIcon}><ChevronDownIcon /></div>}
                        </div>
                    }
                    closePath={onClose}
                    rightButton={
                        <SaveButton
                            tag="div"
                            noborder
                            nopadding
                            className={styles.saveButton}
                            btnStyle="transparent"
                            size="small"
                            isInitialHidden={false}
                            onClick={onClose}
                        />
                    }
                />
            }>
                <div className={styles.selector}>
                    <BlockSelector page={page} website={website} onChange={onChange} value={value} name={name} />
                </div>
            </ModalSheetPane>
        </ModalSheet>
    );
};

AnchorSelector.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    onClose: PropTypes.func.isRequired,
    website: PropTypes.object.isRequired,
    page: PropTypes.object.isRequired,
    togglePageModal: PropTypes.func.isRequired,
};

export default AnchorSelector;
