import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { ToggleField } from '@mssgme/ui';

ConnectEmailField.propTypes = {
    name: PropTypes.string.isRequired,
};

function ConnectEmailField({ name }) {
    const { t } = useTranslation();

    return (
        <div style={{ marginBottom: 10 }}>
            <ToggleField builderInput name={`${name}.enabled`} label={t('accountForm.label.notifications.email')} />
        </div>
    );
}

export default ConnectEmailField;
