import cc from 'currency-codes';
import symbols from 'currency-symbol-map/map';

const factory = () => {
    const preferred = ['USD', 'EUR', 'UAH', 'KZT', 'PLN', 'TRY'];
    const rest = cc.codes().filter((code) => !preferred.includes(code));
    const codes = [...preferred, ...rest];

    return {
        codes,
        options: codes.map((code) => {
            const label = cc.code(code)?.currency || code;
            const symbol = symbols[code] || code;

            return {
                value: code,
                label,
                symbol,
            };
        }),
    };
};

let _cache;
export const CURRENCY = {
    get codes() {
        if (!_cache) {
            _cache = factory();
        }

        return _cache.codes;
    },
    get options() {
        if (!_cache) {
            _cache = factory();
        }

        return _cache.options;
    },
};
