import React from 'react';
import { useTranslation } from 'react-i18next';

import { __t, prettifyUrl } from '@mssgme/shared';
import { LandingsPropTypes, StyledSelectInput } from '@mssgme/ui';

import styles from './SelectPageInput.module.scss';

const inputStyles = {
    control: (baseStyle) => ({
        ...baseStyle,
        border: 'none',
        boxShadow: 'none',
        borderWidth: 0,
        borderRadius: 10,
        height: 38,
        padding: '0 0 0 16px',
    }),
    valueContainer: (baseStyle) => ({
        ...baseStyle,
        padding: 0,
    }),
    indicatorSeparator: (baseStyle) => ({
        ...baseStyle,
        display: 'none',
    }),
    indicatorsContainer: (baseStyle) => ({
        ...baseStyle,
        minWidth: 36,
    }),
    dropdownIndicator: (baseStyle) => ({
        ...baseStyle,
        width: 33,
        padding: '0 10px',
        color: '#333',
    }),
    menu: (baseStyle) => ({
        ...baseStyle,
        borderRadius: 10,
        padding: 0,
        overflow: 'hidden',
    }),
    clearIndicator: (baseStyle) => ({
        ...baseStyle,
        display: 'none',
    }),
    menuList: (baseStyle) => ({
        ...baseStyle,
        padding: 0,
    }),
};

export function SelectPageInput({ website: { homePageId, pages, finalUrl }, website, ...rest }) {
    const { t } = useTranslation('ui');
    const options = pages.map((page) => {
        const isHomePage = page._id === homePageId;
        const data = isHomePage ? {
            name: __t('homePage'),
            finalUrl: website.finalUrl,
        } : {
            name: page.name,
            finalUrl: finalUrl + `/${page.slug}`,
        };

        return {
            page,
            value: page._id,
            label: data.name || t('labels.unnamedPage'),
            url: data.finalUrl,
        };
    });

    return (
        <StyledSelectInput
            menuPortalTarget={document.body}
            menuPosition="fixed"
            captureMenuScroll
            blurInputOnSelect
            isSearchable={false}
            isClearable
            options={options}
            formatOptionLabel={(option, { context }) => (
                <div className={styles.option}>
                    <div>{option.label}</div>

                    {context === 'menu' && option.url && (
                        <div className={styles.url}>{prettifyUrl(option.url)}</div>
                    )}
                </div>
            )}
            styles={inputStyles}
            {...rest}
        />
    );
}

SelectPageInput.propTypes = {
    ...StyledSelectInput.propTypes,
    website: LandingsPropTypes.website.isRequired,
};
