import React, { useCallback, useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { useBilling } from '@mssgme/hooks';
import { EnhancedFormik, Form, SaveButton, TextareaField, Button } from '@mssgme/ui';

export default function UnsubscribeReasonForm({ onSubmit, onClose }) {
    const { t } = useTranslation('billing');
    const { billing } = useBilling();
    const [isSubmitted, setIsSubmitted] = useState(false);
    const initial = useMemo(
        () => ({
            reason: billing.unsubscribeReason.reason,
        }),
        [billing.unsubscribeReason.reason]
    );

    const handleSubmit = useCallback(
        async (values) => {
            await onSubmit(values);
            setIsSubmitted(true);
        },
        [onSubmit]
    );

    return (
        <EnhancedFormik initialValues={initial} validateOnMount onSubmit={handleSubmit}>
            {(form) => (
                <Form autoComplete="off">
                    <TextareaField
                        required
                        builderInput
                        style={{ marginBottom: 0 }}
                        placeholder={t('UnsubscribeModal.reasonPlaceholder')}
                        name="reason"
                        maxLength={5000}
                        minRows={4}
                        maxRows={8}
                    />
                    <SaveButton
                        btnStyle="danger"
                        fullWidth
                        size="standart"
                        isInitialHidden={false}
                        disabled={isSubmitted || form.isSubmitting}
                        style={{ marginTop: 24, fontSize: 16 }}
                        isFetching={form.isSubmitting}
                    >
                        {t('UnsubscribeModal.buttons.cancelSubscription')}
                    </SaveButton>
                    <Button
                        tag="div"
                        fullWidth
                        btnStyle="secondary2"
                        style={{ marginTop: 10, fontSize: 16 }}
                        onClick={onClose}
                    >
                        {t('UnsubscribeModal.buttons.closeModal')}
                    </Button>
                </Form>
            )}
        </EnhancedFormik>
    );
}

UnsubscribeReasonForm.propTypes = {
    onSubmit: PropTypes.func.isRequired,
    onClose: PropTypes.func.isRequired,
};
