import { object, string, array } from 'yup';

export const VideoGalleryItemSchema = object({
    url: string().required('common:errorsList.video_required'),
    annotation: string()
        .ensure()
        .max(50, 'common:errorsList.block_image_annotation_length'),
});

export const VideoGalleryBlockSchema = object({
    videos: array().min(1, 'common:errorsList.block_video_gallery_count'),
});
