import PropTypes from 'prop-types';
import React from 'react';
import { Link } from 'react-router-dom';

import { cls } from '@mssgme/helpers';
import { useProductsContext } from '../../hooks';
import { ProductAdminItem } from './ProductAdminItem';

import styles from './ProductsListDraggable.module.scss';

export function ProductsList({ products, renderMeta, onClick, noPadding, collectionId, ...rest }) {
    const { router } = useProductsContext();

    if (!products.length) {
        return null;
    }

    return (
        <ul {...cls(styles.root, rest)}>
            {products.map((product) => (
                <ProductAdminItem
                    key={product._id}
                    outer="li"
                    tag={Link}
                    product={product}
                    to={router.product.toEdit(product._id, collectionId)}
                    title={product.name}
                    renderMeta={renderMeta}
                    onClick={onClick}
                    noPadding={noPadding}
                />
            ))}
        </ul>
    );
}

ProductsList.propTypes = {
    products: PropTypes.array.isRequired,
    renderMeta: PropTypes.func,
    onClick: PropTypes.func,
    noPadding: PropTypes.bool,
    collectionId: PropTypes.string,
};
