import { useMemo } from 'react';
import { matchPath, useLocation, useRouteMatch } from 'react-router-dom';

export const useBuilderModes = (page) => {
    const { url } = useRouteMatch();
    const { pathname } = useLocation();
    const pageId = page._id;

    return useMemo(
        () => {
            const previewBase = `${url}/preview`;
            const mobileBase = `${previewBase}/mobile`;
            const isGuideMode = !!matchPath(pathname, `${url}/guide`);
            const isViewMode = !!matchPath(pathname, previewBase);
            const isMobileMode = isViewMode && !!matchPath(pathname, mobileBase);
            const isDesktopMode = isViewMode && !isMobileMode;
            const basePathName = isViewMode ? (isMobileMode ? mobileBase : `${previewBase}/desktop`) : url;
            const baseUrl = basePathName.replace(pageId, ':pageSlug');

            return {
                baseUrl,
                isGuideMode,
                isViewMode,
                isMobileMode,
                isDesktopMode,
            };
        },
        [url, pathname, pageId]
    );
};
