import { EventEmitter } from 'events';
import { throttle } from 'lodash';

export const identityRect = { left: 0, top: 0, right: 0, bottom: 0, width: 0, height: 0 };

export const getClientRect = (node) => {
    if (!(node && node.getBoundingClientRect)) {
        return identityRect;
    }

    const { left, top, right, bottom } = node.getBoundingClientRect();
    const width = right - left;
    const height = bottom - top;

    if (!(width || height)) {
        return identityRect;
    }

    let parent = node;
    let dx = 0;
    let dy = 0;

    while (parent) {
        dy += parent.scrollTop || 0;
        dx += parent.scrollLeft || 0;

        parent = parent.parentNode;
    }

    return {
        left: left + dx,
        top: top + dy,
        right: right + dx,
        bottom: bottom + dy,
        width,
        height,
    };
};

/**
 * Combine lists of selected block rects into list of rects of groups of sequential blocks.
 *
 * @param {Array<{ _id: string; nested: boolean }>} blocks List of blocks
 * @param {Array<{ id: string; area: DOMRect }>} ranges List of selection range areas
 * @yields {Iterable<{ id: string; area: DOMRect }>} List of selection range areas, where consecutive areas were
 * combined.
 */
export function* getSequencedRectRanges(blocks, ranges) {
    /*
          IN            OUT
        +---------+    +---------+
        | block 1 |    | block 1 |
        +---------+    +---------+
          block 2        block 2
        +---------+    +---------+
        | block 3 |    | block 3 |
        +---------+    |         |
        | block 4 |    | block 4 |
        +---------+    +---------+
          block 5        block 5
        +---------+    +---------+
        | block 6 |    | block 6 |
        +---------+    +---------+
     */
    let consecutive = [];
    const map = new Map(ranges.map((item) => [item.id, item]));
    const squash = () => {
        const range = consecutive.map((id) => map.get(id));
        const head = range[0];
        consecutive = [];

        return {
            id: head.id,
            area: squashRange(range), // combine the areas of the group
        };
    };

    for (const { _id: blockId, nested } of blocks) {
        if (nested) {
            // nested blocks (inside block groups) are excluded from selections
            continue;
        }

        if (map.has(blockId)) {
            consecutive.push(blockId);
        } else if (consecutive.length) {
            yield squash();
        }
    }

    if (consecutive.length) {
        yield squash();
    }
}

/**
 * Combine block client rects into one. Returns the smallest rect that includes all input rects.
 *
 * @param {Array<{ id: string; area: DOMRect }>} range List of block client areas
 * @returns {DOMRect} Resulting rect
 */
export const squashRange = (range) => {
    const rect = {
        left: Infinity,
        right: -Infinity,
        top: Infinity,
        bottom: -Infinity,
        width: 0,
        height: 0,
    };

    range.forEach(({ area: { left, top, right, bottom } }) => {
        if (left < rect.left) {
            rect.left = left;
        }
        if (top < rect.top) {
            rect.top = top;
        }
        if (right > rect.right) {
            rect.right = right;
        }
        if (bottom > rect.bottom) {
            rect.bottom = bottom;
        }
    });

    rect.width = rect.right - rect.left;
    rect.height = rect.bottom - rect.top;

    return rect;
};

/**
 * Expands input rect by specified values. Modifies supplied rect object.
 *
 * @param {DOMRect} rect Input rect
 * @param {[number, number]} expand Expand rect by [X, Y] pixels on [horizontal, vertical] sides
 * @returns {DOMRect} Modified rect
 */
export const expandRect = (rect, expand) => {
    const [extendX, extendY] = expand;
    rect.left -= extendX;
    rect.right += extendX;
    rect.top -= extendY;
    rect.bottom += extendY;
    rect.width += extendX * 2;
    rect.height += extendY * 2;

    return rect;
};

export const isEqualRect = (a, b) =>
    a.left === b.left && a.top === b.top && a.width === b.width && a.height === b.height;

export const updateRangesList = (old, id, area) => {
    const result = [];
    let shouldInsert = area && area.height;
    let updated = false;

    for (const item of old) {
        if (item.id !== id) {
            result.push(item);

            continue;
        }

        if (!shouldInsert) {
            continue;
        }

        if (isEqualRect(item.area, area)) {
            result.push(item);
        } else {
            result.push({ id, area });
        }

        updated = true;
        shouldInsert = false;
    }

    if (shouldInsert && !updated) {
        result.push({ id, area });
        updated = true;
    }

    if (updated || result.length !== old.length) {
        return result;
    }
};

export const makeSelectionObserver = ({ get, set, maxListeners = 500, throttleMs = 5 }) => {
    const EVENT = 'selectionchange';
    const ee = new EventEmitter();
    ee.setMaxListeners(maxListeners);

    return {
        emit: throttle(() => ee.emit(EVENT, get()), throttleMs),
        update: (id, area) => {
            const result = updateRangesList(get(), id, area);

            if (result) {
                set(result);
            }
        },
        listen: (listener) => {
            ee.addListener(EVENT, listener);

            listener(get());

            return () => ee.removeListener(EVENT, listener);
        },
    };
};
