import { useI18n } from '@mssgme/i18n';

export const useLandingsTranslations = () => {
    return useI18n('landings', (language) =>
        import(
            /* webpackChunkName: "locales.landings" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/landings.json`
        )
    );
};
