import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { SwappableItemContainer } from '@mssgme/ui';
import { FeedbackItem } from './FeedbackItem';

export const SwipeableFeedbackItem = SwappableItemContainer(function SwipeableFeedbackItem({
    item,
    to,
    render,
    onDeleting,
    onAction,
    ...rest
}) {
    const handleAction = useCallback((action) => onDeleting(() => onAction(item, action)), [
        item,
        onAction,
        onDeleting,
    ]);

    return render({
        item,
        id: item._id,
        children: <FeedbackItem item={item} to={to} {...rest} />,
        onAction: handleAction,
    });
});

SwipeableFeedbackItem.propTypes = {
    ...SwipeableFeedbackItem.propTypes,
    item: PropTypes.object.isRequired,
    to: PropTypes.string,
};
