import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Features } from '@mssgme/userpage-core';
import {
    BlockEditForm,
    EditStylingForm,
    FeaturesBlockSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';
import { FeaturesItemsField } from './FeaturesItemsField';

export function FeaturesEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: FeaturesEdit });

    return (
        <BlockEditForm {...bind} validationSchema={FeaturesBlockSchema} title={t('Page.Edit.Features.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }} contentStyle={{ overflow: 'auto' }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <FeaturesItemsField name="items" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

FeaturesEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

FeaturesEdit.block = Features;
