export const setFloatingElemPosition = (rect, floatingElem, anchorElem, verticalGap, horizontalOffset) => {
    const scrollerElem = anchorElem.parentElement;
    const isValid = !!(rect && scrollerElem);
    let left = 0;
    let top = 0;

    if (isValid) {
        const { width, height } = floatingElem.getBoundingClientRect();
        const { left: anchorLeft, top: anchorTop } = anchorElem.getBoundingClientRect();
        const { top: scrollerTop, right: scrollerRight } = scrollerElem.getBoundingClientRect();
        const scrollerLeftSpace = scrollerRight - width;

        top = rect.top - height - verticalGap;
        left = rect.left - horizontalOffset;

        if (top < scrollerTop) {
            top += height + rect.height + verticalGap * 2;
        }

        if (left > scrollerLeftSpace) {
            left = scrollerLeftSpace - horizontalOffset;
        }

        top -= anchorTop;
        left -= anchorLeft;
    }

    placeFloatingElem(floatingElem, isValid, left, top);

    return isValid;
};

const OFFSCREEN = -10000;

export const placeFloatingElem = (floatingElem, show, x, y) => {
    if (!show) {
        x = y = OFFSCREEN;
    }

    floatingElem.style.opacity = show ? 1 : 0;
    floatingElem.style.transform = `translate(${x}px, ${y}px)`;
};
