import PropTypes from 'prop-types';
import React from 'react';
import { FaWhatsapp, FaTelegramPlane, FaFacebookMessenger, FaViber, FaVk, FaSkype, FaInstagram } from 'react-icons/fa';

import SkypeFullIcon from '../../../icons/skype-rounded.svg';
import TelegramFullIcon from '../../../icons/telegram-rounded.svg';
import ViberFullIcon from '../../../icons/viber-rounded.svg';
import VkFullIcon from '../../../icons/vk-rounded.svg';
import WhatsAppFullIcon from '../../../icons/whatsapp-rounded.svg';
import MessengerFullIcon from '../../../icons/messenger-rounded.svg';

const messengersMap = {
    messenger: {
        type: 'messenger',
        flatIcon: FaFacebookMessenger,
        icon: MessengerFullIcon,
        name: 'Facebook Messenger',
        background: '#0078FF',
    },
    telegram: {
        type: 'telegram',
        flatIcon: FaTelegramPlane,
        icon: TelegramFullIcon,
        name: 'Telegram',
        background: '#0088cc',
    },
    vk: {
        type: 'vk',
        flatIcon: FaVk,
        icon: VkFullIcon,
        name: 'VK',
        background: '#4c75a3',
    },
    viber: {
        type: 'viber',
        flatIcon: FaViber,
        icon: ViberFullIcon,
        name: 'Viber',
        background: '#665CAC',
    },
    skype: {
        type: 'skype',
        flatIcon: FaSkype,
        icon: SkypeFullIcon,
        name: 'Skype',
        background: '#00AFF0',
    },
    whatsapp: {
        type: 'whatsapp',
        flatIcon: FaWhatsapp,
        icon: WhatsAppFullIcon,
        name: 'WhatsApp',
        background: '#00E35D',
    },
    instagram: {
        type: 'instagram',
        flatIcon: FaInstagram,
        icon: WhatsAppFullIcon,
        name: 'Instagram Direct',
        background: '#E1306C',
    },
};

export const MessengerIcon = ({ type, messenger, ...rest }) => {
    const iconData = messengersMap[messenger];

    if (!iconData) {
        return null;
    }

    const { icon, flatIcon, name, background } = iconData;
    const Icon = type === 'full' ? icon : flatIcon;

    return <Icon title={name} {...rest} />;
};

MessengerIcon.propTypes = {
    type: PropTypes.oneOf(['flat', 'full']),
    messenger: PropTypes.string.isRequired,
};

MessengerIcon.defaultProps = {
    type: 'full',
};

MessengerIcon.map = messengersMap;
