import PropTypes from 'prop-types';

// const upload = PropTypes.shape({
//     path: PropTypes.string.isRequired,
//     url: PropTypes.string.isRequired,
// });
const block = PropTypes.shape({
    kind: PropTypes.string.isRequired,
    status: PropTypes.bool,
});
const page = PropTypes.shape({
    enabled: PropTypes.bool,
    name: PropTypes.string,
    slug: PropTypes.string,
    blocks: PropTypes.arrayOf(block.isRequired).isRequired,
});
const template = PropTypes.shape({
    public: PropTypes.bool,
    name: PropTypes.string.isRequired,
    primaryTags: PropTypes.arrayOf(PropTypes.string).isRequired,
    secondaryTags: PropTypes.arrayOf(PropTypes.string).isRequired,
    blocks: PropTypes.arrayOf(block.isRequired).isRequired,
    // uploads: PropTypes.arrayOf(upload).isRequired,
});
const website = PropTypes.shape({
    enabled: PropTypes.bool,
    name: PropTypes.string.isRequired,
    slug: PropTypes.string.isRequired,
    // uploads: PropTypes.arrayOf(upload),
});

const websitePartial = PropTypes.shape({
    enabled: PropTypes.bool,
    name: PropTypes.string,
    slug: PropTypes.string,
});

const feedbackStatus = PropTypes.oneOf(['new', 'processing', 'rejected', 'accepted']);

const price = PropTypes.shape({
    amount: PropTypes.number,
    currency: PropTypes.string,
});
const orderItem = PropTypes.shape({
    productId: PropTypes.string.isRequired,
    kind: PropTypes.oneOf(['product', 'service']),
    name: PropTypes.string.isRequired,
    price,
    quantity: PropTypes.number.isRequired,
    subtotal: price.isRequired,
    photoUrl: PropTypes.string,
});
const orderStatus = PropTypes.oneOf(['new', 'pending', 'paid', 'failed']);
const paymentMethod = PropTypes.oneOf([
    'stripe',
    'paypal',
    'wayforpay',
    'robokassa',
    'liqpay',
    'cod',
    'transfer',
    'other',
]);
const order = PropTypes.shape({
    _id: PropTypes.string.isRequired,
    website: PropTypes.oneOfType([PropTypes.string, website]).isRequired,
    status: orderStatus.isRequired,
    finalized: PropTypes.bool,
    contactInfo: PropTypes.shape({}),
    shippingAddress: PropTypes.shape({}),
    paymentMethod: paymentMethod,
    metadata: PropTypes.object,
    total: price.isRequired,
    items: PropTypes.arrayOf(orderItem.isRequired).isRequired,
    createdAt: PropTypes.oneOfType([PropTypes.string, PropTypes.object]).isRequired,
});

const restriction = PropTypes.shape({
    rule: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]).isRequired,
    subject: PropTypes.any,
    iconOnly: PropTypes.bool,
    exclusive: PropTypes.bool,
    context: PropTypes.any,
});

const linkThemeFields = PropTypes.shape({
    social: PropTypes.string.isRequired,
    color: PropTypes.string.isRequired,
    contrastedColor: PropTypes.string.isRequired,
});

const smartLink = PropTypes.shape({
    url: PropTypes.string,
    page: PropTypes.string,
});

const inputLabel = PropTypes.oneOfType([PropTypes.string, PropTypes.bool, PropTypes.element]);

export const LandingsPropTypes = {
    restriction,
    block,
    page,
    website,
    websitePartial,
    feedbackStatus,
    template,
    order,
    orderItem,
    orderStatus,
    paymentMethod,
    price,
    linkThemeFields,
    smartLink,
    inputLabel,
    partials: {
        website: PropTypes.shape({
            status: PropTypes.bool,
            name: PropTypes.string,
            slug: PropTypes.string,
        }),
        template: PropTypes.shape({
            public: PropTypes.bool,
            name: PropTypes.string,
            primaryTags: PropTypes.arrayOf(PropTypes.string),
            secondaryTags: PropTypes.arrayOf(PropTypes.string),
            blocks: PropTypes.arrayOf(block),
        }),
    },
};
