import { array, object, string } from 'yup';

import { GENERIC_ICON_GROUPS, socialNameForUrl } from '@mssgme/socials';
import { normalizeWebsiteUrl } from '@mssgme/helpers';

export const SocialsItemSchema = object({
    url: string()
        .ensure()
        .trim()
        .required('common:errorsList.enter_url')
        .url('common:errorsList.enter_url')
        .max(800, 'common:errorsList.url_length')
        .transform((value) => value && normalizeWebsiteUrl(value))
        .test('isSupportedSocial', 'common:errorsList.block_socials_not_supported', (value) => {
            const network = socialNameForUrl(value, null);

            return !!(network && GENERIC_ICON_GROUPS.social.includes(network));
        }),
});

export const SocialsBlockSchema = object({
    items: array().min(1, 'common:errorsList.block_socials_count'),
});
