import {
    PageStyling,
    PageStylingAlignment,
    PageStylingBackgroundColor,
    PageStylingBackgroundImage,
    PageStylingFonts,
    PageTheme,
} from './Sheets';

export const pageStylingRouting = {
    path: 'styling',
    component: PageStyling,
    exact: true,
    children: [
        { path: 'theme', component: PageTheme },
        { path: 'alignment', component: PageStylingAlignment },
        { path: 'background-color', component: PageStylingBackgroundColor },
        { path: 'background-image', component: PageStylingBackgroundImage },
        { path: 'fonts', component: PageStylingFonts },
    ],
};
