import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { FiPlus } from 'react-icons/fi';

import { useNavigationUtils } from '@mssgme/hooks';
import { BlockListItemPreview } from '@mssgme/ui';

export const ChooseBlockListItem = ({ item, ...rest }) => {
    const restrict = useMemo(
        () => ({ rule: 'page-blocks-publish', subject: { kind: item.kind, enabled: true }, iconOnly: true }),
        [item.kind]
    );
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();

    return (
        <BlockListItemPreview
            tag={Link}
            to={generateUrl(item.to)}
            labelIcon={item.icon}
            label={t(item.label)}
            subLabel={t(item.subLabel)}
            rightIcon={item.rightIcon || <FiPlus size={20} color="#007aff" />}
            restrict={restrict}
            style={{ padding: 8 }}
            data-event="choose-editor-block"
            data-block-kind={item.kind}
            {...rest}
        />
    );
};

ChooseBlockListItem.propTypes = {
    item: PropTypes.shape({
        kind: PropTypes.string.isRequired,
        to: PropTypes.any.isRequired,
        icon: PropTypes.any.isRequired,
        rightIcon: PropTypes.any,
        label: PropTypes.string.isRequired,
        subLabel: PropTypes.string,
    }).isRequired,
};
