import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';

import { PortalModalSheet } from '../ModalSheet';
import { MessengerNameField, EnhancedFormik } from '../Form';
import { Button } from '../Inputs';
import FieldTip from '../FieldTip';

export const MessengersItemEditModal = ({ name, platform, onApply, validationSchema, ...rest }) => {
    const { t } = useTranslation('ui');
    const { getFieldMeta } = useFormikContext();
    const type = ['telegram', 'messenger', 'vk', 'instagram'].includes(platform) ? 'auto' : 'private';
    const { value: messenger = { platform, type, value: '', enabled: true } } = getFieldMeta(name);

    return (
        <PortalModalSheet
            title={t(`messengers.${platform}`)}
            onClose={onApply}
            render={({ handleClose }) => (
                <EnhancedFormik
                    initialValues={messenger}
                    validationSchema={validationSchema}
                    validateOnMount={false}
                    onSubmit={handleClose}
                >
                    {({ values, dirty, submitForm }) => (
                        <div>
                            <FieldTip
                                title={t('messengers.help.tip')}
                                style={{ whiteSpace: 'pre-wrap', fontSize: 14, marginBottom: 16 }}
                            >
                                {t(`messengers.help.${platform}.${values.type}`, t(`messengers.help.${platform}.auto`))}
                            </FieldTip>
                            <MessengerNameField
                                required
                                name="value"
                                typeName="type"
                                platformName="platform"
                                placeholder={t(`messengers.placeholder.${platform}.${values.type}`)}
                            />
                            <Button
                                fullWidth
                                size="standart"
                                style={{ marginTop: 16 }}
                                disabled={!dirty}
                                onClick={submitForm}
                            >
                                {t('labels.apply')}
                            </Button>
                        </div>
                    )}
                </EnhancedFormik>
            )}
            {...rest}
        />
    );
};

MessengersItemEditModal.propTypes = {
    name: PropTypes.string.isRequired,
    platform: PropTypes.string.isRequired,
    onApply: PropTypes.func.isRequired,
    validationSchema: PropTypes.any,
};
