import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';

import { Feature, Form, FormikProvider, RestrictionScope } from '@mssgme/ui';
import { useNavigationUtils } from '@mssgme/hooks';
import { useProductEdit } from '../../../hooks';

export function ProductEditForm({ isNew, product, children }) {
    const history = useHistory();
    const { generateUrl } = useNavigationUtils();

    const { formik, isFetching, isRedirecting, hasExternalUrl, toggleLink, handleDelete } = useProductEdit({
        product,
        isNew,
        onSuccess: useCallback(() => setTimeout(() => history.goBack(), 100), [history]),
    });

    return (
        <Feature
            rule="products:edit"
            subject={product}
            modal
            onClose={() => history.push(generateUrl('..'))}
            padding={false}
        >
            <FormikProvider value={formik}>
                <RestrictionScope>
                    <Form autoComplete="off">
                        {children({
                            ...formik,
                            handleDelete,
                            hasExternalUrl,
                            toggleLink,
                            isFetching,
                            isRedirecting,
                        })}
                    </Form>
                </RestrictionScope>
            </FormikProvider>
        </Feature>
    );
}

ProductEditForm.propTypes = {
    product: PropTypes.object.isRequired,
    collectionId: PropTypes.string,
    isNew: PropTypes.bool,
    children: PropTypes.any.isRequired,
};

ProductEditForm.defaultProps = {
    collectionId: null,
};
