import classNames from 'classnames';
import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useSelector } from 'react-redux';
import { useToggle } from 'react-use';

import { useNavigationUtils } from '@mssgme/hooks';
import { SecondLevelScreen, Button } from '@mssgme/ui';
import {
    PlanTitle,
    SubscriptionStatus,
    Unsubscribe,
    UpcomingInvoice,
    RenewSubscriptionButton,
    PayNow,
    BannerContainer,
} from '../UI';
import { SupportModal } from '@mssgme/support';
import Logo from '@mssgme/ui/icons/logo.svg';

import isSupportProvider from '../utils/isSupportProvider';

import styles from './BillingHome.module.scss';

function Footer({ children }) {
    return React.Children.count(children) === 0 ? null : <div className={styles.buttonsContainer}>{children}</div>;
}

export default function BillingHome() {
    const { t } = useTranslation('billing');
    const { subscription, highRiskTransaction } = useSelector((state) => state.billingDetails.result || {});
    const { generateUrl } = useNavigationUtils();
    const [isSupportOpen, toggleSupport] = useToggle(false);

    const showStatus = highRiskTransaction || subscription;

    return (
        <SecondLevelScreen light backPath={generateUrl('..')}>
            <div>
                <div className={styles.header}>
                    <Logo className={styles.logo} />
                    <h1 className={styles.heading}>{t('billingHome.heading')}</h1>
                    <p className={styles.subheading}>{t('billingHome.subheading')}</p>
                </div>
                <ul className={classNames(styles.list)}>
                    <li>
                        <span className={styles.key}>{t('billingHome.billingPlan')}</span>
                        <span className={styles.value}>
                            <PlanTitle subscription={subscription} />
                        </span>
                    </li>
                    {showStatus && (
                        <li>
                            <span className={styles.key}>{t('billingHome.status')}</span>
                            <span className={styles.value}>
                                <SubscriptionStatus
                                    subscription={subscription}
                                    highRiskTransaction={highRiskTransaction}
                                />
                            </span>
                        </li>
                    )}
                    {subscription?.nextPayment && (
                        <li>
                            <span className={styles.key}>{t('billingHome.invoices.planned.header')}</span>
                            <span className={styles.value}>
                                <UpcomingInvoice invoice={subscription.nextPayment} />
                            </span>
                        </li>
                    )}
                    {subscription?.card && (
                        <li>
                            <span className={styles.key}>{t('billingHome.cardUsed')}</span>
                            <span className={styles.value}>{subscription.card}</span>
                        </li>
                    )}
                </ul>
                <BannerContainer />
                <Footer>
                    {subscription?.actions && isSupportProvider(subscription?.provider) && (
                        <>
                            {subscription.actions.payNow && <PayNow toggleSupport={toggleSupport} />}
                            {subscription.actions.changePlan && (
                                <Button
                                    tag={Link}
                                    to={generateUrl('plans')}
                                    fullWidth
                                    btnStyle="secondary2"
                                    style={{ backgroundColor: '#f7f7fa' }}
                                    data-event="click-change-plan"
                                >
                                    {t('billingHome.button.changeBillingPlan')}
                                </Button>
                            )}
                            {subscription.actions.changePaymentMethod && (
                                <Button
                                    tag={Link}
                                    to={generateUrl('change-card')}
                                    fullWidth
                                    btnStyle="secondary2"
                                    style={{ backgroundColor: '#f7f7fa' }}
                                    data-event="click-change-card"
                                >
                                    {t('billingHome.button.changeCard')}
                                </Button>
                            )}
                            {subscription.actions.cancel && (
                                <Unsubscribe />
                            )}
                            {subscription.actions.renew && <RenewSubscriptionButton />}
                        </>
                    )}
                    {subscription && (
                        <Button
                            type="button"
                            onClick={toggleSupport}
                            fullWidth
                            btnStyle="secondary2"
                            style={{ backgroundColor: '#f7f7fa' }}
                            data-event="click-support"
                        >
                            {t('billingHome.button.support')}
                        </Button>
                    )}
                </Footer>
            </div>
            {isSupportOpen && <SupportModal onClose={toggleSupport} />}
        </SecondLevelScreen>
    );
}
