import PropTypes from 'prop-types';
import React from 'react';
import { AiOutlineLine } from 'react-icons/ai';
import { BsEyeSlash } from 'react-icons/bs';

import { Styling } from '@mssgme/ui';

const MarginIcon = ({ margin }) => (
    <div style={{ width: 15 }}>
        <div style={{ height: 1, background: 'currentColor', borderRadius: 10 }} />
        <div style={{ height: 2, background: 'currentColor', borderRadius: 10, margin: `${margin}px 0` }} />
        <div style={{ height: 1, background: 'currentColor', borderRadius: 10 }} />
    </div>
);

MarginIcon.propTypes = {
    margin: PropTypes.number.isRequired,
};

/* eslint-disable react/display-name */
export const dividerStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('template', {
                label: t('Page.fields.dividerType.label'),
                options: [
                    { value: 'line', label: t('Page.fields.dividerType.values.line'), icon: AiOutlineLine },
                    { value: 'space', label: t('Page.fields.dividerType.values.space'), icon: BsEyeSlash },
                ],
            }),
            Styling.s.list('margin', {
                label: t('Page.fields.dividerMargin.label'),
                options: [
                    {
                        value: 'small',
                        label: t('Page.fields.dividerMargin.values.small'),
                        icon: () => <MarginIcon margin={3} />,
                    },
                    {
                        value: 'medium',
                        label: t('Page.fields.dividerMargin.values.medium'),
                        icon: () => <MarginIcon margin={5} />,
                    },
                    {
                        value: 'large',
                        label: t('Page.fields.dividerMargin.values.large'),
                        icon: () => <MarginIcon margin={7} />,
                    },
                ],
                disabledValue: null,
            }),
            isAdvancedMode && [
                fields.color({
                    path: 'backgroundColor',
                    if: (get) => get('template') !== 'space',
                }),
                Styling.s.range('width', {
                    label: t('Page.fields.width.label'),
                    min: 1,
                    max: 100,
                    step: 1,
                    restrict: 'page-styling',
                    if: (get) => get('template') === 'line',
                }),
                Styling.s.range('height', {
                    label: t('Page.fields.height.label'),
                    min: 1,
                    max: 10,
                    step: 1,
                    restrict: 'page-styling',
                    if: (get) => get('template') === 'line',
                }),
                Styling.s.list('alignment', {
                    label: t('Page.fields.alignment.label'),
                    options: [
                        { value: 'left', label: t('Page.fields.alignment.values.left') },
                        { value: 'center', label: t('Page.fields.alignment.values.center') },
                        { value: 'right', label: t('Page.fields.alignment.values.right') },
                    ],
                    if: (get) => ['dots', 'line'].includes(get('template')),
                    disabledValue: null,
                    restrict: 'page-styling',
                }),
            ],
        ]),
    };
};
