import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { pick } from 'lodash';

import { FormikProvider, renderNode, useEnhancedFormik } from '@mssgme/ui';

const EDITABLE_FIELDS = ['status', 'notes'];

export const ItemForm = ({ item, children, onSubmit }) => {
    const handleSubmit = useCallback(
        (values) => {
            const diff = {};

            for (const [key, value] of Object.entries(values)) {
                if (value !== item[key]) {
                    diff[key] = value;
                }
            }

            if (Object.keys(diff).length) {
                onSubmit && onSubmit(diff);
            }
        },
        [item, onSubmit]
    );

    const formik = useEnhancedFormik({
        initialValues: pick(item, EDITABLE_FIELDS),
        onSubmit: handleSubmit,
    });

    useEffect(() => void formik.setValues(pick(item, EDITABLE_FIELDS)), [item]);

    return <FormikProvider value={formik}>{renderNode(children, { handleSubmit: formik.submitForm })}</FormikProvider>;
};

ItemForm.propTypes = {
    item: PropTypes.object.isRequired,
    onSubmit: PropTypes.func,
};
