import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory, useParams } from 'react-router-dom';

import { Button, ModalPane } from '@mssgme/ui';
import { useActions, useFetching, useNavigationUtils } from '@mssgme/hooks';

export function TemplateDelete() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { templateId } = useParams();
    const closePath = generateUrl('..');

    const { pending, run: handleDelete } = useFetching(async () => {
        await api.pageTemplates.delete({ templateId });

        history.push('/pages/templates');
    }, [templateId, history]);

    return (
        <ModalPane title={t('Template.Edit.Delete.heading')} onClose={closePath}>
            <p style={{ margin: 0, color: '#777', textAlign: 'center' }}>{t('Template.Edit.Delete.fieldTip')}</p>
            <Button tag={Link} to={generateUrl('..')} fullWidth style={{ marginTop: 16 }} btnStyle="secondary2">
                {t('labels.template.cancelDelete')}
            </Button>
            <Button fullWidth onClick={handleDelete} style={{ marginTop: 10 }} btnStyle="danger" isFetching={pending}>
                {t('labels.template.delete')}
            </Button>
        </ModalPane>
    );
}
