import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useTemplate } from '@mssgme/hooks';
import { ListField } from '@mssgme/ui';
import { TemplateEditForm } from '../../../../UI';

export function TemplateStatus() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { templateId } = useParams();
    const template = useTemplate(templateId);

    return (
        <TemplateEditForm
            title={t('Template.Edit.Status.heading')}
            template={template}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {() => (
                <>
                    <ListField
                        name="public"
                        options={[
                            { label: t('Template.fields.public.published'), value: true },
                            { label: t('Template.fields.public.notPublished'), value: false },
                        ]}
                    />
                </>
            )}
        </TemplateEditForm>
    );
}
