import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { useActions } from '@mssgme/hooks';
import { ArrayField, ImageListField, ImagePreview, PriceCompoundField, TextareaField, TextField } from '@mssgme/ui';
import { MediaSizes, useUploads } from '@mssgme/userpage-core';
import { ImageListUpload, ServicesGridItemSchema, useWebsiteEditor } from '../components';

import styles from './ServicesGridItem.module.scss';

const ServicesGridItem = ({ value, className, textClassName }) => {
    const { getMediaSizeUrl } = useUploads();

    return (
        <div className={className}>
            <ImagePreview url={getMediaSizeUrl(value.photoUrl, MediaSizes.Icon)} />

            <div className={textClassName}>{value.title?.replace(/\n/g, ' ')}</div>
        </div>
    );
};

ServicesGridItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const ServicesGridItemsField = (props) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { websiteId } = useWebsiteEditor();

    const itemFactory = useCallback(() => ({
        title: '',
        description: '',
        photos: [],
        price: null,
        url: '',
    }), []);
    const handleApply = useCallback((values) => {
        if (!(values.price && (values.price.amount !== null || values.price.discount !== null))) {
            return { ...values, price: null };
        }
    }, []);

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.ServicesGrid.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.ServicesGrid.itemAddMore')}
            modalTitle={t('Page.Edit.ServicesGrid.itemFormHeading')}
            Item={ServicesGridItem}
            applyTransform={handleApply}
            validationSchema={ServicesGridItemSchema}
            {...props}
        >
            {({ values, setFieldValue, setFieldTouched }) => (
                <div>
                    <ImageListField
                        required
                        builderInput
                        as={ImageListUpload}
                        name="photos"
                        label={t('Page.Edit.ServicesGrid.fields.photoUrl.label')}
                        labelClassName={styles.noPadding}
                        maxImages={8}
                        upload={(payload) => api.websites.uploadMany({ ...payload, websiteId, field: 'photos' })}
                        onChange={({ value, changed }) => {
                            setFieldValue('photoUrl', value[0] || '');
                            setFieldValue('photos', value);
                            setFieldTouched('photos', changed);
                        }}
                    />

                    <TextField
                        required
                        builderInput
                        name="title"
                        label={t('Page.Edit.ServicesGrid.fields.title.label')}
                        placeholder={t('Page.Edit.ServicesGrid.fields.title.placeholder')}
                        maxLength={50}
                    />

                    <PriceCompoundField
                        builderInput
                        name="price"
                        value={values.price || { currency: 'USD', amount: null, discount: null }}
                        label={t('Page.Edit.ServicesGrid.fields.price.label')}
                    />

                    <TextareaField
                        builderInput
                        name="description"
                        label={t('Page.Edit.ServicesGrid.fields.description.label')}
                        placeholder={t('Page.Edit.ServicesGrid.fields.description.placeholder')}
                        maxLength={4000}
                    />

                    {/* fixme: if this is re-enabled, also enable analytics for this url in `userpageAnalytics/website.js` */}
                    {/*<TextField*/}
                    {/*    builderInput*/}
                    {/*    name="url"*/}
                    {/*    autoCapitalize="off"*/}
                    {/*    mask={handleNormalizeUrl}*/}
                    {/*    label={t('Page.Edit.ServicesGrid.fields.url.label')}*/}
                    {/*    placeholder={t('Page.Edit.ServicesGrid.fields.url.placeholder')}*/}
                    {/*/>*/}
                </div>
            )}
        </ArrayField>
    );
};
