import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { GiphySelector } from './GiphySelector';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Giphy, usePageTheme } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

export function GiphyEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { style } = usePageTheme(true, true);
    const { bind, isNew } = useBlockEditor({ website, page, blockId, backPath, as: GiphyEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Giphy.heading')} isInitialDirty={isNew}>
            {({ values, styling }) => {
                return (
                    <EditStylingForm page={page} element={values} styling={styling}>
                        <StyledBlockPreview style={{ marginBottom: 16 }}>
                            <bind.blockComponent block={values} />
                        </StyledBlockPreview>
                        <GiphySelector backgroundColor={style.backgroundColor} />
                    </EditStylingForm>
                );
            }}
        </BlockEditForm>
    );
}

GiphyEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

GiphyEdit.block = Giphy;
