import React from 'react';
import { useTranslation } from 'react-i18next';

import { StreamlinedScreen } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { ProductsSettingsForm } from '../ProductsSettingsForm';
import { ShopcartSettingsForm } from '../forms';

export function ProductsOrderForm() {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();

    return (
        <StreamlinedScreen
            preferDynamicBack
            modal
            inverse={false}
            nopadding={false}
            fullHeight={false}
            backPath={router.settings.toSettings()}
            title={t('ProductsOrderForm.title')}
        >
            <ProductsSettingsForm successRedirectPath={router.settings.toSettings()}>
                <ShopcartSettingsForm />
            </ProductsSettingsForm>
        </StreamlinedScreen>
    );
}
