import { useCallback, useMemo, useState } from 'react';
import { useAsyncFn, useToggle } from 'react-use';
import { pick } from 'lodash';

import { useActions } from '@mssgme/hooks';
import { handleError } from '@mssgme/lib';
import { useEnhancedFormik } from '@mssgme/ui';
import { useProductsContext } from './useProductsContext';

export const useProductEdit = ({ product, isNew, onSuccess }) => {
    const { api } = useActions();
    const { crm } = useProductsContext();
    const defaultCurrency = crm.defaultCurrency;
    const initialValues = useMemo(() => {
        const url = product.externalButton?.url || product.externalUrl || '';

        return {
            ...product,
            price: product.price || { amount: null, discount: null, currency: defaultCurrency },
            externalButton: {
                title: product.externalButton?.title || '',
                url,
            },
            _cols: Object.values(product.cols || {}).map(({ collectionId }) => collectionId),
        };
    }, [product, defaultCurrency]);
    const productId = initialValues._id;
    const [hasExternalUrl, toggleLink] = useToggle(!!initialValues.externalButton.url);
    const [isRedirecting, setRedirecting] = useState(false);

    const handleSubmit = useCallback(
        async ({ price, externalButton, _cols, ...values }) => {
            const payload = {
                ...pick(values, ['name', 'description', 'website', 'slug', 'photos', 'published', 'notes']),
                externalButton: hasExternalUrl ? externalButton : { title: null, url: null },
                price: ((price?.amount || price?.amount === 0) && price) || null,
                cols: _cols.reduce((acc, collectionId) => {
                    const order = product.cols?.[collectionId]?.order;

                    if (order) {
                        acc[collectionId] = { collectionId, order: order };
                    } else {
                        acc[collectionId] = { collectionId };
                    }

                    return acc;
                }, {}),
            };

            if (isNew) {
                await api.products.create({ payload });
            } else {
                await api.products.update({ productId, payload });
            }

            setRedirecting(true);
            onSuccess({ action: 'submit' });
        },
        [productId, hasExternalUrl, product.cols]
    );

    const [{ loading: isRemoving }, handleDelete] = useAsyncFn(
        async (e) => {
            e?.preventDefault();

            try {
                await api.products.delete({ productId });

                setRedirecting(true);
                onSuccess({ action: 'remove' });
            } catch (e) {
                handleError(e);
            }
        },
        [productId]
    );

    const formik = useEnhancedFormik({
        initialValues,
        onSubmit: handleSubmit,
    });

    return {
        isNew,
        isRedirecting,
        isFetching: isRemoving || formik.isSubmitting,
        defaultCurrency,
        hasExternalUrl,
        toggleLink,
        handleDelete,
        formik,
    };
};
