import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useHistory } from 'react-router-dom';

import { Button, ModalPane } from '@mssgme/ui';
import { useActions, useFetching, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';

import styles from './PageDelete.module.scss';

export function PageDelete() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { websiteId, pageId, homePageId } = useWebsitePageBySlugs();
    const closePath = generateUrl('..');

    const { pending, run: handleDelete } = useFetching(async () => {
        await api.pages.delete({ websiteId, pageId });

        history.push(`/pages/-/${websiteId}/${homePageId}/edit/pages`);
    }, [websiteId, pageId, homePageId, history]);

    return (
        <ModalPane title={t('Page.Edit.Delete.heading')} onClose={closePath}>
            <p className={styles.tip}>{t('Page.Edit.Delete.fieldTip')}</p>
            <Button className={styles.cancel} tag={Link} to={generateUrl('..')} fullWidth btnStyle="secondary2">
                {t('labels.page.cancelDelete')}
            </Button>
            <Button className={styles.confirm} fullWidth onClick={handleDelete} btnStyle="danger" isFetching={pending}>
                {t('labels.page.delete')}
            </Button>
        </ModalPane>
    );
}
