import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { TemplateHome, TemplatesList } from './Template';

export const LandingsTemplatesRouter = () => {
    return (
        <Switch>
            <Route path="/pages/templates/:templateId([a-f\d]{24})" component={TemplateHome} />
            <Route path="/pages/templates" component={TemplatesList} />
        </Switch>
    );
};
