import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { CopyLink, LandingsPropTypes, PersonalLinkNav } from '@mssgme/ui';
import { useNavigationUtils } from '@mssgme/hooks';
import { useWebsiteUrl } from '../../hooks';

import styles from './CopyableWebsiteLink.module.scss';

export function CopyableWebsiteLink({ website, page, containerClassName, ...rest }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const url = useWebsiteUrl(website, page);

    return (
        <div className={containerClassName}>
            <CopyLink
                clickable
                className={styles.link}
                tagClassName={styles.tag}
                href={page.enabled ? url : ''}
                copiable={false}
                noUrlLabel={t('Website.View.Home.not_published')}
                {...rest}
            />

            {website.enabled && page.enabled && (
                <PersonalLinkNav
                    className={styles.personalLink}
                    // embedTo={generateUrl('share/embed')}
                    sharePageTo={generateUrl('share')}
                    qrPageTo={{ pathname: generateUrl('standalone/qr'), state: { backPath: '../..' } }}
                    textToCopy={url}
                    website={website}
                />
            )}
        </div>
    );
}

CopyableWebsiteLink.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    containerClassName: PropTypes.string,
};
