import React from 'react';

import { cls } from '@mssgme/helpers';
import styles from './BlockListItemPreview.module.scss';

export function BlockListItemPreviewList(props) {
    return (
        <ul {...cls(styles.list, props)}>{React.Children.map(props.children, (child) => child && <li>{child}</li>)}</ul>
    );
}
