import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { ListInput } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';

export default function Branding() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm
            title={t('Website.Edit.Branding.heading')}
            website={website}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {({ values, setFieldValue }) => (
                <>
                    <ListInput
                        value={values.branding}
                        options={[
                            { label: t('Website.fields.branding.enabled'), value: true },
                            { label: t('Website.fields.branding.disabled'), value: false },
                        ]}
                        onChange={(value) => setFieldValue('branding', value)}
                    />
                </>
            )}
        </WebsiteEditForm>
    );
}
