import React, { useEffect, useState, Fragment } from 'react';
import { useSelector } from 'react-redux';

import config from 'config';
import { useActions } from '@mssgme/hooks';
import { EventNotifier, FaviconMarker, SoundMarker } from '@mssgme/services';

import { useRecentFeedback } from '../../hooks';

export class FeedbackEventNotifier extends EventNotifier {
    constructor() {
        super([
            new FaviconMarker('/favicon_dot32.png', 'link[data-icon="fav32"]'),
            new SoundMarker(
                [
                    `${config.statics_url}/webapp/sound/inbox.ogg`,
                    `${config.statics_url}/webapp/sound/inbox.mp3`,
                    `${config.statics_url}/webapp/sound/inbox.wav`,
                ],
                { duration: 4000 }
            ),
        ]);
    }
}

export const FeedbackObserver = ({ children }) => {
    const { api } = useActions();
    const { updateTimestamp } = useRecentFeedback(({ status }) => status === 'new');
    const [eventNotifier] = useState(() => new FeedbackEventNotifier());
    const reconnected = useSelector((state) => state.socket.reconnected);

    useEffect(() => {
        api.listenCreateFeedback();
        api.listenUpdateFeedback();
    }, []);

    useEffect(() => {
        function handleDismiss() {
            eventNotifier.dismiss();
            document.removeEventListener('mousemove', handleDismiss);
        }

        if (updateTimestamp) {
            // eventNotifier.notify();
            document.addEventListener('mousemove', handleDismiss);

            return handleDismiss;
        }
    }, [updateTimestamp, eventNotifier]);

    return <Fragment key={`c-f-${reconnected}`}>{children}</Fragment>;
};

FeedbackObserver.propTypes = {};
