import { Styling } from '@mssgme/ui';

export const imageGalleryStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('ratio', {
                label: t('Page.fields.ratio.label'),
                options: [
                    {
                        value: 'original',
                        label: t('Page.fields.ratio.values.original.label'),
                        shortLabel: t('Page.fields.ratio.values.original.shortLabel'),
                    },
                    {
                        value: 'square',
                        label: t('Page.fields.ratio.values.square.label'),
                        shortLabel: t('Page.fields.ratio.values.square.shortLabel'),
                    },
                    {
                        value: 'rectangle',
                        label: t('Page.fields.ratio.values.rectangle.label'),
                        shortLabel: t('Page.fields.ratio.values.rectangle.shortLabel'),
                    },
                ],
            }),
            isAdvancedMode && [
                Styling.s.range('perView', {
                    label: t('Page.fields.perView.label'),
                    min: 1,
                    max: 5,
                    step: 1,
                }),
                Styling.s.list('effect', {
                    label: t('Page.fields.slideshowEffect.label'),
                    options: [
                        { value: '', label: t('ui:labels.notSet') },
                        { value: 'flip', label: t('Page.fields.slideshowEffect.values.flip') },
                        { value: 'fade', label: t('Page.fields.slideshowEffect.values.fade') },
                    ],
                    if: (get) => get('perView') === 1,
                }),
                Styling.s.range('autoplay', {
                    label: t('Page.fields.autoplay.label'),
                    min: 0,
                    max: 30,
                    step: 1,
                    makeLabel: (value) => (value ? t('labels.timeSeconds', { time: value }) : t('ui:labels.notSet')),
                }),
                fields.borderRadius({
                    min: 1,
                    max: 200,
                    if: (get) => get('padding') || get('perView') > 1,
                    disabledValue: null,
                }),
            ],
        ]),
    };
};
