import PropTypes from 'prop-types';
import React, { useCallback, useRef } from 'react';
import { useTranslation } from 'react-i18next';
import { FormikProvider } from 'formik';

import { useActions, useFetching, useRestriction } from '@mssgme/hooks';
import { Form, LandingsPropTypes, ModalEditPane, SaveButton, usePresenceRemoval, useEnhancedFormik } from '@mssgme/ui';

export function DomainEditForm({ website, children, title, saveButtonTitle, backPath }) {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const formikRef = useRef(null);
    const websiteId = website._id;
    const isNew = !websiteId;
    const initialValues = {
        name: website.domain?.name || '',
    };
    const { handleBlockedEvent, hasWarning } = useRestriction('domain', website.domain || initialValues);

    const handleSubmit = useCallback(
        async (payload) => api.websites.domain.update({ websiteId, payload }),
        [websiteId]
    );

    const { run: handleDelete, pending: isDeleting } = useFetching(async () => {
        await api.websites.domain.delete({ websiteId });

        formikRef.current?.setValues({ name: '' });
    }, [websiteId]);

    const formik = useEnhancedFormik({ initialValues, onSubmit: handleSubmit });
    formikRef.current = formik;

    usePresenceRemoval();

    return (
        <FormikProvider value={formik}>
            <ModalEditPane guardChanges={!hasWarning} onClose={backPath} title={title}>
                <Form autoComplete="off">
                    {children({ handleDelete, isDeleting, ...formik })}

                    {!website.domain?.name && (
                        <SaveButton
                            fullWidth
                            size="standart"
                            isInitialHidden={false}
                            disabled={formik.isSubmitting || !formik.dirty}
                            style={{ marginTop: 16 }}
                            isFetching={formik.isSubmitting}
                            {...(handleBlockedEvent ? { onClick: handleBlockedEvent } : {})}
                        >
                            {saveButtonTitle || (isNew ? t('labels.website.create') : t('ui:labels.apply'))}
                        </SaveButton>
                    )}
                </Form>
            </ModalEditPane>
        </FormikProvider>
    );
}

DomainEditForm.propTypes = {
    website: LandingsPropTypes.partials.website.isRequired,
    children: PropTypes.any.isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.any.isRequired,
    saveButtonTitle: PropTypes.string,
};

