import { useCallback, useState } from 'react';

export const useDataFactoryState = (initial) => {
    const [factory, setFactory] = useState(() => initial);

    const isotropicSet = useCallback(
        (factoryOrData) => setFactory(
            factoryOrData
                ? typeof factoryOrData === 'function' ? () => factoryOrData : () => () => factoryOrData
                : null
        ),
        []
    );

    return [factory, isotropicSet];
};
