import PropTypes from 'prop-types';
import React, { useCallback, useRef, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { cls } from '@mssgme/helpers';
import { Button, ColorPicker } from '../../../../../Inputs';

import styles from './DropDown.module.scss';

const drop = (e) => {
    // we need to prevent text selection here to prevent dragging behavior

    if (e.target?.tagName === 'INPUT') {
        // do nothing, if the element is an input
        return;
    }

    e.preventDefault();
    e.stopPropagation();

    // as we prevented the default event behavior, we now need to manually blur the input, if necessary
    document.activeElement?.blur?.();
};

export const DropDownColor = ({ value, onSelect, ...rest }) => {
    const savedRef = useRef(value);
    const { t } = useTranslation('ui');
    const [saved, setSaved] = useState(value);

    const handleApply = useCallback(() => onSelect(savedRef.current), [onSelect]);
    const handleSave = useCallback((value) => setSaved((savedRef.current = value)), []);

    return (
        <div onMouseDown={drop} onClick={drop} {...cls(styles.picker, rest)}>
            <ColorPicker value={saved || '#ffffff00'} onChange={handleSave} />
            <Button className={styles.applyButton} fullWidth onClick={handleApply}>
                {t('labels.apply')}
            </Button>
        </div>
    );
};

DropDownColor.propTypes = {
    value: PropTypes.string,
    onSelect: PropTypes.func.isRequired,
};
