import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useRouteMatch } from 'react-router-dom';

import { isMatchId, toDashedCase } from '@mssgme/helpers';
import { CustomBlockKind } from '@mssgme/shared';
import {
    ArrowDownIcon,
    ArrowUpIcon,
    CopyIcon,
    Edit2Icon,
    TrashIcon,
    EyeIcon,
    EyeOffIcon,
    ZapIcon,
    ZapOffIcon,
} from '@mssgme/icons';
import { useFeatures } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';
import { DashboardActionItem } from './DashboardActionItem';

import styles from './Dashboard.module.scss';

TargetedDashboardActions.propTypes = {
    isDisabled: PropTypes.bool,
    selected: PropTypes.arrayOf(PropTypes.string).isRequired,
    blocks: PropTypes.arrayOf(LandingsPropTypes.block).isRequired,
    onAction: PropTypes.func.isRequired,
};

const singularFilter = ({ block }) => !!block;
const areAllEnabled = (blocks) => blocks.every(({ enabled }) => enabled);
const areAllSameEnabled = (blocks) => {
    if (!blocks.length) {
        return false;
    }

    const { enabled } = blocks[0];

    return blocks.every((item) => item.enabled === enabled);
};
const isGroup = ({ block }) => !!(block?.kind === CustomBlockKind);
const actions = [
    {
        title: 'edit',
        icon: () => Edit2Icon,
        filter: singularFilter,
        to: ({ block, url }) => block && `${url}/block/${toDashedCase(block.kind)}/${block._id}`,
    },
    {
        action: 'up',
        title: 'up',
        slim: true,
        icon: () => ArrowUpIcon,
        filter: singularFilter,
        disabled: ({ index }) => index <= 0,
    },
    {
        action: 'down',
        slim: true,
        title: 'down',
        icon: () => ArrowDownIcon,
        filter: singularFilter,
        disabled: ({ index, blocks }) => index < 0 || index >= blocks.length - 1,
    },
    {
        action: 'remove',
        title: 'remove',
        icon: () => TrashIcon,
    },
    {
        action: 'clone',
        restriction: 'page-blocks:edit',
        title: 'clone',
        icon: () => CopyIcon,
    },
    {
        action: 'toggle',
        title: ({ selection }) => (areAllEnabled(selection) ? 'hide' : 'show'),
        icon: ({ selection }) => (areAllEnabled(selection) ? EyeOffIcon : EyeIcon),
        disabled: ({ selection }) => !areAllSameEnabled(selection),
        parameters: ({ selection }) => ({ enable: !areAllEnabled(selection) }),
    },
    {
        action: 'group',
        slim: true,
        title: (ctx) => (isGroup(ctx) ? 'ungroup' : 'group'),
        icon: (ctx) => (isGroup(ctx) ? ZapOffIcon : ZapIcon),
        filter: ({ block, feature }) => (block?.kind === CustomBlockKind || !block) && feature('block_grouping'),
        parameters: (ctx) => ({ group: !isGroup(ctx) }),
    },
];

export function TargetedDashboardActions({ blocks, selected, isDisabled, onAction }) {
    const feature = useFeatures();
    const { url } = useRouteMatch();
    const { context, index } = useMemo(() => {
        const isSingular = selected.length === 1;
        const filter = isMatchId(selected[0]);
        const index = isSingular ? blocks.findIndex(filter) : -1;
        const block = isSingular ? blocks.find(filter) : null;

        return {
            index,
            context: {
                selection: blocks.filter(({ _id }) => selected.includes(_id)),
                blocks,
                block,
                url,
                feature,
            },
        };
    }, [url, selected, blocks, feature]);

    // if (!selected.length) {
    //     return <div className={cn(styles.row, styles.tight)} />;
    // }

    return (
        <div className={cn(styles.row, styles.toolbar, styles.padLeft, !selected.length && styles.noSelection)}>
            <div className={styles.divider} />

            {actions.map((item) => (
                <DashboardActionItem
                    key={item.action || item.title}
                    labeled
                    index={index}
                    context={context}
                    disabled={isDisabled || !selected}
                    item={item}
                    onAction={onAction}
                />
            ))}
        </div>
    );
}
