import React from 'react';
import { useTranslation } from 'react-i18next';

import { useActions, useFetching } from '@mssgme/hooks';
import { Button } from '@mssgme/ui';

export default function RenewSubscriptionButton() {
    const { t } = useTranslation('billing');
    const { api } = useActions();

    const { pending, run: handleRenew } = useFetching(() => api.billing.subscription.renew(), []);

    return (
        <>
            <Button
                btnStyle="secondary2"
                fullWidth
                isFetching={pending}
                onClick={handleRenew}
                style={{ backgroundColor: '#f7f7fa' }}
                data-event="click-renew"
            >
                {t('billingHome.button.subscribe')}
            </Button>
        </>
    );
}
