import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { Link } from 'react-router-dom';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { MoreHorizontalIcon } from '@mssgme/icons';
import { LandingsPropTypes } from '../../domainTypes';
import { PlanBadge } from '../PlanBadge';
import { BlockListItemPreviewList } from './BlockListItemPreviewList';
import { BlockListItemPreviewWithACL, BlockListProps } from './BlockListItemPreviewWithACL';

import styles from './BlockListItemPreview.module.scss';

export { BlockListProps };

export function BlockListItemPreview({
    tag: Tag,
    label,
    subLabel,
    labelClassName,
    subLabelClassName,
    labelIcon: Icon,
    labelColor,
    iconColor,
    iconProps,
    iconSplit,
    rightIcon,
    previewType,
    preview,
    disabled,
    onClick,
    restrictMode = 'badge',
    restrict = {},
    more,
    ...rest
}) {
    const { rule, subject, exclusive, iconOnly, context } = restrict;
    const { warning, handleBlockedEvent } = useRestriction(rule, subject, context);

    if (warning && exclusive) {
        return null;
    }

    const isBadgeRestrict = restrictMode === 'badge';
    const rightNode = (isBadgeRestrict && warning?.target) ? (
        <PlanBadge plan={warning.target} />
    ) : (
        rightIcon
    );
    const handleClick = (!iconOnly && handleBlockedEvent) || onClick;
    const interactiveProps = handleClick && { onClick: handleClick };

    disabled = disabled || !!(warning && !warning.target);

    return (
        <>
            <Tag
                {...interactiveProps}
                {...cls({
                    [styles.root]: true,
                    [styles.disabled]: disabled,
                    [styles.iconSplit]: Icon && iconSplit,
                    [styles.interactive]: interactiveProps,
                    [styles.hasMore]: more,
                }, rest)}
            >
                {Icon && (
                    <div className={cn({ [styles.icon]: true, [styles.large]: !!subLabel })}>
                        <Icon size={subLabel ? 22 : 18} color={iconColor} {...iconProps} />
                    </div>
                )}
                <div className={cn({ [styles.box]: true, [styles.withSubLabel]: !!subLabel })}>
                    <div className={styles.label} style={{ color: labelColor }}>
                        <div className={cn(styles.title, labelClassName)}>
                            {label}
                            {(warning && !isBadgeRestrict) && ' *'}
                        </div>
                        {subLabel && <div className={cn(styles.subTitle, subLabelClassName)}>{subLabel}</div>}
                    </div>
                    {(preview && !warning) && (
                        <div className={styles.preview} style={{ paddingRight: previewType === 'image' ? 0 : 6 }}>
                            {previewType === 'text' && preview}
                            {previewType === 'image' && (
                                <div className={styles.imageBox}>
                                    <img src={preview} alt="preview" />
                                </div>
                            )}
                        </div>
                    )}

                    {rightNode}
                </div>
            </Tag>

            {more && (
                <Link className={styles.more} {...more}>
                    <MoreHorizontalIcon size={22} color="#333"/>
                </Link>
            )}
        </>
    );
}

BlockListItemPreview.List = BlockListItemPreviewList;
BlockListItemPreview.ACLItem = BlockListItemPreviewWithACL(BlockListItemPreview);

BlockListItemPreview.propTypes = {
    tag: PropTypes.any,
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.number]).isRequired,
    subLabel: PropTypes.any,
    labelIcon: PropTypes.any.isRequired,
    rightIcon: PropTypes.any,
    previewType: PropTypes.oneOf(['text', 'image']),
    preview: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.number]),
    className: PropTypes.string,
    labelColor: PropTypes.string,
    iconColor: PropTypes.string,
    iconSplit: PropTypes.bool,
    iconProps: PropTypes.object,
    more: PropTypes.object,
    onClick: PropTypes.func,
    restrict: LandingsPropTypes.restriction,
    disabled: PropTypes.bool,
    restrictMode: PropTypes.oneOf(['badge', 'asterisk']),
    labelClassName: PropTypes.string,
    subLabelClassName: PropTypes.string,
};

BlockListItemPreview.defaultProps = {
    tag: 'div',
    previewType: 'text',
    labelColor: '#333',
    iconColor: '#333',
};
