import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { __t , isMatchedById } from '@mssgme/shared';
import { ListInput, ModalSheet, ModalSheetHeader } from '@mssgme/ui';

import styles from './PageSelector.module.scss';

const LabelItem = ({ name, url }) => (
    <div className={styles.labelItem}>
        <div className={styles.labelName}>{name}</div>
        <div className={styles.labelUrl}>{url}</div>
    </div>
);

LabelItem.propTypes = {
    name: PropTypes.string,
    url: PropTypes.string,
};

const PageSelector = ({ name, value, onChange, onClose, website, ...rest }) => {
    const { t } = useTranslation('admin-ui');
    const { pages, homePageId, finalUrl } = website;

    const options = useMemo(
        () =>
            pages
                .filter(({ enabled, blocks }) => enabled && blocks.length > 0)
                .map((page) => {
                    const isHomePage = page._id === homePageId;
                    const url = isHomePage ? finalUrl : finalUrl + `/${page.slug}`;
                    const name = isHomePage ? __t('homePage') : (page.name || t('labels.unnamedPage'));

                    return {
                        value: page._id,
                        label: <LabelItem name={name} url={url} />,
                    };
                }),
        [pages, homePageId, finalUrl]
    );

    const handleSelect = (pageId) => {
        onChange(name, { page: pageId, block: pages.find(isMatchedById(pageId)).blocks[0]._id });
        onClose();
    };

    return (
        <ModalSheet {...rest} onClose={onClose}>
            <div className={styles.root}>
                <ModalSheetHeader
                    title={t('PageSelector.title')}
                    closePath={onClose}
                />
                <div className={styles.selector}>
                    <ListInput options={options} onChange={handleSelect} value={value.page}/>
                </div>
            </div>
        </ModalSheet>
    );
};

PageSelector.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    onClose: PropTypes.func.isRequired,
    website: PropTypes.object.isRequired,
};

export default PageSelector;
