const defSkipConfig = {
    filter: (prop) =>
        !(prop.match(/^absolute.*Url$/i) || ['_id', 'id', 'redirectUrl', 'createdAt', 'updatedAt'].includes(prop)),
    arrays: ['items', 'messengers', 'images', 'videos'],
    doc: (prop) => prop === 'link',
};

/**
 * @param {{
 *   filter?: function(prop: string, val: any): boolean,
 *   arrays?: string[],
 *   doc?: function(prop: string, val: any): boolean,
 * }} config Cleaning config
 * @returns {(function(*=): (*))|*} Transform function
 */
export const cleanClonedDocument = (config = defSkipConfig) => {
    const cleaner = (doc) => {
        if (!doc) {
            return doc;
        }

        const result = Array.isArray(doc) ? [] : {};

        for (const [prop, value] of Object.entries(doc)) {
            if (!config.filter?.(prop, value)) {
                continue;
            }

            if (config.arrays && Array.isArray(value) && config.arrays.includes(prop)) {
                result[prop] = value.map(cleaner);
            } else if (value && config.doc?.(prop, value)) {
                result[prop] = cleaner(value);
            } else {
                result[prop] = value;
            }
        }

        return result;
    };

    return cleaner;
};

export const cleanCommonClonedDocument = (additionalProps) =>
    cleanClonedDocument(
        additionalProps
            ? { ...defSkipConfig, filter: (prop) => !additionalProps.includes(prop) && defSkipConfig.filter(prop) }
            : defSkipConfig
    );
