import { Embed, InstagramShareNew, Qr, ShareNew, Socials, TikTokShare } from './Page';
import { WebsiteDomainEdit, WebsiteSlugEdit } from './Website';

export const bioShareRouting = {
    path: ':socials(socials|bio)',
    component: Socials,
    children: [
        { path: 'instagram', component: InstagramShareNew },
        { path: 'tiktok', component: TikTokShare },
    ],
};

export const publishNameRouting = {
    path: 'name',
    component: WebsiteSlugEdit,
    children: [
        { path: 'domain', component: WebsiteDomainEdit },
    ],
};

export const shareRouting = {
    path: ':share(share|publish/published/share|share/share|standalone)',
    component: ShareNew,
    children: [
        { path: 'embed', component: Embed },
        { path: 'qr', component: Qr },
        publishNameRouting,
        bioShareRouting,
    ],
};
