import '@mssgme/scss-basics';
import React from 'react';
import { render } from 'react-dom';
import { Workbox } from 'workbox-window';
import { analytics, GoogleTagManagerProvider } from '@mssgme/analytics';

import { UserAdminApp } from '@mssgme/webapp-core';

if (!(__DEV__ && !__DEV__.sw)) { //TODO: redo
    if ('serviceWorker' in navigator) {
        const wb = new Workbox('/sw.js');
        wb.register();
    }
}

analytics.registerProvider(new GoogleTagManagerProvider({ skipInitialization: true }));
analytics.initialize();

render(<UserAdminApp />, document.getElementById('root'));
