import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useHistory } from 'react-router-dom';

import { usePresenceRemoval } from '../../../hooks';
import { ModalEditPane } from '../../ModalEditPane';
import { SecondLevelScreen } from '../SecondLevelScreen';
import { Section } from './Section';

import styles from './StreamlinedScreen.module.scss';

export const StreamlinedScreen = ({
    modal,
    fullHeight,
    topPanelChildren,
    rightButton,
    editing,
    preferDynamicBack,
    backPath,
    guardChanges,
    ...props
}) => {
    const history = useHistory();

    const handleBack = useCallback(() => {
        if (preferDynamicBack) {
            history.goBack();
        } else if (typeof backPath === 'function') {
            backPath();
        } else {
            history.push(backPath);
        }
    }, [preferDynamicBack, backPath]);

    usePresenceRemoval();

    if (modal) {
        return (
            <ModalEditPane
                fullHeight={fullHeight}
                fullWidth
                editing={editing}
                guardChanges={guardChanges}
                rightButton={<div className={styles.rightButton}>{rightButton}</div>}
                bottomContent={topPanelChildren}
                onClose={handleBack}
                {...props}
            />
        );
    }

    return (
        <SecondLevelScreen
            slimTop
            topPanelChildren={topPanelChildren}
            titleRightContent={rightButton}
            preferDynamicBack={preferDynamicBack}
            backPath={backPath}
            {...props}
        />
    );
};

StreamlinedScreen.propTypes = {
    ...SecondLevelScreen.propTypes,
    modal: PropTypes.bool,
    editing: PropTypes.bool,
    fullHeight: PropTypes.bool,
};

StreamlinedScreen.defaultProps = {
    ...SecondLevelScreen.defaultProps,
    modal: false,
    light: true,
    inverse: true,
    nopadding: true,
    editing: false,
    fullHeight: true,
};

StreamlinedScreen.Section = Section;
