import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { AnimateableRoutes } from '@mssgme/ui';
import { LandingsContextsProvider } from '../UI';
import { PageDelete, PageHome, PageNameEdit, PageSettings, PageSetup, PageSlugEdit, PageStatusEdit } from './Page';
import { VisualBuilder } from './VisualBuilder';

const routing = [
    {
        path: 'settings',
        component: PageSettings,
        children: [
            { path: 'slug', component: PageSlugEdit },
            { path: 'name', component: PageNameEdit },
            { path: 'status', component: PageStatusEdit },
            { path: 'delete', component: PageDelete },
        ],
    },
];

export const LandingsPageRouter = () => {
    const { website, page } = useWebsitePageBySlugs();

    if (!page) {
        return null;
    }

    return (
        <LandingsContextsProvider base={`/pages/-/${website._id}/${page._id}/edit`} website={website} page={page}>
            <Switch>
                <Route path="/pages/-/:websiteSlug/:pageSlug/setup" component={PageSetup} />
                <Route path="/pages/-/:websiteSlug/:pageSlug/edit" component={VisualBuilder} />
                <Route path="/pages/-/:websiteSlug/:pageSlug">
                    <PageHome />
                    <AnimateableRoutes routing={routing} />
                </Route>
            </Switch>
        </LandingsContextsProvider>
    );
};
