import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { Modal } from '../Modal';
import IconClose from '../../../icons/close.svg';
import styles from './ModalNotification.module.scss';

let container;

export class ModalNotificationProvider extends React.Component {
    static propTypes = {
        children: PropTypes.node,
    };

    constructor(props) {
        super(props);

        container = this;

        this.state = {
            modalNotification: null,
        };
    }

    showModalNotification = (modalNotification) => {
        this.setState({
            modalNotification,
        });
    };

    hideModalNotification = () => {
        this.setState({
            modalNotification: null,
        });
    };

    render() {
        const { modalNotification } = this.state;
        const { children } = this.props;

        return (
            <>
                {modalNotification && (
                    <ModalNotification
                        {...modalNotification.props}
                        onClose={this.hideModalNotification}
                        description={modalNotification.message.description}
                        title={modalNotification.message.title}
                    />
                )}
                {children}
            </>
        );
    }
}

export default class ModalNotification extends React.Component {
    static propTypes = {
        closeButton: PropTypes.bool,
        title: PropTypes.oneOfType([PropTypes.string]),
        description: PropTypes.any,
        className: PropTypes.string,
        onClose: PropTypes.func,
        size: PropTypes.oneOf(['small', 'medium', 'large']),
    };

    static defaultProps = {
        closeButton: true,
    };

    static notify(message, props) {
        container.showModalNotification({ message, props });
    }

    static clear() {
        container.hideModalNotification();
    }

    closeModal = () => {
        this.setState({ showModal: false }, () => {
            if (this.props.onClose) {
                this.props.onClose();
            }

            ModalNotification.clear();
        });
    };

    render() {
        const { className, description, title, closeButton, size } = this.props;

        return (
            <Modal onClose={this.closeModal} className={styles.container} size={size}>
                <div className={classNames(styles.root, className)}>
                    <div className={styles.iconBtn} onClick={this.closeModal}>
                        {closeButton && <IconClose className={styles.icon} />}
                    </div>
                    <div className={styles.header}>
                        <div className={styles.title}>{typeof title !== 'function' ? title : title()}</div>
                    </div>
                    <div className={styles.content}>{description}</div>
                </div>
            </Modal>
        );
    }
}
