import invariant from 'tiny-invariant';
import { useMemo } from 'react';
import { useRouteMatch } from 'react-router-dom';
import { flatten } from 'lodash';

const join = (parent, child) => ((parent && child) ? `${parent}/${child}` : child).replace(/\/{2,}/g, '/');
const joinPathArrays = (parent, child) => {
    if (Array.isArray(parent)) {
        return flatten(parent.map((part) => joinPathArrays(part, child)));
    }

    return Array.isArray(child)
        ? child.map((part) => join(parent, part))
        : join(parent, child);
};

const mapChildRoute = function* (parent, routeConfig) {
    invariant(routeConfig, `Miss-configured route "${parent}/*"`);

    const { path: childPath, component, children } = routeConfig;
    const path = childPath ? joinPathArrays(parent, childPath) : parent;

    if (component) {
        const descriptor = {
            path,
            component,
        };

        if (routeConfig.exact ?? !children) {
            descriptor.exact = true;
        }

        if (routeConfig.strict) {
            descriptor.strict = true;
        }

        if (routeConfig.sensitive) {
            descriptor.sensitive = true;
        }

        if (routeConfig.props) {
            descriptor.props = routeConfig.props;
        }

        yield descriptor;
    }

    if (children?.length) {
        for (const config of children) {
            yield* mapChildRoute(path, config);
        }
    }
};

const mapRouting = (path, children) =>
    flatten(
        Array.from(mapChildRoute(path, { children }))
    );

/**
 * @template {{
 *   path: string|string[];
 *   component?: ComponentType;
 *   exact?: boolean;
 *   strict?: boolean;
 *   sensitive?: boolean;
 *   props?: *;
 * }} Route
 * @template {Route & { children?: RouteDefinition[] }} RouteDefinition
 * @param {RouteDefinition[]} routes Route definitions list
 * @param {string} pathname Current location pathname
 * @param {string} [base] Optional base path if no `base` specified - current matched route path would be used
 * @returns {Route[]} Mapped and flattened routes list
 */
export const useRoutes = (routes, pathname, base) => {
    const { path, url } = useRouteMatch();
    // when recursively nesting routers, remember this node depth
    const nested = useMemo(() => path.split('/').length, [path]);
    // throw-away rest of the location if matching upper node of nested routing
    const baseline = useMemo(
        () => path.split('/').slice(0, nested).join('/'),
        [nested, url]
    );
    const basePath = base || baseline;

    return useMemo(() => mapRouting(basePath, routes), [basePath, routes]);
};
