import { Styling } from '@mssgme/ui';

export const featuresStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                fields.color(),
                fields.color({
                    path: 'backgroundColor',
                    label: t('Page.fields.backgroundColor.shortLabel'),
                }),
            ],
        ]),
    };
};
