import React from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { useNavigationUtils, useActions, useFetching } from '@mssgme/hooks';
import { GuideSlider, GuideSlideItem, GuideControl } from '../../../../UI';

import slide1Img from '../../../../img/editorGuide/1.webp';
import slide2Img from '../../../../img/editorGuide/2.webp';
import slide3Img from '../../../../img/editorGuide/3.webp';

const slidesImages = [slide1Img, slide2Img, slide3Img];

export function GuideBuilder() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const history = useHistory();
    const actions = useActions();
    const finishGuide = useFetching(() => actions.api.onboarding.update({ payload: { editor: { finished: true } } }));
    const slidesTranslations = t('Editor.GuideBuilder.slides', { returnObjects: true });

    const handleFinish = async () => {
        await finishGuide.run();
        history.push(generateUrl('..'));
    };

    const renderControl = ({ currentSlideIndex, ...rest }) => (
        <GuideControl
            isFetching={finishGuide.pending}
            data-event="click-guide-builder"
            data-slide={currentSlideIndex}
            {...rest}
        />
    );

    return (
        <GuideSlider modal onClose={handleFinish} onFinish={handleFinish} renderControl={renderControl}>
            {slidesTranslations.map((slide, index) => (
                <GuideSlideItem key={index} {...slide}>
                    <img src={slidesImages[index]} alt={slide.title} />
                </GuideSlideItem>
            ))}
        </GuideSlider>
    );
}
