import React from 'react';
import { Route, Switch } from 'react-router-dom';

import { AnimateableRoutes, ThemeContext } from '@mssgme/ui';
import { LanguageContext } from '@mssgme/hooks';
import { useAutoTheme, usePageFrontendTranslations } from '@mssgme/userpage-core';
import { WebsiteChooseTemplate, WebsitesList } from './Website';
import { LandingsWebsiteRouter } from './LandingsWebsiteRouter';
import { LandingsTemplatesRouter } from './LandingsTemplatesRouter';

const routing = [
    {
        path: 'pages/create',
        component: WebsiteChooseTemplate,
    },
];

export function LandingsPage() {
    const { theme } = useAutoTheme();
    const { i18n, isLoaded } = usePageFrontendTranslations();

    if (!isLoaded) {
        return null;
    }

    return (
        <LanguageContext.Provider value={i18n.language}>
            <ThemeContext.Provider value={theme}>
                <Switch>
                    <Route exact path={['/pages', '/pages/create']} component={WebsitesList} />
                    <Route path="/pages/templates" component={LandingsTemplatesRouter} />
                    <Route path="/pages/-/:websiteSlug" component={LandingsWebsiteRouter} />
                    <Route path="/s" component={WebsitesList} />
                </Switch>

                <AnimateableRoutes base="/" routing={routing} />
            </ThemeContext.Provider>
        </LanguageContext.Provider>
    );
}
