import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useFormikContext } from 'formik';

import { usePresenceRemoval } from '../../hooks';
import { NavigationGuardMessage, useNavigationGuard } from '../NavigationGuard';
import { PaneContent } from './PaneContent';

const blockerMotions = {
    initial: 'hidden',
    enter: 'visible',
    exit: 'hidden',
    variants: {
        visible: { opacity: 1 },
        hidden: { opacity: 0 },
    },
    transition: { delay: 0.2 },
};

const contentMotions = (dirty) => ({
    initial: dirty ? 'hidden' : 'visible',
    enter: 'visible',
    exit: 'hidden',
    variants: {
        visible: { opacity: 1 },
        hidden: { opacity: 0 },
    },
    transition: { delay: 0.2 },
});

export const GuardedPane = ({ guardChanges, children, ...rest }) => {
    const { dirty, status } = useFormikContext();
    const hasChanges = guardChanges && dirty && status !== 'success';
    const { isBlocking, wasBlocking, cancel, leave } = useNavigationGuard({
        blocked: hasChanges,
        forbid: typeof guardChanges === 'function' ? guardChanges : null,
    });
    const isPresent = usePresenceRemoval();
    const shouldBlock = isBlocking || (wasBlocking && !isPresent);
    const motionProps = useMemo(() => contentMotions(dirty), [dirty]);

    return shouldBlock ? (
        <PaneContent
            key="blocker"
            {...rest}
            motionProps={blockerMotions}
            nopadding={false}
            closePath={null}
            rightButton={null}
            contentStyles={null}
        >
            <NavigationGuardMessage cancel={cancel} leave={leave} />
        </PaneContent>
    ) : (
        <PaneContent key="content" motionProps={guardChanges ? motionProps : null} {...rest}>
            {children}
        </PaneContent>
    );
};

GuardedPane.propTypes = {
    ...PaneContent.propTypes,
    guardChanges: PropTypes.oneOfType([PropTypes.bool, PropTypes.func]),
};
