import { useEffect, useRef } from 'react';

export const useBlockAddedEvent = (pageId, allBlocks, onEvent) => {
    const all = allBlocks.length;
    const currentRef = useRef();
    const lastRef = useRef();
    const fnRef = useRef();

    fnRef.current = onEvent;
    lastRef.current = [pageId, all, allBlocks[all - 1]?._id];

    if (!currentRef.current) {
        currentRef.current = lastRef.current;
    }

    useEffect(() => {
        const [prevId, prevAll, prevLastId] = currentRef.current;
        const [pageId, all, lastId] = lastRef.current;

        currentRef.current = lastRef.current;

        if ((prevId !== pageId) || (lastId === prevLastId)) {
            return;
        }

        if (all > prevAll) {
            return fnRef.current(allBlocks[all - 1]);
        }
    }, lastRef.current);
};
