import React, { useCallback, useMemo, useState } from 'react';

import { renderNode } from '../../helpers';
import { SwipeableContext } from './useSwipeable';

import styles from './SwipeableList.module.scss';

export const SwipeableList = ({ children }) => {
    const [openedId, setOpenedId] = useState();
    const handleAside = useCallback((id, aside) => setOpenedId(aside ? id : null), []);
    const context = useMemo(() => ({ openedId, onAside: handleAside }), [openedId]);

    return (
        <SwipeableContext.Provider value={context}>
            <ul className={styles.list}>{renderNode(children, context)}</ul>
        </SwipeableContext.Provider>
    );
};
