import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useParams } from 'react-router-dom';
import { FiSettings, FiArrowLeft, FiPlus } from 'react-icons/fi';

import { useNavigationUtils, useTemplate, useWebsitesFactory } from '@mssgme/hooks';
import { Nav, NavItemLink, IsGranted, NavItem } from '@mssgme/ui';
import { TemplatePill } from '../../../../UI';
import { TemplateRouter } from '../../TemplateRouter';

import styles from './TemplateHome.module.scss';

export function TemplateHome() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { templateId } = useParams();
    const { pending, create } = useWebsitesFactory();
    const template = useTemplate(templateId);
    const handleCreate = useCallback(() => create({ templateId }), [templateId]);

    if (!template) {
        return null;
    }

    return (
        <>
            <div className={styles.root}>
                <div className={styles.topPanel}>
                    <div className={styles.header}>
                        <Link to={generateUrl('..')} className={styles.backLink}>
                            <FiArrowLeft size={24} color="#333" />
                        </Link>
                        <h1 className={styles.title}>{template.name}</h1>
                    </div>

                    <TemplatePill template={template} className={styles.preview} />
                </div>
                <div style={{ padding: '5px 16px 0 16px' }}>
                    <Nav>
                        <IsGranted
                            rule="website:create"
                            renderNoAccess={() => (
                                <NavItem disabled icon={FiPlus}>
                                    {t('labels.page.createNew')}
                                </NavItem>
                            )}
                        >
                            <NavItem disabled={pending} onClick={handleCreate} icon={FiPlus}>
                                {t('labels.page.createNew')}
                            </NavItem>
                        </IsGranted>
                        <IsGranted rule="page-templates:edit" subject={template}>
                            <NavItemLink disabled={pending} to={generateUrl('settings')} icon={FiSettings}>
                                {t('Template.Header.items.settings')}
                            </NavItemLink>
                        </IsGranted>
                    </Nav>
                </div>
            </div>
            <TemplateRouter />
        </>
    );
}
