import PropTypes from 'prop-types';
import React from 'react';

import { URLView } from '../../URLView';
import { CustomViewInput } from '../CustomViewInput';

import styles from './URLInput.module.scss';

export const URLInput = ({ pretty, active, afterView, ...props }) => (
    <CustomViewInput {...props}>
        <URLView
            className={props.value ? undefined : styles.placeholder}
            isUrl={!!props.value}
            pretty={pretty}
            active={active}
            url={props.value || props.placeholder}
        >
            {afterView}
        </URLView>
    </CustomViewInput>
);

URLInput.propTypes = {
    ...CustomViewInput.propTypes,
    pretty: PropTypes.bool,
    active: PropTypes.bool,
    afterView: PropTypes.any,
};
