import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import { LinkBlank } from '../LinkBlank';
import styles from './URLView.module.scss';

const REGEXP = /^((?:[^:\n]+:\/\/)?)((?:www\.)?)([^/$\n]+\/?)((?:.*)?)$/;
const prevent = (event) => {
    event.preventDefault();
    event.stopPropagation();
};

export const URLView = ({ active, pretty = true, url, isUrl, className, children }) => {
    const [, proto, , domain, path] = ((isUrl || isUrl === undefined) && (url || '').match(REGEXP)) || [];
    const Tag = active ? LinkBlank : 'div';
    const attributes = active && { href: url, onFocus: prevent };

    return (
        <span className={cn(styles.root, className)}>
            {domain || path || (!pretty && proto) ? (
                <Tag {...attributes}>
                    {!pretty && <span className={styles.domain}>{proto}</span>}
                    <span className={styles.domain}>{domain}</span>
                    <span className={cn(pretty ? styles.path : styles.domain)}>{domain ? path : path || url}</span>
                </Tag>
            ) : (
                <span className={styles.nonUrl}>{url}</span>
            )}
            {children}
        </span>
    );
};

URLView.propTypes = {
    url: PropTypes.string,
    isUrl: PropTypes.bool,
    active: PropTypes.bool,
    pretty: PropTypes.bool,
    className: PropTypes.string,
};
