import PropTypes from 'prop-types';
import React from 'react';

import { TextFieldProps } from './types';
import TextField from './TextField';

const numberToString = (min, max) => (value) => String(Math.max(min, Math.min(+value || 0, max)));
const stringToNumber = (min, max) => (value) => Math.max(min, Math.min(+value || 0, max));

const NumberField = (props) => (
    <TextField
        type="number"
        mask={stringToNumber(props.min, props.max)}
        unmask={numberToString(props.min, props.max)}
        {...props}
    />
);

NumberField.propTypes = {
    ...TextFieldProps,
    value: PropTypes.number,
};

export default NumberField;
