import { object, string, array } from 'yup';

import { __t } from '../trans';

const toArray = (value) => typeof value === 'string' ? [value] : value;

export const DEFAULT_DYNAMIC_LABEL_TYPE = {
    Name: 'name',
    Email: 'email',
    Phone: 'phone',
    Address: 'address',
    'Shipping address': 'address',
    Comment: 'comment',
    Paid: 'paid',
};

export const DYNAMIC_FIELD_SIZE_NORMAL = 250;
export const DYNAMIC_FIELD_SIZE_LARGE = 5000;
export const getLocalizedDynamicLabel = ({ label, type }, translate = __t) => {
    if (label) {
        const labelType = DEFAULT_DYNAMIC_LABEL_TYPE[label];

        if (labelType) {
            return translate(`field_label_${labelType}`);
        }

        return label;
    }

    return translate(`field_label_${type}`, null, type);
};
export const mapDynamicFieldToName = ({ _id }, index) => _id || `field-${index}`;
export const createDynamicInputsValidationSchema = (items) =>
    object(Object.fromEntries(items.map(({ _id, type, required, size, options }, index) => {
        let validator = string()
            .trim()
            .max(size || DYNAMIC_FIELD_SIZE_NORMAL, __t('field_too_long'));

        if (required) {
            validator = validator.required(__t('field_required'));
        }

        switch (type) {
            case 'email': {
                validator = validator.email(__t('field_email'));

                break;
            }

            case 'phone': {
                validator = validator.matches(/\+?[\d\-\s()]{5,}/, {
                    name: 'phone',
                    message: __t('field_phone'),
                    excludeEmptyString: true,
                });

                break;
            }

            case 'checkbox':
            case 'select':
            case 'radio':
            case 'country': {
                const optionValues = options?.split(/\r?\n/).filter(Boolean);

                if (optionValues?.length) {
                    validator = validator.oneOf(
                        optionValues,
                        __t(type === 'country' ? 'field_country' : 'field_required')
                    );
                }

                if (type === 'checkbox') {
                    if (!required) {
                        validator = validator.required(__t('field_required'));
                    }

                    const itemValidator = validator;

                    validator = array().transform(toArray).test({
                        name: 'checkbox',
                        message: __t('field_required'),
                        test: (value) => {
                            if (!(value && Array.isArray(value))) {
                                return !required;
                            }

                            try {
                                for (const one of value) {
                                    itemValidator.validateSync(one, { abortEarly: true });
                                }
                            } catch {
                                return false;
                            }

                            return !(required && (value.length <= 0));
                        },
                    });
                }

                break;
            }
        }

        return [_id || `field-${index}`, validator];
    })));
