import PropTypes from 'prop-types';
import React from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { useFeature } from '@mssgme/hooks';
import { LandingsPropTypes, TextField } from '@mssgme/ui';
import { ActionField } from '../ActionField';
import AutosuggestStyle from './AutosuggestStyle';

export function SmartLinkField({
    themeFields,
    required,
    extended = true,
    actionLabel,
    urlLabel,
    titleLabel,
    titleName = 'title',
    actionName = 'action',
    ...rest
}) {
    const { t } = useTranslation('admin-ui');
    const { getFieldMeta } = useFormikContext();
    const { value } = getFieldMeta(name);
    const applySuggestions = useFeature('link_auto_branding');

    return (
        <div>
            <div {...rest}>
                <TextField
                    builderInput
                    name={titleName}
                    label={titleLabel || t('SmartLinkField.title.label')}
                    placeholder={t('SmartLinkField.title.placeholder')}
                />

                <ActionField
                    required={required}
                    extended={extended}
                    name={actionName}
                    title={value?.[titleName]}
                    blockKind="link"
                    urlLabel={urlLabel}
                    actionLabel={actionLabel}
                    style={{ marginBottom: 16 }}
                />
            </div>

            {applySuggestions && themeFields && <AutosuggestStyle link={value} themeFields={themeFields} />}
        </div>
    );
}

SmartLinkField.propTypes = {
    required: PropTypes.bool,
    extended: PropTypes.bool,
    urlLabel: LandingsPropTypes.inputLabel,
    actionLabel: LandingsPropTypes.inputLabel,
    titleLabel: LandingsPropTypes.inputLabel,
    themeFields: LandingsPropTypes.linkThemeFields,
    titleName: PropTypes.string,
    actionName: PropTypes.string,
};
