import React from 'react';
import PropTypes from 'prop-types';

import { Styling } from '@mssgme/ui';

const SizeIcon = ({ fontSize }) => {
    return <div style={{ fontSize, fontWeight: 'bold', lineHeight: 1 }}>Aa</div>;
};

SizeIcon.propTypes = {
    fontSize: PropTypes.number.isRequired,
};

/* eslint-disable react/display-name */
export const headingStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('size', {
                label: t('Page.fields.fontSize.label'),
                options: [
                    {
                        value: 'huge',
                        label: t('Page.fields.size.items.huge'),
                        icon: () => <SizeIcon fontSize={22} />,
                    },
                    {
                        value: 'large',
                        label: t('Page.fields.size.items.large'),
                        icon: () => <SizeIcon fontSize={16} />,
                    },
                    {
                        value: 'medium',
                        label: t('Page.fields.size.items.medium'),
                        icon: () => <SizeIcon fontSize={14} />,
                    },
                    isAdvancedMode && {
                        value: 'small',
                        label: t('Page.fields.size.items.small'),
                        icon: () => <SizeIcon fontSize={12} />,
                    },
                ].filter(Boolean),
            }),
            fields.textAlign((field) => ({
                ...field,
                options: isAdvancedMode ? field.options : field.options.filter(({ value }) => value !== 'justify'),
            })),
            isAdvancedMode && [
                fields.textStyle((field) => ({
                    ...field,
                    options: field.options
                        .filter(({ value }) => !['normal'].includes(value))
                        .map((option) =>
                            option.value !== 'bold' ? option : { ...option, props: { style: { display: 'none' } } }
                        ),
                    initial: ['bold'],
                    mask: null,
                    style: { marginBottom: 16 },
                })),
                fields.color(),
            ],
        ]),
    };
};
