import { useMemo } from 'react';

import { merge, pick } from 'lodash';
import { getPageName } from '@mssgme/userpage-core';

const PAGE_FIELDS = ['enabled', 'name', 'slug', 'seo', 'tracking', 'theme'];

export const usePageInitialData = (page, initialValuesRaw, isHomePage) => {
    return useMemo(() => merge(
        {
            enabled: true,
            name: '',
            slug: '',
            seo: { title: '', description: '' },
            tracking: { facebook: '', google: '', yandex: '' },
            theme: {},
        },
        initialValuesRaw,
        pick(page, PAGE_FIELDS),
        // todo: remove when home-page name fallback no longer needed
        { name: getPageName(page, isHomePage) }
    ), [page, isHomePage]);
};
