import cn from 'classnames';
import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useMeasure } from 'react-use';
import { FiInstagram } from 'react-icons/fi';
import { SiTiktok } from 'react-icons/si';

import { prettifyUrl } from '@mssgme/shared';
import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Edit2Icon, EyeIcon, ShareIcon } from '@mssgme/icons';
import { ModalPane } from '@mssgme/ui';
import { Page } from '@mssgme/userpage-core';

import { useWebsiteUrl } from '../../../../hooks';

import styles from './SharePublished.module.scss';

export function SharePublished() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const [ref, { height: pageWrapperHeight }] = useMeasure();
    const websiteUrl = useWebsiteUrl(website);
    const closePath = generateUrl('../..');
    const scale = 193 / 343; // from figma
    const pageHeight = useMemo(() => pageWrapperHeight / scale, [pageWrapperHeight]);

    return (
        <ModalPane fullHeight onClose={closePath} sheetPaneProps={{ style: { backgroundColor: '#f7f7fa' } }}>
            <div className={styles.root}>
                <div className={styles.intro}>
                    <h1 className={styles.title}>{t('Editor.SharePublished.heading')}</h1>
                    <p className={styles.subHeading}>{t('Editor.SharePublished.subHeading')}</p>
                </div>
                <div className={styles.pageContainer} ref={ref}>
                    <div
                        className={styles.pageWrapper}
                        style={{ transform: `scale(${scale})`, borderRadius: 20 / scale }}
                    >
                        <Page
                            embedded
                            interactive={false}
                            website={website}
                            page={page}
                            style={{ height: pageHeight }}
                        />
                        <a
                            href={websiteUrl}
                            target="_blank"
                            rel="noopener noreferrer"
                            className={styles.pageWrapperLink}
                        />
                    </div>
                </div>
                <div className={styles.userLink}>
                    <div className={styles.status} />
                    <a href={websiteUrl} target="_blank" rel="noopener noreferrer" className={styles.link}>
                        {prettifyUrl(websiteUrl)}
                    </a>
                    <Link to={generateUrl('name')} className={styles.editBtn}>
                        <Edit2Icon size={22} />
                    </Link>
                </div>
                <div className={styles.actions}>
                    <ul className={styles.list}>
                        <li>
                            <Link to={generateUrl('share')} className={styles.button}>
                                <div className={styles.circle}>
                                    <ShareIcon size={24} />
                                </div>
                                <span className={styles.title}>{t('Editor.SharePublished.nav.share')}</span>
                            </Link>
                        </li>
                        <li>
                            <Link to={generateUrl('bio')} className={styles.button}>
                                <div className={styles.circle}>
                                    <div className={styles.iconsSlider}>
                                        <div className={cn(styles.icon)}>
                                            <FiInstagram size={24} />
                                        </div>
                                        <div className={cn(styles.icon)}>
                                            <SiTiktok size={24} />
                                        </div>
                                    </div>
                                </div>
                                <span className={styles.title}>{t('Editor.SharePublished.nav.bio')}</span>
                            </Link>
                        </li>
                        <li>
                            <a href={websiteUrl} target="_blank" rel="noopener noreferrer" className={styles.button}>
                                <div className={styles.circle}>
                                    <EyeIcon size={24} />
                                </div>
                                <span className={styles.title}>{t('Editor.SharePublished.nav.view')}</span>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </ModalPane>
    );
}
