import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { FiEye, FiEyeOff, FiMinusSquare, FiPlusSquare } from 'react-icons/fi';

import { cls } from '@mssgme/helpers';
import { DragHandle, useExperiment } from '@mssgme/ui';

import styles from './CollectionItemsField.module.scss';

export const ActiveButtonEye = ({ active, onClick }) => {
    return (
        <div className={styles.button} onClick={onClick}>
            {active ? <FiEye color="#333" size={20} /> : <FiEyeOff size={20} />}
        </div>
    );
};

ActiveButtonEye.propTypes = {
    active: PropTypes.bool,
    onClick: PropTypes.func,
};

export const ActiveButtonPlus = ({ active, onClick }) => {
    return (
        <div className={styles.button} onClick={onClick}>
            {active ? <FiMinusSquare color="red" size={20} /> : <FiPlusSquare color="#2979ff" size={20} />}
        </div>
    );
};

ActiveButtonPlus.propTypes = {
    active: PropTypes.bool,
    onClick: PropTypes.func,
};

const ExperimentsMap = {
    eye: ActiveButtonEye,
    plus: ActiveButtonPlus,
};

export const CollectionItemField = ({ item, active, sortable, onToggle, ...rest }) => {
    const expId = useExperiment({ id: 'collections_field' });
    const { t } = useTranslation('products');
    const ActiveButton = ExperimentsMap[expId] || ActiveButtonEye;

    const handleToggle = useCallback(() => onToggle(item), [item, onToggle]);

    return (
        <div {...cls([styles.row, active && styles.active], rest)}>
            <div className={styles.left}>
                <DragHandle active={!!sortable} iconSize={9} small="auto" />
            </div>

            <div className={styles.value}>{item.default ? t('labels.allProducts') : item.name}</div>

            <div className={styles.buttons}>
                <ActiveButton active={active} onClick={handleToggle} />
            </div>
        </div>
    );
};

CollectionItemField.propTypes = {
    item: PropTypes.object.isRequired,
    active: PropTypes.bool,
    sortable: PropTypes.bool,
    onToggle: PropTypes.func.isRequired,
};

CollectionItemField.defaultProps = {
    sortable: false,
};
