import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextField, FieldTip } from '@mssgme/ui';

Wayforpay.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Wayforpay({ enabled, name }) {
    const { t } = useTranslation('products');

    return (
        <>
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}merchantAccount`}
                label={t('fields.payments.wayforpay.merchantAccount.label')}
                placeholder={t('fields.payments.wayforpay.merchantAccount.placeholder')}
                disabled={!enabled}
                required
            />
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}merchantSecretCode`}
                label={t('fields.payments.wayforpay.merchantSecretCode.label')}
                placeholder={t('fields.payments.wayforpay.merchantSecretCode.placeholder')}
                disabled={!enabled}
                required
            />
            <FieldTip>
                <p style={{ margin: 0 }} dangerouslySetInnerHTML={{ __html: t('Payments.Wayforpay.tip') }} />
            </FieldTip>
        </>
    );
}
