import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextField, ToggleField } from '@mssgme/ui';

Robokassa.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Robokassa({ enabled, name }) {
    const { t } = useTranslation('products');

    return (
        <>
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}merchantLogin`}
                label={t('fields.payments.robokassa.merchantLogin.label')}
                placeholder={t('fields.payments.robokassa.merchantLogin.placeholder')}
                disabled={!enabled}
                required
            />
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}merchantPassword`}
                label={t('fields.payments.robokassa.merchantPassword.label')}
                placeholder={t('fields.payments.robokassa.merchantPassword.placeholder')}
                disabled={!enabled}
                required
            />
            <ToggleField
                builderInput
                name={`${name ? name + '.' : ''}test`}
                label={t('fields.payments.robokassa.test.label')}
                disabled={!enabled}
            />
        </>
    );
}
