import PropTypes from 'prop-types';
import React from 'react';
import { matchPath, useLocation } from 'react-router-dom';
import { AnimatePresence } from 'framer-motion';

import { SettingsMenu } from '../SettingsMenu';
import { AccountPage } from '../AccountPage';
import { NotificationsPage } from '../NotificationsPage';
// import { InvitesPage } from '../InvitesPage';
import { ChangePasswordPage } from '../ChangePasswordPage';
import { SupportPage } from '../SupportPage';
import { DeleteAccountPage } from '../DeleteAccountPage';

export function SettingsPage({ backPath }) {
    const location = useLocation();
    const match = (to, Component) => {
        const matched = matchPath(location.pathname, to);

        return matched && <Component key={to.path} backPath={backPath} {...matched.params} />;
    };

    return (
        <AnimatePresence>
            {match({ path: '/s', exact: true }, SettingsMenu)}
            {match({ path: '/s/support', exact: true }, SupportPage)}
            {/*{match({ path: '/s/invites', exact: true }, InvitesPage)}*/}
            {match({ path: '/s/notifications', exact: false }, NotificationsPage)}
            {match({ path: '/s/account', exact: false }, AccountPage)}
            {match({ path: '/s/account/change-password', exact: true }, ChangePasswordPage)}
            {match({ path: '/s/delete-account', exact: true }, DeleteAccountPage)}
        </AnimatePresence>
    );
}

SettingsPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
