import { string } from 'yup';

export const valueTest = (testFn, message, fields) => {
    const keys = Object.keys(fields);
    const values = Object.values(fields);

    return [
        keys,
        {
            is: (...fieldValues) => {
                for (const [index, value] of Object.entries(fieldValues)) {
                    if (value !== values[index]) {
                        return false;
                    }
                }

                return true;
            },
            then: string()
                .trim()
                .test(`is-${values.join('-')}`, message, (value, ...args) =>
                    String(value || '').trim() ? testFn(value, ...args) : true
                ),
        },
    ];
};
