import React from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import config from 'config';
import { useCopyText, useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { Button, FieldTip, ModalPane } from '@mssgme/ui';

import styles from './Embed.module.scss';

export function Embed() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { websiteId, pageId } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { isCopied, copyText } = useCopyText();
    const closePath = generateUrl('..');
    const tempWidgetCode = `<script src="${config.main_url}/widget/${websiteId}/${pageId}" async></script>`;

    return (
        <ModalPane title={t('Website.View.Embed.heading')} onClose={closePath}>
            <FieldTip style={{ whiteSpace: 'pre-wrap', fontSize: 14, marginBottom: 16 }}>
                {t('Website.View.Embed.tip')}
            </FieldTip>
            <span className={styles.label}>{t('Website.View.Embed.codeLabel')}</span>
            <div className={styles.code}>{tempWidgetCode}</div>
            <Button
                style={{ pointerEvents: isCopied ? 'none' : 'auto' }}
                btnStyle={!isCopied ? 'primary' : 'success'}
                fullWidth
                onClick={() => copyText(tempWidgetCode)}
            >
                {!isCopied ? t('Website.View.Embed.copyCode') : t('Website.View.Embed.copied')}
            </Button>
        </ModalPane>
    );
}
