import React, { Suspense } from 'react';
import { Route, Router, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import { ErrorBoundary } from 'react-error-boundary'; // TODO: use sentry error Boundary

import { history } from '@mssgme/services';
import { useI18n } from '@mssgme/i18n';
import {
    EventEmitterProvider,
    ExperimentsProvider,
    ModalNotificationProvider,
    NotificationsBag,
    useUITranslations,
} from '@mssgme/ui';
import { useAdminUITranslations } from '@mssgme/admin-ui';
import { useLandingsTranslations } from '@mssgme/landings';
import { useBillingTranslations } from '@mssgme/billing';
import { useProductsTranslations } from '@mssgme/products';
import { configureStore } from '@mssgme/store';

import { SplashScreen, InstallPromptProvider, SomethingWrong } from '../UI';
import { PrivatePage, PrivateRoute, AuthorizePage, LogoutPage, AccountDeletedPage } from '../Pages';
import { HelpWidget } from './HelpWidget';

const store = configureStore();
const ab = {
    experiments: [],
};

const LocalesLoader = ({ children }) => {
    useI18n('common', (language) =>
        import(
            /* webpackChunkName: "locales.common" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/common.json`
        )
    );
    useUITranslations();
    useAdminUITranslations();
    useLandingsTranslations();
    useBillingTranslations();
    useProductsTranslations();

    return children;
};

function fallbackRender({ error }) {
    return (
        <SomethingWrong error={typeof error === 'string' ? new Error(error) : error} />
    );
}

function handleError(err) {
    window.Sentry?.captureException(err);
}

export function AppShell({ children }) {
    return (
        <Suspense fallback={<SplashScreen />}>
            <LocalesLoader>
                <Provider store={store}>
                    <EventEmitterProvider>
                        <ExperimentsProvider config={ab}>
                            <InstallPromptProvider>
                                <ModalNotificationProvider />
                                <NotificationsBag />
                                <Router history={history}>
                                    <ErrorBoundary fallbackRender={fallbackRender} onError={handleError}>
                                        <Suspense fallback={<SplashScreen />}>
                                            {children}
                                        </Suspense>
                                    </ErrorBoundary>
                                </Router>
                            </InstallPromptProvider>
                        </ExperimentsProvider>
                    </EventEmitterProvider>
                </Provider>
            </LocalesLoader>
        </Suspense>
    );
}

export function UserAdminApp() {
    return (
        <AppShell>
            <Switch>
                <Route path="/authorize" exact component={AuthorizePage} />
                <Route path="/account-deleted" exact component={AccountDeletedPage} />
                <PrivateRoute path="/logout" component={LogoutPage} />
                <PrivateRoute path="/" component={PrivatePage} />
            </Switch>
            <HelpWidget />
        </AppShell>
    );
}
