import PropTypes from 'prop-types';
import React from 'react';

import styles from './commonStyles.module.scss';

const clamp = (value, step, edge) => {
    const stepped = ~~(value / step);

    return edge < 0 ? stepped : stepped + 1;
};

export const clampRect = ({ area, dx, dy }) => {
    const left = clamp(area.left, dx, -1);
    const x2 = clamp(area.right, dx, +1);
    const top = clamp(area.top, dy, -1);
    const y2 = clamp(area.bottom, dy, +1);
    const width = x2 - left;
    const height = y2 - top;

    return {
        left,
        top,
        width,
        height,
    };
};

export const scaleRect = ({ area, dx, dy }) => ({
    left: area.left * dx,
    top: area.top * dy,
    width: area.width * dx,
    height: area.height * dy,
});

export const AreaCover = (props) => {
    const { left, top, width, height } = scaleRect(props);

    return (
        <div className={styles.area}>
            <div className={styles.marker} style={{ left, top, width, height }} />

            <div className={styles.line + ' ' + styles.v} style={{ left }} />
            <div className={styles.line + ' ' + styles.v} style={{ left: left + width - 1 }} />
            <div className={styles.line + ' ' + styles.h} style={{ top }} />
            <div className={styles.line + ' ' + styles.h} style={{ top: top + height - 1 }} />
        </div>
    );
};

AreaCover.propTypes = {
    area: PropTypes.shape().isRequired,
    dx: PropTypes.number.isRequired,
    dy: PropTypes.number.isRequired,
};
