import PropTypes from 'prop-types';
import cn from 'classnames';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { FiCheck } from 'react-icons/fi';

import { CURRENCY } from '@mssgme/shared';
import { useModal } from '../../Modal';
import { CurrencyModal } from './CurrencyModal';
import Badge from '../../Badge';
import ListInput from '../ListInput';

import styles from './CurrencyPicker.module.scss';

export const CurrencyPicker = ({ name, value, onChange }) => {
    const { t } = useTranslation('ui');
    const { isOpen, show, hide } = useModal();

    const renderCurrency = useCallback(({ option: { value, symbol, label }, active, lax }) => (
        <>
            <div className={cn(styles.currencyBadgeWrapper, lax && styles.lax)}>
                <Badge badgeStyle="flat">{symbol || value}</Badge>
                <div className={styles.currencyCode}>{value}</div>
            </div>
            <div className={styles.currencySeparator}>&sdot;</div>
            <div className={ListInput.styles.label} title={label}>
                {label}
            </div>
            <div className={cn(ListInput.styles.checkMark, active && ListInput.styles.active)}>
                <FiCheck size={24} />
            </div>
        </>
    ));

    const handleApply = useCallback(
        (value) => {
            hide();

            if (value) {
                onChange && onChange({ target: { name, value } });
            }
        },
        [name, onChange, hide]
    );

    return (
        <div className={styles.root} onClick={show}>
            {renderCurrency({
                option: CURRENCY.options.find((option) => value === option.value),
                lax: true,
            })}
            <span className={styles.changeLabel}>{t('labels.change')}</span>

            {isOpen && <CurrencyModal value={value} renderCurrency={renderCurrency} onApply={handleApply} />}
        </div>
    );
};

CurrencyPicker.propTypes = {
    name: PropTypes.string,
    value: PropTypes.string,
    onChange: PropTypes.func,
};
