import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { ImageListUpload as BaseImageListUpload, ImageListUploadItem } from '@mssgme/ui';
import { useUploads, MediaSizes } from '@mssgme/userpage-core';
import { UploadsGallery } from '../UploadsGallery';
import styles from './ImageListUpload.module.scss';

const ThumbnailableImageListUploadItem = ({ url, removePhoto, ...rest }) => {
    const { getMediaSizeUrl } = useUploads();
    const handleRemove = useCallback((_, itemIndex) => void removePhoto(url, itemIndex), [url, removePhoto]);

    return <ImageListUploadItem url={getMediaSizeUrl(url, MediaSizes.Half)} removePhoto={handleRemove} {...rest} />;
};

ThumbnailableImageListUploadItem.propTypes = ImageListUploadItem.propTypes;

export function ImageListUpload({ gallery, onSelect, ...rest }) {
    return (
        <BaseImageListUpload className={styles.root} {...rest} Item={ThumbnailableImageListUploadItem}>
            {/*{({ addButton }) =>*/}
            {/*    gallery*/}
            {/*        ? [*/}
            {/*              addButton,*/}
            {/*              <div*/}
            {/*                  key="uploads-gallery"*/}
            {/*                  style={{ width: '100%', height: '100%', paddingTop: '100%', position: 'relative' }}*/}
            {/*              >*/}
            {/*                  <UploadsGallery*/}
            {/*                      key="gallery-button"*/}
            {/*                      style={{ width: '100%', height: '100%', position: 'absolute', top: 0, left: 0 }}*/}
            {/*                      size="inline"*/}
            {/*                      onSelect={onSelect}*/}
            {/*                  />*/}
            {/*              </div>,*/}
            {/*          ]*/}
            {/*        : addButton*/}
            {/*}*/}
        </BaseImageListUpload>
    );
}

ImageListUpload.propTypes = {
    ...BaseImageListUpload.propTypes,
    gallery: PropTypes.bool,
    onSelect: PropTypes.func,
};

ImageListUpload.defaultProps = {
    ...BaseImageListUpload.defaultProps,
    gallery: true,
};
