const normalizeTelegram = (value) => {
    return `https://t.me/${value.replace(/^(https?:\/\/)?((t(?:elegram)?)\.me\/)/i, '').replace(/^@/, '')}`;
};

const normalizeWhatsApp = (value) => {
    const domain = value.match(/chat\.whatsapp\.com\//i) ? 'chat.whatsapp.com' : 'wa.me';

    return `https://${domain}/${value.replace(/^(https?:\/\/)?((wa\.me|chat\.whatsapp\.com)\/)?/i, '')}`;
};

const normalizeFBMessenger = (value) => {
    return `https://m.me/${value.replace(/^(https?:\/\/)?(www\.)?((m\.me|fb\.com|facebook\.com)\/)?/i, '')}`;
};

const normalizeVk = (value) => {
    return `https://vk.me/${value.replace(/^(https?:\/\/)?((vk\.me|vk\.com|vkontakte\.(com|ru))\/)?/i, '')}`;
};

const normalizeInstagram = (value) => {
    return `https://ig.me/m/${value.replace(/^((https?:\/\/)?((ig\.me|instagram\.com)\/)?(m\/)?)?@?/i, '')}`;
};

export const normalizer = {
    telegram: normalizeTelegram,
    whatsapp: normalizeWhatsApp,
    messenger: normalizeFBMessenger,
    vk: normalizeVk,
    instagram: normalizeInstagram,
};

export const normalizeMessenger = ({ value, platform, type }) => {
    if (type === 'auto') {
        return normalizer[platform](value);
    }

    return value;
};
