import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { FiX } from 'react-icons/fi';

import { useActions, useFetching, useBilling } from '@mssgme/hooks';
import { Button, PortalModalSheet, Spinner } from '@mssgme/ui';

import styles from './PayNow.module.scss';

export default function PayNow({ toggleSupport }) {
    const { t } = useTranslation('billing');
    const { api } = useActions();
    const {
        billing: { retryingPayment },
    } = useBilling();
    const [isModal, toggleModal] = useToggle(false);
    const payNow = useFetching(() => api.billing.subscription.payNow());
    const handlePayNow = () => {
        payNow.run();
        toggleModal();
    };

    return (
        <>
            <Button
                type="button"
                onClick={handlePayNow}
                fullWidth
                btnStyle="primary"
                data-event="click-pay-now"
                isFetching={payNow.pending || isModal}
                disabled={retryingPayment}
                beforeIcon={retryingPayment && <Spinner size={14} />}
                afterIcon={retryingPayment && <div style={{ width: 34, height: 34 }} />}
            >
                {t(retryingPayment ? 'payNowMessage.button.paymentInProgress' : 'payNowMessage.button.payNow')}
            </Button>
            {isModal && payNow.fulfilled && (
                <PortalModalSheet
                    onClose={toggleModal}
                    render={({ handleClose }) => (
                        <>
                            <div className={styles.closeIcon} onClick={handleClose}>
                                <FiX size={22} color="#333" />
                            </div>
                            <div className={styles.content}>
                                <div className={styles.contentIcon}></div>
                                <h3 className={styles.title}>{t('payNowMessage.title')}</h3>
                                <p className={styles.subtitle}>{t('payNowMessage.subtitle')}</p>
                                <div>
                                    {toggleSupport && (
                                        <Button
                                            type="button"
                                            onClick={toggleSupport}
                                            fullWidth
                                            btnStyle="secondary2"
                                            className={styles.supportButton}
                                        >
                                            {t('payNowMessage.button.support')}
                                        </Button>
                                    )}
                                    <Button type="button" onClick={handleClose} fullWidth btnStyle="primary">
                                        {t('payNowMessage.button.close')}
                                    </Button>
                                </div>
                            </div>
                        </>
                    )}
                />
            )}
        </>
    );
}

PayNow.propTypes = {
    toggleSupport: PropTypes.func,
};
