import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ToggleField } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';
import { MenuItemsField } from './MenuItemsField';

export default function Menu() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website } = useWebsitePageBySlugs();

    return (
        <WebsiteEditForm
            title={t('Website.Edit.Menu.heading')}
            website={website}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            <ToggleField style={{ marginBottom: 16 }} name="menu.enabled" label={t('Website.fields.menuEnabled.label')} />

            <MenuItemsField name="menu.items" website={website} />
        </WebsiteEditForm>
    );
}
