import { object, string, boolean, number, mixed } from 'yup';
import { DYNAMIC_FIELD_TYPES } from '../constants';

export const DynamicFieldSchema = object({
    type: mixed().oneOf(DYNAMIC_FIELD_TYPES),
    label: string()
        .trim()
        .ensure()
        .min(3, 'common:errorsList.dynamic_field.item_label_length')
        .max(250, 'common:errorsList.dynamic_field.item_label_length'),
    required: boolean(),
    size: number(),
    options: string()
        .when('type', {
            is: (type) => ['select', 'radio', 'checkbox'].includes(type),
            then: string().required('common:errorsList.empty_field'),
        }),
});
