import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useToggle } from 'react-use';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useActions, useEventCallback, useRestriction } from '@mssgme/hooks';
import {
    EnhancedFormik,
    Form,
    LandingsPropTypes,
    ModalEditPane,
    renderNode,
    SaveButton,
    usePresenceRemoval,
} from '@mssgme/ui';
import { useThemeStylingDefinitions } from '../../Theming';
import { usePageInitialData, WebsiteEditorContext } from '../../../hooks';

import styles from './PageEditForm.module.scss';

export function PageEditForm({
    initialValues: initialValuesRaw,
    initialDirty,
    website,
    page,
    children,
    title,
    restrictRule,
    backPath = '/pages',
    blocked,
    fullHeight = false,
    formStyles = {},
}) {
    const websiteId = website && website._id;
    const pageId = page && page._id;
    const isHomePage = website.homePageId === pageId;
    const history = useHistory();
    const context = useMemo(() => ({ websiteId, pageId, blockId: null }), [websiteId, pageId]);
    const getThemingForPath = useThemeStylingDefinitions();
    const styling = getThemingForPath('theme', 'page');
    const isNew = !pageId;
    const initialValues = usePageInitialData(page, initialValuesRaw, isHomePage);
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { handleBlockedEvent } = useRestriction(restrictRule, page || initialValues);
    const [isClosing, setClosing] = useToggle(false);

    blocked = blocked || handleBlockedEvent;

    const handleSubmit = useEventCallback(async (values) => {
        const payload = values; // { ...values, theme: styling.enrich(values.theme) };

        if (isNew) {
            await api.pages.create({ websiteId, payload });
        } else {
            await api.pages.update({ websiteId, pageId, payload });
        }

        setClosing(true);
        setTimeout(() => history.push(backPath), 200);
    });

    usePresenceRemoval();

    return (
        <WebsiteEditorContext.Provider value={context}>
            <EnhancedFormik initialValues={initialValues} onSubmit={handleSubmit}>
                {(form) => (
                    <ModalEditPane
                        fullHeight={fullHeight}
                        guardChanges={!isClosing && !blocked}
                        onClose={backPath}
                        title={title}
                        rightButton={
                            <SaveButton
                                noborder
                                nopadding
                                className={styles.saveButton}
                                btnStyle="transparent"
                                size="small"
                                isInitialHidden={false}
                                disabled={isClosing || !(initialDirty || form.dirty)}
                                isFetching={form.isSubmitting}
                                onClick={blocked}
                            >
                                {isNew ? t('labels.page.create') : t('ui:labels.apply')}
                            </SaveButton>
                        }
                    >
                        <Form autoComplete="off" style={formStyles}>{renderNode(children, { ...form, styling })}</Form>
                    </ModalEditPane>
                )}
            </EnhancedFormik>
        </WebsiteEditorContext.Provider>
    );
}

PageEditForm.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page,
    children: PropTypes.oneOfType([PropTypes.func, PropTypes.any]).isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.string,
    restrictRule: PropTypes.string,
    blocked: PropTypes.func,
    initialValues: PropTypes.object,
    initialDirty: PropTypes.bool,
    fullHeight: PropTypes.bool,
    formStyles: PropTypes.object,
};
