import React from 'react';
import { useTranslation } from 'react-i18next';

import { LandingsPropTypes, Toggle } from '@mssgme/ui';
import { useSuggestedTheming } from './useSuggestedTheming';

import styles from './SmartLinkField.module.scss';

AutosuggestStyle.propTypes = {
    link: LandingsPropTypes.smartLink.isRequired,
    themeFields: LandingsPropTypes.linkThemeFields.isRequired,
};

export default function AutosuggestStyle({ link, themeFields }) {
    const { t } = useTranslation('admin-ui');
    const { hasNoSuggestion, isUsingSuggestion, onAutoApplyToggle } = useSuggestedTheming(link, themeFields);

    if (hasNoSuggestion) {
        return null;
    }

    return (
        <Toggle
            className={styles.autosuggestToggle}
            checked={isUsingSuggestion}
            placeholder={t('SmartLinkField.useSocial.placeholder')}
            onChange={onAutoApplyToggle}
        />
    );
}
