import React, { useMemo } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePage, useWebsiteBySlug, useRestriction } from '@mssgme/hooks';
import { StyleEditor } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageStylingBackgroundColor() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const { handleBlockedEvent } = useRestriction('page-styling', { type: 'backgroundColor' });
    const closePath = useMemo(() => generateUrl('../..'), [websiteSlug]);

    return (
        <PageEditForm
            website={website}
            page={page}
            title={t('Page.fields.backgroundColor.label')}
            blocked={handleBlockedEvent}
            backPath={closePath}
        >
            {({ styling }) => (
                <StyleEditor styling={styling} focusPaths={[{ path: 'backgroundColor', undecorated: true }]} />
            )}
        </PageEditForm>
    );
}
