import { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { isMatchedById } from '@mssgme/shared';
import { useFetch, useNavigationUtils } from '@mssgme/hooks';
import { useBlockEditContext } from '../../../hooks';
import { BLOCK_FACTORIES } from './factories';

export const useBlockEditor = ({ blockId, as, website, page, backPath = '.', ...rest }) => {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { callback, mixin } = useBlockEditContext();
    const isNew = !blockId;
    const paths = useMemo(
        () => ({
            basePath: generateUrl('.'),
            backPath: generateUrl(backPath),
            successRedirectPath: generateUrl(backPath),
        }),
        [backPath]
    );
    const blockType = as.block.kind;
    const block = isNew ? undefined : page.blocks.find(isMatchedById(blockId));
    const initialValues = useMemo(
        () => {
            const factory = BLOCK_FACTORIES[blockType];

            return {
                ...(block && !isNew ? block : factory({ kind: blockType, enabled: true }, t)),
                ...mixin,
            };
        },
        [isNew, blockType, block, mixin]
    );
    const isDeleted = !!(blockId && !initialValues._id);
    const preload = as.block.preload;
    const preloader = () => preload?.();
    const { pending } = useFetch(preloader, [as.block]);

    if (pending && preload) {
        throw preloader();
    }

    return {
        isNew,
        bind: {
            key: blockId,
            websiteId: website._id,
            pageId: page._id,
            blockId,
            page,
            block,
            blockType,
            initialValues,
            isDeleted,
            isSafe: !callback,
            blockComponent: as.block,
            stylingPath: `blocks.${blockType.toLowerCase()}`,
            themePropPath: 'theme',
            canToggle: false,
            onDone: callback,
            ...paths,
            ...rest,
        },
    };
};
