import React from 'react';
import { useTranslation, Trans } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, FieldTip, LinkBlank, TextField } from '@mssgme/ui';
import { DomainEditForm } from '../../../../UI';
import { useDomain } from '../../../../hooks';
import { DnsRecord } from './DnsRecord';
import { DomainStatusMark } from './DomainStatusMark';

import styles from './Domain.module.scss';

export default function Domain(props) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website } = useWebsitePageBySlugs();
    const { websiteUrl, domainName, isNew, isReady, isWaitingSSl, state, dns_records } = useDomain(website);

    return (
        <DomainEditForm
            title={t('Website.Edit.Domain.heading')}
            website={website}
            backPath={generateUrl('..')}
            saveButtonTitle={t('Website.Edit.Domain.buttons.connect')}
            {...props}
        >
            {({ isSubmitting, handleDelete, isDeleting }) => (
                <>
                    {domainName ? (
                        isNew && <DnsRecord record={dns_records?.[1]} />
                    ) : (
                        <FieldTip>
                            <p>{t('Website.Edit.Domain.tips.empty.messages.1')}</p>
                            <p style={{ margin: 0 }}>
                                <Trans t={t} i18nKey="Website.Edit.Domain.tips.empty.messages.2">
                                    You can buy domain on <LinkBlank href="https://godaddy.com">godaddy.com</LinkBlank>, <LinkBlank href="https://www.namecheap.com">namecheap.com</LinkBlank>, <LinkBlank href="https://nic.ua">nic.ua</LinkBlank> or other domain sellers.
                                </Trans>
                            </p>
                        </FieldTip>
                    )}
                    {isWaitingSSl && (
                        <FieldTip title={t('Website.Edit.Domain.tips.waitingSSL.title')}>
                            {t('Website.Edit.Domain.tips.waitingSSL.messages.1')}
                        </FieldTip>
                    )}
                    {isReady && (
                        <FieldTip title={t('Website.Edit.Domain.tips.ready.title')}>
                            {t('Website.Edit.Domain.tips.ready.messages.1')}
                        </FieldTip>
                    )}

                    {!domainName && (
                        <TextField
                            style={{ marginTop: 16 }}
                            builderInput
                            name="name"
                            label={t('Website.Edit.Domain.form.domainName.label')}
                            placeholder={t('Website.fields.domain.name.placeholder')}
                            maxLength={50}
                            autoCapitalize="off"
                            disabled={isNew || isWaitingSSl}
                            // restrictRule="domain:edit"
                        />
                    )}

                    {domainName && (
                        <>
                            <div className={styles.statusContainer}>
                                <span className={styles.label}>{t('Website.Edit.Domain.form.domainName.label')}</span>
                                <div className={styles.status}>
                                    <div className={styles.domain}>
                                        {websiteUrl ? (
                                            <LinkBlank href={websiteUrl}>{domainName}</LinkBlank>
                                        ) : (
                                            <span>{domainName}</span>
                                        )}
                                    </div>

                                    <DomainStatusMark state={state} />
                                </div>
                            </div>
                            <Button
                                fullWidth
                                tag="div"
                                btnStyle="danger"
                                isFetching={isDeleting || isSubmitting}
                                onClick={handleDelete}
                                style={{ marginTop: 16 }}
                            >
                                {t('Website.Edit.Domain.buttons.remove')}
                            </Button>
                        </>
                    )}
                </>
            )}
        </DomainEditForm>
    );
}
