import React from 'react';
import { useBilling } from '@mssgme/hooks';
import { TrialBanner, UpgradeBanner } from '@mssgme/ui';
import { useIsTrialAvailable } from '../hooks';

export default function BannerContainer() {
    const { isFree } = useBilling();
    const isTrialAvailable = useIsTrialAvailable();

    if (!isFree) return null;
    return isTrialAvailable ? <TrialBanner /> : <UpgradeBanner />;
}
