import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useLocation } from 'react-router-dom';

import { useFeature, useNotificationsSettings } from '@mssgme/hooks';
import { Button, FieldTip, Row, ToggleField } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';

export const NotificationsSettingsForm = () => {
    const { pathname } = useLocation();
    const { t } = useTranslation('products');
    const { router, crm } = useProductsContext();
    const { enabled, email, telegram, webhook } = useNotificationsSettings(crm);
    const webhookEnabled = useFeature('notifications_webhook');

    const globalButton = (
        <Button
            fullWidth
            tag={Link}
            btnStyle="secondary"
            style={{ marginTop: 16 }}
            to={router.settings.toGlobalNotifications(pathname)}
        >
            {t('ProductsNotifications.labels.configure')}
        </Button>
    );

    return (
        <>
            {enabled ? (
                <>
                    <ToggleField
                        builderInput
                        name="notifications.email"
                        label={t('ProductsNotifications.labels.notifications.email')}
                        disabled={!email}
                    />
                    {!email && <FieldTip tipId="products:ProductsNotifications.settingsDisabled.email" />}

                    <ToggleField
                        builderInput
                        name="notifications.telegram"
                        label={t('ProductsNotifications.labels.notifications.telegram')}
                        disabled={!telegram}
                    />

                    {!telegram && (
                        <Row>
                            <FieldTip tipId="products:ProductsNotifications.settingsDisabled.telegram" />
                        </Row>
                    )}

                    {webhookEnabled && (
                        <>
                            <ToggleField
                                builderInput
                                name="notifications.webhook"
                                label={t('ProductsNotifications.labels.notifications.webhook')}
                                disabled={!webhook}
                            />
                            {!webhook && (
                                <Row>
                                    <FieldTip tipId="products:ProductsNotifications.settingsDisabled.webhook" />
                                </Row>
                            )}
                        </>
                    )}
                </>
            ) : (
                <FieldTip>{t('ProductsNotifications.settingsHint')}</FieldTip>
            )}
            {globalButton}
        </>
    );
};
