import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { PasswordChangeForm } from '../PasswordChangeForm';

export function ChangePasswordPage({ backPath }) {
    const { t } = useTranslation();
    const { generateUrl } = useNavigationUtils();

    return (
        <PasswordChangeForm
            title={t('changePasswordForm.heading')}
            backPath={generateUrl('..')}
            successRedirectPath={backPath}
        >
            <TextField
                builderInput
                name="password"
                type="password"
                label={t('changePasswordForm.label.new_password')}
                placeholder={t('changePasswordForm.placeholder.new_password')}
            />
            <TextField
                builderInput
                name="passwordRepeat"
                type="password"
                label={t('changePasswordForm.label.new_password_repeat')}
                placeholder={t('changePasswordForm.placeholder.new_password_repeat')}
                style={{ marginBottom: 8 }}
            />
        </PasswordChangeForm>
    );
}

ChangePasswordPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
