import React from 'react';

import { renderNode } from '../../helpers';
import { ModalSheet } from '../ModalSheet';
import { PaneContent } from './PaneContent';
import { GuardedPane } from './GuardedPane';

export const ModalEditPane = ({
    children,
    editing = true,
    guardChanges,
    contentStyles,
    light,
    nopadding,
    title,
    rightButton,
    bottomContent,
    onClose,
    ...rest
}) => {
    const paneProps = { nopadding, contentStyles, title, light, bottomContent, rightButton };

    return (
        <ModalSheet onClose={onClose} {...rest}>
            {(handleClose) =>
                editing ? (
                    <GuardedPane guardChanges={guardChanges} closePath={onClose} {...paneProps}>
                        {renderNode(children, handleClose)}
                    </GuardedPane>
                ) : (
                    <PaneContent closePath={onClose} {...paneProps}>
                        {renderNode(children, handleClose)}
                    </PaneContent>
                )
            }
        </ModalSheet>
    );
};

ModalEditPane.propTypes = {
    ...ModalSheet.propTypes,
    ...GuardedPane.propTypes,
};
