import React from 'react';
import PropTypes from 'prop-types';
import { FiAlignLeft, FiAlignCenter, FiAlignRight } from 'react-icons/fi';

import { Styling } from '@mssgme/ui';

const SizeIcon = ({ fontSize }) => {
    return <div style={{ fontSize, fontWeight: 'bold', lineHeight: 1 }}>Aa</div>;
};

SizeIcon.propTypes = {
    fontSize: PropTypes.number.isRequired,
};

/* eslint-disable react/display-name */
export const socialsStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                Styling.s.list('size', {
                    label: t('Page.fields.size.label'),
                    options: [
                        {
                            value: 'large',
                            label: t('Page.fields.size.items.large'),
                            icon: () => <SizeIcon fontSize={22} />,
                        },
                        {
                            value: 'medium',
                            label: t('Page.fields.size.items.medium'),
                            icon: () => <SizeIcon fontSize={16} />,
                        },
                        {
                            value: 'small',
                            label: t('Page.fields.size.items.small'),
                            icon: () => <SizeIcon fontSize={14} />,
                        },
                    ],
                }),
                Styling.s.list('alignment', {
                    label: t('Page.fields.alignment.label'),
                    options: [
                        { value: 'left', label: t('Page.fields.alignment.values.left'), icon: FiAlignLeft },
                        { value: 'center', label: t('Page.fields.alignment.values.center'), icon: FiAlignCenter },
                        { value: 'right', label: t('Page.fields.alignment.values.right'), icon: FiAlignRight },
                    ],
                }),
                fields.color(),
            ],
        ]),
    };
};
