import PropTypes from 'prop-types';
import React from 'react';
import { useSelector } from 'react-redux';
import { useTranslation } from 'react-i18next';

import { filters } from '@mssgme/helpers';
import { useActions, useInfiniteList } from '@mssgme/hooks';
import { Button, LoadingIndicator } from '@mssgme/ui';

import styles from './AudienceList.module.scss';

const byTerm = filters.byProps(['name', 'email', 'phone'], filters.isSubstring);

export const AudienceList = ({ websiteId, hasForm }) => {
    const store = useSelector((store) => store.audience);
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { isFetching, items, hasMore, handleLoadMore } = useInfiniteList(
        store,
        { website: -1, term: byTerm },
        (query) => api.websites.audience.list({ websiteId, query })
    );

    return (
        <div>
            <ul className={styles.list}>
                {items.map((item) => (
                    <li key={item._id} title={item.name} className={styles.item}>
                        <div className={styles.name}>{item.name}</div>
                        <div className={styles.details}>
                            {item.email && (
                                <span>{item.email}</span>
                            )}
                            {item.phone && (
                                <span>{item.phone}</span>
                            )}
                        </div>
                    </li>
                ))}
            </ul>

            {!items.length &&
                (isFetching ? (
                    <LoadingIndicator color="#2979ff" />
                ) : (
                    <div className={styles.emptyMessage}>
                        <h3>{!hasForm && t('Website.View.Audience.labels.noForm')}</h3>
                        <h4>{t('Website.View.Audience.labels.emptyMessage')}</h4>
                    </div>
                ))}

            {hasMore && (
                <Button fullWidth className={styles.more} isFetching={isFetching} onClick={handleLoadMore}>
                    {t('Website.View.Audience.labels.loadMore')}
                </Button>
            )}
        </div>
    );
};

AudienceList.propTypes = {
    websiteId: PropTypes.string.isRequired,
    hasForm: PropTypes.bool,
};
