import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { Button, PortalModalSheet } from '@mssgme/ui';

import styles from './PromptModal.module.scss';

export function PromptModal({ handle, message, okText, btnStyle = 'danger', isFetching, ...rest }) {
    const { t } = useTranslation('ui');

    return (
        <PortalModalSheet fullWidth onClose={handle} {...rest}>
            {({ handleTrue, handleFalse }) => (
                <div>
                    <div className={styles.message}>{message}</div>

                    <Button fullWidth className={styles.horizontalButton} btnStyle="secondary2" onClick={handleFalse}>
                        {t('button.cancel')}
                    </Button>

                    <Button
                        fullWidth
                        className={styles.horizontalButton}
                        btnStyle={btnStyle}
                        isFetching={isFetching}
                        onClick={handleTrue}
                    >
                        {okText || t('button.create')}
                    </Button>
                </div>
            )}
        </PortalModalSheet>
    );
}

PromptModal.propTypes = {
    handle: PropTypes.func.isRequired,
    title: PropTypes.string.isRequired,
    message: PropTypes.string.isRequired,
    okText: PropTypes.string.isRequired,
    btnStyle: Button.propTypes.btnStyle,
    isFetching: PropTypes.bool,
};
