import PropTypes from 'prop-types';
import React, { useMemo, useRef } from 'react';
import { cloneDeep, merge } from 'lodash';

import { useForceUpdate, useWebsiteById, useWorkspace } from '@mssgme/hooks';
import { CRM_IDENTITY } from '@mssgme/userpage-core';
import { makeProductsRouter, ProductsContext } from '../../hooks';

export function ProductsContextProvider({ children, base = '', settings, websiteId = null, modal = true }) {
    const update = useForceUpdate();
    const workspace = useWorkspace();
    const website = useWebsiteById(websiteId);
    const router = useRef();

    const usedCRMConfig = website ? website.crm : workspace.crm;
    const crm = useMemo(() => merge(cloneDeep(CRM_IDENTITY), usedCRMConfig), [usedCRMConfig]);
    const context = useMemo(() => {
        router.current = null;

        const ctx = {
            back: base,
            path: `${base}/products`,
            settings,
            specifier: { website: websiteId },
            isSpecific: !!websiteId,
            crm,
            get router() {
                return router.current || (router.current = makeProductsRouter(ctx));
            },
            setEntry(path) {
                const current = router.current?.toEntry();

                if (path) {
                    if (path === current) {
                        return;
                    }

                    router.current = makeProductsRouter({ ...ctx, back: path, isBackOverriddenBack: true });
                } else if (current && base !== current) {
                    router.current = makeProductsRouter(ctx);
                } else {
                    return;
                }

                update();
            },
            modal,
        };

        return ctx;
    }, [base, settings, websiteId, crm, modal]);

    return <ProductsContext.Provider value={context}>{children}</ProductsContext.Provider>;
}

ProductsContextProvider.propTypes = {
    base: PropTypes.string.isRequired,
    settings: PropTypes.string,
    websiteId: PropTypes.string,
    modal: PropTypes.bool,
};
