import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { AnimatePresence } from 'framer-motion';

import { isMatchedById } from '@mssgme/shared';
import { ChevronDownIcon } from '@mssgme/icons';
import AnchorSelector from './AnchorSelector';
import PageSelector from './PageSelector';

import styles from './AnchorSelect.module.scss';

export const AnchorSelect = ({
    website,
    onChange,
    name,
    value,
}) => {
    const { t } = useTranslation('admin-ui');
    const { page, block, blockIndex } = useMemo(() => {
        const page = website.pages.find(isMatchedById(value.page)) || website.pages[0];
        let blockIndex = page.blocks.findIndex(isMatchedById(value.block));
        blockIndex = blockIndex === -1 ? 0 : blockIndex;
        const block = page.blocks[blockIndex];

        return { page, blockIndex, block };
    }, [value.page, value.block]);
    const [isAnchorModal, toggleAnchorModal] = useToggle(false);
    const [isPageModal, togglePageModal] = useToggle(false);

    return (
        <AnimatePresence>
            <div className={styles.input} onClick={toggleAnchorModal}>
                <div>
                    {page && block && t('AnchorSelect.label', {
                        index: blockIndex + 1,
                        kind: t(`labels.block.kinds.${block.kind}`),
                        page: page.name,
                    })}
                </div>
                <div><ChevronDownIcon size={14}/></div>
            </div>
            {isAnchorModal && (
                <AnchorSelector
                    key="block"
                    name={name}
                    value={value}
                    onClose={toggleAnchorModal}
                    togglePageModal={togglePageModal}
                    website={website}
                    page={page}
                    onChange={onChange}
                />
            )}
            {isPageModal && (
                <PageSelector
                    key="page"
                    name={name}
                    value={value}
                    onClose={togglePageModal}
                    website={website}
                    onChange={onChange}
                />
            )}
        </AnimatePresence>
    );
};

AnchorSelect.propTypes = {
    website: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    value: PropTypes.object.isRequired,
    name: PropTypes.string.isRequired,
};
