import React, { useCallback } from 'react';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { PlanBadge } from '../../../../PlanBadge';
import { RichEditorProps } from './domainTypes';

import styles from './ToolbarButton.module.scss';

export const ToolbarButton = ({
    as: Tag = 'div',
    small,
    children,
    onClick,
    onSelect,
    active,
    disabled,
    label,
    text,
    icon: Icon,
    restriction,
    fixedSize,
    ...rest
}) => {
    const { warning, handleBlockedEvent } = useRestriction(restriction);

    const classes = [
        styles.toolbarItem,
        fixedSize && styles.fixedSize,
        small && styles.small,
        disabled && styles.disabled,
        active && styles.active,
    ];
    const handleClick = useCallback(
        (e) => {
            e.stopPropagation();
            e.preventDefault();

            !disabled && onClick(e);
        },
        [disabled, onClick]
    );
    const handleClosing = useCallback(() => {
        if (!handleBlockedEvent) {
            return;
        }

        handleBlockedEvent();

        return false;
    }, [handleBlockedEvent]);

    const handlers = onSelect
        ? {
            onSelect,
            onClosing: handleClosing,
        }
        : {
            onClick: handleBlockedEvent || handleClick,
        };

    return (
        <Tag
            aria-label={label || text}
            title={label || text}
            disabled={disabled}
            {...handlers}
            {...cls(classes, rest)}
        >
            {Icon && (
                <span className={styles.icon}>
                    <Icon size={small ? 16 : 18} />
                </span>
            )}

            {text && <span className={styles.text}>{text}</span>}

            {children}

            {warning && <PlanBadge plan={warning.target} className={styles.badge} />}
        </Tag>
    );
};

ToolbarButton.propTypes = RichEditorProps.commandItemProps;
