import { useState } from 'react';

import { useActions, useFetch } from '@mssgme/hooks';

const STATE_NEW = 'new';
const STATE_WAIT_SSL = 'wait_ssl';
const STATE_READY = 'ready';
const empty = { domain: '', state: '', dns_records: null };

export const useDomain = (website) => {
    const { api } = useActions();
    const [resolved, setResolved] = useState(empty);
    const websiteId = website._id;
    const domainName = website.domain?.name;
    const isNew = resolved.state === STATE_NEW;
    const isWaitingSSl = resolved.state === STATE_WAIT_SSL;
    const isReady = resolved.state === STATE_READY;
    const isEmpty = !resolved.state;
    const websiteUrl = isWaitingSSl || isReady ? `http${isReady ? 's' : ''}://${domainName}` : '';

    useFetch(async () => {
        if (domainName) {
            const fetchedStatus = await api.websites.domain.get({ websiteId });

            return setResolved(fetchedStatus);
        }

        setResolved(empty);
    }, [websiteId, domainName]);

    return {
        ...resolved,
        domainName,
        websiteUrl,
        isNew,
        isWaitingSSl,
        isReady,
        isEmpty,
    };
};
