import PropTypes from 'prop-types';
import React from 'react';
import { omit } from 'lodash';

import { normalizePhone } from '@mssgme/helpers';
import { Input, SelectMessengerType } from '../Inputs';
import { InputProps } from './types';
import FieldRow from './FieldRow';

const isPhoneType = (messenger, type) => (messenger === 'viber' || messenger === 'whatsapp') && type === 'private';

export default function MessengerNameField({
    typeName = 'type',
    platformName = 'messenger',
    platform,
    type,
    onChange,
    ...props
}) {
    return (
        <FieldRow {...props}>
            {({ field, form: { getFieldProps, setFieldValue, setFieldTouched, getFieldMeta } }) => {
                const typeValue = type || getFieldMeta(typeName).value;
                const messengerValue = platform || getFieldMeta(platformName).value;

                const wrapChange = (callback) => (e) => {
                    const value = callback ? callback(e.target.value) : e.target.value;

                    if (onChange) {
                        onChange({
                            ...e,
                            target: {
                                name: e.target.name,
                                value,
                            },
                        });
                    } else {
                        setFieldValue(e.target.name, value, false);
                        setFieldTouched(e.target.name, true, false);
                    }
                };
                const handleTypeChange = wrapChange((type) => {
                    setFieldTouched(field.name, true, true);

                    return type;
                });
                const handleChange = wrapChange((value) =>
                    isPhoneType(messengerValue, typeValue) ? normalizePhone(value) : value
                );

                const isNotAuto = typeValue !== 'auto';
                const isTel = typeValue === 'private' && ['whatsapp', 'viber'].includes(messengerValue);
                let beforeInput;

                switch (messengerValue) {
                    case 'messenger':
                        beforeInput = isNotAuto ? 'm.me/' : null;
                        break;
                    case 'vk':
                        beforeInput = isNotAuto ? 'vk.me/' : null;
                        break;
                    case 'telegram':
                    case 'whatsapp':
                    case 'viber':
                        beforeInput = (messengerValue !== 'telegram' || isNotAuto) && (
                            <SelectMessengerType
                                {...getFieldProps(typeName)}
                                builderInput
                                platform={messengerValue}
                                onChange={handleTypeChange}
                            />
                        );
                        break;
                }

                return (
                    <Input
                        {...field}
                        builderInput
                        beforeInput={beforeInput}
                        autoCapitalize="off"
                        autoComplete={isTel ? 'tel' : 'off'}
                        type={isTel ? 'tel' : 'text'}
                        onChange={handleChange}
                    />
                );
            }}
        </FieldRow>
    );
}

MessengerNameField.propTypes = {
    ...omit(InputProps, ['beforeInput', 'afterInput', 'type']),
    typeName: PropTypes.string,
    platformName: PropTypes.string,
    platform: PropTypes.string,
    type: PropTypes.string,
};
