import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { MdSearch } from 'react-icons/md';

import {
    CheckableItem,
    Form,
    FormikProvider,
    PortalModalSheet,
    RestrictionScope,
    TextField,
    useEnhancedFormik,
} from '@mssgme/ui';
import { ProductsList } from '../../ProductsListDraggable';
import { ProductsListFooter } from '../../ProductsListFooter';

import styles from './ProductsSelector.module.scss';

const mapValues = (values) =>
    Object.entries(values)
        .filter(([, value]) => value)
        .map(([key]) => key);

export const ProductsSelector = ({ products, value, onDone, ...rest }) => {
    const { t } = useTranslation('products');
    const initialValues = useMemo(() => value.reduce((acc, id) => ((acc[id] = true), acc), {}), [value]);

    const handleSubmit = useCallback(
        (values) => {
            if (values) {
                onDone({ action: 'success', value: mapValues(values) });
            } else {
                onDone({ action: 'cancel' });
            }
        },
        [onDone]
    );

    const formik = useEnhancedFormik({
        initialValues,
        onSubmit: handleSubmit,
    });

    const handleToggle = useCallback(({ _id }) => formik.setFieldValue(_id, !formik.values[_id]), [formik.values]);

    const renderMeta = useCallback(
        ({ product, meta }) => <CheckableItem active={formik.values[product._id]}>{meta}</CheckableItem>,
        [formik.values]
    );

    return (
        <FormikProvider value={formik}>
            <PortalModalSheet
                padding={false}
                portalTo="#visual-builder,#mainRoot"
                title={t('ProductsSelector.heading')}
                onClose={formik.handleSubmit}
                {...rest}
            >
                <RestrictionScope fullWidth>
                    <Form autoComplete="off" className={styles.root}>
                        <div className={styles.list}>
                            <ProductsList products={products.items} renderMeta={renderMeta} onClick={handleToggle} />
                            <ProductsListFooter products={products} />
                        </div>
                        <div className={styles.filter}>
                            <TextField
                                builderInput
                                name="name"
                                value={products.filter.name}
                                onChange={products.handleFilter}
                                placeholder={t('labels.search')}
                                icon={MdSearch}
                                autoComplete="off"
                                clearable
                            />
                        </div>
                        <div className={cn(styles.filter, styles.static)}>
                            <TextField
                                builderInput
                                name="name"
                                value={products.filter.name}
                                onChange={products.handleFilter}
                                placeholder={t('labels.search')}
                                icon={MdSearch}
                                autoComplete="off"
                                clearable
                            />
                        </div>
                    </Form>
                </RestrictionScope>
            </PortalModalSheet>
        </FormikProvider>
    );
};

ProductsSelector.propTypes = {
    value: PropTypes.arrayOf(PropTypes.string).isRequired,
    products: PropTypes.any.isRequired,
    isInitialDirty: PropTypes.bool,
    onDone: PropTypes.func,
};
