import React from 'react';

import { Styling } from '@mssgme/ui';
import BrandIcon from '../../../icons/hatBrand.svg';
import AvatarIcon from '../../../icons/hatAvatar.svg';

/* eslint-disable react/display-name */
export const hatStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('headerMode', {
                label: t('Page.fields.headerMode.label'),
                options: [
                    { value: 'avatar', label: t('Page.fields.headerMode.values.avatar'), icon: () => <AvatarIcon /> },
                    isAdvancedMode && { value: 'centered', label: t('Page.fields.headerMode.values.centered') },
                    { value: 'brand', label: t('Page.fields.headerMode.values.brand'), icon: () => <BrandIcon /> },
                ].filter(Boolean),
            }),
            isAdvancedMode &&
                fields.color({
                    path: 'textColor',
                    label: t('Page.fields.textColor.label'),
                    if: (get) => get('headerMode') !== 'image',
                }),
            fields.align((field) => ({
                ...field,
                disabled: (get) => !['brand', 'avatar'].includes(get('headerMode')),
                if: (get) => ['brand', 'avatar'].includes(get('headerMode')) || !isAdvancedMode,
            })),
        ]),
    };
};
