import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import moment from 'moment';

import { TextField } from '@mssgme/ui';
import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Timer } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

export function TimerEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind, isNew } = useBlockEditor({ website, page, blockId, backPath, as: TimerEdit });
    const [pickerDate, setPickerDate] = useState(
        moment(bind?.block?.expireDate || Date.now() + 3600 * 1000 * 24).format('YYYY-MM-DDTHH:mm')
    );

    const onChange = (e, setFieldValue) => {
        setPickerDate(e.target.value);
        setFieldValue('expireDate', moment(e.target.value).toISOString() || '');
    };

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Timer.heading')} isInitialDirty={isNew}>
            {({ values, styling, setFieldValue }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <TextField
                        builderInput
                        type="datetime-local"
                        label={t('Page.Edit.Timer.labels.expireDate')}
                        value={pickerDate}
                        onChange={(e) => onChange(e, setFieldValue)}
                        style={{ margin: 0 }}
                        min={moment(new Date()).format('YYYY-MM-DDTHH:mm')}
                        max={moment(new Date(Date.now() + 3600 * 1000 * 24 * 365 * 2)).format('YYYY-MM-DDTHH:mm')}
                    />
                    <TextField name="expireDate" type="hidden" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

TimerEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

TimerEdit.block = Timer;
