import PropTypes from 'prop-types';
import React from 'react';
import { Route } from 'react-router-dom';

import { usePresenceRemoval } from '../../hooks';

export const PresentableRoute = ({ match, component: Component, props }) => {
    usePresenceRemoval();

    return (
        <Route computedMatch={match}>
            <Component key={match.path} {...match.params} {...props} />
        </Route>
    );
};

PresentableRoute.propTypes = {
    component: PropTypes.elementType.isRequired,
    match: PropTypes.object.isRequired,
    props: PropTypes.object,
};
