import isShallowEqual from 'shallow-equal/objects';
import { useMemo, useRef, useState } from 'react';
import { useUpdateEffect } from 'react-use';
import { useFormikContext } from 'formik';
import { get, memoize } from 'lodash';

import { useEventCallback } from '@mssgme/hooks';
import { socialIconColor, socialNameForUrl } from '@mssgme/socials';

const getSuggestionForSocial = memoize(
    (social) =>
        social && {
            social,
            color: socialIconColor(social),
            contrastedColor: true,
        }
);
const resolveSuggestion = ({ page, url }) => {
    // todo: when multi-page implemented, return website theme suggestion here
    // if (page) {
    //     return getSuggestionForWebsite(page);
    // }

    return getSuggestionForSocial(socialNameForUrl(url, null));
};

export const useSuggestedTheming = (link, themeFields) => {
    const { values, setFieldValue } = useFormikContext();
    const suggestion = useMemo(() => resolveSuggestion(link), [link.url, link.page]);
    const previous = useRef(suggestion);
    const actual = useMemo(() => {
        const themingEntries = Object.entries(themeFields)
            .map(([field, path]) => [field, get(values, path)])
            .filter(([, value]) => value);

        if (themingEntries.length) {
            return Object.fromEntries(themingEntries);
        }
    }, [values, themeFields]);

    const hasNoSuggestion = !suggestion;
    const hasNoTheming = !actual;
    const isSameAsSuggestion = (hasNoSuggestion && hasNoTheming) || isShallowEqual(suggestion, actual);
    const [autoApply, setAutoApply] = useState(() => hasNoTheming || isSameAsSuggestion);
    const changedSuggestion = previous.current !== suggestion;
    const isUsingSuggestion = autoApply && isSameAsSuggestion;
    const isAutoApplicable = autoApply && !isSameAsSuggestion;
    const isManuallyThemed = !(isUsingSuggestion || changedSuggestion);
    const hasToAutoApply = isAutoApplicable && changedSuggestion;

    previous.current = suggestion;

    const applyTheming = useEventCallback(() => {
        for (const [field, value] of Object.entries(suggestion)) {
            const themeFieldPath = themeFields[field];

            if (themeFieldPath) {
                setFieldValue(themeFieldPath, value);
            }
        }
    });

    const onAutoApplyToggle = useEventCallback(() => {
        previous.current = null;
        setAutoApply((prev) => !prev);
    });

    useUpdateEffect(() => {
        if (hasNoSuggestion) {
            return;
        }

        if (isManuallyThemed) {
            setAutoApply(false);
        } else if (hasToAutoApply) {
            applyTheming();
        }
    }, [hasNoSuggestion, isManuallyThemed, hasToAutoApply]);

    return {
        hasNoSuggestion,
        isUsingSuggestion,
        onAutoApplyToggle,
    };
};
