import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { Swiper } from '@mssgme/ui';

import styles from './SuggestionsPreview.module.scss';

const SuggestionItem = ({ styling, item, index, theme, render, onAccept, ...rest }) => {
    const handleAccept = useCallback(() => {
        onAccept(theme);
    }, [theme, onAccept]);

    return (
        <div className={styles.root} onClick={handleAccept} {...rest}>
            <div className={styles.item}>
                {React.Children.map(render(item, index), (child) =>
                    React.cloneElement(child, {
                        style: {
                            ...(child.props && child.props.style),
                            flex: 1,
                            pointerEvents: 'none',
                        },
                    })
                )}
            </div>
        </div>
    );
};

SuggestionItem.propTypes = {
    styling: PropTypes.object.isRequired,
    item: PropTypes.object.isRequired,
    index: PropTypes.number.isRequired,
    theme: PropTypes.object.isRequired,
    render: PropTypes.func.isRequired,
    onAccept: PropTypes.func.isRequired,
};

export const SuggestionsPreview = ({ styling, suggestions, render, onAccept, ...rest }) => {
    return (
        <Swiper perView={1} {...rest}>
            {suggestions.map(({ item, theme }, index) => (
                <SuggestionItem
                    key={item._id}
                    styling={styling}
                    item={item}
                    index={index}
                    theme={theme}
                    render={render}
                    onAccept={onAccept}
                />
            ))}
        </Swiper>
    );
};

SuggestionsPreview.propTypes = {
    styling: PropTypes.object.isRequired,
    suggestions: PropTypes.array.isRequired,
    render: PropTypes.func.isRequired,
    onAccept: PropTypes.func.isRequired,
};
