const ALPHA_OFFSET = 'a'.charCodeAt(0);
const DIGIT_OFFSET = '0'.charCodeAt(0);
const randomChar = (offset, idx, range) => String.fromCharCode(offset + (idx % range));
const randomAlpha = (idx) => randomChar(ALPHA_OFFSET, idx, 26);
const randomDigit = (idx) => randomChar(DIGIT_OFFSET, idx, 10);

export default function(length = 10) {
    const buffer = [];

    while (length-- > 0) {
        const idx = ~~(Math.random() * 100);
        const char = idx < 70 ? randomAlpha(idx) : randomDigit(idx);

        buffer.push(char);
    }

    return buffer.join('');
}
