import { useCallback, useEffect, useState } from 'react';

import { useActions, useProducts } from '@mssgme/hooks';
import { useProductsContext } from './useProductsContext';
import { useCollectionsMain } from './useCollectionsMain';

export const useProduct = (productId, collectionId) => {
    const { api } = useActions();
    const { specifier, crm, isSpecific } = useProductsContext();
    const { items: products } = useProducts(specifier);
    const { collections, collectionId: defaultCollection } = useCollectionsMain(specifier, (col) => col.default);

    const [product, setProduct] = useState(() => {
        const found = products.find(({ _id }) => _id === productId);

        if (found) {
            return found;
        }

        const cols = { [defaultCollection]: { collectionId: defaultCollection } };

        return {
            published: true,
            name: '',
            slug: '',
            description: '',
            website: null,
            photos: [],
            price: {
                amount: null,
                discount: null,
                currency: crm.defaultCurrency,
            },
            externalButton: {
                title: '',
                url: '',
            },
            cols: collectionId ? { ...cols, [collectionId]: { collectionId } } : cols,
            ...specifier,
        };
    });

    const isNew = !productId;
    const isLoading = !isNew && !product._id;
    const loadProduct = useCallback(async () => {
        try {
            setProduct(await api.products.retrieve({ productId }));
        } catch (e) {
            // may fail when product was deleted just now
        }
    }, [productId]);

    useEffect(() => {
        if (isLoading) {
            void loadProduct();
        }
    }, [isLoading, loadProduct]);

    useEffect(() => {
        if (productId) {
            const found = products.find(({ _id }) => _id === productId);

            if (found) {
                setProduct(found);
            } else {
                setProduct((old) => ({ ...old, _id: null }));
            }
        }
    }, [productId]);

    return { defaultCollection, product, collections, isNew, isLoading, isSpecific };
};
