import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';

import styles from './FeedbackItemForm.module.scss';

export const DataItem = ({ as: Tag = 'div', label, children, spread, ...rest }) => {
    return (
        <div className={styles.infoLine}>
            {label && <div className={styles.prop}>{label}</div>}
            {!!children && <Tag {...cls([styles.value, spread && styles.spread], rest)}>{children}</Tag>}
        </div>
    );
};

DataItem.propTypes = {
    as: PropTypes.elementType,
    label: PropTypes.any,
    spread: PropTypes.bool,
};
