import arrayMove from 'array-move';
import PropTypes from 'prop-types';
import cn from 'classnames';
import React, { useState, useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { useDeepCompareEffect } from 'react-use';

import { useActions } from '@mssgme/hooks';
import { DragHandle } from '@mssgme/ui';
import { useProductsContext } from '../../hooks';

import styles from './CollectionsListDraggable.module.scss';

const SortableItem = SortableElement(({ col, draggable, renderItem, onClick }) => {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();
    const handleClick = useCallback(
        (e) => {
            if (onClick || col.isFixed) {
                e.preventDefault();
            }

            if (onClick && !col.isFixed) {
                onClick(col);
            }
        },
        [col, onClick]
    );

    const content = (
        <>
            {/*<div className={cn(styles.status, col.published && col.publishedProductsCount > 0 && styles.active)} />*/}
            <div className={styles.info}>
                <div className={styles.label}>{col.default ? t('labels.allProducts') : col.name}</div>
                <div className={styles.counter}>{t('labels.products', { count: col.publishedProductsCount })}</div>
            </div>
        </>
    );

    return (
        <li className={cn(styles.listItem, draggable && styles.draggable, col.isFixed && styles.fixed)}>
            <Link to={router.collection.toViewProducts(col.id)} onClick={handleClick}>
                {renderItem ? renderItem(col, content) : content}
            </Link>

            {draggable && <DragHandle title={t('ui:labels.dragTip')} />}
        </li>
    );
});

const SortableList = SortableContainer(({ collections, draggable, renderItem, onClick }) => {
    return (
        <ul className={styles.list}>
            {collections.map((collection, index) => (
                <SortableItem
                    key={collection._id}
                    index={index}
                    col={collection}
                    disabled={!draggable}
                    draggable={draggable}
                    renderItem={renderItem}
                    onClick={onClick}
                />
            ))}
        </ul>
    );
});

export function CollectionsListDraggable({ collections, draggable, renderItem, onClick, isFetching }) {
    const { api } = useActions();
    const [items, setCollections] = useState([]);

    const onSortEnd = useCallback(
        async ({ oldIndex, newIndex }) => {
            if (oldIndex === newIndex) {
                return;
            }

            const col1Id = items[oldIndex]._id;
            const col2Id = items[newIndex]._id;

            setCollections((prevItems) => arrayMove(prevItems, oldIndex, newIndex));

            await api.collections.sort({ payload: { col1Id, col2Id } });
        },
        [items]
    );

    useDeepCompareEffect(() => {
        setCollections(collections);
    }, [collections]);

    return (
        <div className={cn({ [styles.root]: true, [styles.fetching]: isFetching })}>
            <SortableList
                collections={items}
                renderItem={renderItem}
                onClick={onClick}
                axis="y"
                lockAxis="y"
                onSortEnd={onSortEnd}
                helperClass={cn(styles.sortable, DragHandle.helperClass)}
                draggable={draggable}
                shouldCancelStart={DragHandle.shouldCancelStart}
            />
        </div>
    );
}

CollectionsListDraggable.propTypes = {
    collections: PropTypes.array.isRequired,
    draggable: PropTypes.bool.isRequired,
    isFetching: PropTypes.bool,
    renderItem: PropTypes.func,
    onClick: PropTypes.func,
};

CollectionsListDraggable.defaultProps = {
    draggable: true,
};
