import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { VideoGallery } from '@mssgme/userpage-core';
import {
    BlockEditForm,
    EditStylingForm,
    StyledBlockPreview,
    useBlockEditor,
    VideoGalleryBlockSchema,
} from '../components';
import { VideoGalleryItemsField } from './VideoGalleryItemsField';

export function VideoGalleryEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: VideoGalleryEdit });

    return (
        <BlockEditForm {...bind} validationSchema={VideoGalleryBlockSchema} title={t('Page.Edit.VideoGallery.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview contentStyle={{ padding: 0 }} style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <VideoGalleryItemsField name="videos" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

VideoGalleryEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

VideoGalleryEdit.block = VideoGallery;
