import PropTypes from 'prop-types';
import { useEffect } from 'react';

import { useScript } from '../../hooks';

const WIDGET_ID = 'mssgme_widget';

const propTypes = {
    src: PropTypes.string,
};

function MssgmeWidget({ src }) {
    useScript(src);

    useEffect(() => {
        const widget = document.getElementById(WIDGET_ID);

        // don't "early return" here, as widget might not be fully loaded at this stage,
        // but "unload" callback can be called when the widget is shown, and it must still work
        if (widget) {
            widget.style.display = null;
        }

        return () => {
            const widget = document.getElementById(WIDGET_ID);

            if (widget) {
                widget.style.display = 'none';
            }
        };
    }, []);

    return null;
}

MssgmeWidget.propTypes = propTypes;

export default MssgmeWidget;
