import { useSelector } from 'react-redux';

/**
 * @returns {{
 *   items: *[];
 *   filter: { status?: string };
 *   next?: string;
 *   isFetching: boolean;
 *   guid: number;
 * }} Feedbacks store state
 */
export function useFeedbackStore() {
    return useSelector((state) => state.feedback);
}

export function useFeedbackItem(predicate) {
    return useSelector((state) => state.feedback.items.find(predicate)) || null;
}

export function useFeedbackItemById(id) {
    return useFeedbackItem((item) => item._id === id);
}
