import { useSelector } from 'react-redux';
import { isEqual } from 'lodash';
import { useState } from 'react';
import { useDeepCompareEffect } from 'react-use';

export const useRecentFeedback = (predicate) => {
    const countsRaw = useSelector((state) => state.feedback.recent);
    const recentFeedback = Object.entries(countsRaw)
        .filter(([status, count]) => count > 0 && predicate({ status, count }))
        .reduce((acc, [status, count]) => ((acc[status] = count), acc), {});
    const recentFeedbackCount = Object.entries(recentFeedback).reduce((acc, [, count]) => acc + count, 0);
    const hasRecent = recentFeedbackCount > 0;
    const [prev, setPrev] = useState(() => recentFeedback);
    const [updateTimestamp, setUpdateTimestamp] = useState(0);

    useDeepCompareEffect(() => {
        if (isEqual(recentFeedback, prev)) {
            return;
        }

        setPrev(recentFeedback);

        for (const [status, count] of Object.entries(recentFeedback)) {
            const delta = count - (prev[status] || 0);

            if (delta > 0) {
                setUpdateTimestamp(Date.now());

                break;
            }
        }
    }, [prev, recentFeedback]);

    return {
        recentFeedback,
        recentFeedbackCount,
        updateTimestamp,
        hasRecent,
    };
};
