import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';

import { isMatchId, isSameArray } from '@mssgme/helpers';
import { CloseIcon } from '@mssgme/icons';
import { Badge, Button, FieldRow, useModal } from '@mssgme/ui';
import { CollectionsSelector } from '../../ModalSelectors';

import styles from './CollectionsField.module.scss';

export const CollectionsField = ({
    label,
    toggleLabel,
    emptyLabel,
    fixed,
    collections,
    renderItem,
    groupItems,
    ...props
}) => {
    const { t } = useTranslation('products');
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { isOpen, toggle } = useModal();
    const { value } = getFieldMeta(props.name);
    const items = useMemo(() => {
        const sorted = collections
            .map((col) => ({
                ...col,
                name: col.default ? t('labels.allProducts') : col.name || '',
                isFixed: fixed && (Array.isArray(fixed) ? fixed.includes(col._id) : fixed === col._id),
            }))
            .sort((a, b) => {
                if (a.default !== b.default) {
                    return +b.default - +a.default;
                }

                if (a.isFixed !== b.isFixed) {
                    return +b.isFixed - +a.isFixed;
                }

                return 0;
                // return a.name.localeCompare(b.name);
            });

        return {
            mapped: sorted.filter(({ _id, isFixed }) => isFixed || value.includes(_id)),
            all: sorted,
        };
    }, [collections, value, fixed]);
    const hidden = useMemo(() => value.filter((id) => !collections.find(isMatchId(id))), [value, collections]);

    const handleToggle = useCallback(() => {
        if (props.disabled) {
            return;
        }

        toggle();
    }, [props.disabled, toggle]);
    const handleRemove = useCallback(
        (id) => {
            setFieldValue(props.name, value.filter((_id) => id !== _id));
        },
        [value]
    );
    const handleDone = useCallback(
        ({ action, value: raw }) => {
            if (action === 'success') {
                const changed = [...raw, ...hidden.filter((id) => !raw.includes(id))];

                if (!isSameArray(value, changed)) {
                    setFieldValue(props.name, changed);
                }
            }

            toggle(false);
        },
        [hidden, value]
    );

    const labelNode = toggleLabel ? (
        <>
            <div style={{ flex: 1 }}>{label}</div>
            <Button
                tag="a"
                noborder
                nopadding
                className={styles.changeButton}
                btnStyle="transparent"
                disabled={props.disabled}
                onClick={handleToggle}
            >
                {toggleLabel}
            </Button>
        </>
    ) : (
        label
    );

    return (
        <FieldRow label={labelNode} {...props}>
            <div className={cn(styles.root, props.disabled && styles.disabled)} onClick={handleToggle}>
                <ul>
                    {items.mapped.map((item) => (
                        <CollectionsFieldBadge
                            key={item._id}
                            item={item}
                            disabled={props.disabled || item.isFixed}
                            onRemove={handleRemove}
                        />
                    ))}
                </ul>

                {!items.mapped.length && emptyLabel}
            </div>

            {isOpen && (
                <CollectionsSelector
                    value={value}
                    items={items.all}
                    renderItem={renderItem}
                    groupItems={groupItems}
                    onDone={handleDone}
                />
            )}
        </FieldRow>
    );
};

CollectionsField.propTypes = {
    ...FieldRow.propTypes,
    name: PropTypes.string.isRequired,
    collections: PropTypes.arrayOf(
        PropTypes.shape({
            _id: PropTypes.string,
            name: PropTypes.string,
            default: PropTypes.bool,
        })
    ).isRequired,
    toggleLabel: PropTypes.any,
    emptyLabel: PropTypes.any,
    renderItem: PropTypes.func,
    groupItems: PropTypes.func,
};

export const CollectionsFieldBadge = ({ item, disabled, onRemove }) => {
    const handleRemove = useCallback(
        (e) => {
            if (disabled) {
                return;
            }

            e.preventDefault?.();
            e.stopPropagation?.();
            onRemove(item._id);
        },
        [disabled, item, onRemove]
    );

    return (
        <Badge as="li" badgeStyle="flat" className={styles.badge}>
            <span>{item.name}</span>

            {!item.isFixed && (
                <div className={styles.badgeButton}>
                    <CloseIcon size={12} onClick={handleRemove} />
                </div>
            )}
        </Badge>
    );
};

CollectionsFieldBadge.propTypes = {
    item: PropTypes.object.isRequired,
    disabled: PropTypes.bool,
    onRemove: PropTypes.func.isRequired,
};
