import arrayMove from 'array-move';
import { useCallback } from 'react';
import { useAsyncFn } from 'react-use';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router';

import { useActions, useWebsiteBySlug } from '@mssgme/hooks';

export const ACCEPT_IMAGES = ['image/png', 'image/x-png', 'image/jpeg', 'image/webp'];

export const useProductPhotosEdit = ({ maxPhotos, onChange }) => {
    const { t } = useTranslation('products');
    const { api } = useActions();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    const [{ loading: isUploading }, uploadImages] = useAsyncFn(async (images) => {
        const { success: uploads, failed } = await api.products.uploadPhotos({
            payloads: images,
            websiteId: website._id,
        });

        if (failed.length) {
            throw failed
                .map(({ file, error }) => t('common:errorsList.upload_failed', { file, error: t(error) }))
                .join('. ');
        }

        if (uploads.length) {
            return uploads.map(({ result }) => result);
        }
    });

    const onPhotoChange = useCallback(
        async ({ target: { files } }) =>
            void (await onChange(async (old, onPrepare) => {
                const currentImagesCount = old.length;
                const images = files && Array.from(files).filter(({ type }) => ACCEPT_IMAGES.includes(type));

                if (!images || images.length === 0) {
                    return;
                }

                if (currentImagesCount + images.length > maxPhotos) {
                    throw t('productEdit.errors.invalid_photos_count');
                }

                onPrepare && onPrepare();

                const urls = await uploadImages(images);

                if (urls) {
                    return [...new Set(old.concat(urls))];
                }
            })),
        [maxPhotos, onChange]
    );

    const onRemovePhoto = useCallback((url) => onChange((old) => old.filter((image) => image !== url)), [onChange]);

    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (oldIndex === newIndex) {
                return;
            }

            onChange((old) => arrayMove(old, oldIndex, newIndex));
        },
        [onChange]
    );

    return {
        accept: ACCEPT_IMAGES,
        isUploading,
        onPhotoChange,
        onRemovePhoto,
        onSortEnd,
    };
};
