import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { Button } from '../../Inputs';
import { PlanBadge } from '../../PlanBadge';

import styles from './commonStyles.module.scss';

export const ArrayItemAddButton = ({
    name,
    addButtonLabel,
    addMoreButtonLabel,
    restriction,
    items,
    onClick,
    ...rest
}) => {
    const { t } = useTranslation('ui');
    const { warning } = useRestriction(restriction, { [name]: items });

    const addLabel = (
        (addMoreButtonLabel && items.length >= 1)
            ? addMoreButtonLabel
            : (addButtonLabel || t('labels.add'))
    );

    return (
        <Button
            tag="a"
            fullWidth
            iconFixed="right"
            afterIcon={warning?.target && <PlanBadge inverse plan={warning.target} />}
            onClick={onClick}
            {...cls(styles.addButton, rest)}
        >
            +{' '}{addLabel}
        </Button>
    );
};

ArrayItemAddButton.propTypes = {
    name: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
    addButtonLabel: PropTypes.string,
    addMoreButtonLabel: PropTypes.string,
    restriction: PropTypes.string,
    onClick: PropTypes.func.isRequired,
};
