import React from 'react';
import { useTranslation } from 'react-i18next';

import { CurrencyField, StreamlinedScreen } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { ProductsSettingsForm } from '../ProductsSettingsForm';

export function ProductsCurrency() {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();

    return (
        <StreamlinedScreen
            preferDynamicBack
            modal
            inverse={false}
            nopadding={false}
            fullHeight={false}
            backPath={router.settings.toSettings()}
            title={t('ProductsCurrency.title')}
        >
            <ProductsSettingsForm successRedirectPath={router.settings.toSettings()}>
                <CurrencyField
                    builderInput
                    name="defaultCurrency"
                    // label={t('ProductsCurrency.labels.currency')}
                />
            </ProductsSettingsForm>
        </StreamlinedScreen>
    );
}
