import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router';

import { useNavigationUtils, useUser } from '@mssgme/hooks';
import { SecondLevelScreen, Button } from '@mssgme/ui';
import { PlanPane } from '../UI';

import styles from './PlansPage.module.scss';
import { useProductPrices } from '../hooks';

export default function PlansPage() {
    const { t } = useTranslation('billing');
    const { generateUrl } = useNavigationUtils();
    const { lang } = useUser();
    const productPrices = useProductPrices();

    if (productPrices.length === 1) return <Redirect to={generateUrl(productPrices[0].id)} />;

    return (
        <SecondLevelScreen backPath={generateUrl('..')} preferDynamicBack style={{ backgroundColor: '#f7f7fa' }}>
            <div className={styles.root}>
                <div className={styles.header}>
                    <h1>{t('PlansPage.heading')}</h1>
                    <p>{t('PlansPage.subHeading')}</p>
                </div>
                <ul className={styles.plansList}>
                    {productPrices.map((plan) => (
                        <PlanPane key={plan.id} Tag="li" plan={plan} />
                    ))}
                </ul>
                <div className={styles.footer}>
                    <h2>{t('PlansPage.footerMessage')}</h2>
                    <Button
                        tag="a"
                        href={`https://mssg.me/${lang === 'ru' ? 'help' : 'mssgmeen'}`}
                        target="_blank"
                        rel="noopener noreferrer"
                        outline
                        style={{ background: '#fff', fontSize: 16 }}
                    >
                        {t('PlansPage.common.contactUs')}
                    </Button>
                </div>
            </div>
        </SecondLevelScreen>
    );
}
