import React, { useMemo } from 'react';
import { useAsyncFn } from 'react-use';
import { useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { MdTitle } from 'react-icons/md';

import { HomeIcon, CopyIcon, LinkIcon, TrashIcon } from '@mssgme/icons';
import { useActions, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { AclMenuPane, Spinner } from '@mssgme/ui';
import { getPageName } from '@mssgme/userpage-core';

import SeoIcon from '../../../../icons/seoIcon.svg';

const useMenuItems = () => {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { api } = useActions();
    const { website, page } = useWebsitePageBySlugs();

    const websiteId = website._id;
    const pageId = page._id;
    const isHomePage = website.homePageId === pageId;

    const [{ loading: isChangingHomePage }, handleHomePage] = useAsyncFn(
        async () => {
            await api.websites.updateHomePage({ websiteId, payload: { page: pageId } });

            history.replace(generateUrl('..'));
        },
        [websiteId, pageId]
    );

    const [{ loading: isCloning }, handleClone] = useAsyncFn(
        async () => {
            const result = await api.pages.clone({ websiteId, pageId });

            history.replace(generateUrl(`../../../../${result._id}/edit`));
        },
        [websiteId, pageId]
    );

    return useMemo(() => [
        {
            to: 'name',
            label: t('Page.View.Settings.items.name'),
            icon: MdTitle,
            // todo: `use page.name` when home-page name fallback no longer needed
            preview: getPageName(page, isHomePage),
            'data-event': 'click-page-settings',
            'data-feature': 'name',
        },
        {
            to: 'slug',
            label: t('Page.View.Settings.items.slug'),
            icon: LinkIcon,
            preview: page.slug,
            filter: !isHomePage,
            'data-event': 'click-page-settings',
            'data-feature': 'slug',
        },
        {
            to: 'seo',
            label: t('Page.View.Settings.items.seo'),
            icon: SeoIcon,
            iconProps: { style: { display: 'block', width: 18, height: 18, color: '#333' } },
            restrict: { rule: 'page-seo:edit', iconOnly: true },
            preview: page.seo.title || page.seo.description ? t('labels.changed') : t('labels.asDefault'),
            'data-event': 'click-page-settings',
            'data-feature': 'seo',
        },
        {
            tag: 'a',
            to: 'homepage',
            label: t('Page.View.Settings.items.homepage'),
            icon: HomeIcon,
            filter: !isHomePage,
            onClick: handleHomePage,
            disabled: isChangingHomePage,
            rightIcon: isChangingHomePage && <Spinner size={18} style={{ padding: 0 }} />,
            'data-event': 'click-page-settings',
            'data-feature': 'homepage',
        },
        {
            tag: 'a',
            to: 'clone',
            label: t('Page.View.Settings.items.clone'),
            icon: CopyIcon,
            restrict: { rule: 'pages:create', context: { website } },
            onClick: handleClone,
            disabled: isCloning,
            rightIcon: isCloning && <Spinner size={18} style={{ padding: 0 }} />,
            'data-event': 'click-page-settings',
            'data-feature': 'clone',
        },
        {
            to: 'delete',
            label: t('labels.page.delete'),
            labelColor: '#dc5960',
            icon: TrashIcon,
            iconColor: '#dc5960',
            disabled: isHomePage,
            'data-event': 'click-page-settings',
            'data-feature': 'delete',
        },
    ], [website, page, isChangingHomePage, isCloning, handleHomePage, handleClone]);
};

export function PageSettings() {
    const { t } = useTranslation('landings');
    const items = useMenuItems();

    return <AclMenuPane title={t('Page.View.Settings.heading')} items={items} />;
}
