import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { normalizeWebsiteUrl } from '@mssgme/helpers';
import { useFeature } from '@mssgme/hooks';
import {
    Button,
    PriceCompoundField,
    SaveButton,
    Spinner,
    StreamlinedScreen,
    TextareaField,
    TextField,
    ToggleField,
    usePresenceRemoval,
} from '@mssgme/ui';
import { useProduct, useProductsContext } from '../../../hooks';
import { CollectionsField, ProductEditForm } from '../../../UI';
import { ProductPhotosField } from './ProductPhotosField';
import { BadgeLabel } from './BadgeLabel';

export function ProductEdit({ productId, collectionId }) {
    const { t } = useTranslation('products');
    const { modal, isSpecific } = useProductsContext();
    const { defaultCollection, product, collections, isNew, isLoading } = useProduct(productId, collectionId);
    const fixed = useMemo(() => [defaultCollection, ...(collectionId ? [collectionId] : [])], [
        defaultCollection,
        collectionId,
    ]);
    const productExternalLinkEnabled = useFeature('product_external_link');

    usePresenceRemoval();

    if (isLoading) {
        return <Spinner />;
    }

    return (
        <ProductEditForm key={product._id} product={product} isNew={isNew}>
            {({
                values,
                dirty,
                isFetching,
                isRedirecting,
                setFieldValue,
                handleDelete,
                hasExternalUrl,
                toggleLink,
            }) => (
                <StreamlinedScreen
                    modal={modal}
                    preferDynamicBack
                    editing
                    guardChanges={!isRedirecting}
                    title={t(isNew ? 'productEdit.headerTitle' : 'productEdit.headerEditTitle')}
                    rightButton={
                        <SaveButton nopadding btnStyle="transparent">
                            {isNew ? t('ui:labels.save') : t('ui:labels.apply')}
                        </SaveButton>
                    }
                >
                    <StreamlinedScreen.Section top>
                        <ProductPhotosField
                            builderInput
                            name="photos"
                            required
                            // label={t('productEdit.fields.photo')}
                        />
                        <TextField
                            builderInput
                            name="name"
                            required
                            label={t('productEdit.fields.title.label')}
                            placeholder={t('productEdit.fields.title.placeholder')}
                            title={values.slug && t('productEdit.fields.title.title', { slug: values.slug })}
                            maxLength={100}
                        />
                        <PriceCompoundField
                            builderInput
                            name="price"
                            // optional
                            label={t('productEdit.fields.price')}
                            disabledCurrency
                            // disabledCurrency={isNew || values.price?.amount !== null}
                        />
                        <TextareaField
                            builderInput
                            // optional
                            name="description"
                            label={t('productEdit.fields.description.label')}
                            placeholder={t('productEdit.fields.description.placeholder')}
                            maxLength={4000}
                        />

                        <TextareaField
                            builderInput
                            // optional
                            name="notes"
                            label={t('productEdit.fields.notes')}
                            maxLength={4000}
                        />
                    </StreamlinedScreen.Section>

                    <StreamlinedScreen.Section>
                        <CollectionsField
                            builderInput
                            name="_cols"
                            label={t('productEdit.fields.collections.label')}
                            placeholder={t('productEdit.fields.collections.placeholder')}
                            toggleLabel={t('productEdit.fields.collections.add')}
                            disabled={isFetching || !isSpecific}
                            fixed={fixed}
                            collections={collections.items}
                        />
                    </StreamlinedScreen.Section>

                    {(hasExternalUrl || productExternalLinkEnabled) && (
                        <StreamlinedScreen.Section>
                            <ToggleField
                                builderInput
                                name="_hasExternalButton"
                                label={t('productEdit.fields.url.label')}
                                value={hasExternalUrl}
                                required={hasExternalUrl}
                                onChange={toggleLink}
                            />
                            {hasExternalUrl && (
                                <>
                                    <TextField
                                        builderInput
                                        name="externalButton.url"
                                        placeholder={t('productEdit.fields.url.placeholder')}
                                        maxLength={800}
                                        clearable
                                        onBlur={() => {
                                            setFieldValue(
                                                'externalButton.url',
                                                normalizeWebsiteUrl(values.externalButton?.url)
                                            );
                                        }}
                                    />

                                    <TextField
                                        builderInput
                                        // optional
                                        name="externalButton.title"
                                        label={t('productEdit.fields.url.title.label')}
                                        placeholder={t('productEdit.fields.url.title.placeholder')}
                                        maxLength={50}
                                        clearable
                                    />
                                </>
                            )}
                        </StreamlinedScreen.Section>
                    )}

                    <StreamlinedScreen.Section>
                        <ToggleField builderInput name="published" label={<BadgeLabel active={values.published} />} />

                        {!isNew && (
                            <Button
                                fullWidth
                                type="button"
                                btnStyle="danger"
                                disabled={isFetching || dirty}
                                style={{ marginTop: 32 }}
                                onClick={handleDelete}
                            >
                                {t('ui:button.remove')}
                            </Button>
                        )}
                    </StreamlinedScreen.Section>
                </StreamlinedScreen>
            )}
        </ProductEditForm>
    );
}

ProductEdit.propTypes = {
    productId: PropTypes.string,
    collectionId: PropTypes.string,
};
