import { useDomain } from './useDomain';

export const useWebsiteUrl = (website, page) => {
    const { websiteUrl: domainUrl } = useDomain(website);
    const websiteUrl = domainUrl || website.absoluteUrl;

    if (page && page._id !== website.homePageId) {
        return `${websiteUrl}/${page.slug}`;
    }

    return websiteUrl;
};
