import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { Button } from '@mssgme/ui';

import styles from './GuideControl.module.scss';

export function GuideControl({ currentSlideIndex, isLastSlide, isFirstSlide, ...rest }) {
    const { t } = useTranslation('common');

    return (
        <Button className={styles.button} fullWidth {...rest}>
            {isLastSlide ? t('button.getStarted') : t('button.gotIt')}
        </Button>
    );
}

GuideControl.propTypes = {
    currentSlideIndex: PropTypes.number,
    isLastSlide: PropTypes.bool,
    isFirstSlide: PropTypes.bool,
};
