import React from 'react';
import { useTranslation, Trans } from 'react-i18next';
import { Redirect, useLocation } from 'react-router-dom';
import { LinkButton } from '@mssgme/ui';
import { MainLayout } from '../MainPage';
import styles from './AccountDeletedPage.module.scss';

export function AccountDeletedPage() {
    const { t } = useTranslation();
    const location = useLocation();

    // Redirect to home page if user navigates to this page directly
    if (!location.state?.referrer) {
        return <Redirect to="/" />;
    }

    return (
        <MainLayout>
            <div className={styles.root}>
                <div className={styles.inner}>
                    <h1 className={styles.heading}>
                        {t('AccountDeletedPage.heading')}
                    </h1>
                    <div className={styles.description}>
                        <Trans i18nKey="AccountDeletedPage.description" />
                    </div>
                    <LinkButton size="large" fullWidth to="/">
                        {t('AccountDeletedPage.button')}
                    </LinkButton>
                </div>
            </div>
        </MainLayout>
    );
}
