import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';

import styles from './StreamlinedScreen.module.scss';

export const Section = ({ nopadding, top, ...props }) => {
    return (
        <div
            style={typeof top === 'number' ? { marginTop: top } : undefined}
            {...cls([styles.section, nopadding && styles.noPadding, top && styles.top], props)}
        />
    );
};

Section.propTypes = {
    nopadding: PropTypes.bool,
    top: PropTypes.oneOfType([PropTypes.bool, PropTypes.number]),
};
