import React from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiInstagram } from 'react-icons/fi';
import { SiTiktok } from 'react-icons/si';

import { CheckIcon, CodeIcon, CopyIcon, Share2Icon } from '@mssgme/icons';
import { prettifyUrl } from '@mssgme/shared';
import { useCopyText, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ModalPane, useIsMobile } from '@mssgme/ui';
import { useWebsiteUrl } from '../../../../hooks';

import IconQr from '@mssgme/ui/icons/iconQR.svg';

import styles from './ShareNew.module.scss';

export function ShareNew({ title, isPublishedShare }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const { isCopied, copyText } = useCopyText(2500);
    const websiteUrl = useWebsiteUrl(website, page);
    const isMobile = useIsMobile();
    const closePath = generateUrl('..');

    const handleShareClick = async (e) => {
        if ('share' in navigator && isMobile) {
            e.preventDefault();

            try {
                await navigator.share({ url: websiteUrl });
            } catch (e) {
                if (!e.message.includes('AbortError')) {
                    throw e;
                }
            }
        }
    };

    return (
        <ModalPane fullHeight={isPublishedShare} title={t(title)} onClose={closePath}>
            <div className={styles.root}>
                <div className={styles.intro}>
                    {isPublishedShare && <h1 className={styles.title}>{t('Editor.ShareNew.publishedHeading')}</h1>}
                    <p className={styles.subHeading} style={{ marginTop: isPublishedShare ? 10 : 30 }}>
                        {isPublishedShare
                            ? t('Editor.ShareNew.publishedSubHeading')
                            : t('Editor.ShareNew.shareSubHeading')}
                    </p>
                    <a href={websiteUrl} target="_blank" rel="noopener noreferrer" className={styles.link}>
                        {prettifyUrl(websiteUrl)}
                    </a>
                </div>
                <ul className={styles.list}>
                    <li>
                        <Link
                            to={{ pathname: generateUrl('socials/instagram'), state: { backPath: '../..' } }}
                            data-button="btn-share-instagram"
                        >
                            {t('Editor.ShareNew.labels.addToInstagram')}
                            <div className={styles.icon}>
                                <FiInstagram size={24} />
                            </div>
                        </Link>
                    </li>
                    <li>
                        <Link
                            to={{ pathname: generateUrl('socials/tiktok'), state: { backPath: '../..' } }}
                            data-button="btn-share-tiktok"
                        >
                            {t('Editor.ShareNew.labels.addToTikTok')}
                            <div className={styles.icon}>
                                <SiTiktok size={24} />
                            </div>
                        </Link>
                    </li>
                    <li>
                        <div
                            onClick={() => copyText(websiteUrl)}
                            className={cn({ [styles.copyLink]: true, [styles.copied]: isCopied })}
                            data-button="btn-share-copy-link"
                        >
                            <span className={styles.primaryText}>{t('Editor.ShareNew.labels.copyLink')}</span>
                            <span className={styles.notificationText}>{t('Editor.ShareNew.labels.copied')}</span>
                            <div className={styles.icon}>
                                <CopyIcon size={24} />
                            </div>
                            <div className={cn(styles.icon, styles.hidden)}>
                                <CheckIcon size={24} />
                            </div>
                        </div>
                    </li>
                    <li>
                        <Link to={generateUrl('socials')} onClick={handleShareClick} data-button="btn-share">
                            {t('Editor.ShareNew.labels.share')}
                            <div className={styles.icon}>
                                <Share2Icon size={24} />
                            </div>
                        </Link>
                    </li>
                    <li>
                        <Link to={generateUrl('qr')} data-button="btn-share-qr">
                            {t('Editor.ShareNew.labels.qr')}
                            <div className={styles.icon}>
                                <IconQr style={{ display: 'block', width: 24, height: 24 }} />
                            </div>
                        </Link>
                    </li>
                    <li>
                        <Link to={generateUrl('embed')} data-button="btn-share-widget">
                            {t('Editor.ShareNew.labels.widget')}
                            <div className={styles.icon}>
                                <CodeIcon size={24} />
                            </div>
                        </Link>
                    </li>
                </ul>
            </div>
        </ModalPane>
    );
}

ShareNew.propTypes = {
    title: PropTypes.string,
    isPublishedShare: PropTypes.bool,
};

ShareNew.defaultProps = {
    title: 'Editor.ShareNew.heading',
    isPublishedShare: false,
};
