import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextareaField } from '@mssgme/ui';
import { CustomCode } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, useBlockEditor } from '../components';

export function CustomCodeEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: CustomCodeEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.CustomCode.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <TextareaField
                        builderInput
                        placeholder={t('Page.Edit.CustomCode.fields.content.placeholder')}
                        name="content"
                        style={{ marginBottom: 0 }}
                        maxLength={10000}
                        minRows={10}
                        maxRows={20}
                    />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

CustomCodeEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

CustomCodeEdit.block = CustomCode;
