import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { FaMoneyBillAlt, FaPaypal, FaStripe } from 'react-icons/fa';
import { FiChevronsRight, FiTruck } from 'react-icons/fi';

import { useFeature, useNavigationUtils } from '@mssgme/hooks';
import { BlockListItemPreview, ModalPane } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';

const PAYMENTS = [
    { provider: 'cod', icon: FiTruck },
    { provider: 'paypal', icon: FaPaypal },
    { provider: 'stripe', icon: FaStripe },
    { provider: 'wayforpay', icon: FaMoneyBillAlt },
    // { provider: 'robokassa', icon: FaMoneyBillAlt },
    { provider: 'liqpay', icon: FiChevronsRight },
    // { provider: 'transfer', icon: FaMoneyBillAlt },
    { provider: 'other', icon: FaMoneyBillAlt },
];

export default function Payments() {
    const { t } = useTranslation('products');
    const { generateUrl } = useNavigationUtils();
    const { crm } = useProductsContext();
    const otherEnabled = useFeature('payments_other');

    return (
        <ModalPane title={t('SettingsMenu.items.paymentProviders.heading')} onClose={generateUrl('..')}>
            <BlockListItemPreview.List>
                {PAYMENTS.map(({ provider, icon }) => {
                    const isEnabled = !!crm.payments[provider]?.enabled;

                    if (provider === 'other' && !(isEnabled || otherEnabled)) {
                        return null;
                    }

                    return (
                        <BlockListItemPreview
                            key={provider}
                            tag={Link}
                            to={generateUrl(provider)}
                            label={t(`SettingsMenu.items.paymentProviders.${provider}`)}
                            labelIcon={icon}
                            preview={isEnabled ? t('ui:labels.on') : t('ui:labels.off')}
                        />
                    );
                })}
            </BlockListItemPreview.List>
        </ModalPane>
    );
}
