import arrayMove from 'array-move';
import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useMount } from 'react-use';
import { useFormikContext } from 'formik';

import { renderNode } from '../../../helpers';
import { useDataFactoryState } from './useDataFactoryState';
import { ArrayItemEditModal } from './ArrayItem';
import { SortableItemsField } from './SortableItemsField';
import { ArrayItemAddButton } from './ArrayItemAddButton';

ArrayField.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.array,
    label: PropTypes.string,
    addButtonLabel: PropTypes.string,
    addMoreButtonLabel: PropTypes.string,
    modalTitle: PropTypes.string,
    context: PropTypes.any,
    sortable: PropTypes.bool,
    initialEdit: PropTypes.bool,
    isInitialDirty: PropTypes.bool,
    isEditable: PropTypes.bool,
    isDeletable: PropTypes.bool,
    addRestriction: PropTypes.string,
    Item: PropTypes.elementType.isRequired,
    children: PropTypes.any,
    getItemKey: PropTypes.func,
    getItemCollection: PropTypes.func,
    getItemSortable: PropTypes.func,
    factory: PropTypes.func,
    applyTransform: PropTypes.func,
    validationSchema: PropTypes.object,
    renderFactory: PropTypes.func,
    maxItems: PropTypes.number,
};

ArrayField.defaultProps = {
    sortable: true,
    labels: {},
    isEditable: true,
    isDeletable: true,
    isInitialDirty: true,
};

/**
 * Field with array of items as value;
 *
 * @template T Item shape
 * @param {ElementType} Item A component, that would be used to render items in the list
 * @param {() => any} factory Factory method to create new items with.
 * @param {ReactNode} children Form contents for the edit dialog. * @param name
 * @param {T[]|undefined} [value] Override the value, that is currently in the form data
 * @param {boolean} [initialEdit] If "`true`", immediately show creation dialog, without user clicking "Add item"
 * @param {boolean} [isInitialDirty=true] If "`true`", upon edit dialog opening, "`Save`" button would be enabled even
 * if no data has been changed yet
 * @param {boolean} [isEditable=true] Can items bee edited ('Add item' button and edit modal disabled if `isEditable` is "`false`")
 * @param {booleean} [isDeletable=true] Can items be deleted (would also show deletion action next to items, if true)
 * @param {boolean} [sortable] Can this list be sorted
 * @param {(item: T) => T} [applyTransform] Apply a change to the item after creating/editing (like normalizing data)
 * @param {(item: T, index: number) => any} [getItemKey] Return React "`key`" for items in the array.
 * `item._id || index`is used, if `getItemKey` is not provided
 * @param {(item: T) => boolean} [getItemSortable] Returns `false` if you want to make specific item(s) not sortable
 * @param {(item: T) => number} [getItemCollection] Items can be grouped by `collection` for sorting. They would remain
 * within the specified groups even if user tries to drag them past the group
 * @param {string} [label] Field label
 * @param {string|boolean|ReactNode} [modalTitle] Edit dialog title (or "`false`" to disable the modal header entirely)
 * @param {string} [addButtonLabel] Custom label for "Add item" button
 * @param {string} [addMoreButtonLabel] Custom label for "Add item" button if there are already some items in the array
 * @param {Schema} [validationSchema] Validation schema, applied to item on creation/edit.
 * @param {ReactNode | ((helpers: {}) => ReactNode)} [renderFactory] Renders item edit dialog. Can be a JSX node or factory
 * function, which accepts ArrayFieldHelpers object with two helper methods:
 *
 *     `setEditingIndex(index)` - edit specific item in the array or close the edit dialog (if index <= -1)
 *
 *     `setFactory(factory)` - open edit modal for new item (pass the item factory to `setFactory` as first argument)
 * @param {number} [maxItems] Do not show `Add item` button if array has eq. or more than `maxItems` items
 * @param {string} [addRestriction] "`*:create`" restriction. Can be used to limit creation of items (i.e. paywall after
 * some count)
 * @param {any} [context] Context, that will be passed to every "`Item`" as a "`context`" prop.
 * @param {object} [rest] Optional outer wrapper props
 * @returns {JSX.Element} Array field node
 */
export default function ArrayField({
    name,
    value,
    initialEdit,
    isInitialDirty,
    isEditable,
    isDeletable,
    sortable,
    Item,
    applyTransform,
    getItemKey,
    getItemCollection,
    getItemSortable,
    factory: rawFactory,
    children,
    label,
    addButtonLabel,
    modalTitle,
    addMoreButtonLabel,
    validationSchema,
    renderFactory,
    maxItems,
    addRestriction,
    context,
    ...rest
}) {
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const items = value || getFieldMeta(name).value || [];
    const currentTotal = items.length;
    const [editingIndex, setEditingIndex] = useState((initialEdit && !currentTotal) ? 0 : -1);
    const [factory, safeSetFactory] = useDataFactoryState(rawFactory);
    const isEditing = isEditable && editingIndex >= 0;
    const isNew = currentTotal === editingIndex;
    const canAddMore = isEditable && ((!maxItems) || maxItems > currentTotal);

    useMount(() => {
        if (initialEdit) {
            handleAdd();
        }
    }, [initialEdit]);

    const handleAdd = useCallback(() => setEditingIndex(currentTotal), [currentTotal]);
    const handleEdit = useCallback(
        (item) => {
            safeSetFactory(item);
            setEditingIndex(items.indexOf(item));
        },
        [items]
    );
    const handleApply = useCallback(
        (values) => {
            setEditingIndex(-1);

            if (rawFactory) {
                safeSetFactory(rawFactory);
            } else {
                safeSetFactory(null);
            }

            if (values) {
                let transformed;

                if (applyTransform) {
                    if (false !== (transformed = applyTransform(values, editingIndex))) {
                        values = transformed || values;
                    } else {
                        return;
                    }
                }

                setFieldValue(`${name}.${editingIndex}`, values, true);
                setFieldTouched(`${name}.${editingIndex}`, true, true);
            }
        },
        [editingIndex, applyTransform, rawFactory]
    );
    const handleDeletion = useCallback(
        (item) => {
            setFieldValue(name, items.filter((i) => i !== item), true);
            setFieldTouched(name, true, true);
            setEditingIndex(-1);
        },
        [name, items]
    );

    const onSortStart = useCallback((_, e) => e?.preventDefault?.(), []);
    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (!sortable || oldIndex === newIndex) {
                return;
            }

            setFieldValue(name, arrayMove(items, oldIndex, newIndex));
        },
        [sortable, items]
    );

    return (
        <div {...rest}>
            <SortableItemsField
                name={name}
                label={label}
                items={items}
                sortable={sortable}
                Item={Item}
                context={context}
                getItemKey={getItemKey}
                getItemCollection={getItemCollection}
                getItemSortable={getItemSortable}
                onSortStart={onSortStart}
                onSortEnd={onSortEnd}
                onEdit={isEditable ? handleEdit : null}
                onDelete={isDeletable ? handleDeletion : null}
            />

            {canAddMore && (
                <ArrayItemAddButton
                    name={name}
                    items={items}
                    addButtonLabel={addButtonLabel}
                    addMoreButtonLabel={addMoreButtonLabel}
                    disabled={isEditing}
                    restriction={addRestriction}
                    onClick={handleAdd}
                />
            )}

            {isEditing && (
                factory ? (
                    <ArrayItemEditModal
                        fullWidth
                        title={modalTitle}
                        isNew={isNew}
                        isInitialDirty={isInitialDirty}
                        name={`${name}.${editingIndex}`}
                        factory={factory}
                        validationSchema={validationSchema}
                        onApply={handleApply}
                        onDelete={isDeletable ? handleDeletion : null}
                    >
                        {children}
                    </ArrayItemEditModal>
                ) : (
                    renderNode(renderFactory, { setFactory: safeSetFactory, setEditingIndex })
                )
            )}
        </div>
    );
}
