import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';

import { cls } from '@mssgme/helpers';
import { getThemeOverride } from '@mssgme/shared';
import { useWorkspace } from '@mssgme/hooks';
import { LandingsPropTypes } from '@mssgme/ui';
import { Page } from '@mssgme/userpage-core';

import { websiteFactory } from '../../../../hooks';

import styles from './ChooseTemplate.module.scss';

export const TemplateSlide = ({
    index,
    template,
    themeId,
    scale,
    offset,
    isPrev,
    isNext,
    onClick,
    wrapperRef,
    contentRef,
    ...rest
}) => {
    const { _id: workspace } = useWorkspace();
    const { website, page } = useMemo(() => {
        const theme = themeId ? getThemeOverride(template.theme, themeId) : template.theme;
        const page = { ...template, theme };

        return {
            website: websiteFactory(page, { workspace, theme, uploads: template.uploads, branding: false }),
            page,
        };
    }, [template, themeId]);
    const style = useMemo(
        () => ({
            transformOrigin: `${isPrev ? '100%' : isNext ? 0 : '50%'} 0`,
            transform: `scale(${scale}) translateX(${isPrev ? offset + 'px' : isNext ? -offset + 'px' : 0})`,
            borderRadius: 20 / scale || 1,
        }),
        [isPrev, isNext, scale]
    );
    const handleClick = useCallback(() => onClick && onClick(index), [index, onClick]);

    return (
        <div {...cls(styles.slideContent, rest)}>
            <div ref={wrapperRef} className={styles.slideWrapper}>
                <div ref={contentRef} className={styles.pageWrapper} style={style} onClick={handleClick}>
                    <div className={styles.page}>
                        {themeId || !template.thumbnailUrl ? (
                            <Page embedded interactive={false} website={website} page={page} />
                        ) : (
                            <img alt={template.name} src={template.thumbnailUrl} className={styles.pageThumbnail} />
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

TemplateSlide.propTypes = {
    index: PropTypes.number.isRequired,
    template: LandingsPropTypes.template.isRequired,
    themeId: PropTypes.string,
    scale: PropTypes.number.isRequired,
    offset: PropTypes.number.isRequired,
    isPrev: PropTypes.bool,
    isNext: PropTypes.bool,
    onClick: PropTypes.func,
    wrapperRef: PropTypes.func,
    contentRef: PropTypes.func,
};
