import React, { useMemo } from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { filters } from '@mssgme/helpers';
import { useCollections, useFilter, useListFilter } from '@mssgme/hooks';
import { Button, Feature, FloatingActions, ScrollableBox, StreamlinedScreen, SearchInput } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { CollectionsListDraggable, FilterMessage } from '../../../UI';

const byName = (t) => {
    const translated = t('labels.allProducts');

    return (value) => {
        const isEqual = filters.isSubstring(value);

        return (item) => {
            // this fixes search for 'default' collection, as it's actual 'name' prop value differs from displayed label
            if (item.default && isEqual(translated)) {
                return true;
            }

            return isEqual(item.name || '');
        };
    };
};

export function Collections() {
    const { t } = useTranslation('products');
    const { router, specifier, isSpecific, modal } = useProductsContext();
    const { isFetching, items } = useCollections(specifier);
    const { filter, handleFilter } = useListFilter(() => ({ ...specifier, name: '' }));
    const [collectionsItems] = useFilter(items, filter, useMemo(() => ({ name: byName(t) }), [t]));

    return (
        <StreamlinedScreen
            title={t('Collections.title')}
            topPanelChildren={<SearchInput name="name" value={filter.name} onChange={handleFilter} />}
            backPath={router.toHome()}
            modal={modal}
        >
            <Feature rule="collections:view">
                <ScrollableBox nopadding style={{ flex: 1, marginTop: 8 }}>
                    {collectionsItems.length > 0 ? (
                        <CollectionsListDraggable
                            collections={collectionsItems}
                            isFetching={isFetching}
                            draggable={false}
                        />
                    ) : filter.name ? (
                        <FilterMessage
                            title={t('labels.notFound.collections.heading')}
                            description={t('labels.notFound.collections.description')}
                            style={{ margin: 'auto' }}
                        />
                    ) : (
                        <FilterMessage
                            title={t('Collections.emptyTitle')}
                            description={t('Collections.emptyDescription')}
                            style={{ margin: 'auto' }}
                        />
                    )}
                </ScrollableBox>

                {isSpecific && (
                    <FloatingActions padded style={{ marginTop: 8 }}>
                        <Button fullWidth tag={Link} to={router.collection.toNew()}>
                            + {t('Collections.tipAddCollection')}
                        </Button>
                    </FloatingActions>
                )}
            </Feature>
        </StreamlinedScreen>
    );
}
