import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { FiCopy, FiTrash2 } from 'react-icons/fi';
import { useTranslation } from 'react-i18next';

import { BlockListItemPreview, LandingsPropTypes, PortalModalSheet, RestrictionScope } from '@mssgme/ui';
import { useEventCallback } from '@mssgme/hooks';
import { BlockDeletionModal, useBlockCloning, useBlockDeletion } from '../BlockActions';

import styles from './BlockEditForm.module.scss';

export const BlockEditActions = ({ isFetching, websiteId, pageId, block, onSuccess, ...rest }) => {
    const { t } = useTranslation('landings');
    const restrictionSubjects = useMemo(
        () => ({
            create: { rule: 'page-blocks:create', subject: block },
            delete: { rule: 'page-blocks:delete', subject: block },
        }),
        [block]
    );

    const { isShown: isDeletionShown, initiate: initiateDeletion, handle: handleDeletion } = useBlockDeletion(
        onSuccess && (() => onSuccess({ action: 'remove' }))
    );
    const { pending: cloningIsPending, run: initiateCloning } = useBlockCloning(
        onSuccess && (() => onSuccess({ action: 'clone' }))
    );
    const handleDeleting = useEventCallback(() => initiateDeletion({
        websiteId,
        pageId,
        payload: { blockId: [block._id] },
    }));
    const handleCloning = useEventCallback(() => initiateCloning({
        websiteId,
        pageId,
        payload: { blockId: [block._id] },
    }));

    const disabled = isFetching || isDeletionShown || cloningIsPending;

    return (
        <>
            <PortalModalSheet
                fullWidth
                title={t('labels.block.actions.title')}
                portalTo="#visual-builder"
                {...rest}
            >
                <RestrictionScope fullWidth>
                    <ul className={cn(styles.menuWrapper, disabled && styles.disabled)}>
                        <li className={styles.menuListItem}>
                            <BlockListItemPreview
                                className={styles.menuItem}
                                tag="div"
                                label={t('labels.block.actions.duplicate')}
                                labelIcon={FiCopy}
                                restrict={restrictionSubjects.create}
                                disabled={disabled}
                                onClick={handleCloning}
                            />
                        </li>
                        <li className={styles.menuListItem}>
                            <BlockListItemPreview
                                className={styles.menuItem}
                                tag="div"
                                label={t('labels.block.actions.delete')}
                                labelIcon={FiTrash2}
                                labelColor="#dc5960"
                                iconColor="#dc5960"
                                restrict={restrictionSubjects.delete}
                                disabled={disabled}
                                onClick={handleDeleting}
                            />
                        </li>
                    </ul>
                </RestrictionScope>
            </PortalModalSheet>

            {isDeletionShown && <BlockDeletionModal handle={handleDeletion} />}
        </>
    );
};

BlockEditActions.propTypes = {
    websiteId: PropTypes.string.isRequired,
    pageId: PropTypes.string.isRequired,
    block: LandingsPropTypes.block.isRequired,
    onSuccess: PropTypes.func,
    isFetching: PropTypes.bool,
};
