import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';
import { FiUpload, FiTrash2 } from 'react-icons/fi';
import { motion, AnimatePresence } from 'framer-motion';

import { ImageField, RangeSliderField } from '@mssgme/ui';
import { useUploads, MediaSizes } from '@mssgme/userpage-core';
import ActionButton from './ActionButton';

import styles from './BackgroundPageImage.module.scss';

export const UploadActions = ({ onChange, accept, name, value, isUploading }) => {
    const { t } = useTranslation('landings');

    return (
        <ActionButton
            compact={!value}
            label={t(value ? 'ui:labels.change' : 'ui:labels.upload')}
            icon={FiUpload}
            isFetching={isUploading}
        >
            <input
                value=""
                type="file"
                className={styles.fileInput}
                accept={accept}
                name={name}
                onChange={onChange}
            />
        </ActionButton>
    );
};

UploadActions.propTypes = {
    onChange: PropTypes.func.isRequired,
    accept: PropTypes.string,
    name: PropTypes.string.isRequired,
    value: PropTypes.string,
    isUploading: PropTypes.bool,
};

export const BackgroundPageImageField = ({ opacityName, imageName, colorName, upload, accept }) => {
    const { t } = useTranslation('landings');
    const { getMediaSizeUrl } = useUploads();
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value: path } = getFieldMeta(imageName);
    const { value: opacity } = getFieldMeta(opacityName);
    const { value: bgColor } = getFieldMeta(colorName);
    const imageUrl = getMediaSizeUrl(path, MediaSizes.Large);

    const handleChange = useCallback(({ path }) => setFieldValue(imageName, path), [imageName]);
    const handleImageRemove = useCallback(() => {
        setFieldValue(imageName, null);
        setFieldValue(opacityName, 1);
    }, [imageName, opacityName]);

    return (
        <motion.div className={styles.root} style={{ background: bgColor }}>
            <motion.div className={styles.preview}>
                <AnimatePresence>
                    {imageUrl && (
                        <>
                            <motion.div
                                className={styles.imageContainer}
                                initial={{ opacity: 0, scale: 1.1 }}
                                animate={{ opacity: 1, scale: 1 }}
                                enter={{ opacity: 1, scale: 1 }}
                                exit={{ opacity: 0, scale: 1.1 }}
                                transition={{ ease: 'easeInOut' }}
                            >
                                <img src={imageUrl} alt="background image" style={{ opacity }} />
                            </motion.div>
                            <motion.div
                                className={styles.opacityBlock}
                                initial={{ opacity: 0, y: '-100%' }}
                                animate={{ opacity: 1, y: 0 }}
                                enter={{ opacity: 1, y: 0 }}
                                exit={{ opacity: 0, y: '-100%' }}
                                transition={{ ease: 'easeInOut' }}
                            >
                                <RangeSliderField name={opacityName} min={0} max={1} step={0.01} showLabels={false} />
                                <span>{t('Page.fields.opacity.label')}</span>
                            </motion.div>
                        </>
                    )}
                </AnimatePresence>
                <motion.div
                    layout
                    className={styles.innerContent}
                    style={{ alignItems: imageUrl ? 'flex-end' : 'center' }}
                >
                    <motion.div layout className={styles.actionsContainer}>
                        <ImageField
                            accept={accept}
                            as={UploadActions}
                            name={imageName}
                            upload={upload}
                            onChange={handleChange}
                            style={{ marginBottom: 0 }}
                        />
                        <AnimatePresence>
                            {imageUrl && (
                                <motion.div
                                    style={{ overflow: 'hidden', height: 'auto', opacity: 1 }}
                                    exit={{ height: 0, opacity: 0 }}
                                    transition={{ ease: 'easeInOut' }}
                                >
                                    <ActionButton
                                        label={t('ui:labels.remove')}
                                        icon={FiTrash2}
                                        color="#dc5960"
                                        style={{ marginTop: 10 }}
                                        onClick={handleImageRemove}
                                    />
                                </motion.div>
                            )}
                        </AnimatePresence>
                    </motion.div>
                </motion.div>
            </motion.div>
        </motion.div>
    );
};

BackgroundPageImageField.propTypes = {
    opacityName: PropTypes.string.isRequired,
    imageName: PropTypes.string.isRequired,
    colorName: PropTypes.string.isRequired,
    upload: PropTypes.func.isRequired,
    accept: PropTypes.string.isRequired,
};
