import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { memoize } from 'lodash';

import { cls } from '@mssgme/helpers';
import { useRestriction } from '@mssgme/hooks';
import { DragHandle, LandingsPropTypes, NoAccessOverlay } from '@mssgme/ui';
import { Insets, useRenderer } from '@mssgme/userpage-core';
import { InteractiveBlock } from './InteractiveBlock';

import styles from './SortableBlocksList.module.scss';

// this is memoizable only because Insets.computeComponentInsets returns same values for similar structures
const bakeHasher = (insets, scale) => `${Insets.hashInset(insets.margin)}x${scale}`;
const bakeVerticalInsetsStyle = memoize(
    (insets, scale) =>
        Insets.resolveInsets({
            margin: Insets.removeHorizontalInsets(Insets.scaleInsets(Insets.unpackInsets(insets.margin), scale)),
        }),
    bakeHasher
);
const bakeHorizontalInsets = memoize(
    (insets, scale) => ({
        padding: 0,
        margin: Insets.removeVerticalInsets(Insets.scaleInsets(Insets.unpackInsets(insets.margin), scale)),
    }),
    bakeHasher
);

const computeWrapperStyle = (block, index, { blocks, theme, scale, isScaled }) =>
    bakeVerticalInsetsStyle(Insets.computeComponentInsets(blocks, block, index, theme), isScaled ? scale : 1);

export const computeComponentInsets = (block, index, { blocks, theme, scale, isScaled }) =>
    bakeHorizontalInsets(Insets.computeComponentInsets(blocks, block, index, theme), isScaled ? scale : 1);

const SortableBlocksItem = SortableElement(
    ({ block, blockIndex, options, onBlockClick, onBlockDoubleTap, onBlockToggle, renderBlock }) => {
        if (block.nested) {
            return <div />;
        }

        const { warning } = useRestriction('page-blocks', block);
        const wrapperStyle = computeWrapperStyle(block, blockIndex, options);
        const content = renderBlock(block, blockIndex);

        return (
            <InteractiveBlock
                // todo: memoize?
                className={cn(styles.wrapper, options.isViewMode && styles.interactive, block.nested && styles.nested)}
                block={block}
                index={blockIndex}
                scale={options.scale}
                isScaled={options.isScaled}
                isInteractive={options.isViewMode}
                isSelected={options.selection.includes(block._id)}
                isAnimated={options.isAnimated}
                warning={options.isWebsitePublished && warning}
                style={wrapperStyle}
                onClick={onBlockClick}
                onDoubleTap={onBlockDoubleTap}
                onToggle={onBlockToggle}
            >
                {warning && options.isWebsitePublished ? (
                    <NoAccessOverlay warning={warning}>{content}</NoAccessOverlay>
                ) : (
                    content
                )}
            </InteractiveBlock>
        );
    }
);

export const SortableBlocksList = SortableContainer(
    ({ innerRef, config, draggable, onBlockClick, onBlockDoubleTap, onBlockToggle, children, ...rest }) => {
        const renderBlock = useRenderer();

        return (
            <div ref={innerRef} id="blocks-container" {...cls(styles.root, rest)}>
                {children}

                {config.blocks.map((block, index) => (
                    <SortableBlocksItem
                        key={block._id || `${block.kind}-${index}`}
                        index={index}
                        block={block}
                        blockIndex={index}
                        options={config}
                        disabled={!draggable}
                        renderBlock={renderBlock}
                        onBlockClick={onBlockClick}
                        onBlockDoubleTap={onBlockDoubleTap}
                        onBlockToggle={onBlockToggle}
                    />
                ))}
            </div>
        );
    }
);

SortableBlocksList.propTypes = {
    ...SortableBlocksList.propTypes,
    innerRef: PropTypes.any.isRequired,
    config: PropTypes.shape({
        blocks: PropTypes.arrayOf(LandingsPropTypes.block).isRequired,
        selection: PropTypes.arrayOf(PropTypes.string).isRequired,
        isViewMode: PropTypes.bool,
        isMobileMode: PropTypes.bool,
        isAnimated: PropTypes.bool,
        isWebsitePublished: PropTypes.bool,
        theme: PropTypes.object.isRequired,
    }).isRequired,
    draggable: PropTypes.bool,
    onBlockDoubleTap: PropTypes.func,
    onBlockClick: PropTypes.func.isRequired,
    onBlockToggle: PropTypes.func.isRequired,
};

SortableBlocksList.defaultProps = {
    ...SortableBlocksList.defaultProps,
    axis: 'y',
    lockAxis: 'y',
    lockOffset: ['50%', '50%'],
    pressDelay: 0,
    lockToContainerEdges: true,
    helperClass: styles.dragged,
    shouldCancelStart: DragHandle.shouldCancelStart,
};
