import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useEffect } from 'react';

import { cls } from '@mssgme/helpers';
import { SecondLevelScreenHeader } from '../SecondLevelScreenHeader';

import styles from './SecondLevelScreen.module.scss';

export const SecondLevelScreen = ({
    title,
    children,
    backPath,
    topPanelChildren,
    titleRightContent,
    light,
    inverse,
    nopadding,
    preferDynamicBack,
    forceTop,
    slimTop,
    ...rest
}) => {
    // TODO: fix stuck scroll
    useEffect(() => {
        const mainRootEl = document.querySelector('#mainRoot > div');

        if (mainRootEl && forceTop) {
            mainRootEl.scrollTop = 0;
        }
    }, [forceTop]);

    return (
        <div
            {...cls(
                cn(styles.root, light && styles.light, inverse && styles.inverse, !nopadding && styles.padded),
                rest
            )}
        >
            <div className={cn(styles.topPanel, slimTop && styles.slim)} {...rest}>
                <SecondLevelScreenHeader
                    title={title}
                    backPath={backPath}
                    titleRightContent={titleRightContent}
                    preferDynamicBack={preferDynamicBack}
                    slim={slimTop}
                    {...rest}
                />
                {topPanelChildren}
            </div>
            <div className={styles.content}>{children}</div>
        </div>
    );
};

SecondLevelScreen.propTypes = {
    ...SecondLevelScreenHeader.propTypes,
    topPanelChildren: PropTypes.any,
    forceTop: PropTypes.bool,
    slimTop: PropTypes.bool,
    light: PropTypes.bool,
    nopadding: PropTypes.bool,
};

SecondLevelScreen.defaultProps = {
    topPanelChildren: null,
    forceTop: true,
    backPath: '',
};
