import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { ImMagicWand } from 'react-icons/im';
import { useTranslation } from 'react-i18next';

import { useActions, useBooleanHandler } from '@mssgme/hooks';
import { Button, EnhancedFormik, FormRow, NotificationCenter, PortalModalSheet, TextareaField, Form } from '@mssgme/ui';

export const BlockRequestDialog = ({ websiteId, ...props }) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const [isOpen, handleOpen, handleClose] = useBooleanHandler();

    const handleSubmit = useCallback(async (payload) => {
        if (!payload) {
            return;
        }

        await api.websites.services.requestABlock({ websiteId, payload });

        NotificationCenter.getInstance().success(t('BlockRequestDialog.labels.submitted'), {
            showClose: true,
        });
    }, [t, websiteId]);

    return (
        <Button beforeIcon={<ImMagicWand />} onClick={handleOpen} {...props}>
            {t('BlockRequestDialog.labels.button')}

            {isOpen && (
                <PortalModalSheet
                    title={t('BlockRequestDialog.heading')} onClosing={handleSubmit} onClose={handleClose}
                >
                    {({ handleClose }) => (
                        <EnhancedFormik initialValues={{ websiteId, message: '' }} onSubmit={handleClose}>
                            {(formik) => (
                                <Form onSubmit={formik.handleSubmit}>
                                    <TextareaField
                                        builderInput
                                        name="message"
                                        minRows={5}
                                        placeholder={t('BlockRequestDialog.labels.message.placeholder')}
                                        maxLength={5000}
                                    />

                                    <FormRow>
                                        <Button
                                            fullWidth
                                            type="submit"
                                            isFetching={formik.isSubmitting}
                                            onClick={formik.handleSubmit}
                                        >
                                            {t('BlockRequestDialog.labels.submit')}
                                        </Button>
                                    </FormRow>
                                </Form>
                            )}
                        </EnhancedFormik>
                    )}
                </PortalModalSheet>
            )}
        </Button>
    );
};

BlockRequestDialog.propTypes = {
    websiteId: PropTypes.string.isRequired,
};
