import { useEffect } from 'react';
import { useAutoSave, useEnhancedFormik } from '@mssgme/ui';

export const useInlineFormInput = ({ name, initialValues, onSubmit }) => {
    const formik = useEnhancedFormik({
        initialValues,
        onSubmit,
    });
    const { getFieldProps } = useAutoSave(formik);

    const inputProps = getFieldProps(name);

    useEffect(() => {
        if (inputProps.error) {
            formik.resetForm({ values: initialValues });
        }
    }, [inputProps.error]);

    return {
        bind: inputProps,
        formik,
    };
};
