import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Accordion } from '@mssgme/userpage-core';
import {
    AccordionBlockSchema,
    BlockEditForm,
    EditStylingForm,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';
import { AccordionItemsField } from './AccordionItemsField';

export function AccordionEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: AccordionEdit });

    return (
        <BlockEditForm {...bind} validationSchema={AccordionBlockSchema} title={t('Page.Edit.Accordion.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview
                        page={page}
                        style={{ marginBottom: 16 }}
                        contentStyle={{ overflow: 'hidden', padding: 0 }}
                    >
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <AccordionItemsField name="items" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

AccordionEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

AccordionEdit.block = Accordion;
