import PropTypes from 'prop-types';
import React from 'react';

import { Button, LandingsPropTypes } from '@mssgme/ui';

import { usePublish } from '../../hooks';

export const PublishButton = ({ publishedUrl, website, ...rest }) => {
    const { pending, run: handlePublish } = usePublish(website, publishedUrl);

    if (website.enabled) {
        return null;
    }

    return (
        <Button onClick={handlePublish} isFetching={pending} {...rest} />
    );
};

PublishButton.propTypes = {
    publishedUrl: PropTypes.string,
    website: LandingsPropTypes.website.isRequired,
};
