import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useAsync } from 'react-use';
import { Form } from 'formik';

import {
    Checkbox,
    EnhancedFormik,
    FieldTip,
    LoadingIndicator,
    PortalModalSheet,
    Row,
    Textarea,
    TextField,
} from '@mssgme/ui';
import { useActions } from '@mssgme/hooks';
import { WebhookTestButton } from './WebhookTestButton';

export const WebhookConnectorForm = ({ endpoint, onChange }) => {
    const { t } = useTranslation();
    const { api } = useActions();
    const [withPurchase, setPurchase] = useState(false);
    const initialValues = useMemo(() => ({ enabled: true, endpoint: endpoint || '' }));

    const { loading: isLoading, value: feedback } = useAsync(
        async () => api.endpoints.sample({ query: { withPurchase } }),
        [withPurchase]
    );

    const handlePurchase = useCallback(({ target }) => setPurchase(!!target.checked), []);

    return (
        <PortalModalSheet title={t('ConnectWebhook.heading')} onClose={onChange}>
            {({ handleClose }) => (
                <EnhancedFormik initialValues={initialValues} onSubmit={handleClose}>
                    {({ values }) => (
                        <Form autoComplete="off">
                            <TextField builderInput name="endpoint" label={t('ConnectWebhook.labels.endpoint')} />
                            <FieldTip tipId="common:ConnectWebhook.hint" />

                            <Row>
                                <label>{t('ConnectWebhook.labels.example')}</label>

                                <div style={{ position: 'relative' }}>
                                    {!isLoading ? (
                                        <Textarea
                                            builderInput
                                            style={{
                                                fontSize: '60%',
                                                lineHeight: 1.2,
                                                fontFamily: 'monospace',
                                                marginBottom: 16,
                                            }}
                                            minRows={20}
                                            maxRows={20}
                                            spellCheck="false"
                                            readOnly
                                            value={JSON.stringify(feedback, null, 3)}
                                        />
                                    ) : (
                                        <LoadingIndicator />
                                    )}
                                </div>
                            </Row>

                            <Checkbox
                                builderInput
                                styled="toggle"
                                placeholder={t('ConnectWebhook.fields.withPurchase')}
                                checked={withPurchase}
                                onChange={handlePurchase}
                            />

                            <WebhookTestButton key={values.endpoint} withPurchase={withPurchase} disabled={!feedback} />
                        </Form>
                    )}
                </EnhancedFormik>
            )}
        </PortalModalSheet>
    );
};

WebhookConnectorForm.propTypes = {
    endpoint: PropTypes.string,
    onChange: PropTypes.func.isRequired,
};
