import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation, Trans } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug, useWebsitePage } from '@mssgme/hooks';
import { Button, CopyLink, ModalPane } from '@mssgme/ui';
import { useWebsiteUrl } from '../../../../hooks';

import InstaTipImage from '../../../../img/instagramTip.jpg';

import styles from './InstagramShareNew.module.scss';

export function InstagramShareNew({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl, state: { backPath: historyBackPath } = {} } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const shareUrl = useWebsiteUrl(website, page);
    const closePath = generateUrl(historyBackPath || backPath);

    return (
        <ModalPane title={t('Editor.InstagramShareNew.title')} onClose={closePath}>
            <div className={styles.content}>
                <ol>
                    <li>
                        <Trans t={t} i18nKey="landings:Editor.InstagramShareNew.steps.1" />
                    </li>
                </ol>
                <CopyLink className={styles.userLink} href={shareUrl} />
                <ol start="2">
                    <li>
                        <Trans t={t} i18nKey="landings:Editor.InstagramShareNew.steps.2" />
                    </li>
                    <li>
                        <Trans t={t} i18nKey="landings:Editor.InstagramShareNew.steps.3" />
                    </li>
                </ol>
                <img className={styles.tipImg} src={InstaTipImage} alt="Tip" />
                <Button
                    fullWidth
                    tag="div"
                    btnStyle="transparent"
                    style={{ fontSize: 16, marginBottom: 16 }}
                    onClick={() => window.open('https://www.instagram.com/', '_blank')}
                >
                    {t('Editor.InstagramShareNew.openInstagram')}
                </Button>
            </div>
        </ModalPane>
    );
}

InstagramShareNew.propTypes = {
    backPath: PropTypes.string,
};

InstagramShareNew.defaultProps = {
    backPath: '..',
};
