import { useCallback, useRef } from 'react';
import { useHistory } from 'react-router-dom';
import { useAsyncFn } from 'react-use';
import { pick } from 'lodash';

import { getDefaultTheme, randomSlug } from '@mssgme/shared';
import { useActions } from './useActions';

const getPayload = (fields) => pick(fields, ['enabled', 'name', 'slug', 'seo', 'tracking', 'theme']);

export const usePagesFactory = (websiteId) => {
    const history = useHistory();
    const { api } = useActions();
    const [state, run] = useAsyncFn(async (options = {}) => {
        const { templateId, redirect = true } = options;
        const { name = 'Page', theme = getDefaultTheme(), ...payload } = getPayload(options);
        const result = await api.pages.create({
            websiteId,
            payload: {
                enabled: true,
                slug: randomSlug(5),
                templateId,
                name,
                theme,
                ...payload,
            },
        });

        if (redirect) {
            history.push({
                pathname: `/pages/-/${websiteId}/${result._id}/setup`,
            });
        }

        return result;
    });

    const ref = useRef(run);

    const create = useCallback((options) => ref.current(options), []);

    ref.current = run;

    return {
        run,
        create,
        ...state,
    };
};
