import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useTemplate } from '@mssgme/hooks';
import { TagsField, TextField } from '@mssgme/ui';
import { TemplateEditForm } from '../../../../UI';

export function TemplateTags() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { templateId } = useParams();
    const template = useTemplate(templateId);

    return (
        <TemplateEditForm
            title={t('Template.Edit.Tags.heading')}
            template={template}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {() => (
                <>
                    <TagsField
                        builderInput
                        name="primaryTags"
                        label={t('Template.fields.tags.label')}
                        placeholder={t('Template.fields.tags.placeholder')}
                    />

                    {/*<TagsField*/}
                    {/*    builderInput*/}
                    {/*    required*/}
                    {/*    name="secondaryTags"*/}
                    {/*    label={t('Template.fields.secondaryTags.label')}*/}
                    {/*    placeholder={t('Template.fields.secondaryTags.placeholder')}*/}
                    {/*/>*/}
                </>
            )}
        </TemplateEditForm>
    );
}
