import cn from 'classnames';
import moment from 'moment';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { uniqBy } from 'lodash';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { formatPhone } from '@mssgme/shared';
import { printPrice } from '@mssgme/userpage-core';
import { ClipboardIcon } from '@mssgme/icons';
import { TagsList } from './TagsList';

import styles from './FeedbackItem.module.scss';

const statusMap = {
    new: 'new',
    pending: 'processing',
    paid: 'accepted',
    failed: 'rejected',
};

export const FeedbackItem = ({ item, to }) => {
    const { t } = useTranslation('landings');
    const { date, contacts } = useMemo(
        () => ({
            date: moment(item.createdAt).local(),
            contacts: uniqBy(
                [].concat(...['name', 'phone', 'email'].map((type) => item.data.filter((d) => type === d.type))),
                'type'
            ).map((field, index) => ({
                key: index,
                value: field.type === 'phone' ? formatPhone(field.value) : field.value,
            })),
        }),
        [item.data]
    );

    const itemValue = item.purchase?.total || item.value;
    const itemValueStatus = item.purchase?.status || 'paid';
    const InnerTag = to ? Link : 'div';

    return (
        <InnerTag className={styles.wrapped} to={to}>
            <div className={styles.header}>
                <span>#{item.order}</span>
                <span>{date.format('D MMM HH:mm')}</span>
            </div>
            <div className={styles.content}>
                <ul className={styles.list}>
                    {contacts.map(({ key, value }) => (
                        <li key={key}>{value}</li>
                    ))}
                </ul>
                <div className={styles.contentRight}>
                    <span className={cn(styles.status, styles[item.status])}>
                        {t(`Feedback.View.List.labels.state.${item.status}`)}
                    </span>

                    {!!itemValue && (
                        <span className={cn(styles.status, styles.total, styles[statusMap[itemValueStatus]])}>
                            {printPrice(itemValue)}
                        </span>
                    )}
                </div>
            </div>
            {item.notes && (
                <div className={styles.notes}>
                    <ClipboardIcon />
                    <span>{item.notes}</span>
                </div>
            )}
            {!!item?.tags.length && (
                <TagsList className={styles.tags} tags={item.tags} />
            )}
        </InnerTag>
    );
};

FeedbackItem.propTypes = {
    item: PropTypes.object.isRequired,
    to: PropTypes.string,
};
