import IconQuoteStyleQ from '../../../icons/QuoteStyleQ.svg';
import IconQuoteStyleLine from '../../../icons/QuoteStyleLine.svg';

import { Styling } from '@mssgme/ui';

export const quoteStylingFactory = ({ t, fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            Styling.s.list('template', {
                label: t('Page.fields.quoteTemplate.label'),
                options: [
                    { value: 'quote', label: t('Page.fields.quoteTemplate.values.quote'), icon: IconQuoteStyleQ },
                    { value: 'line', label: t('Page.fields.quoteTemplate.values.line'), icon: IconQuoteStyleLine },
                ],
            }),
            isAdvancedMode && [fields.fontSize(), fields.color()],
        ]),
    };
};
