import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { Spinner } from '@mssgme/ui';
import { LoadMoreButton } from '../LoadMoreButton';
import { FilterMessage } from '../FilterMessage';

export function ProductsListFooter({ products, loadMore, isSearch }) {
    const { t } = useTranslation('products');

    if (products.isFetching) {
        return <Spinner />;
    }

    const isEmptyCollection = products.items.length === 0;

    return (
        <>
            {products.next && loadMore && <LoadMoreButton onClick={loadMore} />}

            {isEmptyCollection &&
                (isSearch || products.filter.name ? (
                    <FilterMessage
                        title={t('labels.notFound.products.heading')}
                        description={t('labels.notFound.products.description')}
                    />
                ) : (
                    <FilterMessage
                        title={t('pageProducts.emptyHeading')}
                        description={t('pageProducts.emptyDescription')}
                    />
                ))}
        </>
    );
}

ProductsListFooter.propTypes = {
    products: PropTypes.shape({
        items: PropTypes.array.isRequired,
        isFetching: PropTypes.bool,
        next: PropTypes.string,
        filter: PropTypes.shape({
            name: PropTypes.string,
        }).isRequired,
    }).isRequired,
    loadMore: PropTypes.func,
    isSearch: PropTypes.bool,
};
