import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { NavLink } from 'react-router-dom';

import { cls } from '@mssgme/helpers';
import { PlanBadge } from '@mssgme/ui';

import styles from './DashboardActionButton.module.scss';

export const DashboardActionButton = ({
    icon: Icon,
    title,
    warning,
    labeled,
    disabled,
    centered = false,
    slim = false,
    active = true,
    rounded = false,
    ...rest
}) => {
    const Tag = rest.to ? NavLink : 'div';

    return (
        <div
            className={cn(
                styles.buttonWrapper,
                labeled && styles.labeled,
                centered && styles.centered,
                slim && styles.slim,
                active && styles.activeWrapper,
                disabled && styles.disabled
            )}
        >
            <Tag
                disabled={disabled}
                title={title}
                {...(rest.to && ({ exact: true, activeClassName: styles.activeButton }))}
                {...cls([styles.button, rounded && styles.rounded], rest)}
            >
                <Icon />

                {labeled && title && <span className={styles.title}>{title}</span>}
            </Tag>

            {warning && <PlanBadge plan={warning.target} className={styles.badge} />}
        </div>
    );
};

DashboardActionButton.propTypes = {
    ...NavLink.propTypes,
    icon: PropTypes.any.isRequired,
    title: PropTypes.any,
    active: PropTypes.bool,
    slim: PropTypes.bool,
    disabled: PropTypes.bool,
    labeled: PropTypes.bool,
    warning: PropTypes.shape({ target: PropTypes.string }),
    to: PropTypes.oneOfType([PropTypes.string, PropTypes.object, PropTypes.func]),
    rounded: PropTypes.bool,
};
