import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { PAYMENT_PROVIDERS } from '@mssgme/shared';
import { TextField, FieldTip } from '@mssgme/ui';
import { usePaymentsConfigValidity, useProductsContext } from '../../../../hooks';

Paypal.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Paypal({ enabled, name }) {
    const { t } = useTranslation('products');
    const { valid, tip } = usePaymentsConfigValidity('paypal');

    return (
        <>
            <TextField
                builderInput
                name={`${name ? name + '.' : ''}clientId`}
                label={t('fields.payments.paypal.clientId.label')}
                placeholder={t('fields.payments.paypal.clientId.placeholder')}
                disabled={!enabled}
                required
            />

            {valid ? <FieldTip>{t('Payments.Paypal.tip')}</FieldTip> : <FieldTip styled="warning">{tip}</FieldTip>}
        </>
    );
}
