import { useCallback, useEffect, useMemo, useState } from 'react';

const mapId = (item) => item._id;

export const useBlockSelector = (prev, current) => {
    const [items, set] = useState([]);
    const hash = useMemo(() => items.join(''), [items]);

    const handleSelect = useCallback((ids) => set(ids), []);
    const handleSelectOne = useCallback((id) => id && set([id]), []);
    const handleToggle = useCallback(
        (id) => id && set((old) => (old.includes(id) ? old.filter((item) => item !== id) : [id, ...old])),
        []
    );

    useEffect(() => {
        const total = items.length;

        if (!total) {
            return;
        }

        const blockIds = current.map(mapId);
        const previousBlockIds = prev.map(mapId);
        const removed = previousBlockIds.filter((id) => !blockIds.includes(id));
        const added = blockIds.filter((id) => !previousBlockIds.includes(id));

        if (removed.length >= total) {
            set([]);

            return;
        }

        if (!(removed.length || added.length)) {
            return;
        }

        set((old) => [...new Set([...old.filter((id) => !removed.includes(id)), ...added])]);
    }, [current]);

    return {
        count: items.length,
        items,
        hash,
        handleSelect,
        handleSelectOne,
        handleToggle,
    };
};
