import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useTemplate } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { TemplateEditForm } from '../../../../UI';

export function TemplateName() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { templateId } = useParams();
    const template = useTemplate(templateId);

    return (
        <TemplateEditForm
            title={t('Template.Edit.Name.heading')}
            template={template}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {() => (
                <>
                    <TextField
                        builderInput
                        name="name"
                        placeholder={t('Template.fields.name.placeholder')}
                        maxLength={250}
                    />
                </>
            )}
        </TemplateEditForm>
    );
}
