import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';

import { useRestriction } from '@mssgme/hooks';
import { PlanBadge } from '@mssgme/ui';
import { ThemePreview } from './ThemePreview';

import styles from './ThemeField.module.scss';

export const PageOption = ({ option, active, renderOption, onSelect }) => {
    const { warning, handleBlockedEvent } = useRestriction('page-themes:edit', option.theme);

    const handleClick = useCallback(() => void (onSelect && onSelect(option.value)), [option.value, onSelect]);

    const content = (
        <div className={styles.simpleContainer}>
            <ThemePreview theme={option.theme} />
            {warning && <PlanBadge className={styles.badge} plan={warning.target} />}
        </div>
    );

    return (
        <div className={cn(styles.pill, active && styles.activePill)} onClick={handleBlockedEvent || handleClick}>
            <div className={styles.bounds}>
                {renderOption
                    ? renderOption({
                        option,
                        active,
                        content,
                        containerClass: styles.preview,
                        contentClass: styles.previewContent,
                    })
                    : content}
            </div>

            <div className={styles.label}>{option.label}</div>
        </div>
    );
};

PageOption.propTypes = {
    option: PropTypes.object.isRequired,
    active: PropTypes.bool.isRequired,
    renderOption: PropTypes.func,
    onSelect: PropTypes.func,
};
