import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import { useActions, useFeedbackItemById, useFeedbackStore, useNavigationUtils } from '@mssgme/hooks';
import { ModalPane } from '@mssgme/ui';
import { useWebsiteData } from '@mssgme/userpage-core';
import { FeedbackItemForm } from '../../../../UI';

export function FeedbackDetails({ feedbackId }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { api } = useActions();
    const { isFetching } = useFeedbackStore();
    const website = useWebsiteData();
    const item = useFeedbackItemById(feedbackId);
    const websiteId = website._id;
    const orderId = item?.purchase?._id;

    const productResolver = useCallback(({ productId }) => api.products.retrieve({ productId }));

    const handleUpdate = useCallback(
        (data) => {
            void api.feedbacks.update({
                websiteId: item.website,
                feedbackId: item._id,
                payload: data,
            });
        },
        [item]
    );

    const handlePurchaseUpdate = useCallback(
        (payload) => {
            void api.orders.update({ orderId, payload });
        },
        [orderId]
    );

    useEffect(() => {
        if (!item) {
            void api.feedbacks.retrieve({ websiteId, feedbackId });
        }
    }, [websiteId, feedbackId, item]);

    if (!item) {
        return null;
    }

    return (
        <ModalPane title={t('Feedback.Edit.Item.heading', item)} onClose={generateUrl('..')}>
            <FeedbackItemForm
                isFetching={isFetching}
                website={website}
                item={item}
                productResolver={productResolver}
                onSubmit={handleUpdate}
                onPurchaseSubmit={handlePurchaseUpdate}
            />
        </ModalPane>
    );
}

FeedbackDetails.propTypes = {
    feedbackId: PropTypes.string.isRequired,
};
