import classNames from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import styles from './SubscriptionStatus.module.scss';

const statusColors = {
    trialing: 'blue',
    active: 'green',
    past_due: 'red',
    canceled: 'red',
    unpaid: 'red',
    paused: 'red',
    pre_paused: 'red',
};

const highRiskTransactionColors = {
    pending: 'blue',
    rejected: 'red',
};

export default function SubscriptionStatus({ subscription, highRiskTransaction }) {
    const { t } = useTranslation('billing');
    let content;
    let color;

    if (subscription) {
        const { status, trialEndedAt, pause } = subscription;
        const time = trialEndedAt || pause?.pausedFrom || null;
        const date = time ? new Date(time) : null;
        color = statusColors[status];
        content = (
            <span title={date && t('{{date, LLL}}', { date })}>
                {t(`billingHome.currentStatus.${status}`, { date, basic: 'basic' })}
            </span>
        );
    } else if (highRiskTransaction) {
        color = highRiskTransactionColors[highRiskTransaction.status];
        content = <span>{t(`billingHome.highRiskTransaction.${highRiskTransaction.status}`)}</span>;
    }

    return <span className={classNames(styles.root, styles[color])}>{content}</span>;
}

SubscriptionStatus.propTypes = {
    subscription: PropTypes.shape({
        plan: PropTypes.shape({
            id: PropTypes.string,
            name: PropTypes.string,
        }),
        status: PropTypes.string,
        trialEndedAt: PropTypes.number,
        pause: PropTypes.shape({
            pausedFrom: PropTypes.number,
        }),
    }),
    highRiskTransaction: PropTypes.shape({
        status: PropTypes.string,
    }),
};
