import { useActions } from '@mssgme/hooks';
import { useProductsBase } from './useProductsBase';

export const useProductsMain = (specifier, collectionId) => {
    const { api, setProductsFilter, clearProductsStore } = useActions();

    return useProductsBase({
        specifier,
        collectionId,
        list: api.products.list,
        setProductsFilter,
        clearProductsStore,
    });
};
