import { useCollections } from '@mssgme/hooks';

export const useCollectionsMain = (specifier, collectionPredicate) => {
    const collections = useCollections(specifier);
    const collection = collectionPredicate && collections.items.find(collectionPredicate);
    const collectionId = collection?._id || '';

    return {
        collections,
        collectionId,
        collection,
    };
};
