import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { useMeasure, useMountedState } from 'react-use';

import { IconButton } from '../Inputs';
import { SwipedAction } from './SwipedAction';
import { useSwipeable } from './useSwipeable';

import styles from './SwipeableList.module.scss';

export const SwipeableItem = ({ as: Tag = 'div', isDeleting, id, actions, children, onAction, ...rest }) => {
    const [ref, { height }] = useMeasure();
    const [beforeRef, { width: beforeWidth }] = useMeasure();
    const [afterRef, { width: afterWidth }] = useMeasure();
    const { bind, containersStyle, isUnderlayed, isBefore, isAfter } = useSwipeable({
        id,
        beforeWidth,
        afterWidth,
    });
    const { before, after } = useMemo(
        () => ({
            before: isUnderlayed && actions(rest, true),
            after: isUnderlayed && actions(rest, false),
        }),
        [isUnderlayed, actions]
    );

    const [capturedHeight, setCapturedHeight] = useState(false);

    useEffect(() => {
        if (isDeleting && height && !capturedHeight) {
            setCapturedHeight(true);
        }
    }, [height, isDeleting, capturedHeight]);

    return (
        <Tag
            ref={ref}
            className={cn(styles.container, isDeleting && capturedHeight && styles.deleting)}
            style={height && isDeleting ? { height } : undefined}
        >
            <div className={styles.item} {...bind}>
                {isUnderlayed && (
                    <div
                        className={cn(styles.actions, styles.before, !isBefore && styles.hidden)}
                        style={containersStyle.before}
                    >
                        <div ref={beforeRef}>
                            {before.map((action, index) => (
                                <IconButton
                                    key={`${index}-${action.title}`}
                                    as={SwipedAction}
                                    action={action}
                                    onSelect={onAction}
                                    {...action.props}
                                />
                            ))}
                        </div>
                    </div>
                )}

                {isUnderlayed && (
                    <div
                        className={cn(styles.actions, styles.after, !isAfter && styles.hidden)}
                        style={containersStyle.after}
                    >
                        <div ref={afterRef}>
                            {after.map((action, index) => (
                                <IconButton
                                    key={`${index}-${action.title}`}
                                    as={SwipedAction}
                                    action={action}
                                    onSelect={onAction}
                                    {...action.props}
                                />
                            ))}
                        </div>
                    </div>
                )}

                <div className={styles.inner} style={containersStyle.inner}>
                    {children}
                </div>
            </div>
        </Tag>
    );
};

SwipeableItem.propTypes = {
    as: PropTypes.elementType,
    actions: PropTypes.func.isRequired,
    id: PropTypes.any.isRequired,
    isDeleting: PropTypes.bool,
    onAction: PropTypes.func.isRequired,
};

export const SwappableItemContainer = (Component) => {
    // noinspection UnnecessaryLocalVariableJS
    const Wrapped = ({ as, actions, onAction, ...props }) => {
        const isMounted = useMountedState();
        const [isDeleting, setDeleting] = useState(false);

        const handleDeleting = useCallback((fn) => {
            setDeleting(true);

            setTimeout(async () => {
                try {
                    await fn();
                } catch (e) {
                    if (isMounted()) {
                        setDeleting(false);
                    }

                    throw e;
                }
            }, 200);
        }, []);

        const render = useCallback(
            (props) => (
                <SwipeableItem as={as} actions={actions} onAction={onAction} isDeleting={isDeleting} {...props} />
            ),
            [isDeleting]
        );

        return <Component render={render} onDeleting={handleDeleting} onAction={onAction} {...props} />;
    };

    if (process.env.NODE_ENV !== 'production') {
        Wrapped.displayName = `SwipeableItemContainer(${Component.displayName || Component.name || 'SwipeableItem'})`;
        Wrapped.propTypes = {
            ...Component.propTypes,
            as: PropTypes.elementType,
            actions: PropTypes.func.isRequired,
            onAction: PropTypes.func.isRequired,
        };
    }

    return Wrapped;
};
