import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';

import { useActions, useUser } from '@mssgme/hooks';
import {
    Form,
    FormikProvider,
    ModalEditPane,
    renderNode,
    SaveButton,
    useEnhancedFormik,
    usePresenceRemoval,
} from '@mssgme/ui';
import { ValidationError } from '@mssgme/api-client';

export function PasswordChangeForm({ children, title, backPath, successRedirectPath }) {
    const { api } = useActions();
    const { t } = useTranslation();
    const [isClosing, setClosing] = useState(false);
    const user = useUser();
    const history = useHistory();

    const handleSubmit = useCallback(
        async (values) => {
            if (values.password !== values.passwordRepeat) {
                throw new ValidationError({ json: { errors: { passwordRepeat: t('changePasswordForm.mismatch') } } });
            } else {
                await api.users.changeMyPassword({ payload: { password: values.password } });

                setClosing(true);
                setTimeout(() => history.push(successRedirectPath), 100);
            }
        },
        [user]
    );

    const formik = useEnhancedFormik({
        initialValues: {
            password: '',
            passwordRepeat: '',
        },
        onSubmit: handleSubmit,
    });

    usePresenceRemoval();

    return (
        <FormikProvider value={formik}>
            <ModalEditPane guardChanges={!isClosing} title={title} onClose={backPath}>
                <Form autoComplete="off">
                    {renderNode(children, formik)}

                    <SaveButton
                        fullWidth
                        size="standart"
                        isInitialHidden={false}
                        disabled={isClosing || !formik.dirty}
                        style={{ marginTop: 8 }}
                        isFetching={formik.isSubmitting}
                    >
                        {t('ui:labels.saveChanges')}
                    </SaveButton>
                </Form>
            </ModalEditPane>
        </FormikProvider>
    );
}

PasswordChangeForm.propTypes = {
    children: PropTypes.any.isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.string.isRequired,
    successRedirectPath: PropTypes.string.isRequired,
};
