import React, { useEffect } from 'react';

import { useActions } from '@mssgme/hooks';

export const SocketObserver = () => {
    const { api } = useActions();

    useEffect(() => {
        api.listenConnect();
        api.listenDisconnect();
        api.listenReconnect();
        api.listenVerifyVersion(); // TODO: remove
    }, []);

    return null;
};

SocketObserver.propTypes = {};
