import { useI18n } from '@mssgme/i18n';

export const useBillingTranslations = () => {
    return useI18n('billing', (language) =>
        import(
            /* webpackChunkName: "locales.billing" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/billing.json`
        )
    );
};
