import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { Badge, LandingsPropTypes, LoadingIndicator } from '@mssgme/ui';

import styles from './FeedbacksBadge.module.scss';

export const FeedbacksBadge = ({ loading, feedbacks, short }) => {
    const { t } = useTranslation('landings');

    if (!(loading || feedbacks)) {
        return null;
    }

    return (
        <Badge badgeStyle="flat" className={styles.badge}>
            {loading ? (
                <LoadingIndicator inline color="#eee" />
            ) : (
                feedbacks.map(({ status, count }) => (
                    <span key={status} className={cn(styles.count, styles[status])}>
                        {short ? count : t(`labels.feedback.counts.${status}`, { count })}
                    </span>
                ))
            )}
        </Badge>
    );
};

FeedbacksBadge.propTypes = {
    short: PropTypes.bool,
    loading: PropTypes.bool,
    feedbacks: PropTypes.arrayOf(
        PropTypes.shape({
            status: LandingsPropTypes.feedbackStatus.isRequired,
            count: PropTypes.number.isRequired,
        })
    ),
};
