import React from 'react';
import PropTypes from 'prop-types';
import { useActions, useFetch } from '@mssgme/hooks';
import providers from './providers';

export const propTypes = {
    planId: PropTypes.string.isRequired,
    period: PropTypes.oneOf(['monthly', 'annually']).isRequired,
    onClose: PropTypes.func,
};

export default function Checkout({ planId, period, onClose, ...rest }) {
    const { api, checkout } = useActions();
    const { result } = useFetch(() => api.billing.subscription.create({ payload: { planId, period } }), [planId]);

    const handleClose = () => {
        onClose?.();
        checkout.close();
    };

    if (!result) return null;

    const Component = providers[result.provider].checkout;

    return (
        <Component
            {...rest}
            {...result.params}
            onPaymentComplete={checkout.completePayment}
            onApplyCoupon={checkout.applyCoupon}
            onRemoveCoupon={checkout.removeCoupon}
            onChangeCountry={checkout.changeCountry}
            onLoad={checkout.load}
            onError={checkout.error}
            onSelectPaymentMethod={checkout.selectPaymentMethod}
            onClose={handleClose}
        />
    );
}

Checkout.propTypes = propTypes;
