import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { LandingsPropTypes, renderNode, StreamlinedScreen, ToggleField } from '@mssgme/ui';
import { useProductsContext } from '../../../hooks';
import { ProductsSettingsForm } from '../ProductsSettingsForm';

const propTypes = {
    title: PropTypes.string.isRequired,
    provider: LandingsPropTypes.paymentMethod.isRequired,
};

export const PaymentsSettingsScreen = ({ provider, title, children, ...rest }) => {
    const { t } = useTranslation('products');
    const { router } = useProductsContext();

    return (
        <StreamlinedScreen
            preferDynamicBack
            modal
            inverse={false}
            nopadding={false}
            fullHeight={false}
            backPath={router.settings.toPayments()}
            title={title}
        >
            <ProductsSettingsForm successRedirectPath={router.settings.toPayments()} restrictRule="payments" {...rest}>
                <ToggleField
                    builderInput
                    name={`payments.${provider}.enabled`}
                    label={t(`fields.payments.${provider}.enabled.label`)}
                />

                <FormWrapper provider={provider}>{children}</FormWrapper>
            </ProductsSettingsForm>
        </StreamlinedScreen>
    );
};

PaymentsSettingsScreen.propTypes = propTypes;

const FormWrapper = ({ provider, children }) => {
    return renderNode(children, useField(`payments.${provider}.enabled`)[0].value);
};

FormWrapper.propTypes = {
    provider: LandingsPropTypes.paymentMethod.isRequired,
};
