import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { useToggle } from 'react-use';
import { useTranslation } from 'react-i18next';

import { LandingsPropTypes, NavItem } from '@mssgme/ui';
import { printPrice } from '@mssgme/userpage-core';
import { DataItem } from './DataItem';
import { PurchaseStatusModal } from './PurchaseStatusModal';

import styles from './FeedbackItemForm.module.scss';

export const PurchaseDetails = ({ website, purchase, onSubmit }) => {
    const { t } = useTranslation('landings');
    const [isOpenStatusModal, toggleStatusModal] = useToggle();

    return (
        <>
            <DataItem label={t('Feedback.Edit.Item.labels.price')}>{printPrice(purchase.total)}</DataItem>

            <DataItem label={t('Feedback.Edit.Item.labels.paymentMethod')}>
                {t(`ui:orders.paymentMethod.${purchase.paymentMethod}`)}
            </DataItem>

            <DataItem label={t('Feedback.Edit.Item.labels.orderStatus')}>
                <NavItem
                    tag="span"
                    className={cn(styles.paymentStatus, styles[purchase.status])}
                    chevron={!!onSubmit}
                    disabled={!onSubmit}
                    onClick={onSubmit ? toggleStatusModal : undefined}
                >
                    {t(`ui:orders.paymentStatus.${purchase.status}`)}
                </NavItem>
            </DataItem>

            {isOpenStatusModal && (
                <PurchaseStatusModal
                    website={website}
                    purchase={purchase}
                    onSubmit={onSubmit}
                    onClose={toggleStatusModal}
                />
            )}
        </>
    );
};

PurchaseDetails.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    purchase: LandingsPropTypes.order.isRequired,
    onSubmit: PropTypes.func,
};
