import { useI18n } from '@mssgme/i18n';

export const useAdminUITranslations = () => {
    return useI18n('admin-ui', (language) =>
        import(
            /* webpackChunkName: "locales.admin-ui" */
            /* webpackInclude: /\.json$/ */
            `../../locales/${language}/admin-ui.json`
        )
    );
};
