import React from 'react';
import PropTypes from 'prop-types';
import { Button } from '@mssgme/ui';

LiqPayCheckout.propTypes = {
    data: PropTypes.string.isRequired,
    signature: PropTypes.string.isRequired,
};

export default function LiqPayCheckout({ data, signature }) {
    return (
        <form method="POST" action="https://www.liqpay.ua/api/3/checkout" acceptCharset="utf-8">
            <input type="hidden" name="data" value={data} />
            <input type="hidden" name="signature" value={signature} />
            <Button fullWidth>Pay</Button>
        </form>
    );
}
