import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import { TopLevelScreenHeader } from './TopLevelScreenHeader';
import { RestrictionScope } from '../../Features';
import styles from './TopLevelScreen.module.scss';

export const TopLevelScreen = ({ title, children, ...rest }) => {
    return (
        <div {...cls([styles.root, !!title && styles.withTitle], rest)}>
            <RestrictionScope>
                {title && <TopLevelScreenHeader title={title} />}

                <div className={styles.container}>{children}</div>
            </RestrictionScope>
        </div>
    );
};

TopLevelScreen.propTypes = {
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.oneOf([false]), PropTypes.element]),
};
