import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useLocation } from 'react-router-dom';

import { AnimateableRoutes } from '@mssgme/ui';
import { useBackProductsPath, useProductsContext } from '../hooks';
import {
    CollectionEdit,
    CollectionProducts,
    Collections,
    PaymentProviderSettingsScreen,
    ProductEdit,
    ProductsContacts,
    ProductsCurrency,
    ProductsHome,
    ProductsImport,
    ProductsNotifications,
    ProductsOrderForm,
} from './Editing';
import { Payments, ProductsSettingsMenu } from './SettingsMenu';

export const productsSettingsRouting = {
    component: ProductsSettingsMenu,
    children: [
        { path: 'currency', component: ProductsCurrency },
        { path: 'messengers', component: ProductsContacts },
        { path: 'notifications', component: ProductsNotifications },
        { path: 'form', component: ProductsOrderForm },
        { path: 'import', component: ProductsImport },
        {
            path: 'payments',
            component: Payments,
            children: [
                { path: ':provider', component: PaymentProviderSettingsScreen },
            ],
        },
    ],
};

const routing = [
    {
        component: ProductsHome,
        children: [
            {
                path: 'settings',
                ...productsSettingsRouting,
            },
            {
                path: 'collections',
                component: Collections,
                children: [
                    { path: 'new', component: CollectionEdit },
                    {
                        path: ':collectionId([a-f\\d]{24})',
                        component: CollectionProducts,
                        children: [
                            { path: 'new', component: ProductEdit },
                            { path: 'edit', component: CollectionEdit },
                            { path: 'product/edit/:productId', component: ProductEdit },
                        ],
                    },
                ],
            },
            {
                path: [
                    'new/:collectionId?',
                    'edit/:productId',
                ],
                component: ProductEdit,
            },
        ],
    },
];

export function ProductsPage() {
    const { modal } = useProductsContext();
    const { state } = useLocation();
    const backUrl = useMemo(() => state?.blockUrl, []);

    useBackProductsPath(backUrl);

    return <AnimateableRoutes modal={modal} routing={routing} />;
}

ProductsPage.propTypes = {
    defaultMessengers: PropTypes.any,
};
