import React, { Suspense } from 'react';
import { useNavigationUtils } from '@mssgme/hooks';
import { LoadingIndicator, SecondLevelScreen } from '@mssgme/ui';

import { SinglePlanPageContent } from '../UI';

export default function SinglePlanPage() {
    const { generateUrl } = useNavigationUtils();

    return (
        <SecondLevelScreen backPath={generateUrl('../..')} preferDynamicBack light>
            <Suspense fallback={<LoadingIndicator />}>
                <SinglePlanPageContent />
            </Suspense>
        </SecondLevelScreen>
    );
}
