import { Styling } from '@mssgme/ui';

export const customStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            fields.color({
                path: 'backgroundColor',
                label: t('Page.fields.backgroundColor.shortLabel'),
            }),
            fields.borderRadius({
                min: 1,
                max: 200,
                disabledValue: null,
            }),
        ]),
    };
};
