import { useFrontendTranslations } from '@mssgme/ui';

const loadLandingLocales = async (lang) => {
    return import(
        /* webpackInclude: /\.json$/ */
        /* webpackChunkName: "locale." */
        `../locales/${lang}/userwebsite.json`
    );
};

export const usePageFrontendTranslations = (language) => {
    return useFrontendTranslations(loadLandingLocales, language);
};
