import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link, useRouteMatch } from 'react-router-dom';
import { useToggle } from 'react-use';
import { useActions, useFetching } from '@mssgme/hooks';
import { Button } from '@mssgme/ui';
import { buttonPropTypes } from './propTypes';
import PaymentPreviewModal from './PaymentPreviewModal';
import { usePlanDetails } from '../../hooks';

export default function ChangePlanButton({ planId, period }) {
    const { t } = useTranslation('billing');
    const { url } = useRouteMatch();
    const { plan, subscription, isCurrentPlan } = usePlanDetails(planId);
    const { api } = useActions();
    const [isPreviewOpen, togglePreviewOpen] = useToggle(false);
    const changePlan = useFetching(() => api.billing.changePlan({ payload: { planId, period } }));

    const buttonProps = {
        fullWidth: true,
        style: { fontWeight: 600, fontSize: 16 },
        'data-button': isCurrentPlan ? 'change-plan-period' : 'change-plan-to',
        children: isCurrentPlan
            ? t('PlansPage.common.changePlanPeriod')
            : t('PlansPage.common.changePlanTo', { plan: plan.name }),
    };

    if (subscription?.actions?.preview) {
        return (
            <>
                <Button {...buttonProps} onClick={togglePreviewOpen} isFetching={changePlan.pending} />
                <PaymentPreviewModal
                    plan={plan}
                    period={period}
                    isOpen={isPreviewOpen}
                    onAccept={changePlan.run}
                    onClose={togglePreviewOpen}
                    pendingPlanChange={changePlan.pending}
                />
            </>
        );
    }

    return <Button {...buttonProps} tag={Link} to={`${url}/${period}`} />;
}

ChangePlanButton.propTypes = buttonPropTypes;
