import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import {
    CalendarIcon,
    MailIcon,
    PhoneCallIcon,
    UserIcon,
    TypeIcon,
    CheckCircleIcon,
    CheckSquareIcon,
    GlobeIcon,
    ClockIcon,
    HashIcon,
    ListWithDotsIcon,
} from '@mssgme/icons';
import { getLocalizedDynamicLabel } from '@mssgme/shared';

import styles from './DynamicFormItem.module.scss';

export const DYNAMIC_FIELDS = {
    name: { icon: UserIcon },
    phone: { icon: PhoneCallIcon },
    email: { icon: MailIcon },
    text: { icon: TypeIcon, props: { large: true } },
    select: { icon: ListWithDotsIcon },
    radio: { icon: CheckCircleIcon },
    checkbox: { icon: CheckSquareIcon },
    country: { icon: GlobeIcon },
    date: { icon: CalendarIcon },
    time: { icon: ClockIcon },
    number: { icon: HashIcon },
};

export const DynamicFormItem = ({ value, className, textClassName }) => {
    const Icon = DYNAMIC_FIELDS[value.type]?.icon || TypeIcon;
    const label = getLocalizedDynamicLabel(value);

    return (
        <div className={className}>
            <div className={cn(textClassName, styles.label)}>
                <div className={styles.icon}>
                    <Icon size={20} />
                </div>
                <span>{label.replace(/\n/g, ' ')}</span>
                <span>{value.required && '*'}</span>
            </div>
        </div>
    );
};

DynamicFormItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};
