import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ListField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageStatusEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();

    return (
        <PageEditForm
            title={t('Page.Edit.Status.heading')}
            website={website}
            page={page}
            backPath={generateUrl('..')}
        >
            <ListField
                builderInput
                name="enabled"
                options={[
                    { label: t('ui:labels.published'), value: true },
                    { label: t('ui:labels.notPublished'), value: false },
                ]}
            />
        </PageEditForm>
    );
}
