import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { createPortal } from 'react-dom';
import { Form, useField, useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { FiTrash2 } from 'react-icons/fi';

import { renderNode } from '../../../helpers';
import { KnobButton } from '../../Inputs';
import { useControlledModalSheet } from '../../ModalSheet';
import { ModalEditPane } from '../../ModalEditPane';
import { DragHandle } from '../../DragHandle';
import EnhancedFormik from '../EnhancedFormik';
import SaveButton from '../SaveButton';

import styles from './commonStyles.module.scss';

export const ArrayItemEditModal = ({
    name,
    factory,
    isNew,
    isInitialDirty,
    validationSchema,
    children,
    onApply,
    onDelete,
    ...rest
}) => {
    const { isClosing, onClose, onExit } = useControlledModalSheet({ onClose: onApply });
    const { t } = useTranslation('ui');
    const { getFieldMeta } = useFormikContext();
    const { value = factory() } = getFieldMeta(name);

    const modal = (
        <EnhancedFormik initialValues={value} validateOnMount validationSchema={validationSchema} onSubmit={onClose}>
            {(form) => (
                <ModalEditPane
                    title={t('labels.add')}
                    isClosing={isClosing}
                    onClose={onClose}
                    onExit={onExit}
                    guardChanges={false}
                    rightButton={() => {
                        const isModified = true; // form.dirty || isInitialDirty;

                        if (isModified) {
                            return (
                                <SaveButton
                                    noborder
                                    nopadding
                                    className={styles.saveButton}
                                    btnStyle="transparent"
                                    size="small"
                                    isNew={isNew}
                                    isInitialHidden={false}
                                    // disabled={!form.isValid || isClosing}
                                    {...(isInitialDirty ? { disabled: false } : {})}
                                    isFetching={form.isSubmitting}
                                />
                            );
                        }

                        // if (isNew) {
                        //     return null;
                        // }
                        //
                        // return <ItemActions item={value} onDelete={onDelete} onClose={onClose} />;
                    }}
                    {...rest}
                >
                    <Form autoComplete="off">{renderNode(children, form)}</Form>
                </ModalEditPane>
            )}
        </EnhancedFormik>
    );

    return createPortal(modal, document.querySelector('#visual-builder,#mainRoot'));
};

ArrayItemEditModal.propTypes = {
    name: PropTypes.string.isRequired,
    children: PropTypes.any.isRequired,
    factory: PropTypes.oneOfType([PropTypes.func, PropTypes.object]).isRequired,
    validationSchema: PropTypes.object,
    onApply: PropTypes.func.isRequired,
    onDelete: PropTypes.func,
    isNew: PropTypes.bool.isRequired,
    isInitialDirty: PropTypes.bool.isRequired,
};

export const ArrayItem = ({ context, name, sortable, Item, onEdit, onDelete }) => {
    const [{ value }] = useField(name);

    const handleEdit = useCallback(() => void (onEdit && onEdit(value)), [value, onEdit]);
    const handleRemove = useCallback(
        (e) => {
            e && e.preventDefault();
            e && e.stopPropagation();
            onDelete && onDelete(value);
        },
        [value, onDelete]
    );

    return (
        <div className={styles.item}>
            {!!sortable && <DragHandle className={styles.drag} active={!!sortable} iconSize={9} />}

            <div className={styles.itemContainer}>
                <div className={styles.itemContentWrapper} onClick={handleEdit}>
                    <Item
                        className={styles.itemContent}
                        textClassName={styles.itemText}
                        name={name}
                        value={value}
                        context={context}
                        onEdit={handleEdit}
                        onDelete={handleRemove}
                    />
                </div>

                {onDelete && (
                    <KnobButton type="transparent" simple onClick={handleRemove}>
                        <FiTrash2 size={20} />
                    </KnobButton>
                )}
            </div>
        </div>
    );
};

ArrayItem.propTypes = {
    name: PropTypes.string.isRequired,
    validationSchema: PropTypes.object,
    sortable: PropTypes.bool,
    Item: PropTypes.elementType.isRequired,
    context: PropTypes.any,
    onEdit: PropTypes.func,
    onDelete: PropTypes.func,
};
