import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { TextField, FieldTip } from '@mssgme/ui';
import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { WebsiteEditForm } from '../../../../UI';

const PLATFORMS = {
    facebook: {
        label: 'Facebook pixel ID',
    },
    google: {
        label: 'Google MEASUREMENT ID',
    },
    yandex: {
        label: 'Yandex Metrika tracking ID',
    },
    tiktok: {
        label: 'TikTok tracking ID',
    },
};

export default function TrackingPlatform({ platform }) {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm
            website={website}
            title={t(`labels.platform.${platform}`)}
            restrictRule="page-tracking"
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {() => (
                <>
                    <FieldTip style={{ marginBottom: 16, whiteSpace: 'pre-wrap' }}>
                        {t(`Website.fields.tracking.${platform}.fieldTip`)}
                    </FieldTip>
                    <TextField
                        builderInput
                        label={PLATFORMS[platform].label}
                        name={`tracking.${platform}`}
                        placeholder={t(`Website.fields.tracking.${platform}.placeholder`)}
                    />
                </>
            )}
        </WebsiteEditForm>
    );
}

TrackingPlatform.propTypes = {
    platform: PropTypes.oneOf(['facebook', 'google', 'yandex', 'tiktok']).isRequired,
};
