import { useFilter } from './useFilter';
import { useEventCallback } from './useEventCallback';

/**
 * @template T
 * @param {object} config Config
 * @param {T[]} config.items List items
 * @param {string} [config.next] Next item token
 * @param {boolean} [config.isFetching] Request is fetching
 * @param {object} config.filter Current filter state
 * @param {object} filters Filters to apply in useFilter
 * @param {(data: object) => Promise<{ results: T[]; next?: string; hasNext?: boolean }>} fn Request method
 * @returns {{ isFetching: boolean; hasMore: boolean; items: T[]; handleLoadMore: () => Promise<void> }} Dynamic list
 */
export const useInfiniteList = (config, filters, fn) => {
    const [items, filter] = useFilter(config.items, config.filter, filters);

    const handleLoadMore = useEventCallback(() => {
        if (!config.next) {
            return;
        }

        void fn({ ...filter, next: config.next });
    });

    return {
        isFetching: config.isFetching,
        hasMore: !!config.next,
        items,
        handleLoadMore,
    };
};
