import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { BlockListItemPreview, FloatingActions, ListNoStyle, StreamlinedScreen } from '@mssgme/ui';
import { ChevronRightIcon, ListIcon } from '@mssgme/icons';
import { CreatePageButton } from '../../../../UI';
import { PageItem } from './PageItem';

export function PagesList() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, pageId } = useWebsitePageBySlugs();
    const homePageId = website.homePageId;
    const pages = useMemo(
        () => website.pages.slice().sort((a, b) => (b._id === homePageId) - (a._id === homePageId)),
        [website.pages, homePageId]
    );

    return (
        <StreamlinedScreen
            modal
            light
            fullHeight={false}
            backPath={generateUrl('..')}
            title={t('Page.View.List.heading')}
        >
            <StreamlinedScreen.Section top>
                <ListNoStyle>
                    {pages.map((page) => (
                        <PageItem key={page._id} website={website} page={page} activeId={pageId} />
                    ))}
                </ListNoStyle>
            </StreamlinedScreen.Section>

            <StreamlinedScreen.Section top={1}>
                {pages.length > 1 && (
                    <ListNoStyle>
                        <BlockListItemPreview
                            iconSplit
                            tag={Link}
                            to={generateUrl('menu')}
                            label={t('Page.View.List.customizeMenu')}
                            labelIcon={ListIcon}
                            rightIcon={<ChevronRightIcon />}
                        />
                    </ListNoStyle>
                )}
                <FloatingActions padded pullout>
                    <CreatePageButton />
                </FloatingActions>
            </StreamlinedScreen.Section>
        </StreamlinedScreen>
    );
}
