import { Styling } from '@mssgme/ui';

export const textStylingFactory = ({ fields, isAdvancedMode }) => {
    return {
        ...Styling.d.section([
            //
            isAdvancedMode && fields.fontSize(),
            fields.textStyle((field) => ({
                ...field,
                options: isAdvancedMode
                    ? field.options
                    : field.options.filter((option) => option.value !== 'normal' && option.value !== 'line-through'),
                style: { marginBottom: 16 },
            })),
            fields.textAlign((field) => ({
                ...field,
                options: isAdvancedMode ? field.options : field.options.filter((option) => option.value !== 'justify'),
            })),
            isAdvancedMode && fields.color(),
        ]),
    };
};
