import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import cn from 'classnames';

import {
    BackgroundImage,
    BLOCKS_MAP,
    Insets,
    useHomePageData,
    useResolvedTheme,
} from '@mssgme/userpage-core';
import { ThemeContext } from '@mssgme/ui';

import styles from './BlockSelector.module.scss';

export function BlockSelector({ name, value, onChange, website, page }) {
    const homePage = useHomePageData(website);
    const blocks = useMemo(() => page.blocks.filter((block) => block.enabled && !block.nested), [page.blocks]);
    const { theme, style, resolvedTheme, align } = useResolvedTheme(homePage);

    const handleSelect = (blockId) => {
        onChange(name, { page: page._id, block: blockId });
    };

    // TODO: unify block renderers
    const renderBlock = useMemo(
        // eslint-disable-next-line react/display-name
        () => (block, index) => {
            const Component = BLOCKS_MAP[block.kind];
            const insets = Insets.computeComponentInsets(blocks, block, index, resolvedTheme);
            const key = block._id || `${block.kind + index}`;
            const isActive = value.block === block._id;

            return Component ? (
                <div key={key} onClick={() => handleSelect(block._id)} className={styles.blockWrapper}>
                    <Component
                        insets={insets}
                        block={block}
                        interactive={false}
                        className={cn(styles.block, isActive && styles.active)}
                    />
                </div>
            ) : (
                <div key={key} className={isActive && styles.active} onClick={() => handleSelect(block._id)}>
                    Unknown block kind &quot;{block.kind}&quot;
                </div>
            );
        },
        [blocks, resolvedTheme, handleSelect]
    );

    const contents = (
        <div style={style} className={styles.root}>
            <div className={styles.bg}>
                {theme.backgroundImage && (
                    <BackgroundImage
                        path={theme.backgroundImage}
                        opacity={theme.backgroundImageOpacity}
                    />
                )}
            </div>
            <div style={align.container}>
                <div style={align.inner}>
                    {blocks.map(renderBlock)}
                </div>
            </div>
        </div>
    );

    return (
        <ThemeContext.Provider value={resolvedTheme}>
            {contents}
        </ThemeContext.Provider>
    );
}

BlockSelector.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.object.isRequired,
    onChange: PropTypes.func.isRequired,
    website: PropTypes.object.isRequired,
    page: PropTypes.object.isRequired,
};
