import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { ArrayField, TextareaField } from '@mssgme/ui';
import { ListItemSchema } from '../components';

const ListItem = ({ value, className, textClassName }) => (
    <div className={className}>
        <div className={textClassName}>{value.content.replace(/\n/g, ' ')}</div>
    </div>
);

ListItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const ListItemsField = (props) => {
    const { t } = useTranslation('landings');
    const itemFactory = useCallback(() => ({ content: '' }));

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.List.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.List.itemAddMore')}
            modalTitle={t('Page.Edit.List.itemFormHeading')}
            Item={ListItem}
            validationSchema={ListItemSchema}
            {...props}
        >
            <TextareaField
                required
                builderInput
                name="content"
                // label={t('Page.Edit.List.fields.content.label')}
                placeholder={t('Page.Edit.List.fields.content.placeholder')}
                maxLength={800}
            />
        </ArrayField>
    );
};

ListItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
