import React from 'react';
import { matchPath } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { UpgradeTopBanner, TrialTopBanner, PastDueBanner, PromoBanner } from '@mssgme/ui';
import { useExpired, useBilling } from '@mssgme/hooks';
import { useIsTrialAvailable } from '@mssgme/billing';
import config from 'config';

const EXPIRED_DAYS = 14;

export default function useBanner(location) {
    const { t } = useTranslation('ui');
    const { isFree, isPastDue, plan } = useBilling();
    const isTrialAvailable = useIsTrialAvailable();
    const [isTrialBannerVisible, hideTrialBanner] = useExpired('trialBanner', EXPIRED_DAYS);
    const [isProBannerVisible, hideProBanner] = useExpired('proBanner', EXPIRED_DAYS);

    const isBillingPage = matchPath(location.pathname, '/billing');

    if (isBillingPage) {
        return config.paddle_coupon ? <PromoBanner message={t('PromoBanner.blackFriday')}/> : null;
    }
    if (isPastDue) {
        return <PastDueBanner plan={plan} />;
    }
    if (!isFree) {
        return null;
    }
    if (isTrialAvailable && isTrialBannerVisible) {
        return <TrialTopBanner onClose={hideTrialBanner} />;
    }
    if (!isTrialAvailable && isProBannerVisible) {
        return <UpgradeTopBanner onClose={hideProBanner} />;
    }
    return null;
}
