import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { MdArrowBack, MdArrowForward } from 'react-icons/md';

import { Button, IconButton, PortalModalSheet, useModal } from '@mssgme/ui';
import { useFetching } from '@mssgme/hooks';
import { Page, useWebsiteData } from '@mssgme/userpage-core';

import styles from './commonStyles.module.scss';

export const TemplatePill = ({ option, onSelect }) => {
    const website = useWebsiteData();
    const { t } = useTranslation('landings');
    const { isOpen, show, hide } = useModal();
    const overriddenWebsite = useMemo(
        () => ({ ...website, pages: [...website.pages, option.page], branding: false }),
        [website, option.page]
    );
    const handleClick = useCallback(() => {
        if (option.value) {
            show();
        } else {
            onSelect(option.value);
        }
    }, [option.value, onSelect]);
    const { pending, run: handleAccept } = useFetching(
        async (accept) => {
            if (accept) {
                await onSelect(option.value);
            }
        },
        [option.value, onSelect]
    );

    return (
        <div className={cn(styles.option)} onClick={handleClick}>
            <div className={styles.pill}>
                {option.page ? (
                    <img src={option.page.thumbnailUrl} alt={option.page.name} />
                ) : (
                    <div className={styles.innerLabel}>{option.label}</div>
                )}
            </div>

            {isOpen && option.page && (
                <PortalModalSheet
                    fullWidth
                    fullHeight
                    padding={false}
                    onClosing={handleAccept}
                    onClose={hide}
                    render={({ handleFalse, handleTrue }) => (
                        <div className={styles.preview}>
                            <Page
                                interactive={false}
                                website={overriddenWebsite}
                                page={option.page}
                                className={styles.templateView}
                            />

                            <div className={styles.actions}>
                                <IconButton
                                    className={cn(styles.button, styles.cancel)}
                                    disabled={pending}
                                    onClick={handleFalse}
                                >
                                    <MdArrowBack size={24} />
                                </IconButton>
                                <Button
                                    className={styles.button}
                                    afterIcon={<MdArrowForward size={24} />}
                                    isFetching={pending}
                                    onClick={handleTrue}
                                >
                                    {t('ui:button.choose')}
                                </Button>
                            </div>
                        </div>
                    )}
                />
            )}
        </div>
    );
};

TemplatePill.propTypes = {
    option: PropTypes.object.isRequired,
    onSelect: PropTypes.func.isRequired,
};
