import React from 'react';
import { useTranslation } from 'react-i18next';
import { useToggle, useMountedState } from 'react-use';
import { Link as BaseLink } from 'react-router-dom';
import { Button as UIButton } from '@mssgme/ui';
import config from 'config';
import { Checkout } from '../Subscription';
import { buttonPropTypes } from './propTypes';
import { useIsTrialAvailable } from '../../hooks';

function BasicButton(props) {
    const { t } = useTranslation('billing');
    const isTrialAvailable = useIsTrialAvailable();

    return (
        <UIButton
            fullWidth
            style={{ fontWeight: 600, fontSize: 16 }}
            data-button="choose-plan-and-continue" // TODO: remove
            {...props}
        >
            {isTrialAvailable
                ? t('pagePlans.choose_button.try_trial', { days: config.trial })
                : t('pagePlans.choose_button.go_to_payment')}
        </UIButton>
    );
}

export function Link({ planId, period }) {
    return (
        <BasicButton
            tag={BaseLink}
            to={`/billing/plans/${planId}/${period}`}
            data-event="click-subscribe"
            data-plan-id={planId}
            data-period={period}
        />
    );
}

Link.propTypes = buttonPropTypes;

export function Button({ planId, period }) {
    const [on, toggle] = useToggle(false);
    const isMounted = useMountedState();

    const handleClick = () => toggle(true);
    const handleClose = () => {
        if (isMounted()) {
            toggle(false);
        }
    };

    return (
        <>
            <BasicButton
                onClick={handleClick}
                data-event="click-subscribe"
                data-plan-id={planId}
                data-period={period}
            />
            {on && <Checkout planId={planId} period={period} onClose={handleClose} />}
        </>
    );
}

Button.propTypes = buttonPropTypes;

const SubscribeButton = process.env.CHECKOUT_MODE === 'inline' ? Link : Button;

export default SubscribeButton;
