import { object, string, array } from 'yup';

import { MESSENGERS } from '@mssgme/shared';
import { valueTest } from './helpers';

const isPhone = (val) => /^\+\d{3,}$/.test(val);
const isViber = isPhone;
const isBot = (val) => /^[\w-]+$/.test(val);
const isViberCommunity = (val) => /^https?:\/\/invite\.viber\.com\/.+$/i.test(val);
const isWhatsApp = isPhone;
const isWhatsAppGroup = (val) => /^https?:\/\/chat\.whatsapp\.com\/.+/i.test(val);
const isAutoWhatsApp = (val) =>
    /^((\+?[\d ()-]{4,})((\/\?|\?).*)?|((https?:\/\/)?((wa\.me|chat\.whatsapp\.com)\/).+))$/i.test(val);
const isMessenger = (val) => /^[0-9a-zA-Z_.-]+$/.test(val);
const isAutoMessenger = (val) =>
    /^(https?:\/\/)?(www\.)?((m\.me|fb\.com|facebook\.com)\/)?[0-9a-zA-Z_.-]+((\/\?|\?).*)?$/i.test(val);
const isVk = (val) => /^[a-zA-Z0-9_.-]+$/.test(val);
const isAutoVk = (val) =>
    /^(https?:\/\/)?((vk\.me|vk\.com|vkontakte\.(com|ru))\/)?[0-9a-zA-Z_.-]+((\/\?|\?).*)?$/i.test(val);
const isSkype = (val) => /^(live:)?[\w.-]+$/.test(val);
const isTelegram = (val) => /^[a-zA-Z][\w_-]{4,}$/i.test(val);
const isAutoTelegram = (val) => /^((https?:\/\/)?((t(?:elegram)?)\.me\/))?\+?@?[\w_-]{4,}((\/\?|\?).*)?$/i.test(val);
const isTelegramInvite = (val) => /^([a-zA-Z\d][\w_-]{4,})|(https?:\/\/t\.me\/(\+|joinchat\/).*)$/i.test(val);
const isAutoInstagram = (val) =>
    /^((https?:\/\/)?((ig\.me|instagram\.com)\/))?(m\/)?@?[\w._]{2,}((\/\?|\?).*)?$/i.test(val);

const BotError = 'errorsList.bad_bot_uri';
const UsernameError = 'errorsList.bad_value';
const InvalidUrl = 'errorsList.enter_url';

export const MessengerItemSchema = object({
    platform: string()
        .oneOf(MESSENGERS)
        .required('errorsList.bad_value'),
    type: string()
        .oneOf(['private', 'public', 'community', 'secret', 'group', 'auto'])
        .required('errorsList.bad_value'),
    value: string()
        .when(...valueTest(isBot, BotError, { platform: 'viber', type: 'public' }))
        .when(...valueTest(isViberCommunity, InvalidUrl, { platform: 'viber', type: 'community' }))
        .when(...valueTest(isViber, UsernameError, { platform: 'viber', type: 'private' }))

        .when(...valueTest(isAutoWhatsApp, UsernameError, { platform: 'whatsapp', type: 'auto' }))
        .when(...valueTest(isWhatsApp, UsernameError, { platform: 'whatsapp', type: 'private' }))
        .when(...valueTest(isWhatsAppGroup, InvalidUrl, { platform: 'whatsapp', type: 'public' }))

        .when(...valueTest(isAutoTelegram, UsernameError, { platform: 'telegram', type: 'auto' }))
        .when(...valueTest(isTelegram, UsernameError, { platform: 'telegram', type: 'private' }))
        .when(...valueTest(isTelegramInvite, InvalidUrl, { platform: 'telegram', type: 'secret' }))

        .when(...valueTest(isAutoMessenger, UsernameError, { platform: 'messenger', type: 'auto' }))
        .when(...valueTest(isMessenger, UsernameError, { platform: 'messenger', type: 'private' }))

        .when(...valueTest(isAutoVk, UsernameError, { platform: 'vk', type: 'auto' }))
        .when(...valueTest(isVk, UsernameError, { platform: 'vk', type: 'private' }))

        .when(...valueTest(isSkype, UsernameError, { platform: 'skype' }))

        .when(...valueTest(isAutoInstagram, UsernameError, { platform: 'instagram', type: 'auto' })),
});

export const MessengerBlockSchema = object({
    messengers: array(MessengerItemSchema).min(1, 'errorsList.block_messengers_at_least_one'),
});
