import { object, string, array } from 'yup';

export const AccordionItemSchema = object({
    title: string()
        .ensure()
        .min(3, 'errorsList.block_accordion_item_title_length')
        .max(250, 'errorsList.block_accordion_item_title_length'),
    content: string()
        .ensure()
        .min(3, 'errorsList.block_accordion_item_content_length')
        .max(20000, 'errorsList.block_accordion_item_content_length'),
});

export const AccordionBlockSchema = object({
    items: array(AccordionItemSchema).min(1, 'errorsList.block_accordion_count'),
});
