import React from 'react';
import PropTypes from 'prop-types';

import { cls } from '@mssgme/helpers';
import { Spinner } from '@mssgme/ui';

import styles from './ActionButton.module.scss';

export default function ActionButton({
    isFetching,
    icon: Icon,
    label,
    tag: Tag,
    color,
    style,
    children,
    compact,
    ...rest
}) {
    return (
        <Tag
            style={{ backgroundColor: color, pointerEvents: isFetching ? 'none' : 'auto', ...style }}
            {...cls({ [styles.root]: true, [styles.compact]: compact }, rest)}
        >
            <div className={styles.icon}>
                {isFetching ? (
                    <Spinner color="white" size={compact ? 24 : 16} />
                ) : (
                    <Icon size={compact ? 24 : 16} color="#fff" />
                )}
            </div>
            <div className={styles.label}>{label}</div>
            {children}
        </Tag>
    );
}

ActionButton.propTypes = {
    isFetching: PropTypes.bool,
    icon: PropTypes.any.isRequired,
    label: PropTypes.string.isRequired,
    tag: PropTypes.any,
    color: PropTypes.string,
    style: PropTypes.object,
    children: PropTypes.any,
    compact: PropTypes.bool,
};

ActionButton.defaultProps = {
    isFetching: false,
    tag: 'div',
    color: '#007aff',
    style: {},
    children: null,
    compact: false,
};
