import { cloneDeep, merge, uniqBy } from 'lodash';

import { getDefaultTheme, HeadingBlockKind, TextBlockKind } from '@mssgme/shared';

export const BLANK_TAG = { tag: 'blank', label: 'blank' };
export const TEMPLATE_TAGS = [
    { tag: 'links', label: 'links', icon: '' },
    { tag: 'contacts', label: 'contacts', icon: '' },
    { tag: 'shop', label: 'shop', icon: '' },
];

export const createBlankTemplate = ({ t }) => ({
    _id: '',
    name: t('Website.Edit.ChooseTemplate.tags.blank'),
    uploads: [],
    secondaryTags: [],
    primaryTags: ['blank'],
    theme: merge(cloneDeep(getDefaultTheme()), {
        page: {
            alignment: 'middle',
        },
    }),
    blocks: [
        {
            _id: 'header',
            kind: HeadingBlockKind,
            enabled: true,
            content: t('Website.Edit.ChooseTemplate.blankLabels.heading'),
            theme: {
                blocks: { heading: { textAlign: 'center' } },
            },
        },
        {
            _id: 'text',
            kind: TextBlockKind,
            enabled: true,
            content: t('Website.Edit.ChooseTemplate.blankLabels.description'),
            theme: {
                blocks: { text: { textAlign: 'center' } },
            },
        },
    ],
});

export const filterTemplatesByTags = (templates, tags) => {
    /** @member {{ _id: string }[][]} taggedTemplates */
    const taggedTemplates = tags.map(({ tag }) => templates.filter(hasPrimaryTag(tag)));
    const templatesList = [].concat(...taggedTemplates);

    return uniqBy(templatesList, '_id');
};

export const hasPrimaryTag = (tag) => ({ primaryTags }) => primaryTags.includes(tag);

export const getPrimaryTag = (template) => template?.primaryTags?.[0];
