import React from 'react';

import { RangeProps } from './types';
import { RangeSlider } from '../Inputs';
import FieldRow from './FieldRow';

const numberToString = (value, min, max) => String(Math.max(min, Math.min(+value || 0, max)));
const stringToNumber = (value, min, max) => Math.max(min, Math.min(+value || 0, max));

const RangeSliderField = (props) => (
    <FieldRow {...props}>
        {({
            field: { value, name, onChange, builderInput, ...field },
            form: { setFieldValue, setFieldTouched },
            meta: { initialValue },
        }) => (
            <RangeSlider
                name={name}
                value={+numberToString(value, field.min, field.max)}
                onChange={(e) => {
                    const normalized = stringToNumber(e.target.value, field.min, field.max);

                    if (props.onChange) {
                        props.onChange(normalized);
                    } else {
                        setFieldValue(name, normalized);
                        setFieldTouched(name, initialValue !== normalized, initialValue !== normalized);
                    }
                }}
                {...field}
            />
        )}
    </FieldRow>
);

RangeSliderField.propTypes = RangeProps;

export default RangeSliderField;
