import { Styling } from '@mssgme/ui';

export const subscriptionFormStylingFactory = ({ t, isAdvancedMode, fields }) => {
    return {
        ...Styling.d.section([
            isAdvancedMode && [
                fields.borderRadius({
                    min: 1,
                    max: 30,
                }),
                fields.color({
                    path: 'color',
                    label: t('Page.Edit.SubscriptionForm.styleLabels.labelsColor'),
                }),
                fields.color({
                    path: 'inputTextColor',
                    label: t('Page.Edit.SubscriptionForm.styleLabels.inputTextColor'),
                }),
                fields.color({
                    path: 'inputBackgroundColor',
                    label: t('Page.Edit.SubscriptionForm.styleLabels.inputBackgroundColor'),
                }),
                fields.color({
                    path: 'buttonTextColor',
                    label: t('Page.Edit.SubscriptionForm.styleLabels.buttonTextColor'),
                }),
                fields.color({
                    path: 'buttonBackground',
                    label: t('Page.Edit.SubscriptionForm.styleLabels.buttonBackground'),
                }),
            ],
        ]),
    };
};
