import arrayMove from 'array-move';
import React, { useCallback, useEffect, useState } from 'react';
import { isEqual } from 'lodash';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';

import { useFetching } from '@mssgme/hooks';
import { mediaUtils } from '@mssgme/shared';
import { ImageListUpload, ACCEPT_IMAGES } from '../Inputs';
import { ImageListFieldProps } from './types';
import FieldRow from './FieldRow';

export const InvalidImagesCount = 'InvalidImagesCount';

export const useUploadCallback = ({ upload, current, max, accept, onChange }) => {
    const { t } = useTranslation('ui');
    const [errors, setErrors] = useState(false);
    const [isUploading, setUploading] = useState(false);
    const flags = useFetching(
        async ({ target: { files: raw } }) => {
            setUploading(true);

            try {
                const files = raw && Array.from(raw || []).filter(({ type }) => accept.includes(type));

                if (!files.length) {
                    return;
                }

                setErrors(false);

                if (current.length + files.length > max) {
                    return setErrors(t('ImageList.errors.invalid_images_count', { max }));
                }

                const { success, failed } = await upload({ payloads: files });

                if (failed.length) {
                    setErrors(
                        failed
                            .map(({ file, error }) => t('ImageList.errors.upload_failed', { file, error: t(error) }))
                            .join('. ')
                    );
                }

                if (success.length) {
                    onChange(success.map(({ result }) => result));
                }
            } finally {
                setUploading(false);
            }
        },
        [upload, onChange]
    );

    return {
        ...flags,
        pending: isUploading,
        errors,
    };
};

const toTag = (list) => [...list].sort().join('*');

export default function ImageListField({ as: Component, upload, name, transformUpload, onChange, children, ...rest }) {
    const { setFieldError, getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const { value } = getFieldMeta(name);

    const handleChange = useCallback(
        /**
         * @param {{ value: Array<*>, action: string, changed: boolean }} payload Change descriptor
         */
        (payload) => {
            const changed = !isEqual(toTag(value), toTag(payload.value));

            if (onChange) {
                onChange({ changed, ...payload });
            } else {
                setFieldValue(name, payload.value);
                setFieldTouched(name, changed, false);
            }
        },
        [value, onChange]
    );
    const handleSelect = useCallback(
        (uploads) => {
            if (!Array.isArray(uploads)) {
                uploads = [uploads];
            }

            handleChange({
                value: [...value, ...uploads.map(transformUpload)],
                action: 'add',
                uploads,
            });
        },
        [value, handleChange]
    );
    const { run: handleUpload, pending: isFetching, errors, getValidationError } = useUploadCallback({
        upload,
        name,
        current: value,
        max: rest.maxImages,
        accept: rest.accept,
        onChange: handleSelect,
    });
    const handleRemove = useCallback(
        (url, index) => {
            let filteredIndex;
            const relative = mediaUtils.getRelativeUrl(url);
            const atIndex = value[index];

            if (atIndex === url || atIndex === relative) {
                filteredIndex = index;
            } else {
                filteredIndex = value.findIndex((item) => item === url || mediaUtils.getRelativeUrl(item) === relative);
            }

            handleChange({
                value: value.filter((_, i) => i !== filteredIndex),
                action: 'remove',
            });
        },
        [value, handleChange]
    );
    const handleSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            handleChange({
                value: arrayMove(value, oldIndex, newIndex),
                action: 'sort',
            });
        },
        [value, handleChange]
    );

    const error = errors || getValidationError(name);

    useEffect(() => {
        if (error) {
            setFieldError(name, error);
            setFieldTouched(name, true, false);
        }
    }, [error]);

    return (
        <FieldRow name={name} {...rest}>
            {({ field }) => (
                <Component
                    {...field}
                    isUploading={isFetching}
                    onUpload={handleUpload}
                    onSelect={handleSelect}
                    onSortEnd={handleSortEnd}
                    onRemove={handleRemove}
                >
                    {children}
                </Component>
            )}
        </FieldRow>
    );
}

ImageListField.propTypes = ImageListFieldProps;

ImageListField.defaultProps = {
    as: ImageListUpload,
    maxImages: 6,
    accept: ACCEPT_IMAGES,
    multiple: true,
    transformUpload: ({ url }) => url,
};
