import React, { useCallback, useContext } from 'react';

const BlockEditContext = React.createContext();

const initialState = { callback: null, mixin: null };
const reducer = (state, action) => {
    switch (action.type) {
        case 'callback':
            return { ...state, callback: action.callback };

        case 'mixin':
            return { ...state, mixin: action.mixin };

        case 'reset':
            return initialState;
    }
};

export const BlockEditContextProvider = ({ children }) => {
    const [{ callback, mixin }, dispatch] = React.useReducer(reducer, initialState);
    const setCallback = useCallback((callback) => dispatch({ type: 'callback', callback }), [dispatch]);
    const setMixin = useCallback((mixin) => dispatch({ type: 'mixin', mixin }), [dispatch]);
    const reset = useCallback(() => dispatch({ type: 'reset' }), [dispatch]);

    return (
        <BlockEditContext.Provider value={{ callback, mixin, setCallback, setMixin, reset }}>
            {children}
        </BlockEditContext.Provider>
    );
};

/**
 * @returns {{ callback: Function, mixin: object|null, setCallback: Function, setMixin: Function, reset: Function }} Context
 */
export const useBlockEditContext = () => {
    const context = useContext(BlockEditContext);

    if (!context) {
        throw new Error('BlockEditContext provider is missing in the hierarchy');
    }

    return context;
};
