import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation, Trans } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, CopyLink, ModalPane } from '@mssgme/ui';
import { useWebsiteUrl } from '../../../../hooks';

import TikTokTipImage from '../../../../img/tiktokTip.jpg';

import styles from './TikTokShare.module.scss';

export function TikTokShare({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl, state: { backPath: historyBackPath } = {} } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const shareUrl = useWebsiteUrl(website, page);
    const closePath = generateUrl(historyBackPath || backPath);

    return (
        <ModalPane title={t('Editor.TikTokShare.title')} onClose={closePath}>
            <div className={styles.content}>
                <ol>
                    <li>
                        <Trans t={t} i18nKey="Editor.TikTokShare.steps.1" />
                    </li>
                </ol>
                <CopyLink className={styles.userLink} href={shareUrl} />
                <ol start="2">
                    <li>
                        <Trans t={t} i18nKey="Editor.TikTokShare.steps.2" />
                    </li>
                    <li>
                        <Trans t={t} i18nKey="Editor.TikTokShare.steps.3" />
                    </li>
                </ol>
                <img className={styles.tipImg} src={TikTokTipImage} alt="Tip" />
                <Button
                    fullWidth
                    tag="div"
                    btnStyle="transparent"
                    style={{ fontSize: 16, marginBottom: 16 }}
                    onClick={() => window.open('https://www.tiktok.com/', '_blank')}
                >
                    {t('Editor.TikTokShare.openTikTok')}
                </Button>
            </div>
        </ModalPane>
    );
}

TikTokShare.propTypes = {
    backPath: PropTypes.string,
};

TikTokShare.defaultProps = {
    backPath: '..',
};
