import { createContext, useContext, useEffect, useMemo, useState } from 'react';

import { useDrag } from '../../hooks';

export const SwipeableContext = createContext({ openedId: null, onAside: () => {} });

export const useSwipeableContext = () => useContext(SwipeableContext);

export const useSwipeable = ({ id, beforeWidth, afterWidth }) => {
    const [isLocked, setLocked] = useState(0);
    const [commit, setCommit] = useState(0);
    const { openedId, onAside } = useSwipeableContext();
    const active = !(openedId && openedId !== id);
    const { isDragging, offset, bind } = useDrag({ active, dataElement: `element-drag-${id}` });
    const supposedOffset = isLocked + offset.x;
    const breakpoint = isDragging ? Math.abs(supposedOffset) : 0;
    const isUnderlayed = isDragging || !!isLocked;
    const isBefore = isUnderlayed && supposedOffset > 0;
    const isAfter = isUnderlayed && supposedOffset < 0;
    const dir = Math.sign(supposedOffset);
    const breakOut = dir * (dir < 0 ? afterWidth : beforeWidth);
    const progress =
        (commit ? Math.min(Math.abs(commit), Math.abs(supposedOffset)) : Math.abs(supposedOffset)) /
        (Math.abs(breakOut) || 1);

    const containersStyle = useMemo(() => {
        const active = isDragging || isLocked;

        return {
            before: {
                opacity: progress,
            },
            after: {
                opacity: progress,
            },
            inner: active
                ? {
                    transform: `translateX(${supposedOffset}px)`,
                    pointerEvents: isDragging ? 'none' : 'auto',
                    transition: isDragging ? 'none' : undefined,
                }
                : null,
        };
    }, [isDragging, isLocked, supposedOffset, progress]);

    useEffect(() => {
        const breakIn = breakOut * 0.5;

        if (!(breakpoint && breakOut)) {
            return;
        }

        if (breakpoint >= Math.abs(breakOut)) {
            if (commit !== breakOut) {
                setCommit(breakOut);
            }
        } else if (breakpoint && commit && breakpoint <= Math.abs(breakIn)) {
            setCommit(0);
        }
    }, [breakOut, breakpoint, commit]);

    useEffect(() => {
        if (!isDragging && isLocked !== commit) {
            setLocked(commit);
        }
    }, [isDragging, isLocked]);

    useEffect(() => {
        if (active) {
            return;
        }

        setLocked(0);
        setCommit(0);
    }, [active]);

    useEffect(() => void onAside(id, isUnderlayed), [onAside, id, isUnderlayed]);

    return {
        bind,
        containersStyle,
        isUnderlayed,
        isBefore,
        isAfter,
        progress,
    };
};
