import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { IoMdImage } from 'react-icons/io';

import { __t, mediaUtils } from '@mssgme/shared';
import { LazyImage, renderNode } from '@mssgme/ui';

import styles from './ProductAdminItem.module.scss';

// todo: reuse ProductItem here
export function ProductAdminItem({
    tag: Tag = 'div',
    outer: Outer = 'div',
    product,
    to,
    renderMeta,
    onClick,
    noPadding,
}) {
    const { t } = useTranslation('products');
    const meta = (
        <>
            <div className={styles.title}>{product.name}</div>

            <div className={styles.columns}>
                <div>
                    {product.price && (
                        <div className={styles.price}>
                            {product.price.formattedDiscount && (
                                <span className={styles.discountPrice}>{product.price.formattedDiscount}</span>
                            )}
                            {product.price.amount === 0 ? (
                                __t('free')
                            ) : (
                                <span className={product.price.formattedDiscount ? styles.lineThrough : null}>
                                    {product.price.formatted}
                                </span>
                            )}
                        </div>
                    )}

                    <div className={cn(styles.status, product.published && styles.published)}>
                        {t(`labels.published.${!!product.published}`)}
                    </div>
                </div>

                {product.notes && <div className={styles.notes}>{product.notes}</div>}
            </div>
        </>
    );

    const handleClick = useCallback(
        (e) => {
            e.preventDefault();

            onClick(product);
        },
        [onClick, product]
    );

    return (
        <Outer
            className={cn({
                [styles.root]: true,
                [styles.noPadding]: noPadding,
                [styles.disabled]: !product.published,
            })}
        >
            <Tag className={styles.productRoot} to={to} title={product.name} onClick={onClick && handleClick}>
                <div className={styles.thumbnail}>
                    {product.photoUrl ? (
                        <LazyImage src={mediaUtils.generateProductThumbnailUrl(product.photoUrl)} alt={product.name} />
                    ) : (
                        <div className={styles.emptyPlaceholder}>
                            <IoMdImage size="40%" color="#fff" />
                        </div>
                    )}
                </div>
                <div className={styles.meta}>{renderNode(renderMeta || meta, { product, meta })}</div>
            </Tag>
        </Outer>
    );
}

ProductAdminItem.propTypes = {
    product: PropTypes.object.isRequired,
    tag: PropTypes.any,
    outer: PropTypes.any,
    hasDragHandle: PropTypes.bool,
    to: PropTypes.string,
    renderMeta: PropTypes.func,
    onClick: PropTypes.func,
    noPadding: PropTypes.bool,
};
