import { useCallback, useEffect, useRef, useState } from 'react';
import { useUnmount } from 'react-use';

export const useListFilter = (initial, onChange) => {
    const ref = useRef(onChange);
    const [filter, setFilter] = useState(initial);

    const handleFilter = useCallback(
        ({ target: { name, value } }) => setFilter((old) => (old[name] === value ? old : { ...old, [name]: value })),
        []
    );
    const clearFilter = useCallback(() => setFilter(initial), []);

    ref.current = onChange;

    useEffect(() => void ref.current?.(filter), [filter]);
    useUnmount(() => ref.current?.(initial));

    return {
        filter,
        clearFilter,
        setFilter,
        handleFilter,
    };
};

