import { useHistory } from 'react-router-dom';

import { clearWebsitePayload, useActions, useEventCallback, useFetching, useNavigationUtils } from '@mssgme/hooks';
import { usePublishCheck } from './usePublishCheck';

// eslint-disable-next-line jsdoc/require-returns
/**
 * @param {*} website Website
 * @param {string} [publishedUrl] Custom routes to navigate to
 */
export const usePublish = (website, publishedUrl) => {
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { api } = useActions();
    const history = useHistory();
    const warning = usePublishCheck(website.pages);
    const pathname = generateUrl(publishedUrl || '.');

    const handleSuccess = useEventCallback(() => history.push({ pathname, state: { from: 'publishedMessage' } }));
    const showPublishModal = useEventCallback(
        (pageId, pathname) => history.push(`/pages/-/${website._id}/${pageId}/edit/publish/${pathname}`)
    );

    const handlePublish = useFetching(async () => {
        const emptyPage = website.pages.find((page) => !page.blocks.length);

        if (emptyPage) {
            showPublishModal(emptyPage._id, 'no-blocks');
        } else {
            if (warning) {
                return showPublishModal(warning.pageId, 'has-advanced-blocks');
            }

            await api.websites.update({
                websiteId: website._id,
                payload: { ...clearWebsitePayload(website), enabled: true, scaffold: false },
            });

            handleSuccess();
        }
    }, [website, warning]);

    return {
        ...handlePublish,
        canPublish: !warning,
    };
};
