import { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { useMeasure } from 'react-use';

import { useMergedRefs } from '@mssgme/hooks';
import { getClientRect, identityRect } from './helpers';

export const useClientRect = (deps = []) => {
    const [rect, setRect] = useState(identityRect);
    const [measureRef, pos] = useMeasure();
    const divRef = useRef();
    const refFn = useCallback((newRef) => setRect(getClientRect((divRef.current = newRef))), []);
    const ref = useMergedRefs([refFn, measureRef]);

    useLayoutEffect(() => {
        setRect(getClientRect(divRef.current));
    }, [pos, ...deps]);

    return [ref, rect];
};
