import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { List } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, ListBlockSchema, StyledBlockPreview, useBlockEditor } from '../components';
import { ListItemsField } from './ListItemsField';

export function ListEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: ListEdit });

    return (
        <BlockEditForm {...bind} validationSchema={ListBlockSchema} title={t('Page.Edit.List.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <ListItemsField name="items" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

ListEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

ListEdit.block = List;
