import { useTranslation } from 'react-i18next';

import { PAYMENT_PROVIDERS } from '@mssgme/shared';
import { PAYMENT_PROVIDER_FORMS } from '../UI';
import { useProductsContext } from './useProductsContext';

export const usePaymentsConfigValidity = (provider) => {
    const { t } = useTranslation('products');
    const { crm } = useProductsContext();
    const { supported_currencies } = PAYMENT_PROVIDERS[provider] || {};
    const { title } = PAYMENT_PROVIDER_FORMS[provider] || {};
    const currency = crm.defaultCurrency.toUpperCase();
    const isSupportedCurrency = !(supported_currencies && !supported_currencies.includes(currency));

    return {
        valid: isSupportedCurrency,
        tip: !isSupportedCurrency && t(`Payments.${title}.unsupportedCurrencyTip`, { currency }),
    };
};
