import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Socials } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, SocialsBlockSchema, StyledBlockPreview, useBlockEditor } from '../components';
import { SocialsItemsField } from './SocialItemsField';

export function SocialsEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: SocialsEdit });

    return (
        <BlockEditForm {...bind} validationSchema={SocialsBlockSchema} title={t('Page.Edit.Socials.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <SocialsItemsField name="items" />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

SocialsEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

SocialsEdit.block = Socials;
