import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Divider } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

export function DividerEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: DividerEdit, isInitialDirty: !blockId });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Divider.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: -1 }}>
                        <bind.blockComponent embedded block={values} />
                    </StyledBlockPreview>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

DividerEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

DividerEdit.block = Divider;
