import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { SortableElement, SortableContainer } from 'react-sortable-hoc';
import { FiPlus } from 'react-icons/fi';
import { RiVipCrown2Line } from 'react-icons/ri';

import Spinner from '../../Spinner';

import styles from './ImageListUpload.module.scss';

export const ACCEPT_IMAGES = ['image/png', 'image/x-png', 'image/jpeg'];

export const ImageListUploadItem = SortableElement(({ url, removePhoto, itemIndex }) => {
    const handleRemove = useCallback(() => void removePhoto(url, itemIndex), [url, removePhoto]);

    return (
        <li className={styles.imagePreview}>
            <div className={styles.imagePreviewContainer}>
                <img src={url} alt={url} />
            </div>
            {itemIndex === 0 && (
                <div className={styles.mainImageIcon}>
                    <RiVipCrown2Line size={20} color="#fff" />
                </div>
            )}
            <div className={styles.removePhotoBtn} onClick={handleRemove} data-id="remBtn">
                <FiPlus size={12} />
            </div>
        </li>
    );
});

export const ImageListUpload = SortableContainer(
    ({
        name,
        value,
        multiple,
        maxImages,
        accept,
        isUploading,
        disabled,
        children,
        onUpload,
        onRemove,
        className,
        Item,
    }) => {
        const addButton = (
            <div key="add-button" className={styles.addPhotoBtn}>
                <div className={styles.addPhotoBtnContent}>
                    {isUploading ? <Spinner size={14} /> : <FiPlus size={24} color="#007aff" />}
                </div>
                {!isUploading && (
                    <input
                        key={isUploading}
                        type="file"
                        name={name}
                        multiple={multiple}
                        disabled={disabled}
                        onChange={onUpload}
                        className={styles.addPhotoBtnInput}
                        accept={accept.join(',')}
                    />
                )}
            </div>
        );

        return (
            <ul className={cn(styles.root, disabled && styles.disabled, className)}>
                {(value || []).map((url, index) => (
                    <Item key={`${url}-${index}`} index={index} itemIndex={index} url={url} removePhoto={onRemove} />
                ))}

                {!(maxImages && (value || []).length >= maxImages) &&
                    (children ? (
                        React.Children.map(children({ addButton }), (child, index) => (
                            <li key={child.key || index} className={styles.imagePreview}>
                                {child}
                            </li>
                        ))
                    ) : (
                        <li className={styles.imagePreview}>{addButton}</li>
                    ))}
            </ul>
        );
    }
);

ImageListUpload.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.array.isRequired,
    accept: PropTypes.arrayOf(PropTypes.string.isRequired),
    maxImages: PropTypes.number,
    multiple: PropTypes.bool,
    isUploading: PropTypes.bool,
    isFetching: PropTypes.bool,
    children: PropTypes.func,
    onSortEnd: PropTypes.func,
    onUpload: PropTypes.func,
    onRemove: PropTypes.func,
    Item: PropTypes.elementType,
};

ImageListUpload.defaultProps = {
    ...ImageListUpload.defaultProps,
    Item: ImageListUploadItem,
    axis: 'xy',
    maxImages: 6,
    accept: ACCEPT_IMAGES,
    multiple: true,
    helperClass: styles.sortable,
    shouldCancelStart: (e) => {
        if (e.target.dataset.id === 'remBtn') {
            return true;
        }
    },
};
