import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import styles from './ProductEdit.module.scss';

export const BadgeLabel = ({ active }) => {
    const { t } = useTranslation('products');

    return (
        <div className={styles.publishedLabel}>
            <span className={cn(styles.publishedDot, active && styles.green)} />
            <span>{active ? t('productEdit.fields.active') : t('productEdit.fields.inactive')}</span>
        </div>
    );
};

BadgeLabel.propTypes = {
    active: PropTypes.bool,
};
