import React from 'react';
import { flatten } from 'lodash';

import { GENERIC_ICON_GROUPS, socialIconColor, GenericIcon } from '../GenericIcon';

import ListField from './ListField';
import styles from './IconsField.module.scss';

const GENERIC_ICON_GROUPS_ENTRIES = Object.entries(GENERIC_ICON_GROUPS);

/* eslint-disable react/display-name */

const socialIconOption = (t, group, iconName) => ({
    group,
    value: iconName,
    label: iconName || t('ui:labels.notSet'),
    // rounded: !!iconName,
    shortLabel: !iconName && t('ui:labels.notSet'),
    fullWidth: !iconName,
    props: {
        children: iconName ? (
            <div className={styles.iconPreview}>
                <div className={styles.iconWrapper}>
                    <GenericIcon name={iconName} size={20} style={{ color: socialIconColor(iconName) }} />
                </div>
            </div>
        ) : (
            <div className={styles.iconNoIcon}>{t('ui:labels.noIcon')}</div>
        ),
    },
});

export const iconFieldPropsFactory = ({ t, nullable, groupLabel, skip }) => {
    const allGroups = skip
        ? GENERIC_ICON_GROUPS_ENTRIES.map(([group, icons]) => [group, icons.filter((icon) => !skip.includes(icon))])
        : GENERIC_ICON_GROUPS_ENTRIES;
    const options = flatten(
        allGroups.map(([group, iconNames]) => iconNames.map((iconName) => socialIconOption(t, group, iconName)))
    );
    const groups = allGroups.map(([group, icons]) => ({
        id: group,
        label: groupLabel(group),
        values: icons,
    }));

    if (nullable) {
        options.push(socialIconOption(t, 'default', null));
        groups.push({
            id: 'default',
            label: '',
            values: [null],
        });
    }

    return {
        options,
        groups,
    };
};

export default function IconsField(props) {
    return (
        <ListField
            containerClassName={styles.iconsContainer}
            choiceClassName={styles.choiceButton}
            activeClassName={styles.active}
            groupLabelClassName={styles.groupLabel}
            fullWidthClassName={styles.fullWidth}
            {...props}
        />
    );
}

IconsField.propTypes = ListField.propTypes;
