import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useWebsitePage, useWebsiteBySlug, useNavigationUtils } from '@mssgme/hooks';
import { StyleEditor } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageStylingAlignment() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { website, page } = useWebsitePage(useWebsiteBySlug(websiteSlug));
    const closePath = useMemo(() => generateUrl('../..'), [websiteSlug]);

    return (
        <PageEditForm
            website={website}
            page={page}
            title={t('Page.fields.contentAlignment.label')}
            backPath={closePath}
        >
            {({ styling }) => <StyleEditor styling={styling} focusPaths={[{ path: 'alignment', undecorated: true }]} />}
        </PageEditForm>
    );
}
