import { useCallback } from 'react';

const { useTranslation } = require('react-i18next');

export function useFormatPrice() {
    const { t } = useTranslation();
    return useCallback(
        ({ amount, currency }) => {
            return t(`prices.${currency}`, { amount, defaultValue: `${amount} ${currency}` });
        },
        [t]
    );
}
