import cn from 'classnames';
import TinyColor2 from 'tinycolor2';
import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import { FiCheck } from 'react-icons/fi';

import { isLightColor } from '@mssgme/helpers';
import styles from './ColorPaletteField.module.scss';

const ColorSwatch = ({ small, isSelected, value, onSelect }) => {
    const handleClick = useCallback(() => onSelect(value, true), [value, onSelect]);
    const color = new TinyColor2(value);
    const isLight = isLightColor(color);

    return (
        <div key={color} className={cn(styles.colorBlock, small && styles.small)}>
            <div className={styles.colorItem}>
                <div className={styles.colorPreview} style={{ background: color.toHex8String() }} onClick={handleClick}>
                    <FiCheck
                        className={cn({ [styles.checkMark]: true, [styles.active]: isSelected })}
                        color={isLight ? '#333' : '#fff'}
                        size={small ? 16 : 22}
                    />
                </div>
            </div>
        </div>
    );
};

ColorSwatch.propTypes = {
    isSelected: PropTypes.bool,
    small: PropTypes.bool,
    value: PropTypes.any.isRequired,
    onSelect: PropTypes.func.isRequired,
};

export default function ColorPaletteField({ small, value, onChange, colors }) {
    return (
        <div className={styles.root}>
            <div className={styles.colorsList}>
                {colors.map((color) => (
                    <ColorSwatch
                        key={color}
                        small={small}
                        value={color}
                        isSelected={color === value}
                        onSelect={onChange}
                    />
                ))}
            </div>
        </div>
    );
}

ColorPaletteField.propTypes = {
    small: PropTypes.bool,
    value: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    colors: PropTypes.array.isRequired,
};
