import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Messengers } from '@mssgme/userpage-core';
import { MessengersItemsField } from '@mssgme/ui';
import {
    BlockEditForm,
    EditStylingForm,
    MessengerBlockSchema,
    MessengerItemSchema,
    StyledBlockPreview,
    useBlockEditor,
} from '../components';

export function MessengersEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: MessengersEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Messengers.heading')} validationSchema={MessengerBlockSchema}>
            {({ styling, values }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }} contentStyle={{ padding: 0 }}>
                        <bind.blockComponent block={values} />
                    </StyledBlockPreview>

                    <MessengersItemsField
                        name="messengers"
                        mode="small"
                        validationSchema={MessengerItemSchema}
                        style={{ marginBottom: 16 }}
                    />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

MessengersEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

MessengersEdit.block = Messengers;
