import React, { useCallback, useState } from 'react';
import cn from 'classnames';

import Input from '../Input';

import styles from './CustomViewInput.module.scss';

export const CustomViewInput = ({ onBlur, containerClassName, className, children, onKeyDown, disabled, ...rest }) => {
    const [focused, setFocused] = useState(false);

    const onInputFocus = useCallback((e) => {
        if (disabled) {
            return;
        }

        e.preventDefault();
        e.stopPropagation();

        setFocused(true);
    }, [disabled]);

    const onInputKeyDown = useCallback(
        (event) => {
            if (onKeyDown && false === onKeyDown(event)) {
                return;
            }

            const key = event.key;

            if (key !== 'Escape') {
                return;
            }

            event.preventDefault();

            setFocused(false);
        },
        [onKeyDown]
    );

    const onFieldBlur = useCallback(
        (e) => {
            setFocused(false);

            if (onBlur) {
                onBlur(e);
            }
        },
        [onBlur]
    );

    return focused ? (
        <Input
            containerClassName={containerClassName}
            className={className}
            disabled={disabled}
            onKeyDown={onInputKeyDown}
            onBlur={onFieldBlur}
            autoFocus
            {...rest}
        />
    ) : (
        <div className={cn(styles.root, containerClassName)} tabIndex={0} onFocus={onInputFocus}>
            <div className={cn(styles.input, className)}>{children}</div>
        </div>
    );
};

CustomViewInput.propTypes = Input.propTypes;
