import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useHistory, useLocation } from 'react-router-dom';

import { useActions, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { NotificationCenter } from '@mssgme/ui';
import { SetupForm } from './SetupForm';
import { SetupBypass } from './SetupBypass';

export function PageSetup() {
    const history = useHistory();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const { state } = useLocation();
    const { website, page } = useWebsitePageBySlugs();

    const websiteId = website._id;
    const pageId = page._id;
    const templateId = state?.templateId;
    const backPath = useMemo(() => generateUrl('.'), []);
    const successPath = useMemo(() => generateUrl('../edit'), []);

    const handleApply = useCallback(
        async (templateId) => {
            try {
                if (templateId) {
                    await api.pages.applyTemplate({ websiteId, pageId, templateId });
                }

                setTimeout(() => history.push(successPath), 100);
            } catch (e) {
                NotificationCenter.getInstance().danger(t(e.message), { showClose: true });
            }
        },
        [t, websiteId, pageId, page, successPath]
    );

    if (templateId) {
        return <SetupBypass initialId={templateId} onApply={handleApply} />;
    }

    return (
        <SetupForm backPath={backPath} onApply={handleApply} />
    );
}
