import React from 'react';
import { Route, Redirect, Switch, useHistory } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { LinkButton } from '@mssgme/ui';

import { BackButton } from '../../UI';
import CreateWorkspaceForm from './CreateWorkspaceForm';
import styles from './EmptyWorkspace.module.scss';

const getStep = (location) => (location.pathname === '/' ? 1 : 2);

export function EmptyWorkspace() {
    const { t } = useTranslation();
    const { location } = useHistory();
    const step = getStep(location);

    return (
        <div className={styles.root}>
            <div className={styles.content}>
                <h2 className={styles.heading}>{`${t('emptyWorkspace.step')} ${step}/2`}</h2>
                <Switch>
                    <Route
                        exact
                        path="/"
                        render={() => (
                            <div>
                                <p className={styles.message}>{t('emptyWorkspace.step1.message')}</p>
                                <p className={styles.buttonsContainer}>
                                    <LinkButton to="/create">{t('emptyWorkspace.step1.button.create')}</LinkButton>
                                    <span>{t('ui:labels.or')}</span>
                                    <LinkButton to="/connect">{t('emptyWorkspace.step1.button.connect')}</LinkButton>
                                </p>
                            </div>
                        )}
                    />
                    <Route
                        exact
                        path="/connect"
                        render={() => (
                            <div>
                                <p className={styles.message}>{t('emptyWorkspace.step2_connect.message')}</p>
                            </div>
                        )}
                    />
                    <Route
                        exact
                        path="/create"
                        render={() => (
                            <div>
                                <p className={styles.message}>{t('emptyWorkspace.step2_create.message')}</p>
                                <CreateWorkspaceForm />
                            </div>
                        )}
                    />
                    <Redirect to="/" />
                </Switch>
                {step > 1 && (
                    <div className={styles.footer}>
                        <BackButton to="/" />
                    </div>
                )}
            </div>
        </div>
    );
}
