import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect, useHistory } from 'react-router-dom';
import { noop } from 'lodash';

import { isMatchId } from '@mssgme/helpers';
import { useActions, useWebsites } from '@mssgme/hooks';
import {
    Badge,
    CheckableItem,
    EnhancedFormik,
    FieldTip,
    StreamlinedScreen,
    SubmitButton,
    usePresenceRemoval,
} from '@mssgme/ui';
import { useImportableCollections, useProductsSublist, useProductsContext } from '../../../hooks';
import { CollectionsField, ProductsList, ProductsListFooter } from '../../../UI';

import styles from './ProductsImport.module.scss';

export function ProductsImport() {
    const history = useHistory();
    const websites = useWebsites();
    const { t } = useTranslation('products');
    const { api } = useActions();
    const { router, specifier, modal } = useProductsContext();
    const { items, excludes } = useImportableCollections(specifier);
    const websiteId = specifier.website;

    const renderItem = useCallback((col, active, styles) => (
        <CheckableItem active={active} disabled={!!col.isFixed}>
            <div className={styles.title}>{col.name}</div>
            <div className={styles.subtitle}>{t('labels.products', { count: col.productsCount })}</div>
        </CheckableItem>
    ), []);
    const groupItems = useCallback(
        (items) => {
            const hasGroups = items.find(({ website }) => website);

            if (hasGroups <= 1) {
                return null;
            }

            const unassigned = {
                key: '-',
                items: [],
                label: (
                    <div className={styles.colsGroup}>
                        <div>{t('labels.notImported')}</div>
                    </div>
                ),
            };
            const groups = [];
            const sites = new Map();
            const getWebsite = (id) => {
                const found = sites.get(id);

                if (found === undefined) {
                    const website = websites.find(isMatchId(id)) || null;

                    sites.set(id, website);

                    return website;
                }

                return found;
            };

            for (const item of items) {
                const websiteId = item.website;
                const website = getWebsite(websiteId);

                if (website) {
                    const group = groups.find((group) => group.key === websiteId);

                    if (group) {
                        group.items.push(item);
                    } else {
                        groups.push({
                            key: websiteId,
                            items: [item],
                            label: (
                                <div className={styles.colsGroup}>
                                    <div>{website.name}</div>
                                    <Badge badgeStyle="flat">{website.finalUrl}</Badge>
                                </div>
                            ),
                        });
                    }
                } else {
                    unassigned.items.push(item);
                }
            }

            if (unassigned.items.length) {
                groups.unshift(unassigned);
            }

            return groups;
        },
        [websites]
    );

    const handleSubmit = useCallback(
        async (payload) => {
            await api.products.migrate({ payload });

            history.push(router.toHome());
        },
        [router.toHome()]
    );
    const groups = useMemo(() => groupItems(items), []);

    const initialValues = useMemo(
        () => ({
            websiteId,
            collections: groups.length > 1 ? [] : items.map(({ _id }) => _id),
        }),
        [websiteId]
    );

    usePresenceRemoval();

    if (!websiteId) {
        return <Redirect to={router.toHome()} />;
    }

    if (!items.length) {
        return (
            <StreamlinedScreen preferDynamicBack inverse={false} nopadding={false} modal={modal}>
                <div className={styles.intro}>
                    <h1>{t('ProductsImport.title')}</h1>
                    <p>{t('ProductsImport.nothingToImport.header')}</p>
                    <p>{t('ProductsImport.nothingToImport.message')}</p>
                </div>
            </StreamlinedScreen>
        );
    }

    return (
        <StreamlinedScreen preferDynamicBack inverse={false} nopadding={false} modal={modal}>
            <div className={styles.intro}>
                <h1>{t('ProductsImport.title')}</h1>
                <p>{t('ProductsImport.subtitle')}</p>
            </div>

            <EnhancedFormik initialValues={initialValues} onSubmit={handleSubmit}>
                {({ values }) => (
                    <>
                        <CollectionsField
                            builderInput
                            name="collections"
                            label={t('ProductsImport.labels.collections')}
                            toggleLabel={t('ui:labels.change')}
                            emptyLabel={<FieldTip style={{ marginBottom: 16 }}>{t('ProductsImport.select')}</FieldTip>}
                            collections={items}
                            renderItem={renderItem}
                            groupItems={groupItems}
                        />

                        <SubmitButton fullWidth disabled={!values.collections.length} style={{ lineHeight: 44 }}>
                            {t('ProductsImport.labels.import')}
                        </SubmitButton>

                        <FilteredProductsList
                            key={values.collections.join(',')}
                            collections={values.collections}
                            specifier={excludes}
                        />
                    </>
                )}
            </EnhancedFormik>
        </StreamlinedScreen>
    );
}

export const FilteredProductsList = ({ collections, specifier }) => {
    const { t } = useTranslation('products');
    const { products, loadMore } = useProductsSublist(specifier, useMemo(() => collections.join(','), [collections]));

    if (!collections.length) {
        return null;
    }

    return (
        <div>
            <FieldTip style={{ marginTop: 16 }}>{t('ProductsImport.productsTip')}</FieldTip>
            <ProductsList products={products.items} onClick={noop} />
            <ProductsListFooter products={products} loadMore={loadMore} isSearch />
        </div>
    );
};

FilteredProductsList.propTypes = {
    collections: PropTypes.array.isRequired,
    specifier: PropTypes.object,
};
