import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo } from 'react';
import { useField } from 'formik';

import { useActions } from '@mssgme/hooks';
import { FieldRow, ListField } from '@mssgme/ui';
import { PagePreview } from '../../../PagePreview';
import { ThemeInput } from './ThemeInput';

import styles from './ThemeField.module.scss';

export { ThemeInput };

export const ThemeField = ({ onChange, page, ...rest }) => {
    const { ui } = useActions();
    const [{ value }, , { setValue }] = useField(rest.name);
    const websiteData = useMemo(() => ({ branding: false }), []);

    const handleSelect = useCallback(
        (value) => {
            if (onChange) {
                onChange(value);
            } else {
                setValue(value);
            }
        },
        [setValue, onChange]
    );

    useEffect(() => {
        if (!page) {
            return;
        }

        const option = rest.options.find((option) => option.value === value);

        ui.setPagePreview(option ? { ...page, theme: option.theme } : null);
    }, [rest.options, value]);

    return (
        <FieldRow name={rest.name}>
            <ThemeInput value={value} onChange={handleSelect} {...rest}>
                <div className={styles.pagePreview}>
                    <PagePreview website={websiteData} embedded />
                </div>
            </ThemeInput>
        </FieldRow>
    );
};

ThemeField.propTypes = {
    ...ListField.propTypes,
    ...ThemeInput.propTypes,
    page: PropTypes.object,
};
