import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { useFormikContext } from 'formik';
import { useDeepCompareEffect } from 'react-use';
import { useTranslation } from 'react-i18next';
import { useHistory } from 'react-router-dom';
import { FiPlus } from 'react-icons/fi';

import { toDashedCase } from '@mssgme/helpers';
import { useActions, useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, RangeSliderField, useAreaDrag } from '@mssgme/ui';
import { Custom, mapGridArea } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockChooser, useBlockEditor } from '../components';
import { AreaCover, clampRect } from './AreaCover';
import { LayoutMarkingContextProvider, useLayoutMarkingContext } from './LayoutContext';

import styles from './commonStyles.module.scss';

export const AddButton = ({ index, onClick }) => {
    const handleClick = useCallback(() => onClick(index), [index, onClick]);

    return (
        <Button tag="a" btnStyle="transparent" size="inline" nopadding noborder onClick={handleClick}>
            <FiPlus size={24} />
        </Button>
    );
};

AddButton.propTypes = {
    index: PropTypes.number.isRequired,
    onClick: PropTypes.func.isRequired,
};

export const LayoutField = ({ block, segments }) => {
    const history = useHistory();
    const { generateUrl } = useNavigationUtils({ useMatchUrl: false });
    const { marking, area, setArea } = useLayoutMarkingContext();
    const [bind, { isDragging, parent, area: marked, current }] = useAreaDrag();

    const dx = parent.width / segments;
    const dy = parent.height / segments;
    const clamped = marked && clampRect({ area: marked, dx, dy });
    const workArea = isDragging && current && clampRect({ area: current, dx, dy });
    const displayedArea = marking && (workArea || area);
    const handleEdit = useCallback(
        (e) => {
            let node = e.target;
            let href;

            while (node) {
                if ((href = node.dataset?.blockhref)) {
                    history.push(generateUrl(href));

                    break;
                }

                node = node.parentNode;
            }
        },
        [history, generateUrl]
    );

    const renderItem = useCallback(({ block, area, index, node }) => {
        if (node) {
            return (
                <div
                    key={block._id}
                    className={styles.element}
                    style={{ gridArea: mapGridArea(area), zIndex: index }}
                    {...{ 'data-blockhref': `./block/${toDashedCase(block.kind)}/${block._id}` }}
                    onClick={handleEdit}
                >
                    {React.cloneElement(node, {
                        style: { ...node.style, flex: 1 },
                    })}
                </div>
            );
        }

        return null;
    });

    useDeepCompareEffect(() => {
        setArea(clamped && { ...clamped, segments });
    }, [setArea, clamped || {}, segments]);

    const preview = (
        <Custom block={block} renderItem={renderItem} style={{ pointerEvents: displayedArea ? 'none' : 'auto' }} />
    );

    if (!marking) {
        return preview;
    }

    return (
        <div
            style={{
                display: 'grid',
                userSelect: 'none',
                cursor: isDragging ? 'grabbing' : 'grab',
                minHeight: parent.width || 335,
            }}
            {...bind}
        >
            {preview}

            {displayedArea && <AreaCover area={displayedArea} dx={dx} dy={dy} />}
        </div>
    );
};

LayoutField.propTypes = {
    block: PropTypes.object.isRequired,
    segments: PropTypes.number.isRequired,
};

export const ItemsField = () => {
    const { t } = useTranslation('landings');
    const {
        values: { areas },
        setFieldValue,
        setFieldTouched,
    } = useFormikContext();
    const { marking, area, toggleMarking } = useLayoutMarkingContext();

    const handleAccept = useBlockChooser(
        ({ action, blockId, isNew }) => {
            let newAreas = areas;

            switch (action) {
                case 'update': {
                    if (isNew && area) {
                        newAreas = [...areas, { blockId, area }];
                    }
                    break;
                }

                case 'remove': {
                    newAreas = areas.filter((a) => a.blockId !== blockId);
                    break;
                }

                case 'clone': {
                    break;
                }

                case 'cancel': {
                    return;
                }
            }

            if (newAreas !== areas) {
                setFieldValue('areas', newAreas);
                setFieldTouched('areas', true);
            }

            toggleMarking(false);
        },
        [areas, area, toggleMarking]
    );

    return (
        <div style={{ display: 'flex', placeItems: 'stretch', alignItems: 'stretch' }}>
            {marking && (
                <Button
                    style={{ flexGrow: 1, marginRight: 16 }}
                    socialStyle="socialBtnOnly"
                    tag="a"
                    size="small"
                    nopadding
                    noborder
                    disabled={!area}
                    onClick={handleAccept}
                >
                    <FiPlus size={24} />
                </Button>
            )}

            <Button
                style={{ flexGrow: 1 }}
                tag="a"
                btnStyle={marking ? 'primary' : 'transparent'}
                size="small"
                nopadding
                noborder
                onClick={toggleMarking}
            >
                {marking ? t('ui:button.cancel') : '+' + t(' ')}
            </Button>
        </div>
    );
};

ItemsField.propTypes = {};

export function CustomEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { website, page } = useWebsitePageBySlugs();

    const handleCleanup = useCallback(
        async (block) => {
            const blockIds = [];

            block.areas.forEach(({ blockId }) => blockId && blockIds.push(blockId));

            if (blockIds.length) {
                await api.blocks.deleteMany({
                    websiteId: website._id,
                    pageId: page._id,
                    payload: { blockId: blockIds },
                });
            }
        },
        [website._id, page._id]
    );
    const handleDone = useCallback(
        async ({ action, block, isNew }) => {
            switch (action) {
                case 'cancel': {
                    if (isNew) {
                        return void handleCleanup(block);
                    }

                    break;
                }
            }
        },
        [handleCleanup]
    );

    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: CustomEdit, onDone: handleDone });
    const [segments, setSegments] = useState(6);

    return (
        <BlockEditForm {...bind} isSafe={false} title={t('Page.Edit.Custom.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <LayoutMarkingContextProvider>
                        {({ marking }) => (
                            <>
                                <StyledBlockPreview style={{ border: 0, minHeight: 16 }}>
                                    <LayoutField block={values} segments={segments} />
                                </StyledBlockPreview>

                                {marking && (
                                    <RangeSliderField
                                        name="grid"
                                        value={segments}
                                        label=""
                                        disabled={!marking}
                                        min={1}
                                        max={12}
                                        step={1}
                                        onChange={setSegments}
                                    />
                                )}

                                {/*<ItemsField />*/}
                            </>
                        )}
                    </LayoutMarkingContextProvider>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

CustomEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

CustomEdit.block = Custom;
