import { array, number, object, string } from 'yup';

import { CURRENCY } from '@mssgme/shared';

export const ServicesGridItemSchema = object({
    title: string()
        .trim()
        .ensure()
        .min(3, 'common:errorsList.block_services_grid_item_title_length')
        .max(50, 'common:errorsList.block_services_grid_item_title_length'),
    description: string()
        .trim()
        .ensure()
        .max(4000, 'common:errorsList.block_services_grid_item_description_length'),
    photos: array()
        .min(1, 'common:errorsList.block_services_grid_item_photos_required')
        .max(8, 'common:errorsList.block_services_grid_item_photos_max_count'),
    price: object()
        .nullable()
        .shape({
            amount: number()
                .nullable()
                .min(0, 'common:errorsList.price.amount_invalid')
                .transform((value) => {
                    if (typeof value === 'number') {
                        return value;
                    }

                    return null;
                }),
            discount: number()
                .nullable()
                .min(0, 'common:errorsList.price.amount_invalid')
                .test('isValidDiscount', 'common:errorsList.price.greater_discount', function(value) {
                    return value === null || value < this.parent.amount;
                }),
            currency: string()
                .required('common:errorsList.field_required')
                .oneOf(CURRENCY.codes, 'common:errorsList.invalid_value'),
        })
        .transform((value) => {
            if (value) {
                return value;
            }

            return null;
        }),
    url: string()
        .ensure()
        .trim()
        .url('common:errorsList.enter_url')
        .max(800, 'common:errorsList.url_length'),
});

export const ServicesGridBlockSchema = object({
    items: array(ServicesGridItemSchema).min(1, 'common:errorsList.block_services_grid_count'),
});
