import { useEffect, useState } from 'react';
import { COMMAND_PRIORITY_CRITICAL, SELECTION_CHANGE_COMMAND } from 'lexical';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { mergeRegister } from '@lexical/utils';

import { useEventCallback } from '@mssgme/hooks';
import { useDebouncedCallback } from '../../ui';
import { initialState, isPartiallyEqual } from './utils';
import { getEditorContextState } from './getEditorContextState';

/**
 * @returns {typeof initialState} Context object and command handler
 */
export function useRichEditorContext() {
    const [editor] = useLexicalComposerContext();
    const [state, setState] = useState(initialState);

    const $updateFromState = useDebouncedCallback((editorState) => {
        editorState.read(() => {
            if (editor.isComposing()) {
                return;
            }

            const patch = getEditorContextState(editor, state);

            if (patch) {
                updateState(patch);
            }
        });
    }, 50);

    const updateState = useEventCallback((change) => {
        if (!isPartiallyEqual(change, state)) {
            setState((old) => ({ ...old, ...change }));
        }
    });

    useEffect(() => {
        const read = () => $updateFromState(editor.getEditorState());
        read();

        return mergeRegister(
            editor.registerRootListener((root) => updateState({ isText: !!root })),
            editor.registerEditableListener((isEditable) => updateState({ isEditable })),
            editor.registerUpdateListener(({ editorState }) => $updateFromState(editorState)),
            editor.registerCommand(
                SELECTION_CHANGE_COMMAND,
                () => {
                    read();

                    return false;
                },
                COMMAND_PRIORITY_CRITICAL
            )
        );
    }, [editor, $updateFromState]);

    return state;
}
