import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo, useState, useCallback, useEffect } from 'react';
import { useTranslation } from 'react-i18next';

import { CURRENCY, formatPrice } from '@mssgme/shared';
import FormField from '../../FormField';
import SelectSimple from '../SelectSimple';

import styles from './PriceCompoundInput.module.scss';

const currenciesOptions = CURRENCY.options.map((item) => ({ ...item, label: `${item.value} - ${item.label}` }));
const strToFloat = (str) => (str === '' ? null : parseFloat(str));
const normalizePrice = ({ amount: rawAmount, discount: rawDiscount, currency }, language) => {
    const amount = strToFloat(rawAmount);
    const discount = strToFloat(rawDiscount);

    return {
        amount,
        discount,
        formatted: amount === null ? '' : formatPrice(amount, currency, language),
        formattedDiscount: discount === null ? '' : formatPrice(discount, currency, language),
        currency,
    };
};
export const priceToStrTag = ({ amount, discount, currency }) => `${amount}:${discount}:${currency}`;

const initState = (value, evalDiscount) => ({
    ...(evalDiscount && { hasDiscount: !!value.discount }),
    value: {
        ...value,
        amount: value.amount === null ? '' : String(value.amount),
        discount: value.discount === null ? '' : String(value.discount),
    },
    normalizedValue: { ...value },
});

export const PriceCompoundInput = ({
    name,
    value: rawValue,
    errors,
    refAmount,
    onChange,
    hideDiscount,
    builderInput,
    disabled,
    disabledCurrency,
    ...rest
}) => {
    const { t, i18n } = useTranslation('ui');
    const rawTag = priceToStrTag(rawValue);
    const initialState = useMemo(() => initState(rawValue), []);
    const [value, setValue] = useState(initialState.value);

    const handleChange = useCallback(
        ({ target: { name, value: raw } }) => {
            if (disabled) {
                return;
            }

            const normalized = raw.replace(',', '.').trim();

            if (normalized !== '' && (name === 'amount' || name === 'discount')) {
                const isValid = normalized.match(/^\d{0,9}(\d\.\d{0,2})?$/);

                if (!isValid) {
                    return;
                }
            }

            setValue((old) => ({ ...old, [name]: normalized }));
        },
        [disabled]
    );

    useEffect(() => {
        const normalized = normalizePrice(value, i18n.language);

        if (onChange && rawTag !== priceToStrTag(normalized)) {
            onChange({
                target: {
                    name,
                    value: normalized,
                },
            });
        }
    }, [name, onChange, value, rawTag, i18n.language]);

    return (
        <div className={styles.root} {...rest}>
            <FormField
                builderInput={builderInput}
                name="amount"
                value={value.amount}
                onChange={handleChange}
                type="text"
                errors={t(errors?.amount || errors?.currency)}
                inputMode="decimal"
                autoComplete="off"
                disabled={disabled}
                refInput={refAmount}
                afterInput={
                    <SelectSimple
                        className={cn(styles.currencyBadge, disabledCurrency && styles.disabled)}
                        options={currenciesOptions}
                        name="currency"
                        value={value.currency}
                        disabled={disabledCurrency}
                        onChange={handleChange}
                    />
                }
                clearable
            />
            {!hideDiscount && (
                <FormField
                    builderInput={builderInput}
                    name="discount"
                    value={value.discount}
                    label={t('PriceCompoundInput.discountLabel')}
                    onChange={handleChange}
                    type="text"
                    errors={t(errors?.discount)}
                    inputMode="decimal"
                    autoComplete="off"
                    disabled={disabled}
                    afterInput={<div className={cn(styles.currencyBadge, styles.disabled)}>{value.currency}</div>}
                    clearable
                />
            )}
        </div>
    );
};

PriceCompoundInput.propTypes = {
    value: PropTypes.shape({
        amount: PropTypes.number,
        currency: PropTypes.string.isRequired,
        discount: PropTypes.number,
    }),
    refAmount: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
    name: PropTypes.string,
    onChange: PropTypes.func,
    errors: PropTypes.oneOfType([PropTypes.object, PropTypes.bool]),
    builderInput: PropTypes.bool,
    hideDiscount: PropTypes.bool,
    disabled: PropTypes.bool,
    disabledCurrency: PropTypes.bool,
};

PriceCompoundInput.defaultProps = {
    hideDiscount: false,
};
