import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import TinyColor2 from 'tinycolor2';
import { AlphaPicker, HuePicker } from 'react-color';
import { EditableInput, Saturation } from 'react-color/lib/components/common';

import PickerPointer from './PickerPointer';

import styles from './ColorCustomField.module.scss';

const inputStyles = {
    input: {
        display: 'block',
        width: '100%',
        border: 'none',
        outline: 'none',
        fontSize: 16,
        padding: '8px 16px',
        background: '#f5f5f5',
        borderRadius: 10,
    },
};

export default function ColorCustomField({ value: rawValue, onChange }) {
    const color = new TinyColor2(rawValue);
    const value = color.toHex8String();
    const hexValue = color.toHexString();

    const shouldDropAlpha = useMemo(() => hexValue === '#ffffff', [color.getAlpha() === 0]);

    const commit = (controlAlpha, raw, hex) => {
        if (!hex) {
            const color = new TinyColor2(raw);

            if (controlAlpha) {
                color.setAlpha(shouldDropAlpha ? 1 : color.getAlpha());
            }

            hex = '#' + color.toHex8();
        }

        if (hex !== rawValue) {
            onChange(hex);
        }
    };

    const handleHue = (e) => commit(true, Object.assign(color.toHsl(), { h: e.hsl.h }));
    const handleSaturation = (e) => commit(true, e);
    const handleAlpha = (e) => commit(false, e.rgb);
    const handleInput = (_, e) => commit(false, e.target.value, e.target.value);

    return (
        <div className={styles.container}>
            <HuePicker
                onChange={handleHue}
                color={color.toHex()}
                direction="vertical"
                width={14}
                height={140}
                className={styles.verticalPicker}
                pointer={() => <PickerPointer color={hexValue} style={{ transform: 'translate(-3px, -10px)' }} />}
            />
            <div className={styles.saturationPicker}>
                <Saturation
                    onChange={handleSaturation}
                    hsl={color.toHsl()}
                    hsv={color.toHsv()}
                    pointer={() => <PickerPointer color={hexValue} />}
                />
            </div>
            <AlphaPicker
                onChange={handleAlpha}
                color={color.toRgb()}
                direction="vertical"
                width={14}
                height={140}
                className={styles.verticalPicker}
                pointer={() => <PickerPointer color={hexValue} style={{ transform: 'translate(-6px, -10px)' }} />}
            />
            <div style={{ width: '100%', flexShrink: 0, margin: '20px 0 16px 0' }} className={styles.inputContainer}>
                <EditableInput value={value} onChange={handleInput} style={inputStyles} />
            </div>
        </div>
    );
}

ColorCustomField.propTypes = {
    value: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    colors: PropTypes.array.isRequired,
};
