import PropTypes from 'prop-types';
import React from 'react';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';

import { cls } from '@mssgme/helpers';
import { DragHandle } from '../../DragHandle';
import { ArrayItem } from './ArrayItem';
import FieldRow from '../FieldRow';

import styles from './commonStyles.module.scss';

const SortableItemField = SortableElement(ArrayItem);

export const SortableItemsField = SortableContainer(({
    name,
    items,
    sortable,
    context,
    getItemKey,
    getItemCollection,
    getItemSortable,
    Item,
    onEdit,
    onDelete,
    ...rest
}) => {
    const hasSortableItems = sortable && items.length > 1;

    return (
        <FieldRow builderInput name={name} {...cls(styles.row, rest)}>
            {items.map((item, index) => {
                const itemKey = getItemKey?.(item, index) ?? (item._id || index); // default item._id or item index
                const itemCollection = getItemCollection?.(item) ?? 0; // default 0
                const isItemSortable = !!(hasSortableItems && (getItemSortable?.(item) ?? true)); // default true

                return (
                    <SortableItemField
                        key={itemKey}
                        collection={itemCollection}
                        sortable={isItemSortable}
                        index={index}
                        Item={Item}
                        name={`${name}.${index}`}
                        context={context}
                        onEdit={onEdit}
                        onDelete={onDelete}
                    />
                );
            })}
        </FieldRow>
    );
});

SortableItemsField.propTypes = {
    ...SortableItemsField.propTypes,
    name: PropTypes.string.isRequired,
    items: PropTypes.array.isRequired,
    sortable: PropTypes.bool.isRequired,
    Item: PropTypes.elementType.isRequired,
    context: PropTypes.any,
    getItemKey: PropTypes.func,
    getItemCollection: PropTypes.func,
    getItemSortable: PropTypes.func,
    onEdit: PropTypes.func,
    onDelete: PropTypes.func,
};

SortableItemsField.defaultProps = {
    ...SortableItemsField.defaultProps,
    shouldCancelStart: DragHandle.shouldCancelStart,
    helperClass: DragHandle.helperClass,
    lockAxis: 'y',
    lockToContainerEdges: true,
    lockOffset: '-16px',
};
