import QRCode from 'qrcode';
import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useTimeoutFn } from 'react-use';

import { useNavigationUtils, useWebsitePageBySlugs, useDownload } from '@mssgme/hooks';
import { Button, ModalPane, Spinner } from '@mssgme/ui';

import { useWebsiteUrl } from '../../../../hooks';

import styles from './Qr.module.scss';

export function Qr({ backPath }) {
    const { t } = useTranslation('landings');
    const { generateUrl, state } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const [qrSrc, setQrSrc] = useState(null);
    const websiteUrl = useWebsiteUrl(website, page);
    const historyBackPath = state?.backPath;

    const closePath = generateUrl(historyBackPath || backPath);

    const generateQrSrc = async () => {
        const qrImageSrc = await QRCode.toDataURL(websiteUrl, {
            width: 1000,
        });

        setQrSrc(qrImageSrc);
    };

    useTimeoutFn(generateQrSrc, 1000);
    const { download, pending } = useDownload({ url: qrSrc, filename: 'my_simpl_qr.png' });

    return (
        <ModalPane title={t('Editor.ShareQr.heading')} onClose={closePath}>
            {qrSrc && <p className={styles.tip}>{t('Editor.ShareQr.tip')}</p>}
            <div className={styles.qrWrapper}>
                {!qrSrc && (
                    <div className={styles.spinnerContainer}>
                        <Spinner />
                    </div>
                )}
                {qrSrc && <img className={styles.qr} src={qrSrc} alt="QR-code" />}
            </div>
            {qrSrc && (
                <Button isFetching={pending} onClick={download} className={styles.button} fullWidth>
                    {t('Editor.ShareQr.downloadQr')}
                </Button>
            )}
        </ModalPane>
    );
}

Qr.propTypes = {
    backPath: PropTypes.string,
};

Qr.defaultProps = {
    backPath: '..',
};
