import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { ArrayField, TextField, TextareaField } from '@mssgme/ui';
import { GlyphField, FeaturesItemSchema } from '../components';

const FeatureItem = ({ value, className, textClassName }) => (
    <div className={className}>
        {/*<Glyph className={styles.icon} glyph={value.glyph} size={24} />*/}

        <div className={textClassName}>{value.title?.replace(/\n/g, ' ')}</div>
    </div>
);

FeatureItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const FeaturesItemsField = (props) => {
    const { t } = useTranslation('landings');
    const itemFactory = useCallback(() => ({ title: '', content: '', glyph: { type: 'icon' } }));

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.Features.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.Features.itemAddMore')}
            modalTitle={t('Page.Edit.Features.itemFormHeading')}
            Item={FeatureItem}
            validationSchema={FeaturesItemSchema}
            {...props}
        >
            <TextField
                required
                builderInput
                name="title"
                label={t('Page.Edit.Features.fields.title.label')}
                placeholder={t('Page.Edit.Features.fields.title.placeholder')}
                maxLength={250}
            />
            <TextareaField
                required
                builderInput
                name="content"
                label={t('Page.Edit.Features.fields.content.label')}
                placeholder={t('Page.Edit.Features.fields.content.placeholder')}
                maxLength={20000}
            />
            <GlyphField
                required
                builderInput
                name="glyph"
                allowedTypes={['emoticon']}
                label={t('Page.Edit.Features.fields.glyph.label')}
                innerLabel=""
                placeholder={t('Page.Edit.Features.fields.glyph.placeholder')}
            />
        </ArrayField>
    );
};

FeaturesItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
