import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import styles from './BanBanner.module.scss';
import { useTranslation } from 'react-i18next';

export const ban_OPTIONS = {
    Porn: 'Porn/Adult Dating site',
    Casino: 'Casino promotions etc.',
    Misdirective: 'Missdirective links, probably to hide illegal content',
    Illegal: 'Illegal content',
    'User request': 'By user request',
    DMCA: 'Copyright violation',
    'ToS/Other': 'Other ToS violations',
};

export const BanBanner = ({ moderation: { blocked, pending, reason } }) => {
    const { t } = useTranslation('landings');
    const reasonText = reason && (ban_OPTIONS[reason] || reason);

    return (
        <div className={styles.root}>
            {blocked && <div className={styles.heading}>{t('BanBanner.heading')}</div>}

            {reasonText && (
                <div className={styles.reason}>
                    <span className={styles.reasonHeading}>{t('BanBanner.reasonHeading')}</span>
                    <span className={styles.reasonText}>{reasonText}</span>
                </div>
            )}

            <div className={styles.appeal}>
                {t('BanBanner.appeal')} <Link to="/s/support">{t('BanBanner.link')}</Link>.
            </div>

            {pending && <div className={styles.pending}>{t('BanBanner.pending')}</div>}
        </div>
    );
};

BanBanner.propTypes = {
    moderation: PropTypes.shape({
        blocked: PropTypes.bool.isRequired,
        pending: PropTypes.bool.isRequired,
        reason: PropTypes.string,
    }).isRequired,
};
