import React, { useReducer, useCallback, useContext, useMemo } from 'react';

import { renderNode } from '@mssgme/ui';

const LayoutMarkingContext = React.createContext();

const reducer = (state, action) => {
    switch (action.type) {
        case 'set': {
            return { ...state, ...action.payload };
        }

        case 'toggle-marking': {
            return {
                ...state,
                marking: typeof action.marking === 'boolean' ? !!action.marking : !state.marking,
                area: null,
            };
        }

        case 'area': {
            return { ...state, area: action.area };
        }
    }

    throw new Error();
};

/**
 * @returns {{ marking: boolean, area: *, toggleMarking: Function, setArea: Function }} Context
 */
export const useLayoutMarkingContext = () => {
    const ctx = useContext(LayoutMarkingContext);

    if (!ctx) {
        throw new Error();
    }

    return ctx;
};

export const LayoutMarkingContextProvider = ({ children }) => {
    const [state, dispatch] = useReducer(reducer, { marking: false, area: null });
    const toggleMarking = useCallback((marking) => void dispatch({ type: 'toggle-marking', marking }), [dispatch]);
    const setArea = useCallback((area) => void dispatch({ type: 'area', area }), [dispatch]);

    const context = useMemo(
        () => ({
            ...state,
            toggleMarking,
            setArea,
        }),
        [state, toggleMarking, setArea]
    );

    return (
        <LayoutMarkingContext.Provider value={context}>{renderNode(children, context)}</LayoutMarkingContext.Provider>
    );
};
