import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { RichTextField } from '@mssgme/ui';
import { TextRich } from '@mssgme/userpage-core';
import { useBlockEditor, BlockEditForm, EditStylingForm, BlockStyleProvider } from '../components';

export function TextRichEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: TextRichEdit });

    return (
        <BlockEditForm {...bind} nopadding title={t('Page.Edit.TextRich.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <BlockStyleProvider block={values}>
                        {(style) => (
                            <RichTextField
                                required
                                placeholder={t('Page.Edit.Text.fields.content.placeholder')}
                                name="editorState"
                                inputStyles={style}
                            />
                        )}
                    </BlockStyleProvider>
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

TextRichEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

TextRichEdit.block = TextRich;
