import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { normalizeWebsiteUrl } from '@mssgme/helpers';
import { prettifyUrl } from '@mssgme/shared';
import { socialNameForUrl, ArrayField, TextField } from '@mssgme/ui';
import { Glyph } from '@mssgme/userpage-core';
import { SocialsItemSchema } from '../components';

const SocialsItem = ({ value, className, textClassName }) => {
    const network = socialNameForUrl(value.url, null);

    return (
        <div className={className}>
            <Glyph glyph={{ type: 'icon', icon: network }} size={20} style={{ flexShrink: 0 }} />

            <div className={textClassName}>{prettifyUrl(value.url)}</div>
        </div>
    );
};

SocialsItem.propTypes = {
    value: PropTypes.object.isRequired,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    className: PropTypes.string,
    textClassName: PropTypes.string,
};

export const SocialsItemsField = (props) => {
    const { t } = useTranslation('landings');
    const itemFactory = useCallback(() => ({ url: '' }), []);
    const transform = useCallback((item) => {
        const normalized = normalizeWebsiteUrl(item.url);

        if (normalized !== item.url) {
            return { ...item, url: normalized };
        }
    });

    return (
        <ArrayField
            factory={itemFactory}
            addButtonLabel={t('Page.Edit.Socials.itemAdd')}
            addMoreButtonLabel={t('Page.Edit.Socials.itemAddMore')}
            modalTitle={t('Page.Edit.Socials.itemFormHeading')}
            Item={SocialsItem}
            validationSchema={SocialsItemSchema}
            applyTransform={transform}
            {...props}
        >
            <TextField
                required
                builderInput
                name="url"
                // label={t('Page.Edit.Socials.fields.url.label')}
                placeholder={t('Page.Edit.Socials.fields.url.placeholder')}
                maxLength={800}
            />
        </ArrayField>
    );
};

SocialsItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    initialEdit: PropTypes.bool,
};
