import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { Button, PlanBadge, ProtectAccess } from '@mssgme/ui';
import { usePagesFactory, useWebsiteBySlug } from '@mssgme/hooks';

export function CreatePageButton() {
    const { t } = useTranslation('landings');
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);
    const context = useMemo(() => ({ website }), [website]);

    const { loading, create } = usePagesFactory(website._id);

    return (
        <ProtectAccess strict rule="pages:create" context={context}>
            {(onClick, warning) => (
                <Button
                    fullWidth
                    outline
                    isFetching={loading}
                    afterIcon={warning?.target && <PlanBadge plan={warning.target} />}
                    onClick={onClick || create}
                    iconFixed="right"
                >
                    + {t('labels.page.createNew')}
                </Button>
            )}
        </ProtectAccess>
    );
}
