import classNames from 'classnames';
import PropTypes from 'prop-types';
import React, { useEffect } from 'react';
import { IoIosClose } from 'react-icons/io';

import Avatar from '../../Avatar';

import styles from './ImageBlobUpload.module.scss';

export default function ImageBlobUpload({ className, preview, help, name, blob, disabled, onChange, onRemove }) {
    useEffect(() => {
        onChange && onChange({ target: { name, files: blob ? [blob] : [] } });
    }, [onChange, name, blob]);

    const child = React.Children.only(preview);
    const _preview =
        child &&
        React.cloneElement(child, {
            ...child.props,
            className: classNames(child.props.className, styles.preview),
        });

    return (
        <>
            <div className={classNames(styles.root, className)}>
                {_preview}
                <div>{!!help && <div className={styles.help}>{help}</div>}</div>
                {onRemove && (
                    <div className={classNames(styles.closeBtn, disabled && styles.disabled)} onClick={onRemove}>
                        <IoIosClose size={22} color="#fff" />
                    </div>
                )}
            </div>
        </>
    );
}

ImageBlobUpload.propTypes = {
    className: PropTypes.string,
    name: PropTypes.string,
    blob: PropTypes.object,
    onChange: PropTypes.func,
    help: PropTypes.string,
    disabled: PropTypes.bool,
    hasError: PropTypes.bool,
    errors: PropTypes.string,
    preview: PropTypes.any,
    onRemove: PropTypes.func,
};

ImageBlobUpload.defaultProps = {
    blob: null,
    hasError: false,
    previewComponent: Avatar,
};
