import React, { useContext } from 'react';
import { useMount, useUnmount } from 'react-use';

import { CRM_IDENTITY } from '@mssgme/userpage-core';

export const makeProductsRouter = ({ back, path, settings, isBackOverriddenBack }) => {
    const settingsPath = settings || `${path}/settings`;

    return {
        toEntry: () => back,
        toHome: () => path,
        settings: {
            toSettings: () => settingsPath,
            toCurrency: () => `${settingsPath}/currency`,
            toMessengers: () => `${settingsPath}/messengers`,
            toForm: () => `${settingsPath}/form`,
            toPayments: () => `${settingsPath}/payments`,
            toNotifications: () => `${settingsPath}/notifications`,
            toGlobalNotifications: (back) => ({
                pathname: '/s/notifications',
                state: {
                    backPath: back || `${settingsPath}/notifications`,
                    successRedirectPath: back || `${settingsPath}/notifications`,
                    // commonSettingsTip: true,
                },
            }),
            toImport: () => `${settingsPath}/import`,
        },
        product: {
            toAll: () => path,
            toNew: (collectionId) => (collectionId ? `${path}/collections/${collectionId}/new` : `${path}/new`),
            toEdit: (id, collectionId) =>
                collectionId ? `${path}/collections/${collectionId}/product/edit/${id}` : `${path}/edit/${id}`,
        },
        collection: {
            toAll: () => `${path}/collections`,
            toNew: () => `${path}/collections/new`,
            toViewProducts: (id) => (id ? `${path}/collections/${id}` : `${path}/collections`),
            toEdit: (id) => `${path}/collections/${id}/edit`,
        },
        isBackOverriddenBack,
    };
};

export const ProductsContext = React.createContext({
    path: '/products',
    specifier: { website: '' },
    isSpecific: false,
    crm: CRM_IDENTITY,
    router: makeProductsRouter({ back: '/', path: '/products' }),
    setEntry: () => {},
    modal: true,
});

export const useBackProductsPath = (backUrl) => {
    const { setEntry } = useProductsContext();

    useMount(() => setEntry(backUrl));
    useUnmount(() => setEntry());
};

export const useProductsContext = () => useContext(ProductsContext);
