import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { FiBell, FiClipboard, FiCreditCard, FiDollarSign, FiMail, FiMessageCircle } from 'react-icons/fi';
import { IoIosSwap } from 'react-icons/io';
import { SiTelegram } from 'react-icons/si';
import { VscServerProcess } from 'react-icons/vsc';

import { CURRENCY } from '@mssgme/shared';
import { MESSENGERS_REACT_ICONS_MAP, PAYMENT_METHODS_REACT_ICONS_MAP } from '@mssgme/constants';
import { useNotificationsSettings } from '@mssgme/hooks';
import { AclMenuPane } from '@mssgme/ui';
import { useImportableCollections, useProductsContext } from '../../hooks';

const notificationTypesMap = {
    email: FiMail,
    telegram: SiTelegram,
    webhook: VscServerProcess,
};

const notifiers = (notifications) => {
    const enabled = Object.entries(notificationTypesMap).filter(([type]) => notifications[type]);

    if (!enabled.length) {
        return;
    }

    return (
        <div style={{ display: 'flex', gap: 8, justifyContent: 'flex-end' }}>
            {enabled.map(([type, Tag]) => <Tag key={type} size={16} />)}
        </div>
    );
};

const messengers = (platforms) => {
    if (!platforms.length) {
        return null;
    }

    return (
        <div
            style={{
                display: 'flex',
                flexWrap: 'nowrap',
                alignItems: 'center',
                justifyContent: 'right',
            }}
        >
            {platforms.map(({ platform }, index) => {
                const Icon = MESSENGERS_REACT_ICONS_MAP[platform];

                return <Icon key={index} size={16} style={{ display: 'block', marginLeft: 4 }} />;
            })}
        </div>
    );
};

const paymentMethods = (configs) => {
    const platforms = Object.keys(configs).filter((provider) => configs[provider].enabled);

    if (!platforms.length) {
        return null;
    }

    return (
        <div
            style={{
                display: 'flex',
                flexWrap: 'nowrap',
                alignItems: 'center',
                justifyContent: 'right',
            }}
        >
            {platforms.map((platform) => {
                const Icon = PAYMENT_METHODS_REACT_ICONS_MAP[platform];

                return <Icon key={platform} size={16} style={{ display: 'block', marginLeft: 4 }} />;
            })}
        </div>
    );
};

const currency = (value) => {
    const found = CURRENCY.options.find((option) => value === option.value);

    return found && (
        <div
            style={{
                display: 'flex',
                justifyContent: 'flex-end',
                alignItems: 'center',
            }}
        >
            {/*<Badge badgeStyle="flat">{found.symbol || value}</Badge>*/}
            {found.symbol || value}
            {' - '}
            {value}
        </div>
    );
};

const menu = (t, notifications, router, collections, crm) => [
    {
        to: router.settings.toForm(),
        icon: FiClipboard,
        label: t('SettingsMenu.items.form'),
        restrict: { rule: 'payments:edit' },
    },
    {
        to: router.settings.toPayments(),
        label: t('SettingsMenu.items.payments'),
        icon: FiCreditCard,
        restrict: { rule: 'payments:edit', iconOnly: true },
        preview: paymentMethods(crm.payments) || t('ui:labels.off'),
    },
    {
        to: router.settings.toCurrency(),
        label: t('SettingsMenu.items.currency'),
        icon: FiDollarSign,
        preview: currency(crm.defaultCurrency) || t('ui:labels.no'),
    },
    {
        to: router.settings.toMessengers(),
        label: t('SettingsMenu.items.messengers'),
        icon: FiMessageCircle,
        preview: messengers(crm.contacts.messengers) || t('ui:labels.no'),
    },
    {
        to: router.settings.toNotifications(),
        label: t('SettingsMenu.items.notifications'),
        icon: FiBell,
        restrict: { rule: 'payments:edit' },
        preview: notifiers(notifications) || t('ui:labels.no'),
    },
    {
        to: router.settings.toImport(),
        label: t('SettingsMenu.items.import'),
        icon: IoIosSwap,
        filter: !!collections,
    },
];

export function ProductsSettingsMenu() {
    const { t } = useTranslation('products');
    const { router, specifier, crm } = useProductsContext();
    const { items: collections } = useImportableCollections(specifier);
    const notifications = useNotificationsSettings(crm);
    const importable = !!collections.length;
    const items = useMemo(
        () => menu(t, notifications, router, importable, crm),
        [t, notifications, router, importable, crm]
    );

    return <AclMenuPane title={t('SettingsMenu.header')} items={items} />;
}
