import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { FiCamera, FiPlus } from 'react-icons/fi';
import { RiVipCrown2Line } from 'react-icons/ri';
import { isEqual } from 'lodash';

import { FieldRow, Spinner } from '@mssgme/ui';
import { mediaUtils } from '@mssgme/shared';
import { useProductPhotosEdit } from '../../../hooks';

import styles from './ProductPhotos.module.scss';

const shouldCancelStart = (e) => {
    if (e.target.dataset.id === 'remBtn') {
        return true;
    }
};

const SortableItem = SortableElement(({ url, isMain, onRemove }) => {
    const handleRemove = useCallback(() => onRemove && onRemove(url), [url, onRemove]);

    return (
        <li className={styles.imagePreview}>
            <div className={styles.imagePreviewContainer}>
                <img src={mediaUtils.generateProductThumbnailUrl(url)} alt="Photo" />
            </div>

            {isMain && (
                <div className={styles.mainImageIcon}>
                    <RiVipCrown2Line size={20} color="#fff" />
                </div>
            )}

            {onRemove && (
                <div className={styles.removePhotoBtn} onClick={handleRemove} data-id="remBtn">
                    <FiPlus size={12} />
                </div>
            )}
        </li>
    );
});

// todo: deprecated, use ImageListField instead
const SortableList = SortableContainer(
    ({ name, photos, accept, maxPhotos, onRemovePhoto, isUploading, isFetching, onPhotoChange }) => {
        const { t } = useTranslation('products');

        return (
            <ul className={styles.imagesList}>
                {photos.map((url, index) => (
                    <SortableItem key={url} index={index} url={url} isMain={index === 0} onRemove={onRemovePhoto} />
                ))}

                {photos.length < maxPhotos && (
                    <li className={styles.imagePreview}>
                        <div className={styles.addPhotoBtn}>
                            <div className={styles.addPhotoBtnContent}>
                                {isUploading ? (
                                    <Spinner />
                                ) : (
                                    <div className={styles.addPhotoBtnPlus}>
                                        <FiCamera size={20} />
                                        {t('labels.addPhotos')}
                                    </div>
                                )}
                            </div>

                            {!isUploading && (
                                <input
                                    className={styles.addPhotoBtnInput}
                                    type="file"
                                    name={name}
                                    accept={accept}
                                    disabled={isFetching}
                                    multiple
                                    onChange={onPhotoChange}
                                />
                            )}
                        </div>
                    </li>
                )}
            </ul>
        );
    }
);

export const ProductPhotosField = ({ name, maxPhotos = 6, ...props }) => {
    const { getFieldMeta, setFieldValue, setFieldTouched, setFieldError, isSubmitting } = useFormikContext();
    const { value: photos } = getFieldMeta(name);

    const { accept, isUploading, onSortEnd, onPhotoChange, onRemovePhoto } = useProductPhotosEdit({
        maxPhotos,
        onChange: useCallback(
            async (callback) => {
                try {
                    setFieldTouched(name, true);

                    const data = await callback(photos, () => setFieldError(name, ''));
                    const changed = data && !isEqual(photos, data);

                    setFieldTouched(name, !!changed);

                    if (changed) {
                        setFieldValue(name, data);
                    }
                } catch (e) {
                    // fixme: workaround: consecutive setFieldError might be discarded inside change handler
                    setTimeout(() => setFieldError(name, typeof e === 'string' ? e : e.message), 100);
                }
            },
            [name, photos]
        ),
    });

    return (
        <FieldRow builderInput name={name} {...props}>
            <div className={styles.imageUploadContainer}>
                <div className={styles.imagesBlock}>
                    {photos && (
                        <SortableList
                            photos={photos}
                            accept={accept}
                            maxPhotos={maxPhotos}
                            isUploading={isUploading}
                            isFetching={isSubmitting}
                            onPhotoChange={onPhotoChange}
                            onRemovePhoto={onRemovePhoto}
                            onSortEnd={onSortEnd}
                            axis="xy"
                            helperClass={styles.sortable}
                            shouldCancelStart={shouldCancelStart}
                        />
                    )}
                </div>
            </div>
        </FieldRow>
    );
};

ProductPhotosField.propTypes = {
    name: PropTypes.string.isRequired,
    accept: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
    maxPhotos: PropTypes.number,
    isUploading: PropTypes.bool,
    isFetching: PropTypes.bool,
    onSortEnd: PropTypes.func,
    onPhotoChange: PropTypes.func,
    onRemovePhoto: PropTypes.func,
};
