import { flatten } from 'lodash';

const choicesFieldFactory = (type) => (path, meta = {}) => {
    return { path, type, applyImmediately: !(meta && meta.multiple), ...meta };
};

/**
 * @property {{
 *     list: function(string, object): object,
 *     select: function(string, object): object,
 *     choice: function(string, object): object,
 *     number: function(string, object): object,
 *     range: function(string, object): object,
 *     toggle: function(string, object): object,
 *     color: function(string, object): object,
 *     child: function(string, object): object,
 * }} s Styling field factories
 */
export const Styling = {
    d: {
        sections(sections) {
            return {
                sections: Object.entries(sections).map(([label, styles]) => ({
                    label,
                    styles: styles.filter(Boolean),
                })),
            };
        },
        section(section) {
            return this.sections({ default: flatten(section) });
        },
    },
    s: new Proxy(
        {
            list: choicesFieldFactory('list'),
            select: choicesFieldFactory('select'),
            choice: choicesFieldFactory('choice'),
            icon: choicesFieldFactory('icon'),
            listOrToggle: choicesFieldFactory('listOrToggle'),
            toggle(path, meta = {}) {
                return { path, type: 'toggle', applyImmediately: true, ...meta };
            },
            custom(path, meta = {}) {
                return { path, type: path, ...meta };
            },
        },
        {
            get(target, prop) {
                if (target[prop]) {
                    return target[prop];
                }

                return (path, meta = {}) => {
                    return { path, type: prop, ...meta };
                };
            },
        }
    ),
};
