import React from 'react';

import FieldRow from './FieldRow';
import { LegacyColorPicker } from '../Inputs';
import { ColorFieldProps } from './types';

export default function ColorField({ as: Tag, onChange, ...props }) {
    return (
        <FieldRow {...props}>
            {({ field: { name, ...field }, form: { setFieldValue, setFieldTouched } }) => (
                <Tag
                    {...field}
                    name={name}
                    onChange={(value) => {
                        setFieldValue(name, value);
                        setFieldTouched(name, true, false);

                        if (onChange) {
                            onChange(value);
                        }
                    }}
                />
            )}
        </FieldRow>
    );
}

ColorField.propTypes = ColorFieldProps;

ColorField.defaultProps = {
    as: LegacyColorPicker,
};
