import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextareaField } from '@mssgme/ui';

Other.propTypes = {
    enabled: PropTypes.bool,
    name: PropTypes.string,
};

export default function Other({ enabled, name }) {
    const { t } = useTranslation('products');

    return (
        <>
            <TextareaField
                builderInput
                name={`${name ? name + '.' : ''}instructions`}
                label={t('fields.payments.other.instructions.label')}
                placeholder={t('fields.payments.other.instructions.placeholder')}
                maxLength={5000}
                disabled={!enabled}
                required
            />
        </>
    );
}
