import { useEffect } from 'react';
import { useSwitchApp } from './useSwitchApp';
import { useUser } from './useUser';
import config from 'config';

export function useAutoSwitchApp() {
    const user = useUser();
    const switchApp = useSwitchApp();
    const userApp = user?.app;

    useEffect(() => {
        if (userApp && userApp !== config.app) {
            switchApp(userApp);
        }
    }, [userApp]);
}
