import PropTypes from 'prop-types';
import React, { useCallback, useLayoutEffect, useRef, useState } from 'react';
import { useToggle } from 'react-use';
import { useTranslation } from 'react-i18next';
import { get, pickBy, isEqual } from 'lodash';

import { useUiFlags } from '@mssgme/hooks';
import {
    Button,
    StyleEditor,
    useStylingAccessors,
    PortalModalSheet,
    StyleSimpleToggler,
    StyleCarousel,
} from '@mssgme/ui';
import { useRenderer } from '@mssgme/userpage-core';
import { useCollectedThemingStyle } from '../../../hooks';
import { StyledBlockPreview } from '../../StyledBlockPreview';
import { SuggestionsPreview } from './SuggestionsPreview';

const cleanObj = (obj) => pickBy(obj, (v) => v !== undefined && v !== null);
const extract = (theme, styling, ...args) => cleanObj(get(styling.enrich(theme, ...args), styling.definition.path));

export const EditStylingForm = ({
    page,
    element,
    styling,
    children,
    preview,
    portalTo,
    showToolPanel,
    advancedStyling,
    toolPanelStyle,
}) => {
    const { t } = useTranslation('landings');
    const { isAdvancedStylingMode } = useUiFlags();
    const { setter } = useStylingAccessors(styling);
    const renderBlock = useRenderer();
    const suggestions = useCollectedThemingStyle(page, element, styling);
    const current = extract(element.theme, styling, true);
    const [prev, setPrev] = useState({});
    const [editedStyle, setEditedStyle] = useState(null);
    const [memoizedTheme, setMemoizedTheme] = useState(null);
    const [isShowingSuggestions, toggleSuggestions] = useToggle(false);
    const changed = !isEqual(current, prev);
    const cache = useRef({});

    useLayoutEffect(() => {
        if (!changed) {
            return;
        }

        for (const [prop, value] of Object.entries(current)) {
            if (value === prev[prop]) {
                continue;
            }

            if (prev[prop] === undefined) {
                if (cache.current[prop] === undefined) {
                    // new prop, not cached previously, set and cache
                    cache.current[prop] = value;
                    setter(prop, value);
                } else {
                    // new prop, was cached, restore from cache
                    setter(prop, cache.current[prop]);
                }
            } else {
                // normal prop change, just cache
                cache.current[prop] = value;
            }
        }

        setPrev(current);
    }, [changed]);

    const handleStyleSelect = useCallback(
        (field) => {
            setEditedStyle(field);
            setMemoizedTheme(current);
        },
        [current]
    );
    const handleClosed = useCallback(
        (apply) => {
            setEditedStyle(null);

            if (!apply) {
                setter('', memoizedTheme);
            }
        },
        [memoizedTheme]
    );
    const handleSuggestionsClosed = useCallback(
        (theme) => {
            toggleSuggestions(false);

            const resolved = theme && get(styling.enrich(theme), styling.definition.path);

            if (resolved) {
                setter('', resolved);
            }
        },
        [toggleSuggestions, styling]
    );

    return (
        <>
            {editedStyle && (
                <PortalModalSheet
                    paneMotionProps={{ layout: false }}
                    title={editedStyle.label}
                    portalTo={portalTo}
                    onClose={handleClosed}
                    render={({ handleTrue }) => (
                        <StyleEditor
                            styling={styling}
                            focusPaths={[
                                {
                                    path: editedStyle.path,
                                    undecorated: !['toggle', 'listOrToggle'].includes(editedStyle.type),
                                },
                            ]}
                            renderFooter={({ isDirty }) =>
                                !editedStyle.applyImmediately && (
                                    <Button fullWidth size="standart" disabled={!isDirty} onClick={handleTrue}>
                                        {t('ui:labels.apply')}
                                    </Button>
                                )
                            }
                            onApply={handleTrue}
                        />
                    )}
                />
            )}

            {isShowingSuggestions && (
                <PortalModalSheet
                    title={t('labels.block.restyle')}
                    portalTo={portalTo}
                    padding={false}
                    onClose={handleSuggestionsClosed}
                    render={({ handleClose }) => (
                        <StyledBlockPreview style={{ margin: '0 0 -1px 0', width: '100%' }}>
                            <SuggestionsPreview
                                styling={styling}
                                suggestions={suggestions}
                                render={preview || renderBlock}
                                onAccept={handleClose}
                            />
                        </StyledBlockPreview>
                    )}
                />
            )}

            {children}

            {showToolPanel &&
                styling.definition.sections.length > 0 &&
                (advancedStyling || isAdvancedStylingMode ? (
                    <StyleCarousel style={toolPanelStyle} styling={styling} onSelect={handleStyleSelect} />
                ) : (
                    <StyleSimpleToggler style={toolPanelStyle} styling={styling} onSelect={handleStyleSelect} />
                ))}
        </>
    );
};

EditStylingForm.propTypes = {
    styling: PropTypes.object.isRequired,
    page: PropTypes.object.isRequired,
    element: PropTypes.object.isRequired,
    children: PropTypes.any,
    showToolPanel: PropTypes.bool,
    onSuggestionsResolved: PropTypes.func,
    portalTo: PropTypes.string,
    preview: PropTypes.oneOfType([PropTypes.func, PropTypes.elementType]),
    toolPanelStyle: PropTypes.object,
    advancedStyling: PropTypes.bool,
};

EditStylingForm.defaultProps = {
    portalTo: '#visual-builder',
    onSuggestionsResolved: null,
    children: null,
    preview: null,
    showToolPanel: true,
    advancedStyling: true,
};
