import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { FiTrash2, FiPlus } from 'react-icons/fi';

import { prettifyUrl } from '@mssgme/shared';
import { useRestriction } from '@mssgme/hooks';
import { MessengerIcon } from '../MessengerIcon';
import { FieldRow } from '../Form';
import DragHandle from '../DragHandle';

import styles from './MessengersField.module.scss';

const isTelegramGroup = (messenger, type) => messenger === 'telegram' && type === 'secret';
const RESTRICTIONS = {
    vk: 'ru-integrations',
};

export const MessengersItemField = ({ name, platform, sortable, onEdit, onDelete, mode, ...rest }) => {
    const { t } = useTranslation('ui');
    const { getFieldMeta } = useFormikContext();
    const { value: messenger } = getFieldMeta(name);
    const { hasWarning } = useRestriction(RESTRICTIONS[platform]);

    const handleEdit = useCallback(() => onEdit(platform), [platform, onEdit]);
    const handleRemove = useCallback(() => onDelete(platform), [platform, onDelete]);

    if (hasWarning && !messenger) {
        return <div />;
    }

    const isSmall = mode === 'small';
    const isNew = !messenger;
    const platformLabel = t(`messengers.${platform}`);
    const label = messenger
        ? isTelegramGroup(messenger.platform, messenger.type)
            ? t('messengers.labels.chatInvite', { token: messenger.value })
            : prettifyUrl(messenger.value)
        : '';

    return (
        <FieldRow name={name} style={isSmall ? { marginBottom: 6 } : undefined} {...rest}>
            <div className={cn(styles.messengerRow, styles[mode])}>
                <div className={styles.messengerLeft}>
                    {!!sortable && (
                        <DragHandle
                            active={!!sortable}
                            iconSize={isSmall ? 8 : 9}
                            small="auto"
                            style={{ color: '#7C7C7D' }}
                        />
                    )}
                    {isNew && (
                        <div className={styles.iconWrapper} onClick={handleEdit}>
                            <MessengerIcon type="flat" messenger={platform} className={styles.icon} />
                        </div>
                    )}
                </div>

                <div className={cn(styles.value, !isNew && styles.active)}>{platformLabel}</div>

                {isSmall && <div className={styles.previewValue}>{label}</div>}

                <div className={styles.buttons}>
                    {isNew && (
                        <div onClick={handleEdit} className={cn(styles.button)}>
                            <FiPlus size={20} />
                        </div>
                    )}
                    {!isNew && (
                        <div onClick={handleRemove} className={cn(styles.button, styles.remove)}>
                            <FiTrash2 size={20} />
                        </div>
                    )}
                </div>
                <div className={styles.layer} onClick={handleEdit} />
            </div>
        </FieldRow>
    );
};

MessengersItemField.propTypes = {
    name: PropTypes.string.isRequired,
    platform: PropTypes.string.isRequired,
    sortable: PropTypes.bool,
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    mode: PropTypes.oneOf(['small', 'normal']),
};

MessengersItemField.defaultProps = {
    sortable: false,
    productsOnboard: false,
    mode: 'normal',
};
