import React from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import { useActions, useNavigationUtils } from '@mssgme/hooks';
import PaddleCheckout from './PaddleCheckout';

export default function PaddleChangeCard(props) {
    const { api } = useActions();
    const history = useHistory();
    const { generateUrl } = useNavigationUtils();

    const handleComplete = async () => {
        await api.billing.details();
        history.push(generateUrl('..'));
    };

    return <PaddleCheckout onComplete={handleComplete} {...props} />;
}

PaddleChangeCard.propTypes = {
    ...PaddleCheckout.propTypes,
    override: PropTypes.string.isRequired,
};
