import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';
import { PlanBadge, LandingsPropTypes, LazyImage } from '@mssgme/ui';
import { usePageUpgradeCheck } from '../../hooks';

import styles from './TemplatePill.module.scss';

export function TemplatePill({ template, label, badge, ...rest }) {
    const { upgrade } = usePageUpgradeCheck(template);

    return (
        <div {...cls(styles.root, rest)}>
            {badge && upgrade && (
                <div className={styles.badgeTop}>
                    <PlanBadge plan={upgrade} {...(typeof badge === 'string' ? { className: styles[badge] } : {})} />
                </div>
            )}

            {template ? (
                <LazyImage
                    src={template.thumbnailUrl}
                    alt={label || template.name}
                    title={label || template.name}
                    mode="inline"
                />
            ) : (
                <div className={styles.innerLabel}>{label}</div>
            )}
        </div>
    );
}

TemplatePill.propTypes = {
    template: LandingsPropTypes.template,
    label: PropTypes.string,
    badge: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
};

TemplatePill.defaultProps = {
    template: null,
    label: null,
    badge: true,
};
