import React from 'react';
import PropTypes from 'prop-types';
import { Redirect } from 'react-router-dom';

import { NoAccess } from '@mssgme/ui';

import styles from './RestrictedPage.module.scss';

export const RestrictedPage = ({ location }) => {
    const { reason, referrer } = location.state || {};

    if (!reason) return <Redirect to="/" />;

    return <NoAccess className={styles.root} center reason={reason} referrer={referrer} />;
};

RestrictedPage.propTypes = {
    location: PropTypes.object.isRequired,
};
