import React from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect } from 'react-router-dom';

import { useActions } from '@mssgme/hooks';
import { useEnhancedFormik, Button, FormRow, FormField } from '@mssgme/ui';

export default function CreateWorkspaceForm() {
    const actions = useActions();
    const { t } = useTranslation();
    const formik = useEnhancedFormik({
        initialValues: {
            url: '',
        },
        onSubmit: (payload) => actions.api.workspaces.create({ payload }),
    });

    if (formik.status === 'success') {
        return <Redirect to="/" />;
    }

    return (
        <form onSubmit={formik.handleSubmit}>
            <FormField
                label={t('label.pageUrl')}
                name="url"
                beforeInput={t('label.host')}
                error={t(formik.errors.url)}
                {...formik.getFieldProps('url')}
            />
            <FormRow>
                <Button fullWidth type="submit" isFetching={formik.isSubmitting}>
                    {t('emptyWorkspace.step2_create.create')}
                </Button>
            </FormRow>
        </form>
    );
}
