import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';

import { DYNAMIC_FIELD_SIZE_LARGE, DYNAMIC_FIELD_SIZE_NORMAL, DynamicFieldSchema } from '@mssgme/shared';
import { BlockListItemPreview } from '../../BlockListItemPreview';
import { PortalModalSheet } from '../../ModalSheet';
import ArrayField from '../ArrayField';
import TextField from '../TextField';
import TextareaField from '../TextareaField';
import ToggleField from '../ToggleField';
import { DYNAMIC_FIELDS, DynamicFormItem } from './DynamicFormItem';

const isLarge = (size) => size > DYNAMIC_FIELD_SIZE_NORMAL;
const getSize = (large) => large ? DYNAMIC_FIELD_SIZE_LARGE : DYNAMIC_FIELD_SIZE_NORMAL;

const TYPES_WITH_USER_OPTIONS = ['select', 'radio', 'checkbox'];

export const DynamicFormField = (props) => {
    const { t } = useTranslation('ui');
    const handleFactory = useCallback(({ setFactory, setEditingIndex, ...rest }) => {
        // there are two interactions in the type picker:
        // 1. Select type (calls handleFactory with { type: string; label: string; size: number; large: bool } & ArrayFieldHelpers )
        // 2. Close the picker (calls handleFactory with ArrayFieldHelpers)

        // ArrayFieldHelpers have two helper methods:
        // setEditingIndex - edit specific item in the array or close the edit modal (if index <= -1)
        // setFactory - open edit modal for new item (pass the item factory to `setFactory` as first argument)

        if (rest.type) {
            // initialize the item to be edited
            setFactory(() => {
                const large = !!(rest.large || (rest.size && isLarge(rest.size)));

                return {
                    label: '',
                    required: true,
                    large,
                    size: getSize(large),
                    options: '',
                    ...rest,
                };
            });
        } else {
            // selector dismissed, close the `edit` modal
            setEditingIndex(-1);
        }
    }, []);

    return (
        <ArrayField
            isInitialDirty
            maxItems={20}
            Item={DynamicFormItem}
            modalTitle={t('DynamicForm.itemFormHeading')}
            addButtonLabel={t('DynamicForm.itemAdd')}
            addMoreButtonLabel={t('DynamicForm.itemAddMore')}
            validationSchema={DynamicFieldSchema}
            renderFactory={(callbacks) => (
                <PortalModalSheet
                    onClose={(template) => handleFactory({ ...template, ...callbacks })}
                    title={t('DynamicForm.fieldTypesModalTitle')}
                >
                    {({ handleClose }) => (
                        <BlockListItemPreview.List>
                            {Object.entries(DYNAMIC_FIELDS).map(([type, { icon, props: choiceProps }]) => (
                                <BlockListItemPreview
                                    key={type}
                                    labelIcon={icon}
                                    label={t(`DynamicForm.fieldTypes.${type}.label`)}
                                    restrict={props.addRestriction && ({
                                        rule: props.addRestriction,
                                        subject: { items: [{ type }] },
                                    })}
                                    onClick={() =>
                                        handleClose({
                                            type,
                                            label: t(`DynamicForm.fieldTypes.${type}.initialValue`, ''),
                                            ...choiceProps,
                                        })
                                    }
                                />
                            ))}
                        </BlockListItemPreview.List>
                    )}
                </PortalModalSheet>
            )}
            {...props}
        >
            {({ values: { type, size }, setFieldValue }) => (
                <div>
                    <TextField
                        builderInput
                        name="label"
                        label={t('DynamicForm.fields.label.label')}
                        placeholder={t('DynamicForm.fields.label.placeholder')}
                        maxLength={250}
                    />

                    {TYPES_WITH_USER_OPTIONS.includes(type) &&
                        <TextareaField
                            builderInput
                            name="options"
                            label={t('DynamicForm.fields.options.label')}
                            placeholder={t('DynamicForm.fields.options.placeholder')}
                            maxLength={2000}
                            minRows={8}
                            maxRows={16}
                        />
                    }

                    <ToggleField
                        builderInput
                        name="required"
                        label={t('DynamicForm.fields.required.label')}
                        placeholder={t('DynamicForm.fields.required.placeholder')}
                    />

                    {type === 'text' && (
                        <ToggleField
                            builderInput
                            name="large"
                            value={isLarge(size)}
                            label={t('DynamicForm.fields.large.label')}
                            placeholder={t('DynamicForm.fields.large.placeholder')}
                            onChange={(_, large) => {
                                setFieldValue('size', getSize(large));
                            }}
                        />
                    )}
                </div>
            )}
        </ArrayField>
    );
};

DynamicFormField.propTypes = {
    name: PropTypes.string.isRequired,
    value: PropTypes.array,
    label: PropTypes.string,
    addButtonLabel: PropTypes.string,
    addMoreButtonLabel: PropTypes.string,
    modalTitle: PropTypes.string,
    context: PropTypes.any,
    sortable: PropTypes.bool,
    initialEdit: PropTypes.bool,
    isInitialDirty: PropTypes.bool,
    isEditable: PropTypes.bool,
    isDeletable: PropTypes.bool,
    addRestriction: PropTypes.string,
    factory: PropTypes.func,
    applyTransform: PropTypes.func,
    validationSchema: PropTypes.object,
    maxItems: PropTypes.number,
};

export default DynamicFormField;
