import { useMemo } from 'react';
import { isEqual } from 'lodash';

const filterUnique = (items) => {
    const stack = [];

    for (const item of items) {
        if (stack.length) {
            const similar = stack.find((existing) => isEqual(existing.theme, item.theme));

            if (similar) {
                continue;
            }
        }

        stack.push(item);
    }

    return stack;
};

export function useCollectedThemingStyle(page, element, { definition: { parent, path } }) {
    return useMemo(() => {
        const fullPath = [parent, path].filter(Boolean).join('.');
        let result = [];

        if (fullPath.startsWith('blocks.')) {
            const kind = element.kind;
            const items = page.blocks
                .filter((block) => block.kind === kind)
                .reduce((acc, block) => {
                    acc.push({
                        item: block,
                        theme: block.theme || {},
                    });

                    return acc;
                }, []);

            result = filterUnique(items);
        }

        return result;
    }, [page, element]);
}
