import moment from 'moment';
import formatPhone, { normalizePhone } from './formatPhone';

export const formatDate = (date, format, locale) =>
    moment(date)
        .locale(locale)
        .format(format);

const formatFormData = ({ type, value }, locale = typeof navigator !== 'undefined' && navigator.language) => {
    if (value) {
        switch (type) {
            case 'date': {
                return {
                    normalized: new Date(value).toLocaleDateString(),
                };
            }

            case 'datetime': {
                return {
                    normalized: formatDate(new Date(value), 'LLL', locale),
                };
            }

            case 'address': {
                let data = {};

                try {
                    data = (value && JSON.parse(value)) || {};
                } catch (e) {
                    //
                }

                return {
                    normalized: ['city', 'state', 'address', 'postalCode']
                        .map((key) => data[key])
                        .filter(Boolean)
                        .join(', '),
                };
            }

            case 'phone': {
                return {
                    normalized: formatPhone(value, locale),
                    link: `tel:${normalizePhone(value, 18, false)}`,
                };
            }

            case 'email': {
                return {
                    normalized: value,
                    link: `mailto:${value}`,
                };
            }

            case 'checkbox': {
                return {
                    normalized: value.join(' | '),
                };
            }

            case 'number': {
                return {
                    normalized: value.toString(),
                };
            }
        }
    }

    return { normalized: value || '' };
};

export default formatFormData;
