import { useEffect, useState } from 'react';
import { get } from 'lodash';
import { useMountedState } from 'react-use';

import { ua } from '@mssgme/helpers';
import { useActions } from './useActions';
import { useUser } from './useUser';
import { useUpdatedUser } from './useUpdatedUser';
import open from './open';

const handleMarker = ({ url, marker }) => {
    if (!url) return;

    open(url);
    return marker;
};

// TODO: redo
export function useConnectTelegram(name) {
    const isSafari = ua.Safari;
    const isMounted = useMountedState();
    const user = useUser();
    const { api } = useActions();
    const [isConnected, setConnected] = useState(() => get(user, name));
    const [state, setState] = useState();
    const [resolver] = useState(() => async () => {
        const marker = await api.users.connectTelegram();

        if (marker.url && isMounted()) {
            setState(marker);

            return marker;
        }
    });

    const { isLoading, isUpdated, update } = useUpdatedUser(() => {
        if (isSafari) {
            return state && handleMarker(state);
        }

        return resolver().then(handleMarker);
    });

    useEffect(() => {
        if (state || isConnected || !isSafari) {
            return;
        }

        void resolver();
    }, [state, isConnected]);

    useEffect(() => {
        if (isUpdated) {
            setConnected(!!user.telegramId);
            setState(null);
        }
    }, [isUpdated]);

    return {
        isConnected,
        setConnected,
        isLoading,
        isDisabled: isSafari && !state,
        connect: update,
    };
}
