import format from 'string-template';

export class Translator {
    constructor(locales = {}) {
        this.locales = locales;
    }

    extend(locales) {
        this.locales = Object.assign({}, this.locales, locales);
    }

    t(key, values, def = key) {
        if (!this.locales[key]) return def;
        return format(this.locales[key], values);
    }

    /**
     * Return localized string based on `key` and `values`, or `def` if key not found in localization table.
     *
     * @returns {(key: string, values: object, def?: string) => string} Translator function
     */
    get bound() {
        return this.t.bind(this);
    }
}

export const trans = new Translator();
/**
 * Return localized string based on `key` and `values`, or `def` if key not found in localization table.
 *
 * @type {(key: string, values: object, def?: string) => string} Translator function
 */
export const __t = trans.bound;
