import PropTypes from 'prop-types';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { TextSizeIcon } from '@mssgme/icons';
import { ToolbarDropdownButton } from '../ui';
import { FONT_SIZE_OPTIONS, STYLE_FORMAT_COMMAND } from './constants';

export const FontSizeMenuButton = (props) => {
    const { t } = useTranslation('ui');

    const items = useMemo(() => FONT_SIZE_OPTIONS.map((size) => {
        const value = `${size}px`;

        return {
            command: STYLE_FORMAT_COMMAND,
            value,
            label: value,
            text: value,
            payload: { 'font-size': value },
        };
    }), []);

    return (
        <ToolbarDropdownButton
            icon={TextSizeIcon}
            label={t('RTE.fontSize')}
            text={props.value}
            items={items}
            {...props}
        />
    );
};

FontSizeMenuButton.propTypes = {
    disabled: PropTypes.bool,
    value: PropTypes.string,
    onSelect: PropTypes.func.isRequired,
};
