import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useAsyncFn, useToggle } from 'react-use';
import { useHistory } from 'react-router-dom';
import { kebabCase } from 'lodash';
import { Form } from 'formik';

import { ProductsBlockKind } from '@mssgme/shared';
import { isMatchId } from '@mssgme/helpers';
import { useActions, useCollections, useNavigationUtils } from '@mssgme/hooks';
import {
    Button,
    EnhancedFormik,
    FieldTip,
    FormRow,
    ModalPane,
    SelectField,
    StreamlinedScreen,
    SubmitButton,
    ToggleField,
} from '@mssgme/ui';
import { usePageData, useWebsiteData } from '@mssgme/userpage-core';
import { useProductsContext } from '@mssgme/products';

import styles from './ConvertServices.module.scss';

export function ConvertServices({ blockId }) {
    const history = useHistory();
    const website = useWebsiteData();
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { generateUrl } = useNavigationUtils();
    const { _id: websiteId, collections } = website;
    const { _id: pageId, blocks } = usePageData();
    const { specifier } = useProductsContext();
    const { items } = useCollections(specifier);
    const [isFinished, setFinished] = useToggle(false);
    const collectionName = useMemo(() => t('labels.auto_collection', { count: collections.length + 1 }), []);
    const closePath = useMemo(() => generateUrl('..'), []);
    const block = useMemo(() => blocks.find(isMatchId(blockId)), [blocks, blockId]);
    const initialValues = useMemo(
        () => ({
            to: ProductsBlockKind,
            useCollection: block?.items.length > 1,
            createCollection: block?.items.length > 1,
            createBlock: block?.items.length > 1,
            removeOriginal: true,
            collectionId: '',
            collectionName,
        }),
        [collectionName, block?.items.length]
    );
    const options = useMemo(() => {
        return [
            ...items
                .filter((item) => !item.default)
                .map(({ _id, name }) => ({
                    value: _id,
                    label: name,
                })),
            {
                value: '',
                label: t('ConvertService.labels.createCollection', { name: collectionName }),
            },
        ];
    }, [items]);

    const [{ loading }, handleConvert] = useAsyncFn(
        async (payload) => {
            setFinished(false);

            const { createdBlockId } = await api.blocks.convert({
                websiteId,
                pageId,
                blockId,
                payload,
            });

            setFinished(true);

            if (payload.removeOriginal) {
                if (createdBlockId) {
                    history.push(
                        generateUrl(`./block/${kebabCase(payload.to)}/${createdBlockId}`, { useMatchUrl: true })
                    );
                } else {
                    history.push(generateUrl('.', { useMatchUrl: true }));
                }
            }
        },
        [websiteId, pageId, blockId]
    );
    const handleChange = useCallback(({ useCollection, createBlock, collectionId }, { setFieldValue }) => {
        if (createBlock && !useCollection) {
            setFieldValue('createBlock', false);
        }

        setFieldValue('createCollection', !collectionId);
    });

    if (!block) {
        return null;
    }

    return (
        <ModalPane inverse light nopadding title={t('ConvertService.heading')} onClose={closePath}>
            {(handleClose) => (
                <EnhancedFormik initialValues={initialValues} onChange={handleChange} onSubmit={handleConvert}>
                    {({ values, setFieldValue }) => (
                        <Form>
                            <div className={styles.root}>
                                {isFinished ? (
                                    <StreamlinedScreen.Section>
                                        <div className={styles.success}>{t('ConvertService.successHeading')}</div>
                                        <FormRow>
                                            <FieldTip>{t('ConvertService.successTip')}</FieldTip>
                                        </FormRow>
                                        <FormRow>
                                            <Button type="button" fullWidth onClick={handleClose}>
                                                {t('ui:labels.close')}
                                            </Button>
                                        </FormRow>
                                    </StreamlinedScreen.Section>
                                ) : (
                                    <>
                                        <StreamlinedScreen.Section>
                                            <ToggleField
                                                builderInput
                                                name="useCollection"
                                                label={t('ConvertService.labels.useCollection')}
                                                style={{ flex: 1 }}
                                                onChange={(_, value) => {
                                                    if (value) {
                                                        setFieldValue('createBlock', true);
                                                    }
                                                }}
                                            />
                                            <SelectField
                                                name="collectionId"
                                                disabled={!values.useCollection}
                                                options={options}
                                            />

                                            {values.useCollection && !values.collectionId && (
                                                <FormRow>
                                                    <FieldTip
                                                        tipId="landings:ConvertService.labels.addedToTip"
                                                        tipParams={{ name: values.collectionName }}
                                                    />
                                                </FormRow>
                                            )}
                                        </StreamlinedScreen.Section>

                                        <StreamlinedScreen.Section>
                                            <div className={styles.heading}>
                                                {t('ConvertService.labels.whenFinished')}
                                            </div>

                                            <ToggleField
                                                builderInput
                                                name="removeOriginal"
                                                label={t('ConvertService.labels.removeOriginal')}
                                            />

                                            {values.useCollection && (
                                                <ToggleField
                                                    builderInput
                                                    name="createBlock"
                                                    label={t('ConvertService.labels.createBlock')}
                                                />
                                            )}
                                        </StreamlinedScreen.Section>

                                        <StreamlinedScreen.Section>
                                            <FormRow>
                                                <SubmitButton fullWidth disabled={false} isFetching={loading}>
                                                    {t('ConvertService.labels.convert')}
                                                </SubmitButton>
                                            </FormRow>
                                        </StreamlinedScreen.Section>
                                    </>
                                )}
                            </div>
                        </Form>
                    )}
                </EnhancedFormik>
            )}
        </ModalPane>
    );
}

ConvertServices.propTypes = {
    blockId: PropTypes.string.isRequired,
};
