import { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
// import { useSelector } from 'react-redux';

import { useStylingDefinitions } from '@mssgme/ui';
import { stylingDefinitionsFactory } from './definitions';
import { useFeatures } from '@mssgme/hooks';

export const useThemeStylingDefinitions = () => {
    const { t } = useTranslation('landings');
    const features = useFeatures();
    // const isAdvancedStylingMode = useSelector((state) => state.ui.isAdvancedStylingMode);
    const isAdvancedStylingMode = true;
    const definitions = useMemo(() => stylingDefinitionsFactory({ t, isAdvancedStylingMode, features }), [
        t,
        isAdvancedStylingMode,
        features,
    ]);

    return useStylingDefinitions(definitions);
};
