import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useActions, useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { ImageField, TextareaField, TextField } from '@mssgme/ui';
import { MediaSizes, useUploads } from '@mssgme/userpage-core';
import { WebsiteEditForm } from '../../../../UI';

export default function Seo() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const { api } = useActions();
    const { getMediaSizeUrl } = useUploads();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm
            title={t('Website.Edit.SEO.heading')}
            website={website}
            restrictRule="page-seo"
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {({ values, setFieldValue }) => (
                <>
                    <ImageField
                        gallery={false}
                        removable
                        name="faviconUrl"
                        innerLabel="Favicon"
                        previewUrl={getMediaSizeUrl(values.seo.faviconUrl, MediaSizes.Icon, true)}
                        upload={(payload) => api.websites.uploadFavicon({ websiteId: website._id, ...payload })}
                        onChange={({ path }) => setFieldValue('seo.faviconUrl', path || null)}
                    />
                    <ImageField
                        removable
                        name="ogImageUrl"
                        innerLabel="Open Graph Image"
                        previewUrl={getMediaSizeUrl(values.seo.ogImageUrl, MediaSizes.Icon)}
                        upload={(payload) => api.websites.upload({ websiteId: website._id, ...payload })}
                        onChange={({ path }) => setFieldValue('seo.ogImageUrl', path || null)}
                    />
                    <TextField
                        builderInput
                        label={t('Website.fields.seo.title.label')}
                        name="seo.title"
                        placeholder={values.name}
                        maxLength={80}
                    />
                    <TextareaField
                        builderInput
                        label={t('Website.fields.seo.description.label')}
                        name="seo.description"
                        placeholder={t('Website.fields.seo.description.placeholder')}
                        maxLength={200}
                        maxRows={4}
                    />
                </>
            )}
        </WebsiteEditForm>
    );
}
