import PropTypes from 'prop-types';
import React from 'react';

import { cls } from '@mssgme/helpers';
import { ListInput, Swiper } from '@mssgme/ui';
import { PageOption } from './PageOption';

import styles from './ThemeField.module.scss';

export const ThemeInput = ({
    value,
    options,
    grid,
    radio,
    perView = 7.5,
    renderOption,
    children,
    onChange,
    ...rest
}) => {
    if (grid) {
        return (
            <div {...cls([styles.root, radio && styles.radio], rest)}>
                <ListInput
                    grid
                    value={value}
                    options={options}
                    containerClassName={styles.container}
                    choiceClassName={styles.option}
                    activeClassName={styles.active}
                    renderOption={(data) => (
                        <PageOption key={data.option.value} renderOption={renderOption} {...data} />
                    )}
                    onChange={onChange}
                />
            </div>
        );
    }

    return (
        <div {...cls([styles.root, radio && styles.radio], rest)}>
            {children}

            <Swiper
                key={options.length}
                perView={perView}
                loop={options.length > perView}
                pagination={!radio}
                centeredSlides={!!radio}
                initialSlide={options.findIndex((option) => option.value === value)}
                slideClass={styles.option}
                className={styles.swiper}
            >
                {options.map((option) => (
                    <PageOption
                        key={option.value}
                        option={option}
                        active={option.value === value}
                        renderOption={renderOption}
                        onSelect={onChange}
                    />
                ))}
            </Swiper>
        </div>
    );
};

ThemeInput.propTypes = {
    value: PropTypes.string,
    options: PropTypes.array.isRequired,
    grid: PropTypes.bool,
    radio: PropTypes.bool,
    perView: PropTypes.number,
    renderOption: PropTypes.func,
    onChange: PropTypes.func,
};
