import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Redirect, useLocation } from 'react-router-dom';
import { escapeRegExp } from 'lodash';

import { useActions, useUser } from '@mssgme/hooks';
import { EnhancedFormik, Form, ModalEditPane, SaveButton, renderNode, usePresenceRemoval } from '@mssgme/ui';

const useStateBack = (backPath, successRedirectPath) => {
    const { state, pathname } = useLocation();
    const back = state?.backPath || new RegExp(`^${escapeRegExp(pathname)}\\/.+$`).test(backPath) ? '/pages' : backPath;
    const success = state?.successRedirectPath || successRedirectPath;

    return useMemo(() => ({ back, success }), [success, back]);
};

export function AccountEditForm({ children, title, backPath, successRedirectPath }) {
    const { api } = useActions();
    const { i18n, t } = useTranslation('landings');
    const { back, success } = useStateBack(backPath, successRedirectPath || '/pages');
    const [redirectTo, setRedirect] = useState();
    const user = useUser();

    const initialValues = useMemo(
        () => ({
            name: user.name || '',
            email: user.email || '',
            lang: user.lang || 'en',
            avatar: user.avatar,
            avatarUrl: user.avatarUrl,
            notificationSettings: {
                email: {
                    enabled: user.notificationSettings?.email?.enabled || false,
                },
                telegram: {
                    enabled: user.notificationSettings?.telegram?.enabled || false,
                },
                webhook: {
                    enabled: user.notificationSettings?.webhook?.enabled || false,
                    endpoint: user.notificationSettings?.webhook?.endpoint || '',
                },
            },
        }),
        [user]
    );

    const handleSubmit = useCallback(
        async (values) => {
            const result = await api.users.updateMyAccount({ payload: { ...user, ...values } });
            await i18n.changeLanguage(result.lang);

            setRedirect(success);
        },
        [user, success]
    );

    usePresenceRemoval();

    return (
        <EnhancedFormik initialValues={initialValues} onSubmit={handleSubmit}>
            {(form) => (
                <ModalEditPane onClose={back} title={title}>
                    <Form autoComplete="off">
                        {renderNode(children, form)}

                        <SaveButton
                            fullWidth
                            size="standart"
                            isInitialHidden={false}
                            disabled={!form.dirty}
                            style={{ marginTop: 8 }}
                            isFetching={form.isSubmitting}
                        >
                            {t('ui:labels.saveChanges')}
                        </SaveButton>
                    </Form>

                    {redirectTo && <Redirect to={redirectTo} />}
                </ModalEditPane>
            )}
        </EnhancedFormik>
    );
}

AccountEditForm.propTypes = {
    websiteId: PropTypes.string,
    children: PropTypes.any.isRequired,
    title: PropTypes.string.isRequired,
    backPath: PropTypes.string,
    successRedirectPath: PropTypes.string,
};

AccountEditForm.defaultProps = {
    websiteId: '',
    backPath: '/pages',
    successRedirectPath: '/pages',
};
