import { useCallback, useEffect, useRef, useState } from 'react';

export const useControlledState = (current, id) => {
    const mountRef = useRef(false);
    const [state, setter] = useState({ current, id });
    const set = useCallback((current) => setter({ current, id }), [id]);

    useEffect(() => {
        if (mountRef.current) {
            setter({ current, id });
        } else {
            mountRef.current = true;
        }
    }, [current, id]);

    return {
        current: state.current,
        id: state.id,
        set,
    };
};
