import PropTypes from 'prop-types';
import React from 'react';
import { useToggle } from 'react-use';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { useEventCallback, usePagesFactory } from '@mssgme/hooks';
import { LandingsPropTypes, NotificationCenter } from '@mssgme/ui';
import { PromptModal } from '../../PromptModal';
import { SelectPageField } from '../SelectPageField';

export const ActionPageSelector = ({ website, title, ...rest }) => {
    const { t } = useTranslation('admin-ui');
    const { create, loading, value } = usePagesFactory(website._id);
    const [, , helpers] = useField(rest.name);
    const [modal, toggleModal] = useToggle(false);
    const isOnePage = website.pages.length <= 1;

    const handleModal = useEventCallback(async (accept) => {
        toggleModal(false);

        if (!accept) {
            return;
        }

        const { _id } = await create({
            redirect: false,
            name: title || t('ActionField.labels.newPageTitle'),
        });

        helpers.setValue(_id);
        helpers.setTouched(true);

        NotificationCenter.getInstance().success(t('ActionField.labels.created'), { showClose: true });
    });

    const handleClick = useEventCallback((e) => {
        e?.preventDefault();
        e?.stopPropagation();
        toggleModal(true);
    });

    return (
        <div onClick={isOnePage ? handleClick : null}>
            <SelectPageField
                builderInput
                website={website}
                restrictRule="pages:create"
                restrictBadge={isOnePage}
                disabled={isOnePage}
                {...rest}
            />

            {modal && (
                <PromptModal
                    title="Create new page?"
                    message={t('ActionField.labels.prompt')}
                    okText={t('ActionField.labels.createPage')}
                    btnStyle="success"
                    isFetching={loading || !!value}
                    handle={handleModal}
                />
            )}
        </div>
    );
};

ActionPageSelector.propTypes = {
    ...SelectPageField.propTypes,
    website: LandingsPropTypes.website.isRequired,
    title: PropTypes.string,
};
