import React, { useMemo } from 'react';
import { merge } from 'lodash';
import { usePrevious } from 'react-use';
import { matchPath, Redirect, Route, Switch, useHistory } from 'react-router-dom';

import { useAccessControl, useUtmUpdate, useActions, usePushNotifications } from '@mssgme/hooks';
import { Feature, SocketObserver, RestrictionScope } from '@mssgme/ui';
import { BillingObserver, BillingRouting } from '@mssgme/billing';
import { LandingsPage, FeedbackObserver } from '@mssgme/landings';

import { AppObserver, CheckUpdate } from '../../UI';
import { SettingsPage } from '../Settings';
import { RestrictedPage } from '../RestrictedPage';
import { PlaceholderPage } from '../PlaceholderPage';
import MainLayout from './MainLayout';
import Header from './Header';
import useBanner from './useBanner';

const HEADER_PATH = [
    '/pages',
    '/pages/create',
    '/inbox',
    '/s',
    '/s/support',
    '/s/notifications',
    '/s/account',
    '/s/account/change-password',
    '/s/delete-account',
];
const DYNAMIC_LOCATIONS = {
    exact: false,
    path: ['/pages', '/products', '/inbox', '/billing', '/restricted'],
};

export function MainPage() {
    const { isAdmin } = useAccessControl();
    const history = useHistory();
    const prev = usePrevious({ ...history.location }) || {
        ...history.location,
        pathname: history.location.pathname || '/',
    };
    // TODO: refactor smell code
    const shouldFreeze = !(
        history.location.pathname === '/' || matchPath(history.location.pathname, DYNAMIC_LOCATIONS)
    );
    const freezableLocation = useMemo(() => (shouldFreeze ? prev : history.location), [shouldFreeze]);
    const location = shouldFreeze ? freezableLocation : history.location;
    const backPath = location.pathname === history.location.pathname ? '/' : location.pathname;

    const banner = useBanner(location);
    const { api } = useActions();

    useUtmUpdate();
    usePushNotifications({
        onRegister({ token }) {
            api.users.addApnToken({ payload: { token } });
        },
        onAction({ notification }) {
            history.replace(notification.data.url || '/');
        },
    });

    return (
        <MainLayout
            header={banner}
            footer={<SettingsPage backPath={backPath} />}
        >
            <CheckUpdate />
            <SocketObserver />
            <AppObserver />
            <BillingObserver />
            <FeedbackObserver />
            <RestrictionScope>
                <Route path={HEADER_PATH} exact>
                    <Header />
                </Route>
                <Switch location={location}>
                    <Route
                        path={['/pages', '/s']}
                        render={() => <Feature rule="websites:?" component={LandingsPage} />}
                    />
                    {isAdmin ? (
                        <Route path="/billing" component={BillingRouting} />
                    ) : (
                        <Route
                            path="/billing"
                            render={({ location }) => (
                                <RestrictedPage
                                    location={{
                                        state:
                                            location.state?.reason &&
                                            merge(
                                                { reason: { code: 'upgrade', target: 'pro' } },
                                                location.state
                                            ),
                                    }}
                                />
                            )}
                        />
                    )}
                    <Route path="/restricted" component={RestrictedPage} />
                    <Redirect exact from="/" to="/pages" />
                    <Route path="/" component={PlaceholderPage} />
                </Switch>
            </RestrictionScope>
        </MainLayout>
    );
}
