import { object, string, array } from 'yup';

export const FeaturesItemSchema = object({
    title: string()
        .ensure()
        .min(3, 'errorsList.block_features_item_title_length')
        .max(250, 'errorsList.block_features_item_title_length'),
    content: string()
        .ensure()
        .min(3, 'errorsList.block_features_item_content_length')
        .max(20000, 'errorsList.block_features_item_content_length'),
    glyph: object({
        type: string()
            .required('common:errorsList.field_required')
            .oneOf(['image', 'icon', 'emoticon'], 'common:errorsList.invalid_value'),
    }).test('is-glyph', 'errorsList.field_required', (val) => {
        const map = {
            icon: 'icon',
            image: 'url',
            emoticon: 'text',
        };

        return !!val[map[val.type]];
    }),
});

export const FeaturesBlockSchema = object({
    items: array(FeaturesItemSchema).min(1, 'errorsList.block_features_count'),
});
