import classNames from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';

import { useMultiRef } from '@mssgme/hooks';
import { renderNode } from '../../../../helpers';

import styles from './ButtonChoice.module.scss';

export default function ButtonChoice({
    options,
    value,
    toggle,
    multiple,
    containerClassName,
    choiceClassName,
    activeClassName,
    onChoiceRef,
    onChange,
}) {
    const refs = useMultiRef(options.length, onChoiceRef);
    const isSelected = (selected) => {
        return multiple ? value.includes(selected) : value === selected;
    };
    const handleClick = (selected, e) => {
        e.preventDefault();
        e.stopPropagation();

        const add = toggle && isSelected(selected);

        if (multiple) {
            if (add) {
                onChange(value.filter((item) => item !== selected));
            } else {
                onChange([...new Set([...value, selected])]);
            }
        } else {
            if (add) {
                onChange(undefined);
            } else {
                onChange(selected);
            }
        }
    };

    return (
        <div className={classNames(styles.root, containerClassName)}>
            {options.map((choice, index) => {
                const active = isSelected(choice.value);

                return (
                    <a
                        href="#"
                        key={choice.value}
                        ref={refs[index]}
                        className={classNames(
                            styles.choice,
                            active && styles.active,
                            choiceClassName,
                            active && activeClassName
                        )}
                        onClick={onChange && handleClick.bind(null, choice.value)}
                        {...choice.props}
                    >
                        {renderNode((choice.props && choice.props.children) || choice.label, { choice, active })}
                    </a>
                );
            })}
        </div>
    );
}

const OptionValuePropType = PropTypes.oneOfType([PropTypes.string, PropTypes.number, PropTypes.bool]);

export const OptionProps = {
    label: PropTypes.oneOfType([PropTypes.string, PropTypes.element, PropTypes.func]),
    value: PropTypes.string,
};

ButtonChoice.propTypes = {
    value: PropTypes.oneOfType([OptionValuePropType, PropTypes.arrayOf(OptionValuePropType)]),
    options: PropTypes.arrayOf(PropTypes.shape(OptionProps)).isRequired,
    toggle: PropTypes.bool,
    multiple: PropTypes.bool,
    onChange: PropTypes.func.isRequired,
    onChoiceRef: PropTypes.func,
    containerClassName: PropTypes.string,
    choiceClassName: PropTypes.string,
    activeClassName: PropTypes.string,
};

ButtonChoice.defaultProps = {
    value: null,
    toggle: false,
    multiple: false,
    onChoiceRef: null,
    containerClassName: '',
    choiceClassName: '',
    activeClassName: '',
};
