import PropTypes from 'prop-types';
import { useMemo } from 'react';

import { renderNode } from '@mssgme/ui';
import { useBlockTheme, usePageTheme } from '@mssgme/userpage-core';

export const BlockStyleProvider = ({ block, resolver, children }) => {
    const {
        theme: { backgroundColor, font },
    } = usePageTheme(true);
    const { theme, style } = useBlockTheme(block);

    const result = useMemo(
        () => ({
            borderRadius: 0,
            backgroundColor,
            fontFamily: font?.family || 'inherit',
            ...style,
            ...(resolver && resolver(theme)),
        }),
        [backgroundColor, style, resolver, resolver && theme]
    );

    return renderNode(children, result);
};

BlockStyleProvider.propTypes = {
    block: PropTypes.any.isRequired,
};
