import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useState } from 'react';
import { FiChevronDown } from 'react-icons/fi';
import { motion } from 'framer-motion';

import { cls, isNumeric } from '@mssgme/helpers';
import { StatTypes } from '../../domainTypes';
import styles from './StatsList.module.scss';

const diffStr = (diff) => (diff > 0 ? `+${diff}` : diff);
const StatsValue = ({ amount, prev }) => {
    const diff = prev === undefined ? undefined : amount - prev;

    return (
        <span>
            {!!diff && (
                <span className={styles.delta}>
                    (<span className={cn({ [styles.up]: diff > 0, [styles.down]: diff < 0 })}>
                        {diffStr(diff)}
                    </span>)
                    {' '}
                </span>
            )}

            <span>{amount}</span>
        </span>
    );
};

StatsValue.propTypes = {
    amount: PropTypes.number.isRequired,
    prev: PropTypes.number,
};

const StatsHeader = ({ heading, amount, prev, hasChevron, expandable, expanded, ...rest }) => (
    <div {...cls([styles.total, hasChevron && styles.hasChevron, !expandable && styles.disabled], rest)}>
        <span>{heading}</span>
        <StatsValue amount={amount} prev={prev} />
        {hasChevron && (
            <div className={cn(styles.chevron, expanded && styles.expanded)}>
                <FiChevronDown size={16} color="#333" />
            </div>
        )}
    </div>
);

StatsHeader.propTypes = {
    heading: PropTypes.string.isRequired,
    amount: PropTypes.number.isRequired,
    prev: PropTypes.number,
    hasChevron: PropTypes.bool,
    expandable: PropTypes.bool,
    expanded: PropTypes.bool,
};

const StatsMore = ({ stats, expanded }) => {
    const isDiffMode = isDiff(stats);
    const getPrev = (stat) => {
        const prev = stats.prev.find((s) => s.key === stat.key);

        return prev ? getFlatValue(prev) : undefined;
    };

    return (
        <motion.ul
            className={styles.list}
            animate={{ height: expanded ? 'auto' : 0 }}
            transition={{ ease: 'easeInOut' }}
        >
            {isDiffMode
                ? stats.current.map((stat) => (
                    <li key={stat.key}>
                        <span>{stat.label}</span>
                        <StatsValue amount={stat.value} prev={getPrev(stat)} />
                    </li>
                ))
                : stats
                    .map((stat) => ({ key: stat.key, label: stat.label, ...getStatsValues(stat) }))
                    .map((stat) => (
                        <li key={stat.key}>
                            <span>{stat.label}</span>
                            <StatsValue amount={stat.current} prev={stat.prev} />
                        </li>
                    ))}
        </motion.ul>
    );
};

StatsMore.propTypes = {
    stats: StatTypes.diffable.isRequired,
    expanded: PropTypes.bool.isRequired,
    onToggle: PropTypes.func.isRequired,
};

const getArrayOrFlatValue = (stats) => (Array.isArray(stats) ? getArrayValue(stats) : getFlatValue(stats));
const getArrayValue = (stats) => stats.reduce((prev, curr) => prev + curr.value, 0);
const getFlatValue = (stats) => (isNumeric(stats) ? stats : stats.value || 0);
const getDeltaValue = (stats) => ({
    current: getArrayOrFlatValue(stats.current),
    prev: getArrayOrFlatValue(stats.prev),
});
const isDiff = (stats) => typeof stats === 'object' && 'current' in stats && 'prev' in stats;
const getStatsValues = (stats) =>
    isDiff(stats) ? getDeltaValue(stats) : { current: getArrayOrFlatValue(stats), prev: undefined };
const isArrayStats = (stats) => Array.isArray(isDiff(stats) ? stats.current : stats);
const statsLength = (stats) => (isArrayStats(stats) ? (isDiff(stats) ? stats.current : stats).length : 0);

const StatsList = ({ heading, expandable, stats }) => {
    const [expanded, setExpanded] = useState(false);
    const { current, prev } = getStatsValues(stats);
    const hasMoreStats = expandable && !!statsLength(stats);

    const handleToggle = useCallback(() => setExpanded(!expanded), [stats, expanded]);

    return (
        <div className={styles.root}>
            <StatsHeader
                heading={heading}
                amount={current}
                prev={prev}
                onClick={handleToggle}
                hasChevron={expandable}
                expandable={hasMoreStats}
                expanded={hasMoreStats && expanded}
            />
            {expandable && isArrayStats(stats) && (
                <StatsMore stats={stats} expanded={expanded} onToggle={handleToggle} />
            )}
        </div>
    );
};

StatsList.propTypes = {
    heading: PropTypes.string,
    expandable: PropTypes.bool,
    stats: StatTypes.diffable.isRequired,
};

StatsList.defaultProps = {
    heading: 'Actions',
    expandable: true,
    stats: [],
};

export default StatsList;
