import React from 'react';
import { useTranslation } from 'react-i18next';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';

export function PageNameEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();

    return (
        <PageEditForm title={t('Page.Edit.Name.heading')} website={website} page={page} backPath={generateUrl('..')}>
            <TextField
                builderInput
                name="name"
                placeholder={t('Page.fields.name.placeholder')}
                maxLength={250}
            />
        </PageEditForm>
    );
}
