import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';
import { useFormikContext } from 'formik';

import { useActions, useWebsitePageBySlugs } from '@mssgme/hooks';
import { ImageField, LandingsPropTypes, TextField } from '@mssgme/ui';
import { Hat, MediaSizes, useBlockTheme, useUploads } from '@mssgme/userpage-core';
import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

const HatForm = ({ bind: { websiteId, page, blockComponent: Preview }, styling }) => {
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { getMediaSizeUrl } = useUploads();
    const { values: block, setFieldValue } = useFormikContext();
    const { theme } = useBlockTheme(block, false);
    const isImageMode = theme.headerMode === 'image';
    const isCenteredMode = theme.headerMode === 'centered';
    const isBrandMode = theme.headerMode === 'brand';
    const isAvatarMode = theme.headerMode === 'avatar';
    const previewMargin = !isAvatarMode ? '0 0 16px 0' : '16px 0';

    return (
        <EditStylingForm page={page} element={block} styling={styling} preview={(item) => <Hat block={item} />}>
            <StyledBlockPreview style={{ marginBottom: 16, padding: isAvatarMode ? '16px 0' : 0 }}>
                <Preview block={block} embedded={isAvatarMode} style={{ margin: previewMargin }} />
            </StyledBlockPreview>

            {!isAvatarMode && (
                <ImageField
                    style={{ marginBottom: isImageMode ? 16 : 12 }}
                    removable={!(isImageMode || isCenteredMode || isBrandMode)}
                    name="bgUrl"
                    innerLabel={t('Page.Edit.Hat.fields.bgUrl.label')}
                    previewUrl={getMediaSizeUrl(block.absoluteBgUrl, MediaSizes.Icon)}
                    upload={(payload) => api.websites.upload({ websiteId, ...payload })}
                    onChange={({ path, url }) => {
                        setFieldValue('bgUrl', path || null);
                        setFieldValue('absoluteBgUrl', url || '');
                    }}
                />
            )}

            {!isImageMode && (
                <>
                    <ImageField
                        removable={!isBrandMode}
                        name="logoUrl"
                        innerLabel={t('Page.Edit.Hat.fields.logoUrl.label')}
                        previewUrl={getMediaSizeUrl(block.absoluteLogoUrl, MediaSizes.Icon)}
                        upload={(payload) => api.websites.upload({ websiteId, ...payload })}
                        onChange={({ path, url }) => {
                            setFieldValue('logoUrl', path || null);
                            setFieldValue('absoluteLogoUrl', url || '');
                        }}
                    />

                    <TextField
                        required
                        builderInput
                        label={t('Page.Edit.Hat.fields.title.label')}
                        placeholder={t('Page.Edit.Text.fields.content.placeholder')}
                        name="title"
                        maxLength={100}
                    />

                    <TextField
                        builderInput
                        label={t('Page.Edit.Hat.fields.subtitle.label')}
                        placeholder={t('Page.Edit.Text.fields.content.placeholder')}
                        name="subtitle"
                        style={{ marginBottom: 16 }}
                        maxLength={100}
                    />
                </>
            )}
        </EditStylingForm>
    );
};

HatForm.propTypes = {
    bind: PropTypes.shape({
        websiteId: PropTypes.string.isRequired,
        page: LandingsPropTypes.page.isRequired,
        blockComponent: PropTypes.func.isRequired,
    }).isRequired,
    styling: PropTypes.object.isRequired,
};

export function HatEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: HatEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Hat.heading')}>
            {({ styling }) => <HatForm bind={bind} styling={styling} />}
        </BlockEditForm>
    );
}

HatEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

HatEdit.block = Hat;
