import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import { Button, ProtectAccess } from '@mssgme/ui';

import styles from './CreateWebsiteButton.module.scss';

export function CreateWebsiteButton() {
    const { t } = useTranslation('landings');

    return (
        <ProtectAccess strict rule="websites:create">
            {(onClick) => (
                <Button
                    className={styles.root}
                    fullWidth
                    rounded
                    tag={Link}
                    to="/pages/create"
                    onClick={onClick}
                >
                    + {t('labels.website.createNew')}
                </Button>
            )}
        </ProtectAccess>
    );
}
