import PropTypes from 'prop-types';
import React from 'react';
import { useTranslation } from 'react-i18next';

import { useWebsitePageBySlugs } from '@mssgme/hooks';
import { Link } from '@mssgme/userpage-core';
import { SmartLinkField } from '@mssgme/admin-ui';

import { BlockEditForm, EditStylingForm, StyledBlockPreview, useBlockEditor } from '../components';

const themeFields = {
    social: 'theme.blocks.link.icon',
    color: 'theme.blocks.link.backgroundColor',
    contrastedColor: 'theme.blocks.link.contrastedColor',
};

export function LinkEdit({ blockId, backPath }) {
    const { t } = useTranslation('landings');
    const { website, page } = useWebsitePageBySlugs();
    const { bind } = useBlockEditor({ website, page, blockId, backPath, as: LinkEdit });

    return (
        <BlockEditForm {...bind} title={t('Page.Edit.Link.heading')}>
            {({ values, styling }) => (
                <EditStylingForm page={page} element={values} styling={styling}>
                    <StyledBlockPreview style={{ marginBottom: 16 }}>
                        <bind.blockComponent block={values} interactive={false} />
                    </StyledBlockPreview>

                    <SmartLinkField required themeFields={themeFields} />
                </EditStylingForm>
            )}
        </BlockEditForm>
    );
}

LinkEdit.propTypes = {
    blockId: PropTypes.string,
    backPath: PropTypes.string,
};

LinkEdit.block = Link;
