import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useLayoutEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { FiArrowLeft, FiShare } from 'react-icons/fi';

import { clearPagePayload, useActions, useNavigationUtils } from '@mssgme/hooks';
import { CustomViewInput, LandingsPropTypes, useTitle } from '@mssgme/ui';
import { getPageName } from '@mssgme/userpage-core';

import { useInlineFormInput } from '../../../hooks';
import { PublishButton } from '../../../UI';

import styles from './BuilderHeader.module.scss';

export function BuilderHeader({ isViewMode, isSortMode, website, page }) {
    const { api } = useActions();
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils({ useMatchUrl: true });
    const isPublished = website.enabled;
    const isHomePage = website.homePageId === page._id;
    const isEmptyWebsite = !website.pages.some(({ blocks }) => blocks.length > 0);
    const initialValues = useMemo(() => ({
        ...clearPagePayload(page),
        // todo: remove when home-page name fallback no longer needed
        name: getPageName(page, isHomePage),
    }), [page.name]);

    const { formik, bind } = useInlineFormInput({
        initialValues,
        name: 'name',
        onSubmit: (payload) => api.pages.update({ websiteId: website._id, pageId: page._id, payload }),
    });

    useLayoutEffect(() => void formik.setValues(initialValues), [initialValues]);

    useTitle(page.name);

    return (
        <div className={cn(styles.root, isViewMode && styles.viewMode, isSortMode && styles.sortMode)}>
            <div className={styles.container}>
                <div className={styles.row}>
                    <Link className={styles.backButton} to={generateUrl('../..')}>
                        <FiArrowLeft size={20} color="#333" />
                    </Link>

                    <CustomViewInput
                        {...bind}
                        className={styles.input}
                        containerClassName={styles.title}
                        placeholder={bind.value}
                    >
                        <div className={styles.crop}>{bind.value}</div>
                    </CustomViewInput>

                    {!isEmptyWebsite && (
                        <>
                            {isPublished && (
                                <Link
                                    to={generateUrl('share')}
                                    className={styles.button}
                                    title={t('Editor.Dashboard.header.share')}
                                >
                                    <FiShare size={20} color="#333" />
                                </Link>
                            )}

                            {/*<Link*/}
                            {/*    to={generateUrl(desktopPreview ? 'preview/desktop' : 'preview')}*/}
                            {/*    className={styles.button}*/}
                            {/*    title={t('Editor.Dashboard.header.preview')}*/}
                            {/*>*/}
                            {/*    <FiEye size={20} color="#333" />*/}
                            {/*</Link>*/}

                            <PublishButton
                                className={styles.publishButton}
                                size="small"
                                btnStyle="primary"
                                publishedUrl={generateUrl('../../publish/published')}
                                website={website}
                                page={page}
                                data-button="publish"
                            >
                                {t('Editor.Dashboard.header.publish')}
                            </PublishButton>
                        </>
                    )}
                </div>
                <div className={styles.row}>
                    <span>{t('Editor.Dashboard.header.sortTitle')}</span>
                </div>
            </div>
        </div>
    );
}

BuilderHeader.propTypes = {
    website: LandingsPropTypes.website.isRequired,
    page: LandingsPropTypes.page.isRequired,
    isSortMode: PropTypes.bool,
    isViewMode: PropTypes.bool,
};
