import React, { useEffect, useState } from 'react';
import { $getSelection, $isRangeSelection, COMMAND_PRIORITY_CRITICAL, SELECTION_CHANGE_COMMAND } from 'lexical';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';
import { mergeRegister } from '@lexical/utils';

import { ua } from '@mssgme/helpers';
import { getLinkNode } from './utils';
import { useDebouncedCallback } from './ui';
import { FloatingLinkEditor } from './FloatingLinkEditor';

export function FloatingLinkPlugin() {
    const [editor] = useLexicalComposerContext();
    const [isLink, setIsLink] = useState(false);

    const $updateFromState = useDebouncedCallback((editorState) => {
        editorState.read(() => {
            const selection = $getSelection();
            const isNotInRange = !$isRangeSelection(selection);
            const nativeSelection = window.getSelection();

            if (isNotInRange || (nativeSelection && !editor.getRootElement()?.contains(nativeSelection.anchorNode))) {
                return;
            }

            if (selection) {
                setIsLink(!!getLinkNode(selection));
            }
        });
    }, 50);


    useEffect(() => {
        const read = () => $updateFromState(editor.getEditorState());

        read();

        return mergeRegister(
            editor.registerUpdateListener(({ editorState }) => $updateFromState(editorState)),
            editor.registerCommand(SELECTION_CHANGE_COMMAND, () => (read(), false), COMMAND_PRIORITY_CRITICAL)
        );
    }, [editor, $updateFromState]);

    if (ua.isMobile || !isLink) {
        return null;
    }

    return <FloatingLinkEditor />;
}
