import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { Badge } from '@mssgme/ui';
import styles from './PlanTitle.module.scss';

const SHOW_DEPRECATED_BADGE = false;

export default function PlanTitle({ subscription }) {
    const { t } = useTranslation('billing');

    if (!subscription) {
        return (
            <span className={styles.root}>
                <span>Free</span>
            </span>
        );
    }

    const { plan, price, period } = subscription;

    return (
        <span className={styles.root}>
            <span className={styles.name}>
                {plan.name}
                {SHOW_DEPRECATED_BADGE && plan.deprecated && (
                    <Badge badgeStyle="danger" className={styles.badge}>
                        {t('billingHome.oldPlan')}
                    </Badge>
                )}
            </span>
            {price && (
                <span className={styles.price}>
                    {t(`billingHome.planLabel.${period}`, {
                        price: price.amount,
                        currency: price.currency,
                    })}
                </span>
            )}
        </span>
    );
}

PlanTitle.propTypes = {
    subscription: PropTypes.shape({
        plan: PropTypes.shape({
            id: PropTypes.string,
            name: PropTypes.string,
            deprecated: PropTypes.bool,
        }).isRequired,
        period: PropTypes.string,
        price: PropTypes.shape({
            amount: PropTypes.number.isRequired,
            currency: PropTypes.string.isRequired,
        }),
    }),
};
