import { useActions } from '@mssgme/hooks';
import { useProductsBase } from './useProductsBase';

const getSubProducts = (store) => store.subproducts;

export const useProductsSublist = (specifier, collectionId) => {
    const { api, setProductsSubFilter: setProductsFilter, clearProductsSubStore: clearProductsStore } = useActions();

    return useProductsBase({
        specifier,
        collectionId,
        list: api.products.sublist,
        setProductsFilter,
        clearProductsStore,
        selector: getSubProducts,
    });
};
