import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

import { LinkButton, LandingsPropTypes } from '@mssgme/ui';

import styles from './EmptyPagePreview.module.scss';

export function EmptyPagePreview({
    hasWelcomeTip,
    hasCreateTip,
    hasEmptyBlocksTip,
    hasEmptyBlocksMessage,
    page,
    website,
}) {
    const { t } = useTranslation('landings');

    return (
        <div className={styles.root}>
            <div className={styles.cover} />
            <div className={styles.content}>
                <div className={styles.line} style={{ width: 160 }} />
                <div className={styles.line} style={{ width: 260 }} />
                <div className={styles.line} />
                <div className={styles.messengers}>
                    <div />
                    <div />
                    <div />
                    <div />
                    <div />
                    <div />
                </div>
                <div className={styles.line} style={{ width: 290 }} />
                <div className={styles.line} />
                <div className={styles.line} />
                <div className={styles.image} />
            </div>
            {hasWelcomeTip && (
                <div className={styles.modalContainer}>
                    <div className={styles.modal}>
                        <span className={styles.modalHeading}>{t('EmptyPagePreview.welcomeTip.heading')}</span>
                        <p className={styles.modalContent}>{t('EmptyPagePreview.welcomeTip.text')}</p>
                        <LinkButton to="/pages/create" fullWidth style={{ marginTop: 16 }}>
                            {t('labels.page.create')}
                        </LinkButton>
                    </div>
                </div>
            )}
            {hasCreateTip && (
                <div className={styles.modalContainer}>
                    <div className={styles.modal}>
                        <span className={styles.modalHeading}>{t('EmptyPagePreview.createTip.heading')}</span>
                        <p className={styles.modalContent}>{t('EmptyPagePreview.createTip.text')}</p>
                    </div>
                </div>
            )}
            {hasEmptyBlocksTip && (
                <div className={styles.modalContainer}>
                    <div className={styles.modal}>
                        <span className={styles.modalHeading}>{t('EmptyPagePreview.emptyBlocks.heading')}</span>
                        <p className={styles.modalContent}>{t('EmptyPagePreview.emptyBlocks.text')}</p>
                    </div>
                </div>
            )}
            {hasEmptyBlocksMessage && page && (
                <div className={styles.modalContainer}>
                    <div className={styles.modal}>
                        <span className={styles.modalHeading}>{t('EmptyPagePreview.emptyBlocks.heading')}</span>
                        <p className={styles.modalContent}>{t('EmptyPagePreview.emptyBlocks.textInConstructor')}</p>
                        <LinkButton to={`/pages/-/${website._id}`} fullWidth style={{ marginTop: 16 }}>
                            {t('labels.addBlocks')}
                        </LinkButton>
                    </div>
                </div>
            )}
        </div>
    );
}

EmptyPagePreview.propTypes = {
    hasWelcomeTip: PropTypes.bool,
    hasCreateTip: PropTypes.bool,
    hasEmptyBlocksTip: PropTypes.bool,
    hasEmptyBlocksMessage: PropTypes.bool,
    page: PropTypes.object,
    website: LandingsPropTypes.website.isRequired,
};

EmptyPagePreview.defaultProps = {
    hasWelcomeTip: false,
    hasCreateTip: false,
    hasEmptyBlocksTip: false,
    hasEmptyBlocksMessage: false,
    page: null,
};
