import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import React, { useCallback, useMemo } from 'react';

import { LandingsPropTypes } from '@mssgme/ui';
import { useRestriction } from '@mssgme/hooks';
import { DashboardActionButton } from './DashboardActionButton';

export const DashboardActionItem = ({ index, item, labeled, disabled: globalDisable, context, onAction }) => {
    const { t } = useTranslation('landings');
    const disabled = useMemo(() => !!(globalDisable || (item.disabled && item.disabled({ ...context, index }))), [
        globalDisable,
        item.disabled,
        index,
        context,
    ]);
    const { warning, handleBlockedEvent } = useRestriction(item.restriction, context.selection);
    const title = typeof item.title === 'function' ? item.title(context) : item.title;
    const icon = item.icon(context);
    const active = !(item.filter && !item.filter(context));

    const handleAction = useCallback(
        () => onAction({ ...context, ...item.parameters?.(context), action: item.action }),
        [onAction, context, item.action, item.parameters]
    );

    const props = useMemo(
        () => ({
            slim: item.slim,
            ...(item.to
                ? { to: item.to(context), onClick: handleBlockedEvent }
                : { onClick: handleBlockedEvent || handleAction }),
        }),
        [item.to, item.slim, handleAction, handleBlockedEvent, context]
    );

    return (
        <DashboardActionButton
            title={title && t(`Editor.Dashboard.items.${title}`)}
            icon={icon}
            disabled={disabled}
            active={active}
            labeled={labeled}
            warning={warning}
            {...props}
        />
    );
};

DashboardActionItem.propTypes = {
    index: PropTypes.number.isRequired,
    disabled: PropTypes.bool,
    labeled: PropTypes.bool,
    item: PropTypes.shape({
        title: PropTypes.oneOfType([PropTypes.string, PropTypes.func]).isRequired,
        icon: PropTypes.func.isRequired,
        action: PropTypes.string,
        restriction: PropTypes.string,
        slim: PropTypes.bool,
        disabled: PropTypes.func,
        filter: PropTypes.func,
        parameters: PropTypes.func,
        to: PropTypes.func,
    }).isRequired,
    context: PropTypes.shape({
        block: LandingsPropTypes.block,
        selection: PropTypes.arrayOf(LandingsPropTypes.block).isRequired,
        blocks: PropTypes.arrayOf(LandingsPropTypes.block).isRequired,
        url: PropTypes.string.isRequired,
    }).isRequired,
    onAction: PropTypes.func.isRequired,
};
