import arrayMove from 'array-move';
import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useFormikContext } from 'formik';
import { SortableContainer, SortableElement } from 'react-sortable-hoc';

import { MESSENGERS, normalizeMessenger } from '@mssgme/shared';
import DragHandle from '../DragHandle';
import { FieldRow } from '../Form';
import { MessengersItemField } from './MessengersItemField';
import { MessengersItemEditModal } from './MessengersItemEditModal';

import styles from './MessengersField.module.scss';

const NOT_SUPPORTED = ['skype', 'instagram'];
const SUPPORTED = MESSENGERS.filter((platform) => !NOT_SUPPORTED.includes(platform));

const SortableMessengerField = SortableElement(MessengersItemField);

const SortableMessengersField = SortableContainer(({ items, existing, name, onEdit, onDelete, mode, ...rest }) => (
    <FieldRow name={name} className={styles.list} {...rest}>
        {items.map((platform, index) => {
            const isNew = index >= existing;

            return (
                <SortableMessengerField
                    key={platform}
                    index={index}
                    collection={isNew ? 1 : 0}
                    sortable={existing > 1 && !isNew}
                    platform={platform}
                    name={isNew ? `${name}.${platform}` : `${name}.${index}`}
                    onEdit={onEdit}
                    onDelete={onDelete}
                    mode={mode}
                />
            );
        })}
    </FieldRow>
));

SortableMessengersField.propTypes = {
    ...SortableMessengersField.propTypes,
    items: PropTypes.arrayOf(PropTypes.string).isRequired,
    existing: PropTypes.number.isRequired,
    name: PropTypes.string.isRequired,
};

SortableMessengersField.defaultProps = {
    ...SortableMessengersField.defaultProps,
    shouldCancelStart: DragHandle.shouldCancelStart,
    helperClass: cn(DragHandle.helperClass, styles.sortable),
    lockAxis: 'y',
    lockToContainerEdges: true,
    lockOffset: '0%',
    pressDelay: 0,
};

export const MessengersItemsField = ({ name, validationSchema, mode, builderInput, ...rest }) => {
    const { getFieldMeta, setFieldValue, setFieldTouched } = useFormikContext();
    const { value: messengers } = getFieldMeta(name);
    const [editingPlatform, setEditingPlatform] = useState();
    const platformIndex = messengers.findIndex(({ platform }) => platform === editingPlatform);
    const editingIndex = editingPlatform ? (platformIndex >= 0 ? platformIndex : messengers.length) : -1;
    const existing = messengers.map(({ platform }) => platform);

    const combined = useMemo(() => {
        const empty = SUPPORTED.filter((platform) => existing.indexOf(platform) < 0);

        return existing.concat(empty);
    }, [existing.join(',')]);

    const handleDeletion = useCallback(
        (platform) => {
            setFieldValue(name, messengers.filter((item) => item.platform !== platform));
            setFieldTouched(name, true, true);
        },
        [name, messengers]
    );
    const handleApply = useCallback(
        (values) => {
            if (values) {
                if (!values.value) {
                    handleDeletion(editingPlatform);
                } else {
                    const normalizedValue = normalizeMessenger({
                        value: values.value,
                        platform: values.platform,
                        type: values.type,
                    });

                    setFieldValue(`${name}.${editingIndex}`, { ...values, value: normalizedValue }, true);
                    setFieldTouched(`${name}.${editingIndex}`, true, true);
                }
            }

            setEditingPlatform(null);
        },
        [editingPlatform, editingIndex]
    );
    const onSortEnd = useCallback(
        ({ oldIndex, newIndex }) => {
            if (oldIndex === newIndex) {
                return;
            }

            setFieldValue(name, arrayMove(messengers, oldIndex, newIndex));
        },
        [messengers]
    );

    return (
        <div className={styles.root} {...rest}>
            <SortableMessengersField
                builderInput={builderInput}
                items={combined}
                name={name}
                existing={existing.length}
                onSortEnd={onSortEnd}
                onEdit={setEditingPlatform}
                onDelete={handleDeletion}
                mode={mode}
            />

            {editingPlatform && editingIndex >= 0 && (
                <MessengersItemEditModal
                    fullWidth
                    name={`${name}.${editingIndex}`}
                    platform={editingPlatform}
                    onApply={handleApply}
                    validationSchema={validationSchema}
                />
            )}
        </div>
    );
};

MessengersItemsField.propTypes = {
    name: PropTypes.string.isRequired,
    validationSchema: PropTypes.any,
    mode: MessengersItemField.propTypes.mode,
};
