import React from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { Button, ModalPane } from '@mssgme/ui';

export function NoBlocksMessage() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const closePath = generateUrl('../..');
    const targetPath = generateUrl(`/pages/-/${website._id}/${page._id}/edit`);

    return (
        <ModalPane title={t('Editor.NoBlocksMessage.heading')} onClose={closePath}>
            <p style={{ margin: 0, color: '#777', textAlign: 'center' }}>{t('Editor.NoBlocksMessage.message')}</p>
            {/*<Button fullWidth noborder btnStyle="secondary2" tag={Link} to={closePath} style={{ marginTop: 16 }}>*/}
            {/*    {t('Editor.NoBlocksMessage.cancel')}*/}
            {/*</Button>*/}
            <Button fullWidth tag={Link} to={targetPath} style={{ marginTop: 16 }}>
                {t('Editor.NoBlocksMessage.addBlock')}
            </Button>
        </ModalPane>
    );
}
