import React from 'react';
import { useTranslation } from 'react-i18next';
import { useParams } from 'react-router-dom';

import { useNavigationUtils, useWebsiteBySlug } from '@mssgme/hooks';
import { ListInput } from '@mssgme/ui';
import { WebsiteEditForm } from '../../../../UI';

export default function Status() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { websiteSlug } = useParams();
    const website = useWebsiteBySlug(websiteSlug);

    return (
        <WebsiteEditForm
            title={t('Website.Edit.Status.heading')}
            website={website}
            backPath={generateUrl('..')}
            successRedirectPath={generateUrl('..')}
        >
            {({ values, setFieldValue }) => (
                <ListInput
                    value={values.enabled}
                    options={[
                        { label: t('ui:labels.published'), value: true },
                        { label: t('ui:labels.notPublished'), value: false },
                    ]}
                    onChange={(value) => setFieldValue('enabled', value)}
                />
            )}
        </WebsiteEditForm>
    );
}
