import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import { LandingsPropTypes } from '../../domainTypes';

const item = PropTypes.shape({
    to: PropTypes.any.isRequired,
    label: PropTypes.string.isRequired,
    labelColor: PropTypes.string,
    icon: PropTypes.any.isRequired,
    preview: PropTypes.any,
    filter: PropTypes.bool,
    restrict: PropTypes.oneOfType([PropTypes.func, LandingsPropTypes.restriction]),
});

export const BlockListProps = { item };

const runOrReturn = (functionOrValue, ...args) => {
    if (!(functionOrValue && typeof functionOrValue === 'function')) {
        return functionOrValue;
    }

    return functionOrValue(...args);
};

// todo: merge similar ui components and reduce prop variations between them
export const BlockListItemPreviewWithACL = (Tag) => {
    const Wrapper = ({ item, generateUrl }) => {
        const { to, icon, filter, restrict, ...rest } = item;
        const restriction = runOrReturn(restrict, item);

        return (
            <Tag
                tag={Link}
                to={(typeof to === 'string' && generateUrl?.(to)) || to}
                labelIcon={icon}
                restrict={restriction}
                {...rest}
            />
        );
    };
    Wrapper.propTypes = BlockListItemPreviewWithACL.propTypes;

    return Wrapper;
};

BlockListItemPreviewWithACL.propTypes = {
    item: BlockListProps.item.isRequired,
    generateUrl: PropTypes.func,
};
