import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { BsArrowRepeat, BsChevronUp } from 'react-icons/bs';
import { useFormikContext } from 'formik';

import { useDelay, useRestriction } from '@mssgme/hooks';
import { Swiper } from '../../../Swiper';
import { ThemingPropTypes } from '../../../../domainTypes';
import { StylingContext, useStylingAccessors, useThemeUnits } from '../../../../hooks';
import { PlanBadge } from '../../../PlanBadge';

import styles from './StyleCarousel.module.scss';

const calcProps = (field, base) => {
    return {
        ...base,
        style: {
            ...((base && base.style) || {}),
            opacity: field.disabled ? 0.4 : 1,
            pointerEvents: field.disabled ? 'none' : 'auto',
            textAlign: 'center',
        },
    };
};
const calcPreviewProps = (field, base) => {
    return {
        ...base,
        className: cn(field.className, base && base.className),
        style: {
            ...((base && base.style) || {}),
            textAlign: 'center',
        },
    };
};
const identityValue = (field) => field.value;
const nullableValue = (field, def = identityValue) => {
    const { previewValue, value, t } = field;

    if (previewValue) {
        return previewValue(field);
    }

    return value === null || value === undefined ? t('ui:labels.notSet') : def(field);
};
const ChoicesPreview = (field) => {
    const found = field.options.find((option) => option.value === field.value);
    const fieldProps = (found && found.props) || {};
    const label = found ? found.shortLabel || fieldProps.children || found.label : field.value;

    return <div {...calcPreviewProps(field, fieldProps)}>{nullableValue({ ...field, value: label })}</div>;
};
const IconPreview = (field) => {
    const found = field.options.find((option) => option.value === field.value);
    const fieldProps = (found && found.props) || {};
    const label = found ? found.shortLabel || fieldProps.children || found.label : field.value;

    return (
        <div
            {...calcPreviewProps(field, {
                ...fieldProps,
                className: cn(styles.iconField, found && found.rounded && styles.iconFieldRounded),
            })}
        >
            {nullableValue({ ...field, value: label })}
        </div>
    );
};
const ValuePreview = (field) => <div {...calcPreviewProps(field, { style: {} })}>{nullableValue(field)}</div>;
const UnitsValuePreview = (field) => {
    const units = useThemeUnits(field.path);
    const value = field.makeLabel
        ? field.makeLabel(field.value)
        : nullableValue(field, units ? ({ value }) => `${value}${units}` : identityValue);

    return <div {...calcPreviewProps(field, { style: {} })}>{value}</div>;
};
const TogglePreview = (field) => (
    <div {...calcPreviewProps(field)}>
        {nullableValue(field, ({ t, value }) => (value ? t('labels.on') : t('labels.off')))}
    </div>
);
const ListOrTogglePreview = (field) => (field.options.length > 1 ? ChoicesPreview(field) : TogglePreview(field));
const ColorPreview = (field) => (
    <div {...calcPreviewProps({ ...field, className: styles.colorField })}>
        <div className={styles.colorFieldColor} style={{ backgroundColor: field.value }} />
    </div>
);
const DefaultPreview = (field) => <div {...calcPreviewProps(field)}>{nullableValue(field)}</div>;

const PREVIEW_MAP = {
    default: DefaultPreview,
    number: ValuePreview,
    range: UnitsValuePreview,
    toggle: TogglePreview,
    select: ChoicesPreview,
    choice: ChoicesPreview,
    list: ChoicesPreview,
    listOrToggle: ListOrTogglePreview,
    color: ColorPreview,
    icon: IconPreview,
};

const extractNextOption = (field, accessors, maxCycle = 4) => {
    if (field.type === 'list' && field.options.length <= maxCycle) {
        const value = accessors.getter(field.path);
        const index = field.options.findIndex((option) => option.value === value);

        if (index >= 0) {
            const nextIndex = (index + 1) % field.options.length;

            return field.options[nextIndex];
        }
    }
};

const StylingFieldPreview = ({ accessors, field, onSelect }) => {
    const { t } = useTranslation('ui');
    const { warning, handleBlockedEvent } = useRestriction(field.restrict, field);
    const { values } = useFormikContext();

    const handleClick = useCallback(
        (e) => {
            if (handleBlockedEvent) {
                return handleBlockedEvent(e);
            }

            const next = extractNextOption(field, accessors);

            if (next) {
                return void accessors.setter(field.path, next.value);
            }

            onSelect(field);
        },
        [accessors, field, onSelect, handleBlockedEvent]
    );

    if (field.type === 'child') {
        return null;
    }

    const Preview = field.preview || PREVIEW_MAP[field.type] || PREVIEW_MAP.default;
    const value = accessors.getter(field.path);
    const disabled = field.if && !field.if(accessors.getter);
    const fieldProps = { ...field, disabled, value };
    const next = extractNextOption(field, accessors);

    const gtmProps = {
        'data-event': 'select-editor-block-styling-feature',
        'data-block-kind': values.kind,
        'data-block-styling-feature': field.path,
    };

    return (
        <div className={styles.field} onClick={handleClick} {...gtmProps} {...calcProps(fieldProps)}>
            <div className={styles.knob}>
                {warning?.target ? (
                    <PlanBadge plan={warning.target} className={styles.badge} />
                ) : next ? (
                    <BsArrowRepeat />
                ) : (
                    <BsChevronUp />
                )}
            </div>
            <div className={styles.preview}>
                <Preview t={t} {...fieldProps} calcProps={calcPreviewProps} />
            </div>
            <div className={styles.label}>{field.shortLabel || field.label}</div>
        </div>
    );
};

StylingFieldPreview.propTypes = {
    accessors: PropTypes.object.isRequired,
    field: ThemingPropTypes.styleInput.isRequired,
    onSelect: PropTypes.func.isRequired,
};

const StylingSection = ({ accessors, section: { label, styles: fields }, onSelect }) => {
    const [activeSlide, setSlide] = useState(0);
    const pagination = useMemo(
        () => ({ clickableClass: styles.clickablePagination, bulletActiveClass: styles.activeBullet }),
        []
    );
    const activeFields = useMemo(
        () => fields.filter((field) => field.type !== 'child' && !(field.if && !field.if(accessors.getter))),
        [fields, accessors.getter]
    );
    const fieldsCount = activeFields.length;
    const handleChange = useCallback((swiper) => setSlide(swiper.realIndex), []);

    /* todo: fix this gross workaround
        Swiper not initialized correctly if the component is inside suspense, that was paused mid-render
        Preloading block renderers doesn't fix the problem entirely, as the blocks themselves can have lazy-loaded children
     */
    const ready = useDelay(200);

    return (
        <div className={styles.section}>
            {label && label !== 'default' && <div className={styles.sectionLabel}>{label}</div>}

            <Swiper
                key={`${fieldsCount}-${ready}`}
                flex
                className={styles.fields}
                slideClass={styles.slide}
                perView={3}
                slidesPerGroup={1}
                height={fieldsCount > 3 ? 94 : 64}
                pagination={pagination}
                initialSlide={activeSlide}
                onSlideChange={handleChange}
            >
                {ready &&
                    activeFields.map((field) => (
                        <StylingFieldPreview key={field.path} field={field} accessors={accessors} onSelect={onSelect} />
                    ))}
            </Swiper>
        </div>
    );
};

StylingSection.propTypes = {
    accessors: PropTypes.object.isRequired,
    section: ThemingPropTypes.section.isRequired,
    onSelect: PropTypes.func.isRequired,
};

export const StyleCarousel = ({ onSelect, styling }) => {
    const accessors = useStylingAccessors(styling);

    return (
        <div className={styles.root}>
            <StylingContext.Provider value={accessors}>
                {styling.definition.sections.map((section) => (
                    <StylingSection key={section.label} section={section} accessors={accessors} onSelect={onSelect} />
                ))}
            </StylingContext.Provider>
        </div>
    );
};

StyleCarousel.propTypes = {
    styling: ThemingPropTypes.styling.isRequired,
    onSelect: PropTypes.func.isRequired,
};

StyleCarousel.defaultProps = {};
