import PropTypes from 'prop-types';
import classNames from 'classnames';
import moment from 'moment';
import React, { useEffect, useState, useRef } from 'react';
import { memoize } from 'lodash';
import { useTranslation } from 'react-i18next';
import { FiChevronLeft, FiChevronRight } from 'react-icons/fi';

import { useFetching } from '@mssgme/hooks';
import { Tab, Tabs } from '../Tabs';
import StatsList from './StatsList';
import styles from './Analytics.module.scss';

const DAY_TAB = 0;
const WEEK_TAB = 1;
const MONTH_TAB = 2;

const MONTH_MAX_INTERVAL = 2;

const TAB_DAY_MAP = {
    [DAY_TAB]: 'day',
    [WEEK_TAB]: 'week',
    [MONTH_TAB]: 'month',
};

const generateDateRange = memoize(
    (dateTime, interval) => {
        let from = dateTime.clone().startOf(interval);
        let to = dateTime.clone().endOf(interval);

        return { from, to };
    },
    (dateTime, interval) => `${+dateTime}:${interval}`
);

function getPreviousDate(dateTime, interval) {
    const { min } = getMinMaxDates(interval);
    let prev = dateTime.clone().subtract(1, interval);

    return prev > min ? prev : null;
}

function getNextDate(dateTime, interval) {
    const { max } = getMinMaxDates(interval);
    let next = dateTime.clone().add(1, interval);

    return next < max ? next : null;
}

const generateRanges = (tabIndex, reference = moment().set({ second: 0, millisecond: 0 })) => {
    const interval = TAB_DAY_MAP[tabIndex];
    const prev = getPreviousDate(reference, interval);
    const next = getNextDate(reference, interval);

    return {
        prev,
        next,
        prevRange: prev && generateDateRange(prev, interval),
        ...generateDateRange(reference, interval),
    };
};

function getMinMaxDates(interval) {
    let max = moment().endOf('day');
    let min = moment()
        .subtract(MONTH_MAX_INTERVAL, 'month')
        .startOf('month');

    if (interval !== 'month') min = min.startOf('week');

    return { min, max };
}

const NavButton = ({ disabled = false, ...rest }) => (
    <a href="#" className={classNames(styles.navButton, { [styles.disabled]: disabled })} {...rest} />
);

NavButton.propTypes = {
    disabled: PropTypes.bool,
};

const Header = (props) => <div className={styles.header} {...props} />;

export const Analytics = ({ groups, showDelta, getStats }) => {
    const { t } = useTranslation('ui');
    const [tabIndex, setTabIndex] = useState(WEEK_TAB);
    const [state, setState] = useState(() => generateRanges(tabIndex));

    const handlePrevNextClick = (dir) => (e) => {
        e.preventDefault();

        const reference = state[dir];

        if (reference) {
            setState(generateRanges(tabIndex, reference));
        }
    };

    const handleRange = (tabIndex) => {
        setTabIndex(tabIndex);
        setState(generateRanges(tabIndex));
    };

    const current = useFetching(({ from, to }) => getStats(+from, +to));
    const prev = useFetching(({ from, to }) => getStats(+from, +to));
    const [stats, setStats] = useState({ current: [], prev: [] });

    useEffect(() => {
        current.run(state);

        if (showDelta && state.prevRange) {
            prev.run(state.prevRange);
        }
    }, [state.from, state.to, showDelta]);

    useEffect(() => {
        if (current.result && current.result !== stats.current) {
            setStats((stats) => ({ ...stats, current: current.result }));
        }

        if (prev.result && prev.result !== stats.prev) {
            setStats((stats) => ({ ...stats, prev: prev.result }));
        }
    }, [stats, current.result, prev.result]);

    const filter = (group, stats) => (stats || []).filter((item) => item.action === group);
    const data = groups.map((group) => ({
        ...group,
        stats:
            showDelta && state.prevRange
                ? {
                    prev: filter(group.action, stats.prev),
                    current: filter(group.action, stats.current),
                }
                : filter(group.action, stats.current),
    }));

    return (
        <div>
            <Tabs selected={tabIndex} onSelect={handleRange}>
                <Tab>{t('Analytics.ranges.day')}</Tab>
                <Tab>{t('Analytics.ranges.week')}</Tab>
                <Tab>{t('Analytics.ranges.month')}</Tab>
            </Tabs>
            <Header>
                <NavButton disabled={!state.prev} onClick={handlePrevNextClick('prev')}>
                    <FiChevronLeft />
                </NavButton>
                <p>{t(tabIndex === DAY_TAB ? 'Analytics.rangeLabels.day' : 'Analytics.rangeLabels.range', state)}</p>
                <NavButton disabled={!state.next} onClick={handlePrevNextClick('next')}>
                    <FiChevronRight />
                </NavButton>
            </Header>

            {data.map(({ action, label, expandable, stats }) => (
                <StatsList key={action} expandable={expandable} stats={stats} heading={label} />
            ))}
        </div>
    );
};

Analytics.propTypes = {
    groups: PropTypes.arrayOf(
        PropTypes.shape({
            action: PropTypes.string.isRequired,
            label: PropTypes.string.isRequired,
            expandable: PropTypes.bool,
        }).isRequired
    ).isRequired,
    showDelta: PropTypes.bool,
    getStats: PropTypes.func.isRequired,
};
