import {
    HasAdvancedBlocksMessage,
    NoBlocksMessage,
    NotPublishedMessage,
    PublishedMessage,
    SharePublished,
} from './Page';
import { bioShareRouting, publishNameRouting } from './ShareRouting';

export const publishRouting = {
    path: 'publish',
    children: [
        { path: 'success', component: PublishedMessage },
        {
            path: 'published',
            component: SharePublished,
            exact: false,
            children: [
                bioShareRouting,
                publishNameRouting,
            ],
        },
        { path: 'not-published', component: NotPublishedMessage },
        { path: 'no-blocks', component: NoBlocksMessage },
        { path: 'has-advanced-blocks', component: HasAdvancedBlocksMessage },
    ],
};
