import React from 'react';
import { useTranslation } from 'react-i18next';

import { prettifyUrl } from '@mssgme/shared';
import { useNavigationUtils, useWebsitePageBySlugs } from '@mssgme/hooks';
import { TextField } from '@mssgme/ui';
import { PageEditForm } from '../../../../UI';
import { useWebsiteUrl } from '../../../../hooks';

export function PageSlugEdit() {
    const { t } = useTranslation('landings');
    const { generateUrl } = useNavigationUtils();
    const { website, page } = useWebsitePageBySlugs();
    const websiteUrl = useWebsiteUrl(website);

    return (
        <PageEditForm title={t('Page.Edit.Slug.heading')} website={website} page={page} backPath={generateUrl('..')}>
            <TextField
                builderInput
                name="slug"
                beforeInput={prettifyUrl(websiteUrl) + '/'}
                placeholder={t('Page.fields.slug.placeholder')}
                maxLength={50}
                autoCapitalize="off"
                controlProps={{ style: { textTransform: 'lowercase' } }}
            />
        </PageEditForm>
    );
}
