import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { __t } from '@mssgme/shared';
import { useEventCallback } from '@mssgme/hooks';
import { DynamicFormField, TextareaField, ToggleField } from '@mssgme/ui';
import { isValidAction, SmartLinkField } from '@mssgme/admin-ui';

import styles from './ShopcartSettingsForm.module.scss';

export const ShopcartSettingsForm = () => {
    const { t } = useTranslation('products');
    const [{ value }, , { setValue }] = useField('form.successURL.action');
    const [showAction, setShowAction] = useState(() => isValidAction(value));

    const handleToggle = useEventCallback((_, show) => {
        if (show) {
            setValue({
                ...value,
                type: 'url',
                url: '',
                page: null,
                anchor: {
                    page: null,
                    block: null,
                },
            });
        }

        setShowAction(show);
    });

    return (
        <>
            <div className={styles.section}>
                <p className={styles.sectionTitle}>{t('ProductsOnboarding.labels.sections.form')}</p>

                <DynamicFormField name="form.fields" />
            </div>

            <div className={styles.section}>
                <p className={styles.sectionTitle}>{t('ProductsOnboarding.labels.sections.successScreen')}</p>

                <TextareaField
                    minRows={3}
                    maxLength={5000}
                    builderInput
                    name="form.successMessage"
                    label={t('ProductsOnboarding.labels.successMessage.label')}
                    placeholder={__t('payment_paid_subtitle')}
                />

                <ToggleField
                    builderInput
                    name="form.successURL.show"
                    label={t('ProductsOnboarding.labels.successUrl.toggleLabel')}
                    value={showAction}
                    onChange={handleToggle}
                    className={styles.margin}
                />

                {showAction && (
                    <SmartLinkField
                        extended={false}
                        titleName="form.successURL.title"
                        actionName="form.successURL.action"
                        actionLabel={t('ProductsOnboarding.labels.successUrl.actionLabel')}
                        titleLabel={t('ProductsOnboarding.labels.successUrl.titleLabel')}
                        placeholder={t('ProductsOnboarding.labels.successUrl.placeholder')}
                    />
                )}
            </div>
        </>
    );
};
