import { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';

import { useFetch } from './useFetch';
import { useActions } from './useActions';
import { useFetching } from './useFetching';

export function useTemplates(autoLoad = true) {
    const [isPending, setPending] = useState(autoLoad);
    const { items: templates } = useSelector((state) => state.templates);
    const { api } = useActions();
    const { pending, run: fetch } = useFetching(async () => {
        try {
            return await api.pageTemplates.list();
        } finally {
            setPending(false);
        }
    }, []);

    useEffect(() => {
        if (autoLoad) {
            void fetch();
        }
    }, [autoLoad]);

    return {
        pending: pending || isPending,
        fetch,
        templates,
    };
}

export function useTemplate(id) {
    const { api } = useActions();
    const { templates } = useTemplates(false);
    const found = templates.find(({ _id }) => _id === id);
    const [template, setTemplate] = useState(found);

    useFetch(async () => {
        if (!template) {
            const result = await api.pageTemplates.retrieve({ templateId: id });

            setTemplate(result);
        }
    }, [id]);

    useEffect(() => {
        if (found && found !== template) {
            setTemplate(found);
        }
    }, [found]);

    return template;
}
