import React, { useContext } from 'react';
import { CRM_IDENTITY } from '@mssgme/userpage-core';

export const websiteFactory = (page, props) => ({
    _id: '',
    homePageId: page?._id || null,
    workspace: '',
    slug: '',
    finalUrl: '',
    collections: [],
    uploads: [],
    theme: {},
    seo: {},
    crm: CRM_IDENTITY,
    name: page?.name || '',
    pages: page ? [page] : [],
    ...props,
});

export const WebsiteEditorContext = React.createContext({
    websiteId: null,
    pageId: null,
    blockId: null,
});

export const useWebsiteEditor = () => {
    return useContext(WebsiteEditorContext);
};
