import { useFetch } from '@mssgme/hooks';
import { toDashedCase } from '@mssgme/helpers';
import {
    AccordionEdit,
    AudioEdit,
    CustomCodeEdit,
    CustomEdit,
    DividerEdit,
    FeaturesEdit,
    GiphyEdit,
    HatEdit,
    HeadingEdit,
    ImageEdit,
    ImageGalleryEdit,
    LinkEdit,
    ListEdit,
    MessengersEdit,
    ProductsEdit,
    QuoteEdit,
    ServicesGridEdit,
    SocialsEdit,
    SubscriptionFormEdit,
    TextEdit,
    TextRichEdit,
    TimerEdit,
    VideoEdit,
    VideoGalleryEdit,
} from './Blocks';

export const EDITORS_MAP = {
    [TextRichEdit.block.kind]: TextRichEdit,
    [TimerEdit.block.kind]: TimerEdit,
    [GiphyEdit.block.kind]: GiphyEdit,
    [CustomCodeEdit.block.kind]: CustomCodeEdit,
    [SubscriptionFormEdit.block.kind]: SubscriptionFormEdit,
    [CustomEdit.block.kind]: CustomEdit,
    [ServicesGridEdit.block.kind]: ServicesGridEdit,
    [ProductsEdit.block.kind]: ProductsEdit,
    [SocialsEdit.block.kind]: SocialsEdit,
    [QuoteEdit.block.kind]: QuoteEdit,
    [ListEdit.block.kind]: ListEdit,
    [AudioEdit.block.kind]: AudioEdit,
    [VideoGalleryEdit.block.kind]: VideoGalleryEdit,
    [FeaturesEdit.block.kind]: FeaturesEdit,
    [AccordionEdit.block.kind]: AccordionEdit,
    [ImageGalleryEdit.block.kind]: ImageGalleryEdit,
    [DividerEdit.block.kind]: DividerEdit,
    [HatEdit.block.kind]: HatEdit,
    [HeadingEdit.block.kind]: HeadingEdit,
    [ImageEdit.block.kind]: ImageEdit,
    [MessengersEdit.block.kind]: MessengersEdit,
    [LinkEdit.block.kind]: LinkEdit,
    [TextEdit.block.kind]: TextEdit,
    [VideoEdit.block.kind]: VideoEdit,
};

export const EDITOR_ROUTES = Object.entries(EDITORS_MAP).map(([key, value]) => [toDashedCase(key), value]);

export const useEditorsPreload = () => {
    const { fulfilled } = useFetch(
        async () => Promise.all(Object.values(EDITORS_MAP).map(({ block }) => block.preload?.()))
    );

    return fulfilled;
};
