import { useMemo } from 'react';
import { usePrevious } from 'react-use';

/**
 * Prevent selection rects flickering/resetting/displacement on key changes by pooling selection ranges. Effectively
 * breaks <block id> -> <block id as selection range id> dependency and
 * provides a <block id> -> <pool bucket id> mapping,
 * where <pool bucket id> is a pooled numerical incremental value.
 *
 * Only needed because selection ranges are CSS animated,
 * so there must be deterministic <selection range> -> <dom node key id>
 * mapping to prevent animation glitches on selection changes.
 *
 * @returns {{
 *   size: number;
 *   take(string): (*);
 *   release(string[]): void;
 *   update(string[], function(string): (*)): *[];
 * }} Ranges pool
 */
const makeRangesPool = () => {
    let items = [];
    let UID = 0;

    return {
        size: 0,
        /**
         * Get pool bucket for the provided id
         *
         * @param {string} id Bucked associated ID
         * @returns {{item: null, id: string, key: number}} Pool bucket
         */
        take(id) {
            const has = items.find((i) => i.id === id);

            if (has) {
                // return existing bucket if it is already in the pool;
                return has;
            }

            if (this.size < items.length) {
                // if pool have unused bucket, return it
                const bucket = items[this.size++];
                bucket.id = id;

                return bucket;
            }

            // if pool is already entirely allocated, create new bucket
            const bucket = {
                key: ++UID,
                id,
                item: null,
            };
            items.push(bucket);
            this.size++;

            return bucket;
        },
        /**
         * Release a list of bucket id's back into the pool.
         *
         * @param {string[]} ids Ids to release
         */
        release(ids) {
            const keep = [];
            const release = [];

            for (const bucket of items) {
                if (ids.includes(bucket.id)) {
                    release.push(bucket);
                } else {
                    keep.push(bucket);
                }
            }

            items = [...keep, ...release];
            this.size = keep.length;
        },

        /**
         * @param {string[]} ids List of bucket ids to update
         * @param {function(string): *} mapper Transform method used to update bucket item with
         * @returns {Array<{ id: string; key: number; item: * }>} List of updated buckets
         */
        update(ids, mapper) {
            const result = [];

            for (const id of ids) {
                const item = mapper(id);
                const bucket = this.take(id);

                bucket.item = item;
                result.push(bucket);
            }

            return result;
        },
    };
};

export const usePooledSelectionRanges = (ranges) => {
    const pool = useMemo(() => makeRangesPool(), []);
    const prev = usePrevious(ranges);

    return useMemo(() => {
        const map = new Map(ranges.map((item) => [item.id, item]));
        const removed = prev?.map(({ id }) => id).filter((id) => !map.has(id)) || [];
        const items = pool.update([...map.keys()], (id) => map.get(id));

        pool.release(removed);

        return items;
    }, [ranges]);
};
