import React, { useCallback, useContext, useMemo } from 'react';
import { get, isEqual } from 'lodash';
import { useFormikContext } from 'formik';

import { isSameArray } from '@mssgme/helpers';
import { useTheme } from './useTheme';

const noop = {};
const itemPath = (parent, item) => (parent && item ? `${parent}.${item}` : parent || item);

export const StylingContext = React.createContext(undefined);

export function useThemeUnits(fullFieldPath, def = '') {
    const { styling, stylesPath } = useContext(StylingContext);
    const theme = useTheme();
    const relative = fullFieldPath.replace(`${stylesPath}.`, '');

    return get(theme, `${styling.definition.path}.units.${relative}`, def);
}

export function useStylingAccessors(styling) {
    const {
        propPath,
        enrich,
        definition: { path, theme },
    } = styling;
    const { values, setFieldValue, setFieldTouched } = useFormikContext();

    const stylesPath = itemPath(propPath, path);
    const customTheme = get(values, propPath, noop);
    const enriched = useMemo(() => enrich(customTheme), [customTheme]);
    const getter = useCallback(
        (prop) => {
            const valuesPath = itemPath(path, prop);

            return get(enriched, valuesPath, get(theme, valuesPath));
        },
        [path, propPath, enriched, theme]
    );
    const setter = useCallback(
        (prop, value, touched) => {
            const valuesPath = itemPath(path, prop);
            const fullPath = itemPath(propPath, valuesPath);

            setFieldValue(fullPath, value);

            if (touched === undefined) {
                const initial = get(enriched, valuesPath, get(theme, valuesPath));
                const isArray = initial && value && Array.isArray(initial);
                const isSame = isArray ? isSameArray(initial, value, { isEqual }) : isEqual(initial, value);

                setFieldTouched(fullPath, !isSame);
            } else {
                setFieldTouched(fullPath, touched, touched);
            }
        },
        [path, propPath, enriched, theme]
    );

    return useMemo(() => ({ styling, stylesPath, getter, setter }), [styling, stylesPath, getter, setter]);
}
