import React from 'react';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { FiChevronRight, FiSettings } from 'react-icons/fi';
import { AnimatePresence, motion } from 'framer-motion';

import { LayersIcon } from '@mssgme/icons';
import { useExperiments } from '@mssgme/hooks';
import { Button, FloatingActions, IconButton, ScrollableBox, SearchInput, StreamlinedScreen } from '@mssgme/ui';
import { useCollectionsMain, useProductsContext, useProductsMain } from '../../../hooks';
import { ProductsList, ProductsListFooter } from '../../../UI';

import styles from './ProductsHome.module.scss';

export function ProductsHome() {
    const { t } = useTranslation('products');
    const { isShowingCRMCogs } = useExperiments();
    const { router, isSpecific, specifier, modal } = useProductsContext();
    const { collections, collectionId } = useCollectionsMain(specifier, (col) => col.default);
    const { products, setFilter, loadMore } = useProductsMain(specifier, collectionId);

    const activeCollections = collections.items.filter((col) => col.published && col.publishedProductsCount > 0);
    // const multiCollection = collections.items.length > 1;
    // const totalPublishedProducts = collections.items.reduce(
    //     (acc, col) => acc + (col.default && multiCollection ? 0 : col.publishedProductsCount),
    //     0
    // );

    return (
        <StreamlinedScreen
            title={t('pageProducts.header')}
            rightButton={
                isShowingCRMCogs && (<div className={styles.headerButtons}>
                    <IconButton to={router.settings.toSettings()} as={Link}>
                        <FiSettings size={24} color="#333" />
                    </IconButton>
                </div>)
            }
            topPanelChildren={<SearchInput name="name" value={products.filter.name} onChange={setFilter} />}
            backPath={router.toEntry()}
            modal={modal}
        >
            <AnimatePresence>
                {!products.filter.name && (
                    <motion.div
                        initial={{ height: 0, opacity: 0, overflow: 'hidden' }}
                        animate={{ height: 'auto', opacity: 1 }}
                        exit={{ height: 0, opacity: 0 }}
                        transition={{ stiffness: 1 }}
                    >
                        <StreamlinedScreen.Section>
                            <Link to={router.collection.toAll()} className={styles.navItem}>
                                <span className={styles.navItemLabel}>
                                    <LayersIcon size={24} />
                                    {t('ProductsHome.collections')}
                                </span>
                                <div className={styles.navItemMeta}>
                                    <span className={styles.navItemMetaText}>
                                        {t('labels.active', { count: activeCollections.length })}
                                    </span>
                                    <FiChevronRight size={22} className={styles.navItemIcon} />
                                </div>
                            </Link>
                        </StreamlinedScreen.Section>

                        {/*<div className={styles.storeFrontHeader}>*/}
                        {/*    <span className={styles.storeFrontHeaderHeading}>{t('labels.allProducts')}</span>*/}
                        {/*    <span className={styles.storeFrontHeaderCounter}>*/}
                        {/*        {t('labels.products', { count: totalPublishedProducts })}*/}
                        {/*    </span>*/}
                        {/*</div>*/}
                    </motion.div>
                )}
            </AnimatePresence>
            <ScrollableBox nopadding style={{ flex: 1, marginTop: 8, background: '#fff' }}>
                <StreamlinedScreen.Section
                    nopadding
                    style={{
                        padding: 0,
                        paddingBottom: products.next && loadMore ? 16 : 0,
                        margin: products.items.length ? 0 : 'auto',
                    }}
                >
                    <ProductsList products={products.items} />
                    <ProductsListFooter products={products} loadMore={loadMore} />
                </StreamlinedScreen.Section>
            </ScrollableBox>

            {isSpecific && (
                <FloatingActions padded style={{ marginTop: 8 }}>
                    <Button fullWidth tag={Link} to={router.product.toNew()}>
                        + {t('pageProducts.tipAddProduct')}
                    </Button>
                </FloatingActions>
            )}
        </StreamlinedScreen>
    );
}
