import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useRef, useState } from 'react';
import { createPortal } from 'react-dom';
import { useClickAway } from 'react-use';
import { $getSelection, COMMAND_PRIORITY_LOW, SELECTION_CHANGE_COMMAND } from 'lexical';
import { TOGGLE_LINK_COMMAND } from '@lexical/link';
import { mergeRegister } from '@lexical/utils';
import { useLexicalComposerContext } from '@lexical/react/LexicalComposerContext';

import { EditIcon } from '@mssgme/icons';
import { URLInput } from '../../../../Inputs';
import { getLinkNode, placeFloatingElem, placeFloatingSelectionElem } from '../utils';

import styles from './FloatingLinkEditor.module.scss';

/**
 * @param {object} props Component props
 * @param {Element} [props.anchorElem=document.body] Element to anchor the editor to
 * @returns {JSX.Element} Floating panel
 * @class FloatingLinkEditor Floating panel Renderer
 */
export function FloatingLinkEditor({ anchorElem = document.body }) {
    /** @type {React.MutableRefObject<HTMLElement>} */
    const editorRef = useRef(null);
    /** @type {React.MutableRefObject<HTMLElement>} */
    const buttonRef = useRef(null);
    const selectionRef = useRef(null);
    const [linkUrl, setLinkUrl] = useState('');
    const [editor] = useLexicalComposerContext();

    useClickAway(
        editorRef,
        () => {
            const editorElem = editorRef.current;

            if (!editorElem) {
                return;
            }

            placeFloatingElem(editorElem, false);
            setLinkUrl('');
            selectionRef.current = null;
        },
        ['mousedown', 'touchstart', 'focus']
    );

    const updateLinkEditor = useCallback(() => {
        const editorElem = editorRef.current;

        if (!editorElem) {
            return false;
        }

        const selection = $getSelection();

        if (selection) {
            const linkNode = getLinkNode(selection);

            if (linkNode) {
                setLinkUrl(linkNode.getURL());

                if (placeFloatingSelectionElem(editor, editorElem, anchorElem, true)) {
                    selectionRef.current = selection;

                    return true;
                }
            }
        }

        const activeElement = document.activeElement;

        if (!activeElement?.classList.contains(styles.linkInput)) {
            placeFloatingElem(editorElem, false);
            setLinkUrl('');
            selectionRef.current = null;
        }

        return true;
    }, [editor]);

    const handleChange = useCallback((event) => setLinkUrl(event.target.value), []);

    const handleInputKeyDown = useCallback(
        (event) => {
            if (event.key !== 'Enter' && event.key !== 'Escape') {
                return;
            }

            event.preventDefault();

            if (event.key === 'Enter') {
                if (selectionRef.current && linkUrl) {
                    editor.dispatchCommand(TOGGLE_LINK_COMMAND, linkUrl);
                }
            }

            editorRef.current?.focus();
        },
        [linkUrl, editor]
    );

    const handleEditorKeyDown = useCallback(
        (event) => {
            if (editorRef.current !== event.target) {
                return;
            }

            switch (event.key) {
                case 'Enter':
                    buttonRef.current?.focus();
                    break;
                case 'Escape':
                    break;
                default:
                    editor.focus();
                    return;
            }

            event.stopPropagation();
            event.preventDefault();
        },
        [editor]
    );

    const handleBlur = useCallback(({ target }) => {
        const editorElem = editorRef.current;

        if (editorElem.contains(target)) {
            return;
        }

        placeFloatingElem(editorElem, false);
        setLinkUrl('');
        selectionRef.current = null;
    }, []);

    useEffect(() => {
        editor.getEditorState().read(updateLinkEditor);

        return mergeRegister(
            editor.registerUpdateListener(({ editorState }) => editorState.read(updateLinkEditor)),
            editor.registerCommand(SELECTION_CHANGE_COMMAND, updateLinkEditor, COMMAND_PRIORITY_LOW)
        );
    }, [editor, updateLinkEditor]);

    return createPortal(
        <div
            ref={editorRef}
            className={styles.linkEditor}
            tabIndex={0}
            onKeyDown={handleEditorKeyDown}
            onBlur={handleBlur}
        >
            <URLInput
                containerClassName={styles.linkInput}
                autoFocus
                active
                pretty={false}
                value={linkUrl}
                onChange={handleChange}
                onKeyDown={handleInputKeyDown}
                afterView={
                    <div ref={buttonRef} className={styles.linkEdit} role="button" tabIndex={0}>
                        <EditIcon size={18} />
                    </div>
                }
            />
        </div>,
        document.body
    );
}

FloatingLinkEditor.propTypes = {
    anchorElem: PropTypes.element,
};
