import React from 'react';
import { useSelector } from 'react-redux';
import { Link, Redirect } from 'react-router-dom';
import { useTranslation, Trans } from 'react-i18next';
import { CheckCircleIcon } from '@mssgme/icons';
import { SecondLevelScreen, Spinner } from '@mssgme/ui';
import { useNavigationUtils } from '@mssgme/hooks';
import { CHECKOUT_STATUSES } from '@mssgme/store';

import styles from './CheckoutCompleteSuccessfullyPage.module.scss';

export default function CheckoutCompleteSuccessfullyPage() {
    const { t } = useTranslation('billing');
    const { generateUrl } = useNavigationUtils();
    const status = useSelector((state) => state.checkout.status);
    const subscription = useSelector((state) => state.billingDetails.result?.subscription);

    let content;
    switch (status) {
        case CHECKOUT_STATUSES.applied_plan:
            {
                const {
                    plan,
                    nextPayment,
                    trialEndedAt,
                    status,
                } = subscription; // subscription always available on this status
                content = (
                    <div className={styles.text}>
                        <p>
                            <Trans t={t} i18nKey="CheckoutCompleteSuccessfullyPage.new_plan_applied">
                                The plan <strong>{{ plan: plan.name }}</strong> was applied
                            </Trans>
                            {status === 'trialing' && trialEndedAt && nextPayment && (
                                <>
                                    <br />
                                    <Trans t={t} i18nKey="CheckoutCompleteSuccessfullyPage.trial">
                                        Trial till <strong>{{ date: new Date(trialEndedAt) }}</strong>. In that date you
                                        will be charged <strong>{{ price: nextPayment.price }}</strong>
                                    </Trans>
                                </>
                            )}
                        </p>
                        <p>
                            <Trans t={t} i18nKey="CheckoutCompleteSuccessfullyPage.manage_billing">
                                You can manage you subscription on <Link to={generateUrl('..')}>billing page</Link>
                            </Trans>
                        </p>
                        <p>
                            <Trans t={t} i18nKey="CheckoutCompleteSuccessfullyPage.go_home">
                                Now you can go <Link to="/">home</Link>
                            </Trans>
                        </p>
                    </div>
                );
            }
            break;
        case CHECKOUT_STATUSES.complete_payment_successfully:
            content = (
                <>
                    <div className={styles.text}>{t('CheckoutCompleteSuccessfullyPage.applying_new_plan')}</div>
                    <Spinner />
                </>
            );
            break;
        case CHECKOUT_STATUSES.pending_review:
            content = <div className={styles.text}>{t('CheckoutCompleteSuccessfullyPage.pending_review')}</div>;
            break;
        default:
            return <Redirect to="/" />;
    }

    return (
        <SecondLevelScreen light preferDynamicBack backPath={generateUrl('..')}>
            <h1 className={styles.heading}>
                <CheckCircleIcon className={styles.icon} /> {t('CheckoutCompleteSuccessfullyPage.title')}
            </h1>
            {content}
        </SecondLevelScreen>
    );
}
