import PropTypes from 'prop-types';
import React, { useCallback, useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import { useToggle } from 'react-use';
import { useHistory } from 'react-router-dom';
import { merge } from 'lodash';

import { clearWebsitePayload, useActions, useRestriction } from '@mssgme/hooks';
import {
    EnhancedFormik,
    Form,
    LandingsPropTypes,
    ModalEditPane,
    renderNode,
    SaveButton,
    usePresenceRemoval,
} from '@mssgme/ui';

const DEFAULT_INITIAL_VALUES = {
    enabled: false,
    name: '',
    slug: '',
    seo: {
        faviconUrl: null,
        ogImageUrl: null,
        title: '',
        description: '',
    },
    tracking: {
        facebook: '',
        google: '',
        yandex: '',
        tiktok: '',
    },
    menu: {
        enabled: true,
        items: [],
    },
    branding: true,
    embeddedCode: {
        content: '',
        onLoad: false,
        inHead: false,
        fit: true,
    },
    localization: {},
};

export function WebsiteEditForm({
    initialValues: initialValuesRaw,
    website,
    templateId,
    children,
    title,
    restrictRule,
    backPath,
    successRedirectPath,
    saveButtonTitle,
    noGuardChanges,
    initialEnabledSubmitButton,
    hint,
    ...rest
}) {
    const isNew = !website._id;
    const history = useHistory();
    const initialValues = useMemo(
        () => merge({}, DEFAULT_INITIAL_VALUES, clearWebsitePayload(website), initialValuesRaw),
        [website]
    );
    const { t } = useTranslation('landings');
    const { api } = useActions();
    const { handleBlockedEvent } = useRestriction(restrictRule, website || initialValues);
    const [isClosing, setClosing] = useToggle(false);

    const handleSubmit = useCallback(
        async (payload) => {
            let redirect = successRedirectPath || backPath;

            if (isNew) {
                const result = await api.websites.create({ payload });

                redirect = {
                    pathname: `/pages/-/${result._id}/edit`,
                    state: {
                        templateId,
                    },
                };
            } else {
                await api.websites.update({ websiteId: website._id, payload });
            }

            // Notification.notify(isNew ? t('messages.pageCreated') : t('ui:labels.saved'), {
            //     showClose: true,
            // });

            setClosing(true);

            if (typeof redirect === 'function') {
                redirect();
            } else {
                setTimeout(() => history.push(redirect), 200);
            }
        },
        [website, templateId, backPath, successRedirectPath]
    );

    usePresenceRemoval();

    return (
        <EnhancedFormik initialValues={initialValues} onSubmit={handleSubmit} {...rest}>
            {(form) => (
                <ModalEditPane
                    guardChanges={noGuardChanges ? false : !(isClosing || handleBlockedEvent)}
                    onClose={backPath}
                    title={title}
                >
                    <Form autoComplete="off">
                        {renderNode(children, form)}

                        <SaveButton
                            fullWidth
                            size="standart"
                            style={{ marginTop: 16 }}
                            isInitialHidden={false}
                            disabled={
                                isClosing || (initialEnabledSubmitButton ? false : !form.dirty)
                            }
                            isFetching={form.isSubmitting}
                            onClick={handleBlockedEvent}
                        >
                            {saveButtonTitle || (isNew ? t('labels.website.create') : t('ui:labels.apply'))}
                        </SaveButton>

                        {hint}
                    </Form>
                </ModalEditPane>
            )}
        </EnhancedFormik>
    );
}

WebsiteEditForm.propTypes = {
    website: LandingsPropTypes.partials.website.isRequired,
    children: PropTypes.any.isRequired,
    title: PropTypes.string.isRequired,
    templateId: PropTypes.string,
    restrictRule: PropTypes.string,
    hint: PropTypes.any,
    backPath: PropTypes.any.isRequired,
    successRedirectPath: PropTypes.any,
    initialValues: PropTypes.object,
    saveButtonTitle: PropTypes.string,
    noGuardChanges: PropTypes.bool,
    initialEnabledSubmitButton: PropTypes.bool,
};
