import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useMemo } from 'react';

import { useSelectionRanges } from './useSelectionRanges';
import { usePooledSelectionRanges } from './usePooledSelectionRanges';
import { useWidgetFix } from './useWidgetFix';

import styles from './SelectionRanges.module.scss';

export const SelectionRanges = ({ blocks, active, offset }) => {
    const ranges = useSelectionRanges(blocks, [0, 2]);
    // to test how selection is rendered without pooling, comment usePooledSelectionRanges line,
    // use `ranges` in the iterator, and use `id` prop as the dom node key instead of the `key` prop.
    // Using range index as a key yields even worse results.
    const items = usePooledSelectionRanges(ranges);
    const style = useMemo(() => ({ transform: `translate3d(${-offset.left}px, ${-offset.top}px, 0px)` }), [
        offset.left,
        offset.top,
    ]);

    useWidgetFix(!!items.length);

    return (
        <div className={cn(styles.selectionRanges, !active && styles.disabled)} style={style}>
            {items.map(({ key, item: { area } }) => (
                <div key={key} className={styles.selectionArea} style={area} />
            ))}
        </div>
    );
};

SelectionRanges.propTypes = {
    blocks: PropTypes.arrayOf(PropTypes.object.isRequired).isRequired,
    active: PropTypes.bool,
    offset: PropTypes.shape({ left: PropTypes.number, top: PropTypes.number }).isRequired,
};
