import { useEffect } from 'react';
import { applyElementStyles } from '@mssgme/hooks';

export const useViewportStyles = (style) => {
    useEffect(() => {
        if (!style) {
            return;
        }

        const root = document.querySelector('#mainRoot');
        const parent = root.parentNode;

        return applyElementStyles(
            [document.body, style],
            [root, { width: '100%', height: '100%', borderRadius: 0 }],
            [parent, { padding: 0 }]
        );
    }, [style]);
};
