import cn from 'classnames';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import { ChevronDownIcon, ChevronUpIcon } from '@mssgme/icons';
import { useModal, Fade, StatusBadge } from '@mssgme/ui';

import styles from './DropdownStatus.module.scss';

export const StatusOption = ({ value, label, active, onSelect }) => {
    const handleClick = useCallback(() => onSelect(value), [value, onSelect]);

    return (
        <div className={cn(styles.option, active && styles.active)} onClick={handleClick}>
            <StatusBadge status={value} /> {label}
        </div>
    );
};

StatusOption.propTypes = {
    value: PropTypes.string.isRequired,
    label: PropTypes.string.isRequired,
    active: PropTypes.bool,
    onSelect: PropTypes.func.isRequired,
};

export const DropdownStatus = ({ value, disabled, className, onChange, onToggle }) => {
    const { t } = useTranslation('landings');
    const options = useMemo(
        () => [
            { value: 'new', label: t('Feedback.View.List.labels.state.new') },
            { value: 'processing', label: t('Feedback.View.List.labels.state.processing') },
            { value: 'accepted', label: t('Feedback.View.List.labels.state.accepted') },
            { value: 'rejected', label: t('Feedback.View.List.labels.state.rejected') },
        ],
        [t]
    );
    const { isOpen, toggle, hide } = useModal();
    const currentOption = options.find((o) => o.value === value);

    useEffect(() => void (onToggle && onToggle(isOpen)), [isOpen]);

    const handleSelect = useCallback(
        (v) => {
            onChange(v);
            hide();
        },
        [onChange]
    );

    return (
        <div className={cn(styles.root, styles[value], disabled && styles.disabled, className)}>
            <div className={styles.toggle} onClick={toggle}>
                <span className={styles.label}>{currentOption?.label || ''}</span>
                {isOpen ? <ChevronUpIcon size={10} /> : <ChevronDownIcon size={10} />}
            </div>
            <Fade duration={200} in={isOpen}>
                <ul className={styles.menu}>
                    {options.map((option, key) => (
                        <StatusOption key={key} active={option.value === value} onSelect={handleSelect} {...option} />
                    ))}
                </ul>
            </Fade>
        </div>
    );
};

DropdownStatus.propTypes = {
    value: PropTypes.string.isRequired,
    disabled: PropTypes.bool,
    className: PropTypes.string,
    onChange: PropTypes.func.isRequired,
    onToggle: PropTypes.func,
};
